
import * as React from "react";
import { PivotViewComponent, VirtualScroll, Inject } from "@syncfusion/ej2-react-pivotview";
import { SampleBase } from "./sample-base";
import "./index.scss";

let dataSourceSettings = {
  catalog: 'Adventure Works DW 2008 SE',
  cube: 'Adventure Works',
  providerType: 'SSAS',
  url: 'https://bi.syncfusion.com/olap/msmdpump.dll',
  
  enableSorting: true,
  columns: [
    { name: '[Product].[Product Categories]', caption: 'Product Categories' },
    { name: '[Measures]', caption: 'Measures' }
  ],
  valueSortSettings: { headerDelimiter: ' - ' },
  values: [
    { name: '[Measures].[Customer Count]', caption: 'Customer Count' },
    { name: '[Measures].[Internet Sales Amount]', caption: 'Internet Sales Amount' }
  ],
  rows: [
    { name: '[Customer].[Customer Geography]', caption: 'Customer Geography' }
  ],
  formatSettings: [],
  expandAll: true,
  filters: [{ name: '[Date].[Fiscal]', caption: 'Date Fiscal' }],
  filterSettings: [
    {
      name: '[Date].[Fiscal]',
      items: ['[Date].[Fiscal].[Fiscal Quarter].&[2002]&[4]', '[Date].[Fiscal].[Fiscal Year].&[2005]'],
      levelCount: 3
    }
  ]
};

export default class FieldList extends SampleBase {
  render() {
    return (
      <div className="pivot-view-solo">
        <PivotViewComponent
          id='PivotView'
          dataSourceSettings={dataSourceSettings}
          width={'100%'}
          height={'646'}
          gridSettings={{ columnWidth: 120 }}
        >
          <Inject
              services={[
                VirtualScroll
              ]}
            />
        </PivotViewComponent>
      </div>
    )
  }
}