import React, { useLayoutEffect } from 'react';
import * as am4core from "@amcharts/amcharts4/core";
import * as am4charts from "@amcharts/amcharts4/charts";
import am4themes_animated from "@amcharts/amcharts4/themes/animated";

am4core.useTheme(am4themes_animated);


const WaterfallChart = (props) => {
  const { id, data, isAnimationEnabled } = props

  useLayoutEffect(() => {
    let chart = am4core.create(id, am4charts.XYChart);

    // this makes initial fade in effect
    chart.hiddenState.properties.opacity = 0;
    chart.width = am4core.percent(100);
    chart.height = am4core.percent(100);

    chart.paddingRight = 0;
    chart.paddingLeft = 0;
    chart.paddingTop = 60;
    chart.paddingBottom = 0;
    chart.data = data;

    let categoryAxis = chart.xAxes.push(new am4charts.CategoryAxis());
    categoryAxis.dataFields.category = "category";
    categoryAxis.renderer.minGridDistance = 50;
    categoryAxis.renderer.grid.template.strokeWidth = 0;
    categoryAxis.renderer.grid.template.location = 0;


    let valueAxis = chart.yAxes.push(new am4charts.ValueAxis());
    valueAxis.renderer.grid.template.strokeWidth = 0.5;

    let columnSeries = chart.series.push(new am4charts.ColumnSeries());
    columnSeries.dataFields.categoryX = "category";
    columnSeries.dataFields.valueY = "value";
    columnSeries.dataFields.openValueY = "open";
    columnSeries.fillOpacity = 1;
    columnSeries.sequencedInterpolation = true;
    columnSeries.interpolationDuration = 1500;
    columnSeries.hiddenState.transitionDuration = 0;
    columnSeries.defaultState.transitionDuration = isAnimationEnabled ? 1000 : 0;
    
    let columnTemplate = columnSeries.columns.template;
    columnTemplate.strokeOpacity = 0;
    columnTemplate.propertyFields.fill = "color";


    let stepSeries = chart.series.push(new am4charts.StepLineSeries());
    stepSeries.dataFields.categoryX = "category";
    stepSeries.dataFields.valueY = "stepValue";
    stepSeries.noRisers = true;
    stepSeries.stroke = new am4core.InterfaceColorSet().getFor("alternativeBackground");
    stepSeries.strokeDasharray = "3,3";
    stepSeries.interpolationDuration = 2000;
    stepSeries.sequencedInterpolation = true;
    stepSeries.startLocation = 0.1;
    stepSeries.endLocation = 1.1;


    let sumBullet = columnSeries.bullets.push(new am4charts.LabelBullet());
    sumBullet.label.text = "{valueY}";
    sumBullet.verticalCenter = "top";
    // sumBullet.label.padding(12, 4, 12, 4);
    sumBullet.locationY = -0.03;
    sumBullet.locationX = 0.5;

    sumBullet.label.adapter.add("text", function (text, target) {
      return target.dataItem.valueY - target.dataItem.openValueY;
    });

    sumBullet.label.adapter.add("dy", function (dy, target) {
      let value = target.dataItem.valueY - target.dataItem.openValueY
      return value < 0 ? 8 : -8;
    });

    chart.responsive.enabled = true;

    return () => {
      chart.dispose();
    };
  }, [id, data, isAnimationEnabled]);

  return (
    <div id={id} className="waterfall-chart" style={{ width: "100%", height: "100%" }}></div>
  );
}

export default WaterfallChart;
