import React, { useState, useEffect } from 'react';
import axiosRetry from 'axios-retry';
import axios from 'axios';
import {RadioDD, CheckboxDD, SearchCheckboxDD} from '../../components/customDropdown';
import {FilterContainer, FilterModal} from '../../components/filter';
import Loader from '../../components/loader/Loader';
import Comment from '../../components/comment/Comment';
import { InternalServerError, ResourceNotFoundError } from '../../components/error/Error';
import { optionDateRange, optionTickets, optionRegion, optionMetro } from '../../constants/filterOptions';
import { API_BASE_URL } from '../../constants/config';
import { comments } from '../../constants/comments';
import TicketContent from './TicketContent';


const Tickets = () => {
  const [ticketData, setTicketData] = useState([])
  const [isLoading, setIsLoading] = useState(true)

  // show all filters
  const [showFilter, setShowFilter] = useState(false);
  const onShowFilter = () => {
    setShowFilter(true)
  }
  const onCloseFilter = async () => {
    await onCancelGroup1();
    await onCancelGroup2();
    await onCancelRegion();
    await onCancelMetro();
    setShowFilter(false);
  }
  const onApplyFilter = async () => {
    await onApplyGroup1();
    await onApplyGroup2();
    await onApplyRegion();
    await onApplyMetro();
    setShowFilter(false);
  }

  // comment area
  const [showComment, setShowComment] = useState(false);
  const onCloseComment = () => {
    setShowComment(false);
  };
  const onShowComment = () => {
    setShowComment(true);
  };

  // checkbox for region values
  const [region_val_onApply, set_region_val_onApply] = useState([]);
  const [region_val, set_region_val] = useState([]);
  const onApplyRegion = () => {
    set_region_val_onApply(region_val);
  };
  const onCancelRegion = () => {
    set_region_val(region_val_onApply);
  };

  // searchCheckbox for metro values
  const [metro_val_onApply, set_metro_val_onApply] = useState([]);
  const [metro_val, set_metro_val] = useState([]);
  const onApplyMetro = () => {
    set_metro_val_onApply(metro_val);
  };
  const onCancelMetro = () => {
    set_metro_val(metro_val_onApply);
  };

  // radio for group1 values
  const group1_initial_value = optionDateRange ? optionDateRange[0] : null;
  const [group1_val_onApply, set_group1_val_onApply] = useState(group1_initial_value);
  const [group1_val, set_group1_val] = useState(group1_initial_value);
  const onApplyGroup1 = () => {
    set_group1_val_onApply(group1_val);
  };
  const onCancelGroup1 = () => {
    set_group1_val(group1_val_onApply);
  };

  // radio for group2 values
  const group2_initial_value = optionTickets ? optionTickets[0] : null;
  const [group2_val_onApply, set_group2_val_onApply] = useState(group2_initial_value);
  const [group2_val, set_group2_val] = useState(group2_initial_value);
  const onApplyGroup2 = () => {
    set_group2_val_onApply(group2_val);
  };
  const onCancelGroup2 = () => {
    set_group2_val(group2_val_onApply);
  };

  // Axios call
  const fetchData = async () => {
    let url = `${API_BASE_URL}/account-navigator/tickets/trouble-tickets.json`;
    // Uncomment only when API is now available and working
    // const token = localStorage.getItem('token');
    // TODO: Add url and defaultQueryParameters to call Filter API calls

    setIsLoading(true);

    axiosRetry(axios, { retries: 3 });

    await axios({
      method: 'GET',
      url: url
      // Uncomment only when API is now available and working
      // url: url + defaultQueryParameters,
      // headers: {
      //   'Content-Type': 'application/json',
      //   "Authorization": `bearer ${token}`
      // }
    }).then(result => {
      // Uncomment only if for API testing purposes
      // console.log(result.config.url);
      setTicketData(result.data);
      setIsLoading(false);
    }).catch(error => {
      setTicketData(null);
      console.log(error);
      setIsLoading(false);
    });
  };

  useEffect(() => {
    fetchData();
  }, 
    []
    // Uncomment only when API is now available and working
    // [
    //   group1_val_onApply,
    //   group2_val_onApply,
    //   region_val_onApply,
    //   metro_val_onApply
    // ]
  );

  if (isLoading) return <Loader withContainer />;

  return (
    <>
      <div className={`page-content ${showComment ? 'open' : 'closed'}`}>
        {
          ticketData ? 
          ticketData.length > 0 ?
          <>
            <FilterContainer
              showComment={showComment}
              onShowFilter={onShowFilter}
              onShowComment={onShowComment}
            >
              <div className="filters custom-scroll">
                <CheckboxDD
                  title="Deploy Region"
                  id="region"
                  options={optionRegion}
                  valueApplied={region_val_onApply}
                  checkVal={region_val}
                  setCheckVal={set_region_val}
                  onApply={onApplyRegion}
                  onCancel={onCancelRegion}
                />
                <SearchCheckboxDD
                  title="Deploy Metro"
                  id="metro"
                  options={optionMetro}
                  valueApplied={metro_val_onApply}
                  checkVal={metro_val}
                  setCheckVal={set_metro_val}
                  onApply={onApplyMetro}
                  onCancel={onCancelMetro}
                />
                
                <RadioDD
                  title="Group by"
                  id="group"
                  options={optionDateRange}
                  radioVal={group1_val}
                  setRadioVal={set_group1_val}
                  onApply={onApplyGroup1}
                  onCancel={onCancelGroup1}
                  initialValue={group1_initial_value}
                />
                
                <RadioDD
                  title="Group by"
                  id="group-2"
                  addTitleOnWide
                  options={optionTickets}
                  radioVal={group2_val}
                  setRadioVal={set_group2_val}
                  onApply={onApplyGroup2}
                  onCancel={onCancelGroup2}
                  initialValue={group2_initial_value}
                />
              </div>
              <FilterModal
                title="Tickets"
                setShowFilter={setShowFilter}
                showFilter={showFilter}
                onCloseFilter={onCloseFilter}
                onApplyFilter={onApplyFilter}
              >
                <CheckboxDD
                  title="Deploy Region"
                  id="region-modal"
                  visibleInModal
                  options={optionRegion}
                  checkVal={region_val}
                  setCheckVal={set_region_val}
                />
                <SearchCheckboxDD
                  title="Deploy Metro"
                  id="metro-modal"
                  visibleInModal
                  options={optionMetro}
                  checkVal={metro_val}
                  setCheckVal={set_metro_val}
                />
                <RadioDD
                  title="Group by"
                  id="group-modal"
                  visibleInModal
                  options={optionDateRange}
                  radioVal={group1_val}
                  setRadioVal={set_group1_val}
                  initialValue={group1_initial_value}
                />
                <RadioDD
                  title="Group by"
                  id="group-2-modal"
                  addTitleOnWide
                  visibleInModal
                  options={optionTickets}
                  radioVal={group2_val}
                  setRadioVal={set_group2_val}
                  initialValue={group1_initial_value}
                />
              </FilterModal>
            </FilterContainer>
            {/* ============= Your Component here! ============= */}
            <TicketContent ticketData={ticketData ? ticketData[0] : []} />
          </>
          : <ResourceNotFoundError redirectURL={`/account-navigator/navigator/tickets${window.location.search}`} />
          : <InternalServerError redirectURL={`/account-navigator/navigator/tickets${window.location.search}`}/>
        }
      </div>
      <Comment
        data={comments}
        onCloseComment={onCloseComment}
        showComment={showComment}
      />
    </>
  );
};

export default Tickets;
