
import React, { useState, useEffect } from 'react';
import axiosRetry from 'axios-retry';
import axios from 'axios';
import CustomerContent from './CustomerContent';
import {RadioDD, CheckboxDD, SearchCheckboxDD, ShowRankingDD} from '../../components/customDropdown';
import {FilterContainer, FilterModal} from '../../components/filter';
import Loader from '../../components/loader/Loader';
import Comment from '../../components/comment/Comment';
import { InternalServerError, ResourceNotFoundError } from '../../components/error/Error';
import { API_BASE_URL } from '../../constants/config';
import { comments } from '../../constants/comments';
import { optionMeasure, optionRange, optionVertical2, optionRegion, optionMetro, optionHyperscaler } from '../../constants/filterOptions';


const Customer = () => {
  const [isLoading, setIsLoading] = useState(true)
  const [customerData, setData] = useState()
  const [idState, setReferenceID] = useState(null);
  const [modalShow, setModalShow] = useState(false);
  const [modalData, setModalData] = useState(null);
  const [resultData, setResultData] = useState(null);

  // show all filters
  const [showFilter, setShowFilter] = useState(false);
  const onShowFilter = () => {
    setShowFilter(true)
  }
  const onCloseFilter = async () => {
    await onCancelRegion();
    await onCancelMetro();
    await onCancelMeasure();
    await onCancelRange();
    await onCancelHyperscaler();
    await onCancelShowRanking();
    setShowFilter(false);
  }
  const onApplyFilter = async () => {
    await onApplyRegion();
    await onApplyMetro();
    await onApplyMeasure();
    await onApplyRange();
    await onApplyHyperscaler();
    await onApplyShowRanking();
    setShowFilter(false);
  }

  // comment area
  const [showComment, setShowComment] = useState(false);
  const onCloseComment = () => {
    setShowComment(false);
  };
  const onShowComment = () => {
    setShowComment(true);
  };

  // checkbox for region values
  const [region_val_onApply, set_region_val_onApply] = useState([]);
  const [region_val, set_region_val] = useState([]);
  const onApplyRegion = () => {
    set_region_val_onApply(region_val);
  };
  const onCancelRegion = () => {
    set_region_val(region_val_onApply);
  };

  // searchCheckbox for metro values
  const [metro_val_onApply, set_metro_val_onApply] = useState([]);
  const [metro_val, set_metro_val] = useState([]);
  const onApplyMetro = () => {
    set_metro_val_onApply(metro_val);
  };
  const onCancelMetro = () => {
    set_metro_val(metro_val_onApply);
  };

  // radio for measure values
  const measure_initial_value = optionMeasure ? optionMeasure[0] : null;
  const [measure_val_onApply, set_measure_val_onApply] = useState(measure_initial_value);
  const [measure_val, set_measure_val] = useState(measure_initial_value);
  const onApplyMeasure = () => {
    set_measure_val_onApply(measure_val);
  };
  const onCancelMeasure = () => {
    set_measure_val(measure_val_onApply);
  };

  // radio for vertical values
  const vertical_initial_value = optionVertical2 ? optionVertical2[0] : null;
  const [vertical_val_onApply, set_vertical_val_onApply] = useState(vertical_initial_value);
  const [vertical_val, set_vertical_val] = useState(vertical_initial_value);
  const onApplyVertical = () => {
    set_vertical_val_onApply(vertical_val);
  };
  const onCancelVertical = () => {
    set_vertical_val(vertical_val_onApply);
  };

  // radio for range values
  const range_initial_value = optionRange ? optionRange[0] : null;
  const [range_val_onApply, set_range_val_onApply] = useState(range_initial_value);
  const [range_val, set_range_val] = useState(range_initial_value);
  const onApplyRange = () => {
    set_range_val_onApply(range_val);
  };
  const onCancelRange = () => {
    set_range_val(range_val_onApply);
  };

  // radio for group1 values
  const hyperscaler_initial_value = optionHyperscaler ? optionHyperscaler[0] : null;
  const [hyperscaler_val_onApply, set_hyperscaler_val_onApply] = useState(hyperscaler_initial_value);
  const [hyperscaler_val, set_hyperscaler_val] = useState(hyperscaler_initial_value);
  const onApplyHyperscaler = () => {
    set_hyperscaler_val_onApply(hyperscaler_val);
  };
  const onCancelHyperscaler = () => {
    set_hyperscaler_val(hyperscaler_val_onApply);
  };

  // for show ranking values
  const [rankingVal_onApply, setRankingVal_onApply] = useState({
    topBottom: 'radio_Top',
    numberInput: 10,
    rankingPercentile: 'radio_Ranking'
  });
  const [rankingVal, setRankingVal] = useState({
    topBottom: 'radio_Top',
    numberInput: 10,
    rankingPercentile: 'radio_Ranking'
  });
  const onApplyShowRanking = () => {
    setRankingVal_onApply(rankingVal);
  };
  const onCancelShowRanking = () => {
    setRankingVal(rankingVal_onApply);
  };

  // Axios call
  const fetchData = () => {
    let url = `${API_BASE_URL}/gross-bookings-revenue/customer.json`;
    // Uncomment only when API is now available and working
    // const token = localStorage.getItem('token');
    // let url = `${API_BASE}/orbis/api/totalrevenuegrowth`;
    // let defaultQueryParameters = `?measure=${measure_val_onApply.key}&range=${range_val_onApply.key}&showby=${group1_val_onApply}`;

    setIsLoading(true);

    axiosRetry(axios, { retries: 3 });

    axios({
      method: 'GET',
      url: url
      // Uncomment only when API is now available and working
      // url: url + defaultQueryParameters,
      // headers: {
      //   'Content-Type': 'application/json',
      //   "Authorization": `bearer ${token}`
      // }
    }).then(result => {
      const data = result.data;
      const resultSelected = data.segment_ranking;
      // Uncomment only if for API testing purposes
      // console.log(result.config.url);
      setData(data);
      setResultData(resultSelected);
      setIsLoading(false);
    }).catch(error => {
      console.log(error);
      setData(null);
      setIsLoading(false);
    });;

  };

  useEffect(() => {
    fetchData();
  }, [
    measure_val_onApply,
    range_val_onApply,
    vertical_val_onApply,
    hyperscaler_val_onApply,
    region_val_onApply,
    metro_val_onApply,
    rankingVal_onApply
  ]
  );

  if (isLoading) return <Loader withContainer />;

  return (
    <>
      <div className={`page-content ${showComment ? 'open' : 'closed'}`}>
        {customerData ?
          customerData.customer_ranking.by_rank.data.length > 0 ?
            <>
              <FilterContainer
                showComment={showComment}
                onShowFilter={onShowFilter}
                onShowComment={onShowComment}
              >
                <div className="filters custom-scroll">
                  <CheckboxDD
                    title="Deploy Region"
                    id="region"
                    options={optionRegion}
                    valueApplied={region_val_onApply}
                    checkVal={region_val}
                    setCheckVal={set_region_val}
                    onApply={onApplyRegion}
                    onCancel={onCancelRegion}
                  />
                  <SearchCheckboxDD
                    title="Deploy Metro"
                    id="metro"
                    options={optionMetro}
                    valueApplied={metro_val_onApply}
                    checkVal={metro_val}
                    setCheckVal={set_metro_val}
                    onApply={onApplyMetro}
                    onCancel={onCancelMetro}
                  />
                  <RadioDD
                    title="Vertical"
                    id="vertical"
                    options={optionVertical2}
                    radioVal={vertical_val}
                    setRadioVal={set_vertical_val}
                    onApply={onApplyVertical}
                    onCancel={onCancelVertical}
                  />
                  <RadioDD
                    title="Measure"
                    id="measure"
                    options={optionMeasure}
                    radioVal={measure_val}
                    setRadioVal={set_measure_val}
                    onApply={onApplyMeasure}
                    onCancel={onCancelMeasure}
                  />
                  
                  <RadioDD
                    title="Time Period"
                    id="range2"
                    options={optionRange}
                    radioVal={range_val}
                    setRadioVal={set_range_val}
                    onApply={onApplyRange}
                    onCancel={onCancelRange}
                    initialValue={range_initial_value}
                  />
                  
                  <RadioDD
                    id="hyperscaler"
                    options={optionHyperscaler}
                    radioVal={hyperscaler_val}
                    setRadioVal={set_hyperscaler_val}
                    onApply={onApplyHyperscaler}
                    onCancel={onCancelHyperscaler}
                    initialValue={hyperscaler_initial_value}
                  />
                  
                  <ShowRankingDD
                    id="show_ranking"
                    rankingVal={rankingVal}
                    setRankingVal={setRankingVal}
                    onApply={onApplyShowRanking}
                    onCancel={onCancelShowRanking}
                  />
                </div>

                <FilterModal
                  title="Customer"
                  setShowFilter={setShowFilter}
                  showFilter={showFilter}
                  onCloseFilter={onCloseFilter}
                  onApplyFilter={onApplyFilter}
                >
                  <CheckboxDD
                    title="Deploy Region"
                    id="region-modal"
                    options={optionRegion}
                    visibleInModal
                    checkVal={region_val}
                    setCheckVal={set_region_val}
                  />
                  <SearchCheckboxDD
                    title="Deploy Metro"
                    id="metro-modal"
                    options={optionMetro}
                    visibleInModal
                    checkVal={metro_val}
                    setCheckVal={set_metro_val}
                  />
                  <RadioDD
                    title="Vertical"
                    id="vertical-modal"
                    options={optionVertical2}
                    visibleInModal
                    radioVal={vertical_val}
                    setRadioVal={set_vertical_val}
                  />
                  <RadioDD
                    title="Measure"
                    id="measure-modal"
                    options={optionMeasure}
                    visibleInModal
                    radioVal={measure_val}
                    setRadioVal={set_measure_val}
                  />
                  <RadioDD
                    title="Time Period"
                    id="range-modal"
                    options={optionRange}
                    visibleInModal
                    radioVal={range_val}
                    setRadioVal={set_range_val}
                  />
                  <RadioDD
                    title="Hyperscaler"
                    id="hyperscaler-modal"
                    options={optionHyperscaler}
                    visibleInModal
                    radioVal={hyperscaler_val}
                    setRadioVal={set_hyperscaler_val}
                  />
                </FilterModal>
              </FilterContainer>
              <CustomerContent
                customerData={customerData}
                modalShow={modalShow}
                modalData={modalData}
                setModalShow={setModalShow}
                setModalData={setModalData}
                setReferenceID={setReferenceID}
                measure_val_onApply={measure_val_onApply.key}
                range_val_onApply={range_val_onApply.key}
                vertical_val_onApply={vertical_val_onApply.key}
                hyperscaler_val_onApply={hyperscaler_val_onApply.key}
                rankingVal_onApply={rankingVal_onApply}
                region_val_onApply={region_val_onApply}
                metro_val_onApply={metro_val_onApply}
              />
            </>
            : <ResourceNotFoundError redirectURL={"/executive-cockpit/customer-revenue/metro"} />
          : <InternalServerError redirectURL={"/executive-cockpit/customer-revenue/customer"}/>}
      </div>
      <Comment
        data={comments}
        onCloseComment={onCloseComment}
        showComment={showComment}
      />
    </>
  );
};

export default Customer;
