import React, { useState, useEffect } from 'react';
import axiosRetry from 'axios-retry';
import axios from 'axios';
import MetroContent from './MetroContent';
import { Button, ButtonGroup, Container } from 'react-bootstrap';
import { CheckboxDD, RadioDD } from '../../components/customDropdown';
import { FilterContainer, FilterModal } from '../../components/filter';
import Comment from '../../components/comment/Comment';
import Loader from '../../components/loader/Loader';
import { InternalServerError, ResourceNotFoundError } from '../../components/error/Error';
import { optionRegion, optionVertical2, optionICType } from '../../constants/filterOptions';
import { API_BASE_URL } from '../../constants/config';
import { comments } from '../../constants/comments';


const Metro = () => {
  const [isLoading, setIsLoading] = useState(true);
  const [idState, setReferenceID] = useState(null);
  const [metroData, setData] = useState([]);
  const [modalShow, setModalShow] = useState(false);
  const [modalData, setModalData] = useState(null);

  // show all filters
  const [showFilter, setShowFilter] = useState(false);
  const onShowFilter = () => {
    setShowFilter(true);
  };
  const onCloseFilter = async () => {
    await onCancelRegion();
    setShowFilter(false);
  };
  const onApplyFilter = async () => {
    await onApplyRegion();
    setShowFilter(false);
  };

  // comment area
  const [showComment, setShowComment] = useState(false);
  const onCloseComment = () => {
    setShowComment(false);
  };
  const onShowComment = () => {
    setShowComment(true);
  };

  // checkbox for region values
  const [region_val, set_region_val] = useState([]);
  const [region_val_onApply, set_region_val_onApply] = useState([]);
  const onApplyRegion = () => {
    set_region_val_onApply(region_val);
  };
  const onCancelRegion = () => {
    set_region_val(region_val_onApply);
  };

  // checkbox for a-side values
  const [a_side_vertical_val, set_a_side_vertical_val] = useState([]);
  const [a_side_vertical_val_onApply, set_a_side_vertical_val_onApply] = useState([]);
  const onApplyASideVertical = () => {
    set_a_side_vertical_val_onApply(a_side_vertical_val);
  };
  const onCancelASideVertical = () => {
    set_a_side_vertical_val(a_side_vertical_val_onApply);
  };

  // checkbox for z-side values
  const [z_side_vertical_val, set_z_side_vertical_val] = useState([]);
  const [z_side_vertical_val_onApply, set_z_side_vertical_val_onApply] = useState([]);
  const onApplyZSideVertical = () => {
    set_z_side_vertical_val_onApply(z_side_vertical_val);
  };
  const onCancelZSideVertical = () => {
    set_z_side_vertical_val(z_side_vertical_val_onApply);
  };

  // radio for ic type values
  const ic_type_initial_value = optionICType ? optionICType[0] : null;
  const [ic_type_val_onApply, set_ic_type_val_onApply] = useState(ic_type_initial_value);
  const [ic_type_val, set_ic_type_val] = useState(ic_type_initial_value);
  const onApplyICType = () => {
    set_ic_type_val_onApply(ic_type_val)
  }
  const onCancelICType = () => {
    set_ic_type_val(ic_type_val_onApply)
  }


  // Axios call
  const fetchData = () => {
    let url = `${API_BASE_URL}/ecosystem/metro.json`;
    // Uncomment only when API is now available and working
    // const token = localStorage.getItem('token');
    // let url = `${API_BASE}/orbis/api/totalrevenuemetro`;
    // let defaultQueryParameters = `?measure=${measure_val_onApply.key}&range=${range_val_onApply.key}`;
    // let regionParameter = region_val_onApply.length > 0 ? `&region=${region_val_onApply.join(",").toString()}` : '';

    setIsLoading(true);

    axiosRetry(axios, { retries: 3 });

    axios({
      method: 'GET',
      url: url
      // Uncomment only when API is now available and working
      // url: url + defaultQueryParameters + regionParameter,
      // headers: {
      //   'Content-Type': 'application/json',
      //   "Authorization": `bearer ${token}`
      // }
    }).then(result => {
      // Uncomment only if for API testing purposes
      // console.log(result.config.url);
      setData(result.data);
      setIsLoading(false);
    }).catch(error => {
      console.log(error);
      setData(null);
      setIsLoading(false);
    });
  };

  useEffect(() => {
    fetchData();
  },
    []
    // Uncomment only when API is now available and working
    // [
    //   region_val_onApply,
    //   measure_val_onApply,
    //   range_val_onApply 
    // ]
  );

  if (isLoading) return <Loader withContainer />;
  console.log(metroData)
  return (
    <>
      <div className={`page-content ${showComment ? 'open' : 'closed'}`}>
        {metroData ?
          metroData.metro_ranking.by_rank_id.length > 0 ?
            <>
              <FilterContainer
                showComment={showComment}
                onShowFilter={onShowFilter}
                onShowComment={onShowComment}
              >
                <div className="filters custom-scroll">
                  {/* <ButtonGroup className="sub-tab">
                    <Button
                      variant='light'
                      className={activeTab === 'export' ? 'active' : ''}
                      onClick={() => { setActiveTab('export') }}>
                      Export Region
                		</Button>
                    <Button
                      variant='light'
                      className={activeTab === 'import' ? 'active' : ''}
                      onClick={() => { setActiveTab('import') }}>
                      Import Region
                		</Button>

                  </ButtonGroup>
                  <Separator height='39px' /> */}
                  <CheckboxDD
                    title="Deploy Region"
                    id="region"
                    options={optionRegion}
                    valueApplied={region_val_onApply}
                    checkVal={region_val}
                    setCheckVal={set_region_val}
                    onApply={onApplyRegion}
                    onCancel={onCancelRegion}
                  />
                  <CheckboxDD
                    title="A-side Vertical"
                    id="a-side-vertical"
                    options={optionVertical2}
                    valueApplied={a_side_vertical_val_onApply}
                    checkVal={a_side_vertical_val}
                    setCheckVal={set_a_side_vertical_val}
                    onApply={onApplyASideVertical}
                    onCancel={onCancelASideVertical}
                  />
                  <CheckboxDD
                    title="Z-side Vertical"
                    id="z-side-vertical"
                    options={optionVertical2}
                    valueApplied={z_side_vertical_val_onApply}
                    checkVal={z_side_vertical_val}
                    setCheckVal={set_z_side_vertical_val}
                    onApply={onApplyZSideVertical}
                    onCancel={onCancelZSideVertical}
                  />
                  <RadioDD
                    id="ic-type"
                    options={optionICType}
                    radioVal={ic_type_val}
                    setRadioVal={set_ic_type_val}
                    onApply={onApplyICType}
                    onCancel={onCancelICType}
                    initialValue={ic_type_initial_value}
                  />
                </div>

                <FilterModal
                  title="Import"
                  setShowFilter={setShowFilter}
                  showFilter={showFilter}
                  onCloseFilter={onCloseFilter}
                  onApplyFilter={onApplyFilter}
                >
                  <CheckboxDD
                    title="Deploy Region"
                    id="region-modal"
                    visibleInModal
                    options={optionRegion}
                    valueApplied={region_val_onApply}
                    checkVal={region_val}
                    setCheckVal={set_region_val}
                    onApply={onApplyRegion}
                    onCancel={onCancelRegion}
                  />
                  <CheckboxDD
                    title="A-side Vertical"
                    id="a-side-vertical-modal"
                    visibleInModal
                    options={optionVertical2}
                    valueApplied={a_side_vertical_val_onApply}
                    checkVal={a_side_vertical_val}
                    setCheckVal={set_a_side_vertical_val}
                    onApply={onApplyASideVertical}
                    onCancel={onCancelASideVertical}
                  />
                  <CheckboxDD
                    title="Z-side Vertical"
                    id="z-side-vertical-modal"
                    visibleInModal
                    options={optionVertical2}
                    valueApplied={z_side_vertical_val_onApply}
                    checkVal={z_side_vertical_val}
                    setCheckVal={set_z_side_vertical_val}
                    onApply={onApplyZSideVertical}
                    onCancel={onCancelZSideVertical}
                  />
                  <RadioDD
                    id="ic-type-modal"
                    title='IC Type'
                    options={optionICType}
                    radioVal={ic_type_val}
                    visibleInModal
                    setRadioVal={set_ic_type_val}
                    onApply={onApplyICType}
                    onCancel={onCancelICType}
                    initialValue={ic_type_initial_value}
                  />

                </FilterModal>
              </FilterContainer>
              <MetroContent
                metroData={metroData}
                modalShow={modalShow}
                modalData={modalData}
                idState={idState}
                setReferenceID={setReferenceID}
                setModalData={setModalData}
                setModalShow={setModalShow}
              />
            </>
            : <ResourceNotFoundError redirectURL={"/analytics-dashboard/ecosystem/metro"} />
          : <InternalServerError redirectURL={"/analytics-dashboard/ecosystem/metro"} />}
      </div>
      <Comment
        data={comments}
        onCloseComment={onCloseComment}
        showComment={showComment}
      />
    </>
  );
};

export default Metro;