import React from 'react';
import { Container, Row, Col, Card } from 'react-bootstrap';

import HorizontalBarChart from '../../components/chart/HorizontalBarChart';
import DonutChart from '../../components/chart/DonutChart';
import MapChart from '../../components/chart/MapChart';
import ClusteredColumnChart from '../../components/chart/ClusteredColumnChart';
import StackedColumnChart from '../../components/chart/StackedColumnChart';
import KPI_NEW from '../../components/kpi/KPI_NEW';
import Legend from '../../components/legend/Legend';
import CustomTable from '../../components/customTable/CustomTable';


const TicketContent = (props) => {
  const { ticketData } = props;

  const ticketStatus = (cell, row) => {
    return (
      <span className={`indicator ${cell}`}>
        {cell}
      </span>
    );
  }

  const severity = (cell, row) => {
    return (
      <span className={`indicator ${cell}`}>
        {cell}
      </span>
    );
  }

  const defaultSorted = [
    {
      dataField: 'first_name',
      order: 'desc',
    }
  ];

  const columns = [
    {
      dataField: '_id',
      text: 'id',
      hidden: true
    },
    {
      dataField: 'ticket_number',
      text: 'TICKET NUMBER',
      sort: true,
      
      
    },
    {
      dataField: 'reported_date',
      text: 'REPORTED DATE',
      sort: true,
      
      headerClasses: 'left'
    },
    {
      dataField: 'ibx_code',
      text: 'IBX CODE',
      sort: true,
      
      headerClasses: 'left'
    },
    {
      dataField: 'severity',
      text: 'Ticket Severity',
      sort: true,
      
      
      formatter: severity
    },
    {
      dataField: 'system_name',
      text: 'SYSTEM NAME',
      sort: true,
      
      headerClasses: 'left'
    },
    {
      dataField: 'problem_summary',
      text: 'PROBLEM SUMMARY',
      sort: true,
      
      
      classes: 'problem-summary',
      headerClasses: 'problem-summary'
    },
    {
      dataField: 'ticket_type',
      text: 'TICKET TYPE',
      sort: true,
      
      headerClasses: 'left'
    },
    {
      dataField: 'problem_code',
      text: 'PROBLEM CODE',
      sort: true,
      
      headerClasses: 'left'
    },
    {
      dataField: 'ticket_owner',
      text: 'TICKET OWNER',
      sort: true,
      
      headerClasses: 'left'
    },
    {
      dataField: 'ticket_status',
      text: 'TICKET STATUS',
      sort: true,
      
      
      formatter: ticketStatus
    }
  ]

  return (
    <Container className="tickets" fluid>
      <Row className="contentDivider pb-30">
        <div className="fix-width">
          <Card className="TotalRevenue revenue" >
            <Card.Body>
                <Card.Title>{ticketData.overall.total.label}</Card.Title>
                <KPI_NEW
                    totalValue={ticketData.overall.total.value}
                    totalUnit={ticketData.overall.total.unit}
                    details={ticketData.overall.details}
                />
            </Card.Body>
          </Card>
        </div>
        <Col>
          <Card className="Segment revenue">
            <Card.Body>
              <Card.Title> opened trouble tickets by segment </Card.Title>
              <Legend legend={ticketData.opened_trouble_tickets_by_segment.legend} />
              <div style={{ height: "400px" }}>
                <StackedColumnChart
                  id="_stacked_column_chart"
                  hiddenValueAxis={false}
                  isOneHundredPercent={false}
                  isPercentage={false}
                  largerWidth={true}
                  hideLabelBullet={false}
                  data={ticketData.opened_trouble_tickets_by_segment.data}
                  legend={ticketData.opened_trouble_tickets_by_segment.legend}
                  isAnimationEnabled={true}
                />
              </div>
            </Card.Body>
          </Card>
        </Col>
        <Col style={{ maxWidth: "400px" }}>
          <Card className="Segment revenue">
            <Card.Body>
              <Card.Title>open trouble ticket mix</Card.Title>
              <Legend legend={ticketData.open_trouble_ticket_mix.legend} />
              <div style={{ height: "350px" }}>
                <DonutChart
                  id={"open_trouble_ticket_mix_donut_chart"}
                  totalTitle={"TOTAL RESPONSES"}
                  total={ticketData.open_trouble_ticket_mix.total_responses}
                  data={ticketData.open_trouble_ticket_mix.data}
                  legend={ticketData.open_trouble_ticket_mix.legend}
                  isAnimationEnabled={true}
                />
              </div>
            </Card.Body>
          </Card>
        </Col>
      </Row>
      <Row className="contentDivider" style={{ paddingBottom: " 30px" }}>
        <Col xl={3}>
          <Card className="Segment revenue">
            <Card.Body>
              <Card.Title> open trouble tickets by problem code </Card.Title>
              <div style={{ height: "350px" }}>
                <HorizontalBarChart
                  id={"open_trouble_tickets_by_problem_code_bar"}
                  data={ticketData.open_trouble_tickets_by_problem_code.data}
                  legend={ticketData.open_trouble_tickets_by_problem_code.legend}
                  isAnimationEnabled={true}
                />
              </div>
            </Card.Body>
          </Card>
        </Col>
        <Col xl={5}>
          <Card className="Segment revenue">
            <Card.Body>
              <Card.Title> open tickets by ibx location </Card.Title>
              <Legend legend={ticketData.open_tickets_by_ibx_location.legend} />
              <div style={{ height: "350px" }}>
                <MapChart
                  id={"open_tickets_by_ibx_location_map"}
                  data={ticketData.open_tickets_by_ibx_location.data}
                  legend={ticketData.open_tickets_by_ibx_location.legend}
                />
              </div>
            </Card.Body>
          </Card>
        </Col>
        <Col xl={4}>
          <Card className="Segment revenue">
            <Card.Body>
              <Card.Title>ticket aging by segment</Card.Title>
              <Legend legend={ticketData.ticket_aging_by_segment.legend} />
              <div style={{ height: "350px" }}>
                <ClusteredColumnChart
                  id={"ticket_aging_by_segment"}
                  data={ticketData.ticket_aging_by_segment.data}
                  legend={ticketData.ticket_aging_by_segment.legend}
                  isAnimationEnabled={true}
                />
              </div>
            </Card.Body>
          </Card>
        </Col>
      </Row>
      <Row className="contentDivider">
        <Col>
          <Card className="SegmentDetails revenue">
            <Card.Body>
              <Card.Title>ticket details</Card.Title>
              <CustomTable
                tableName='ticket-response-details scrollable'
                data={ticketData.response_details === undefined ? [] : ticketData.response_details}
                defaultSorted={defaultSorted}
                columns={columns}
              />
            </Card.Body>
          </Card>
        </Col>
      </Row>
    </Container>
  );
};

export default TicketContent;