import React, { useState } from 'react';
import { Container, Row, Col, Card } from 'react-bootstrap';
import { ImArrowUpRight2, ImArrowDownRight2 } from "react-icons/im";
import { convertToUSDGrowth, convertToPercent, convertToWholePercent } from '../../constants/formatter';
import CustomTable, { formatLongText } from '../../components/customTable/CustomTable';
import Legend from '../../components/legend/Legend';
import MicroBarChart from '../../components/chart/MicroBarChart';
import SparklineChart from '../../components/chart/SparklineChart';
import StackedColumnChart from '../../components/chart/StackedColumnChart';
import MapChart from '../../components/chart/MapChart';
import LineChart from '../../components/chart/LineChart';
import CustomerModal from './CustomerModal';


const CustomerContent = (props) => {
  const { customerData } = props;
  const [idState, setReferenceID] = useState(null);
  const [modalShow, setModalShow] = useState(false);
  const [modalData, setModalData] = useState(null);
  const [toggleSelectedRow, setToggleSelectedRow] = useState();
  const [selectedCustomer, setCustomerSelected] = useState(null);

  // Data Distribution
  const by_rank = customerData ? customerData.customer_ranking.by_rank : [];
  const by_rank_id = customerData ? customerData.customer_ranking.by_rank_id : [];
  const deployment_map = customerData ? customerData.deployment_map : [];
  const contribution_to_total_mw_sold_by_customer_tier = customerData ? customerData.contribution_to_total_mw_sold_by_customer_tier : [];
  const usd_per_kva_p4q_percent_growth_by_customer_tier = customerData ? customerData.usd_per_kva_p4q_percent_growth_by_customer_tier : [];


  // Table Config
  const indicatorPercent = (cell, row) => {
    let bgColor = cell >= 0.5 ? 'indicator-up' : cell <= -0.5 ? 'indicator-down' : 'indicator-middle';
    let icon = cell >= 0.5 ? <ImArrowUpRight2 /> : cell <= -0.5 ? <ImArrowDownRight2 /> : <span>&nbsp;</span>;
    return (
      <span className={`indicator ${bgColor}`}>
        {icon}
        {convertToPercent(cell)}
      </span>
    );
  };

  const formatWholePercent = (cell, row) => {
    return <span>{convertToWholePercent(cell)}</span>
  };

  const formatUSDGrowth = (cell, row) => {
    let new_cell = convertToUSDGrowth(cell);
    return formatLongText(new_cell, row)
  };

  const customerFormat = (cell, row) => {
    return formatLongText(cell, row)
  }

  const microBarChart = (cell, row) => {
    return <MicroBarChart loadedData={cell} id={"micro-bar-chart-" + row._id} />
  }

  const sparkline = (cell, row) => {
    return (
      <SparklineChart loadedData={cell} id={'sparkline-chart-' + row._id} />
    );
  }

  const rankFormat = (cell, row, rowIndex, formatExtraData) => {
    return <span>{rowIndex + 1}</span>
  };

  const handleOnSelect = (row, isSelect, rowIndex) => {
    let modalDataby_rank;

    setToggleSelectedRow(row._id)
    setCustomerSelected(row.customer);

    for (const key in by_rank_id) {
      const element = by_rank_id[key];
      if (element._id === row._id) {
        modalDataby_rank = element
      }
    }

    if (isSelect) {
      setModalShow(true);
      setReferenceID(row._id);
      setModalData(modalDataby_rank);
    }
  };

  const selectRow = {
    mode: 'radio',
    clickToSelect: true,
    hideSelectColumn: true,
    onSelect: handleOnSelect,
    classes: 'selected',
    bgColor: '#ecf2ff'
  };

  const onCloseModal = async () => {
    await setModalShow(false)
    await setCustomerSelected(null);
    await setReferenceID(null);
    await setToggleSelectedRow()

    setTimeout(() => {
      setModalData(null);
    }, 1000);
  };

  const rowClasses = (row) => {
    let classes = row._id === toggleSelectedRow ? 'selected' : 'selectable';
    return classes
  };

  const defaultSorted = [{
    dataField: 'mrr_mix_percent',
    order: 'desc',
  }];

  const columns = [
    {
      dataField: '_id',
      text: 'id',
      hidden: true
    },
    {
      dataField: '',
      text: 'Rank',
      formatter: rankFormat,
      classes: 'rank center',
      headerClasses: 'rank center',
    },
    {
      dataField: 'customer',
      text: 'CUSTOMER',
      sort: true,
      formatter: customerFormat,
    },
    {
      dataField: 'vertical',
      text: 'VERTICAL',
      sort: true,
      classes: 'vertical-cell',
      headerClasses: 'vertical-cell',
    },
		{
			dataField: 'usd_per_kva',
			text: '$ / KVA',
			sort: true,
			formatter: formatUSDGrowth,
			classes: 'right',
			headerClasses: 'right'
		},
		{
			dataField: 'usd_per_kva_p4q_growth_percent',
			text: 'p4q % growth',
			sort: true,
			formatter: indicatorPercent,
			classes: 'with-ellipsis center border-right',
			headerClasses: 'center border-right',
		},
		{
			dataField: 'mw_sold',
			text: 'mw sold',
      sort: true,
      formatter: formatWholePercent,
			classes: 'right',
			headerClasses: 'right'
		},
		{
			dataField: 'mw_sold_p4q_growth_percent',
			text: 'p4q % growth',
			sort: true,
			formatter: indicatorPercent,
			classes: 'with-ellipsis center border-right',
			headerClasses: 'center border-right',
		},
		{
			dataField: 'deploy_count',
			text: 'deploy #',
			sort: true,
			classes: 'right',
			headerClasses: 'right'
		},
		{
			dataField: 'deploy_count_p4q_growth_percent',
			text: 'p4q % growth',
			sort: true,
			formatter: indicatorPercent,
			classes: 'with-ellipsis right',
			headerClasses: 'right',
		},
  ];

  return (
    <Container fluid>
      <CustomerModal
        show={modalShow}
        onClose={onCloseModal}
        data={modalData}
        selectedCustomer={selectedCustomer}
      />
        <Row className="contentDivider">
          <Col md={6}>
            <Row className="pb-30">
              <Col md={12}>
                <Card className="revenue-card">
                  <Card.Body>
                    <Card.Title>DEVELOPMENT MAP</Card.Title>
                    <>
                      <Legend legend={deployment_map.legend} />
                      <div className="pt-30" style={{ height: "450px " }}>
                        <MapChart
                          id={`partner-map_chart`}
                          largerWidth={true}
                          data={deployment_map.data}
                        />
                      </div>
                    </>
                  </Card.Body>
                </Card>
              </Col>
            </Row>
            <Row>
              <Col md={6}>
                <Card className="revenue-card">
                  <Card.Body>
                    <Card.Title>CONTRIBUTION TO TOTAL MW SOLD BY CUSTOMER TIER</Card.Title>
                    <Legend legend={contribution_to_total_mw_sold_by_customer_tier.legend} />
                    <div style={{ height: '250px' }}>
                      <StackedColumnChart
                        id={`customer-contribution_to_total`}
                        largerWidth={true}
                        isOneHundredPercent={true}
                        isPercentage={true}
                        insideLabel={false}
                        percentLabel={false}
                        hiddenValueAxis={false}
                        data={contribution_to_total_mw_sold_by_customer_tier.data}
                        legend={contribution_to_total_mw_sold_by_customer_tier.legend}
                        isAnimationEnabled={false}
                      />
                    </div>
                  </Card.Body>
                </Card>
              </Col>
              <Col md={6}>
                <Card className="revenue-card">
                  <Card.Body>
                    <Card.Title>$/KVA P4Q % GROWTH BY CUSTOMER TIER</Card.Title>
                    <Legend legend={usd_per_kva_p4q_percent_growth_by_customer_tier.legend} />
                    <div style={{ height: "250px" }}>
                      <LineChart
                        id={`customer-percent_growth`}
                        data={usd_per_kva_p4q_percent_growth_by_customer_tier.data}
                        legend={usd_per_kva_p4q_percent_growth_by_customer_tier.legend}
                        isAnimationEnabled={false}
                      />
                    </div>
                  </Card.Body>
                </Card>
              </Col>
            </Row>
          </Col>
          <Col md={6}>
            <Card className="revenue-card">
              <Card.Body>
                <CustomTable
                    tableTitle="CUSTOMER RANKING"
                    tableName='customer-ranking scrollable'
                    data={by_rank.data ? by_rank.data : []}
                    defaultSorted={defaultSorted}
                    rowClasses={rowClasses}
                    selectRow={selectRow}
                    columns={columns}
                    addHover
                    addSearch
                  />
              </Card.Body>
            </Card>
          </Col>
        </Row>
      </Container>
  );
};

export default CustomerContent;