import React from 'react';
import CustomTable, { formatLongText } from '../../../components/customTable/CustomTable';


const OverviewNotableTable = ({
  data,
  tableTitle
}) => {

	const textFormat = (cell, row) => {
		return formatLongText(cell, row)
  };
  
  const tagsFormat = (cell, row) => {
		return (
      <ul className="tags-list">
        {cell && cell.map((item)=>{
          let new_color = item.color.replace(/#/g, '')
          return(
            <li key={item.color} className={`color-${new_color}`}>
              {item.value}
            </li>
          )
        })}
      </ul>
    )
	};

	const defaultSorted = [{
		dataField: 'customer',
		order: 'desc',
	}];

	const columns = [
		{
			dataField: '_id',
			text: 'id',
			hidden: true
		},
		{
			dataField: 'customer',
			text: 'CUSTOMER',
      sort: true,
      formatter: textFormat,
      classes: 'customer',
      headerClasses: 'customer'
    },
    {
			dataField: 'vertical',
			text: 'VERTICAL',
      sort: true,
      formatter: textFormat,
		},
		{
			dataField: 'hq_region',
			text: 'HQ REGION',
      sort: true,
      formatter: textFormat,
    },
    {
			dataField: 'company_size',
			text: 'COMPANY SIZE',
      sort: true,
      formatter: textFormat,
		},
    {
			dataField: 'tags',
			text: 'TAGS',
      formatter: tagsFormat,
      classes: 'tags-cell',
      headerClasses: 'tags-cell'
		},
	];

	return (
    <>
        <CustomTable
          tableName='notable-table scrollable'
          className='notable-table-area'
          tableTitle={tableTitle}
          addSearch
          data={data ? data : []}
          defaultSorted={defaultSorted}
          columns={columns}
        />  
    </>
	);
};

export default OverviewNotableTable;