import React, { useLayoutEffect } from 'react';
import * as am4core from "@amcharts/amcharts4/core";
import * as am4maps from "@amcharts/amcharts4/maps";
import am4geodata_worldLow from "@amcharts/amcharts4-geodata/worldLow"
import _ from 'lodash';

const MapModalChartNavigator = (props) => {

  useLayoutEffect(() => {
    let chart = am4core.create(props.id, am4maps.MapChart);
    let currentSeries;
    let mapData;

    chart.width = am4core.percent(100);
    chart.height = am4core.percent(45);
    chart.homeZoomLevel = 3.2;
    chart.homeGeoPoint = {
      latitude: 62,
      longitude: 20
    };

    chart.paddingRight = 0;
    chart.paddingLeft = 0;
    chart.paddingTop = 60;
    chart.paddingBottom = 0;

    chart.geodata = am4geodata_worldLow;

    chart.projection = new am4maps.projections.Miller();
    let polygonSeries = chart.series.push(new am4maps.MapPolygonSeries());

    polygonSeries.useGeodata = true;

    let polygonTemplate = polygonSeries.mapPolygons.template;
    polygonTemplate.fill = am4core.color("#DBE2ED");

    // Remove Antarctica Continent from map
    polygonSeries.exclude = ["AQ"];

    let zoomOut = chart.tooltipContainer.createChild(am4core.ZoomOutButton);
    zoomOut.align = "right";
    zoomOut.margin(20, 20, 20, 20);

    zoomOut.events.on("hit", function () {
      chart.goHome();

      if (props.largerWidth === true) {
        createModalSeries(20, 20);
      } else {
        createModalSeries(15, 15);
      }

      zoomOut.hide();
    });

    const createModalSeries = (minRadius, maxRadius) => {
      mapData = _.chain(props.data).groupBy('metro').map(data => ({
        "metro": data[0].metro,
        "metro_short": data[0].metro_short,
        "latitude": parseFloat(data[0].metro_lat),
        "longitude": parseFloat(data[0].metro_long),
        "color": data[0].color
      })).value();
      // data[0].value === "customer_presence" ? "#F0CC71" : data[0].value === "connected_to_partner" ? "#619061" : data[0].value === "partner_deployed_but_not_connected" ? "#C54D39" : data[0].value === "operational_footprint"  ? "#CD503C": data[0].value === "eqix_deployment" ? "#f0cc71" : "#000000",

      let imageSeries = chart.series.push(new am4maps.MapImageSeries());
      let imageSeriesTemplate = imageSeries.mapImages.template;
      let circle = imageSeriesTemplate.createChild(am4core.Circle);

      circle.radius = 4;
      circle.fillOpacity = 0.8;
      circle.strokeWidth = 2;
      circle.nonScaling = true;
      circle.tooltipText = `{metro}`;
      circle.propertyFields.stroke = "color";
      circle.propertyFields.fill = "color";
      circle.tooltipText = `[#fff]{metro}: [#fff bold]\${value}[/]`;

      // let label = imageSeriesTemplate.createChild(am4core.Label);

      // label.text = "{metro_short}";
      // label.fill = am4core.color("#fff");
      // label.verticalCenter = "middle";
      // label.horizontalCenter = "middle";
      // label.nonScaling = true;

      imageSeries.heatRules.push({
        "target": circle,
        "property": "radius",
        "min": minRadius,
        "max": maxRadius,
        "dataField": "value"
      });

      imageSeriesTemplate.propertyFields.latitude = "latitude";
      imageSeriesTemplate.propertyFields.longitude = "longitude";

      imageSeries.data = mapData;
      currentSeries = imageSeries;

      currentSeries.mapImages.template.events.on("hit", function (ev) {
        currentSeries.dispose();

        zoomOut.icon = new am4core.Sprite();
        zoomOut.icon.fill = am4core.color('#ffffff')
        zoomOut.icon.path = "M16,8 L14,8 L14,16 L10,16 L10,10 L6,10 L6,16 L2,16 L2,8 L0,8 L8,0 L16,8 Z M16,8";

        if (props.largerWidth === true) {
          ev.target.series.chart.zoomToMapObject(ev.target, 20);
          createModalSeries(20, 20);
        } else {
          ev.target.series.chart.zoomToMapObject(ev.target, 40);
          createModalSeries(15, 15);
        }

        zoomOut.show();
      });
    };

    zoomOut.hide();
    if (props.largerWidth === true) {
      createModalSeries(15, 15);
    } else if (props.largerWidth === false) {
      createModalSeries(15, 15);
    }

    return () => {
      chart.dispose();
    };
  }, [props.id, props.data, props.legend, props.largerWidth]);

  return (
    <div id={props.id} className="map-chart"></div>
  );
};

export default MapModalChartNavigator;