import React from 'react';
import { useHistory } from "react-router-dom";
import { Container, Row, Col, Card, Button } from 'react-bootstrap';
import { ReactComponent as ExecutiveViewpoints } from '../../assets/images/binoculars.svg';

const Page = () => {
	let history = useHistory();

	const goTo = (url) => {
		history.push('/analytics-dashboard/' + url + '/overview');
	}

	return (
		<Container fluid className="analytics-home-page">
			<Card style={{ margin: '30px 15px' }}>
				<Card.Body>
					<Card.Title>All dashboards</Card.Title>
					<div className="filter-container">
						<div className="dashboard-search-bar ml-auto">
							<input id="dashboard_search_bar" className="form-control" type="text" placeholder="Search for anything..." maxLength="50" />
						</div>
					</div>

					<div className="product-container">
						<div className="btn-container" key="1">
							<Button
								variant="link"
								id={1}
								className={`checkbox-1}`}
								onClick={() => goTo('revenue')}
							>
								<ExecutiveViewpoints className="icon" />
								<p className="main-text">revenue</p>
								<span className="last-updated">Last Updated 3 days ago</span>
							</Button>
						</div>
						<div className="btn-container" key="1">
							<Button
								variant="link"
								id={1}
								className={`checkbox-1}`}
								onClick={() => goTo('bookings')}
							>
								<ExecutiveViewpoints className="icon" />
								<p className="main-text">bookings</p>
								<span className="last-updated">Last Updated 3 days ago</span>
							</Button>
						</div>
						<div className="btn-container" key="1">
							<Button
								variant="link"
								id={1}
								className={`checkbox-1}`}
								onClick={() => goTo('cross-border-trade')}
							>
								<ExecutiveViewpoints className="icon" />
								<p className="main-text">cross border trade</p>
								<span className="last-updated">Last Updated 3 days ago</span>
							</Button>
						</div>
						<div className="btn-container" key="1">
							<Button
								variant="link"
								id={1}
								className={`checkbox-1}`}
								onClick={() => goTo('pricing')}
							>
								<ExecutiveViewpoints className="icon" />
								<p className="main-text">pricing</p>
								<span className="last-updated">Last Updated 3 days ago</span>
							</Button>
						</div>
						<div className="btn-container" key="1">
							<Button
								variant="link"
								id={1}
								className={`checkbox-1}`}
								onClick={() => goTo('customer-count')}
							>
								<ExecutiveViewpoints className="icon" />
								<p className="main-text">customer count</p>
								<span className="last-updated">Last Updated 3 days ago</span>

							</Button>
						</div>
						<div className="btn-container" key="1">
							<Button
								variant="link"
								id={1}
								className={`checkbox-1}`}
								onClick={() => goTo('channel-performance')}
							>
								<ExecutiveViewpoints className="icon" />
								<p className="main-text">channel performance</p>
								<span className="last-updated">Last Updated 3 days ago</span>
							</Button>
						</div>
						<div className="btn-container" key="1">
							<Button
								variant="link"
								id={1}
								className={`checkbox-1}`}
								onClick={() => goTo('churn')}
							>
								<ExecutiveViewpoints className="icon" />
								<p className="main-text">churn</p>
								<span className="last-updated">Last Updated 3 days ago</span>
							</Button>

						</div>
						<div className="btn-container" key="1">
							<Button
								variant="link"
								id={1}
								className={`checkbox-1}`}
								onClick={() => goTo('new-logos-and-extensions')}
							>
								<ExecutiveViewpoints className="icon" />
								<p className="main-text">new logos and extension</p>
								<span className="last-updated">Last Updated 3 days ago</span>
							</Button>
						</div>
						<div className="btn-container" key="1">
							<Button
								variant="link"
								id={1}
								className={`checkbox-1}`}
								onClick={() => goTo('pipeline')}
							>
								<ExecutiveViewpoints className="icon" />
								<p className="main-text">pipeline</p>
								<span className="last-updated">Last Updated 3 days ago</span>
							</Button>
						</div>
						<div className="btn-container" key="1">
							<Button
								variant="link"
								id={1}
								className={`checkbox-1}`}
								onClick={() => goTo('ecosystem')}
							>
								<ExecutiveViewpoints className="icon" />
								<p className="main-text">ecosystem</p>
								<span className="last-updated">Last Updated 3 days ago</span>
							</Button>
						</div>
						<div className="btn-container" key="1">
							<Button
								variant="link"
								id={1}
								className={`checkbox-1}`}
								onClick={() => goTo('global-platform')}
							>
								<ExecutiveViewpoints className="icon" />
								<p className="main-text">Global Platform</p>
								<span className="last-updated">Last Updated 3 days ago</span>
							</Button>
						</div>
					</div>
				</Card.Body>
			</Card>
		</Container >
	)
};

export default Page;
