import React from 'react';
import { ButtonGroup, Button } from 'react-bootstrap';
import './filter.scss';

const FilterContainer = ({
	children,
	className,
	onShowFilter,
	onClearFilter,
	onShowComment,
	showComment,
	showAddMetric,
	setModalShow
}) => {

	// clear filters
	const onClear = () => window.location.reload();

	return (
		<div className={`filter-container ${className ? className : ''}`}>
			{children}

			<ButtonGroup className={`ml-auto right-group ${showComment ? 'comment-open' : ''}`}>
				{!showAddMetric
					? <Button
						variant="link"
						className="btn-all-filters"
						onClick={onShowFilter}>
						All Filters
            </Button>
					: null
				}

				<Button
					variant="link"
					className="btn-clear"
					onClick={onClearFilter ? onClearFilter : onClear}>
					Clear
        </Button>

				{showAddMetric
					? <Button
						variant="link"
						className="btn"
						onClick={() => setModalShow(true)}>
						Add Metric
            </Button>
					: <Button
						variant="link"
						className="btn-comments"
						onClick={onShowComment}>
						Comments
            </Button>
				}

				<Button
					variant="link"
					className="btn-export"
					onClick={() => alert('This feature will be available soon.')}>
					Export Data
				</Button>
			</ButtonGroup>
		</div >
	);
}

export default FilterContainer;