import React from 'react';

import { ImArrowUpRight2, ImArrowDownRight2 } from "react-icons/im";
import { convertToUSD, convertToPercent, convertToCount, convertToWholePercent } from '../../constants/formatter';
import './kpi.scss';

const KPI_NEW = (props) => {
  const { totalSubLabel, totalValue, totalUnit, details, count, numberOfCounts, setkpiClicked, kpiClicked, isAddText, noHover } = props;
  let total;

  if (totalUnit && totalUnit === "$") {
    let convertedTotal = convertToUSD(totalValue);

    if (/M/g.test(convertedTotal) === false) {
      total = <div className={`total-revenue ${noHover ? 'no-hover' : 'with-hover'}`} onClick={() => {
        if (!noHover) {
          setkpiClicked(totalSubLabel)
        }
      }}>
        {convertedTotal.slice(0, 1)} <span>{convertedTotal.slice(1)}</span>
        {totalSubLabel ? <span className="sub-label">{totalSubLabel}</span> : null}
        {isAddText ? isAddText : null}
      </div>;
    } else {
      total = <div className={`total-revenue ${noHover ? 'no-hover' : 'with-hover'}`} onClick={() => {
        if (!noHover) {
          setkpiClicked(totalSubLabel)
        }
      }}>
        {convertedTotal.slice(0, 1)} <span>{convertedTotal.slice(1, -1)}</span> {convertedTotal.slice(-1)}
        {totalSubLabel ? <span className="sub-label">{totalSubLabel}</span> : null}
        {isAddText ? isAddText : null}
      </div>;
    }
  } else if (totalUnit && totalUnit === "%") {
    let convertedTotal = convertToPercent(totalValue);
    total = <div className={`total-revenue ${noHover ? 'no-hover' : 'with-hover'}`} onClick={() => {
      if (!noHover) {
        setkpiClicked(totalSubLabel)
      }
    }}>
      <span>{convertedTotal.slice(0, -1)}</span> {convertedTotal.slice(-1)}
      {totalSubLabel ? <span className="sub-label">{totalSubLabel}</span> : null}
      {isAddText ? isAddText : null}
    </div>;
  } else {
    total = <div className={`total-revenue ${noHover ? 'no-hover' : 'with-hover'}`} onClick={() => {
      if (!noHover) {
        setkpiClicked(totalSubLabel)
      }

    }}>
      <span>{totalValue ? convertToCount(totalValue) : '-'}</span>{totalUnit ? totalUnit : null}
      {totalSubLabel ? <span className="sub-label">{totalSubLabel}</span> : null}
    </div>;
  }

  return (
    <>
      { total}
      { count ? <div className={`kpi-item border-bottom ${noHover ? 'no-hover' : 'with-hover'}`} onClick={() => {
        if (!noHover) {
          setkpiClicked(count.label)
        }
      }}>
        <div className="title">{count.label}</div>
        <div className="div-table">
          <div className="tr">
            <div className="td val">
              <span>{convertToCount(count.value)}</span>
            </div>
          </div>
        </div>
      </div> : null}
      { details.map((item, index) => {
        let subValue, bgColor, icon;
        let bpsValue = item.bps_value ? item.bps_value : null;
        let percentValue = item.percent_value ? item.percent_value : null;
        let countValue = item.count_value ? item.count_value : null;
        let quarterPeriod = item.quarter_period ? ` vs ${item.quarter_period}` : null;

        if (bpsValue) {
          subValue = bpsValue !== 0 || bpsValue !== '' ? bpsValue <= -5 ?  `${Math.abs(bpsValue)}bps` : `${bpsValue}bps` : "0bps";
          bgColor = bpsValue >= 5 ? 'indicator-up' : bpsValue <= -5 ? 'indicator-down' : 'indicator-middle';
          icon = bpsValue >= 5 ? <ImArrowUpRight2 /> : bpsValue <= -5 ? <ImArrowDownRight2 /> : <span>&nbsp;&nbsp;&nbsp;</span>;
        } else if (percentValue) {
          subValue = percentValue !== 0 || percentValue !== '' ? `${percentValue}%` : "0.0%";
          bgColor = percentValue >= 0.5 ? 'indicator-up' : percentValue <= -0.5 ? 'indicator-down' : 'indicator-middle';
          icon = percentValue >= 0.5 ? <ImArrowUpRight2 /> : percentValue <= -0.5 ? <ImArrowDownRight2 /> : <span>&nbsp;&nbsp;</span>;
        } else {
          subValue = countValue !== 0 || countValue !== '' ? countValue : "0";
          bgColor = countValue ? 'indicator-up' : countValue <= -0.5 ? 'indicator-down' : 'indicator-middle';
          icon = countValue ? <ImArrowUpRight2 /> : countValue <= -0.5 ? <ImArrowDownRight2 /> : <span>&nbsp;</span>;
        }

        return (
          <div className={` kpi-item border-bottom ${kpiClicked === item.label ? 'active' : null} ${noHover ? 'no-hover' : 'with-hover'}`} key={index} onClick={() => {
            if (!noHover) {
              setkpiClicked(item.label) 
            }
          }}>
            <div className="title">{item.label}</div>
            <div className="div-table">
              <div className="tr">
                <div className="td val">
                  <div className="center-val">
                    {/%/g.test(item.label) === true ? (item.value ? <><span>{item.value > 0 && /growth/g.test(item.label.toLowerCase()) ? "+" : ""}{convertToWholePercent(item.value)}</span><span className="val-unit">%</span></> : <span>-</span>) :
                      /\$/g.test(item.label) === true ? (item.value ? <span>{convertToUSD(item.value)}</span> : <span>-</span>) :
                        (item.value ? <><span>{item.unit === "bps" && item.value > 0 ? "+" : ""}{convertToCount(item.value)}</span>{item.unit ? item.unit : null}</> : <span>-</span>)}
                  </div>

                </div>

                {/* <div className="td middle">

                </div> : null} */}
                <div className="td middle">
                  {(bpsValue || percentValue || countValue) ?
                    <div className={`indicator ${bgColor}`}>
                      {icon}
                      {subValue}
                    </div>
                    : null}
                  {/* <br /> */}
                  {quarterPeriod ?
                    <span className="period">{quarterPeriod}</span>
                    : null}

                </div>
              </div>
            </div>
          </div>
        )
      })}
      { numberOfCounts ?
        <>
          <div className=" kpi-item border-top" >
            {numberOfCounts.slice(0, 2).map((item, i) => {
              return (
                <div className="kpi-content" key={i} onClick={() => {
                  setkpiClicked(item.label)
                }}>
                  <div className="title">{item.label}</div>
                  <div className="val">{convertToCount(item.value)}</div>
                </div>
              )
            })}
          </div>
          <div className="kpi-item  border-top">
            {numberOfCounts.slice(Math.max(numberOfCounts.length - 2, 0)).map((item, i) => {
              return (
                <div className="kpi-content" key={i} onClick={() => {
                  setkpiClicked(item.label)
                }}>
                  <div className="title">{item.label}</div>
                  <div className="val">{convertToCount(item.value)}</div>
                </div>
              )
            })}
          </div>
        </> : null}
    </>
  );
}
export default KPI_NEW;