import React, { useLayoutEffect } from 'react';
import * as am4core from "@amcharts/amcharts4/core";
import * as am4charts from "@amcharts/amcharts4/charts";
import am4themes_animated from "@amcharts/amcharts4/themes/animated";

am4core.useTheme(am4themes_animated);


const BoxWhiskerChart = (props) => {
  const { id, data } = props;

  am4core.options.autoSetClassName = true;

  useLayoutEffect(() => {
    let chart = am4core.create(id, am4charts.XYChart);
    chart.width = am4core.percent(100);
    chart.height = am4core.percent(100);
    chart.paddingRight = 0;
    chart.paddingLeft = 0;
    chart.paddingBottom = 0;
    chart.paddingTop = 40;

    chart.data = data;

    let categoryAxis = chart.xAxes.push(new am4charts.CategoryAxis());
    categoryAxis.dataFields.category = "quarter_period";
    categoryAxis.renderer.grid.template.strokeWidth = 0;
    categoryAxis.id = "myCategoryAxis";
    categoryAxis.renderer.paddingTop = 15;
    categoryAxis.renderer.paddingBottom = -8;
    categoryAxis.renderer.grid.template.location = 0;
    categoryAxis.renderer.minGridDistance = 20;
    categoryAxis.renderer.cellStartLocation = 0.2;
    categoryAxis.renderer.cellEndLocation = 0.8;

    let valueAxis = chart.yAxes.push(new am4charts.ValueAxis());
    valueAxis.tooltip.disabled = true;
    valueAxis.renderer.minGridDistance = 50;
    valueAxis.id = "myValueAxis";
    valueAxis.renderer.paddingLeft = -8;
    valueAxis.renderer.paddingRight = 5;

    let series1 = chart.series.push(new am4charts.CandlestickSeries());
    series1.columns.template.width = am4core.percent(60);
    series1.dataFields.categoryX = "quarter_period";
    series1.dataFields.valueY = "close1";
    series1.dataFields.openValueY = "open1";
    series1.dataFields.lowValueY = "low1";
    series1.dataFields.highValueY = "high1";
    series1.columns.template.dx = 15;
    series1.simplifiedProcessing = true;
    series1.riseFromopen1State = undefined;
    series1.dropFromopen1State = undefined;
    series1.columns.template.tooltipText = "[font-size: 12px; #fff]High: [/][font-size: 12px; #fff bold]${high1}[/]\n[font-size: 12px; #fff]Low: [/][font-size: 12px; #fff bold]${low1}[/]\n[font-size: 12px; #fff]Value: [/][font-size: 12px; #fff bold]${close1}[/]\n[font-size: 12px; #fff]Open Value: [/][font-size: 12px; #fff bold]${open1}[/]";

    let topSeries1 = chart.series.push(new am4charts.StepLineSeries());
    topSeries1.noRisers = true;
    topSeries1.startLocation = 0.32;
    topSeries1.endLocation = 0.37;
    topSeries1.dataFields.valueY = "high1";
    topSeries1.dataFields.categoryX = "quarter_period";
    topSeries1.stroke = am4core.color("#CD503C");
    topSeries1.strokeWidth = 1;

    let bottomSeries1 = chart.series.push(new am4charts.StepLineSeries());
    bottomSeries1.noRisers = true;
    bottomSeries1.startLocation = 0.32;
    bottomSeries1.endLocation = 0.37;
    bottomSeries1.dataFields.valueY = "low1";
    bottomSeries1.dataFields.categoryX = "quarter_period";
    bottomSeries1.stroke = am4core.color("#CD503C");
    bottomSeries1.strokeWidth = 1;
    
    series1.dropFromOpenState.properties.fill = am4core.color("#CD503C");
    series1.dropFromOpenState.properties.stroke = am4core.color("#CD503C");
    series1.riseFromOpenState.properties.fill = am4core.color("#CD503C");
    series1.riseFromOpenState.properties.stroke = am4core.color("#CD503C");

    let series2 = chart.series.push(new am4charts.CandlestickSeries());
    series2.dataFields.categoryX = "quarter_period";
    series2.columns.template.width = am4core.percent(60);
    series2.dataFields.valueY = "close2";
    series2.dataFields.openValueY = "open2";
    series2.dataFields.lowValueY = "low2";
    series2.dataFields.highValueY = "high2";
    series2.simplifiedProcessing = true;
    series2.riseFromopenState = undefined;
    series2.dropFromopenState = undefined;
    series2.columns.template.tooltipText = "[font-size: 12px; #fff]High: [/][font-size: 12px; #fff bold]${high2}[/]\n[font-size: 12px; #fff]Low: [/][font-size: 12px; #fff bold]${low2}[/]\n[font-size: 12px; #fff]Value: [/][font-size: 12px; #fff bold]${close2}[/]\n[font-size: 12px; #fff]Open Value: [/][font-size: 12px; #fff bold]${open2}[/]";

    let topseries2 = chart.series.push(new am4charts.StepLineSeries());
    topseries2.noRisers = true;
    topseries2.startLocation = 0.48;
    topseries2.endLocation = 0.52;
    topseries2.dataFields.valueY = "high2";
    topseries2.dataFields.categoryX = "quarter_period";
    topseries2.stroke = am4core.color("#DC7431");
    topseries2.strokeWidth = 1;

    let bottomseries2 = chart.series.push(new am4charts.StepLineSeries());
    bottomseries2.noRisers = true;
    bottomseries2.startLocation = 0.48;
    bottomseries2.endLocation = 0.52;
    bottomseries2.dataFields.valueY = "low2";
    bottomseries2.dataFields.categoryX = "quarter_period";
    bottomseries2.stroke = am4core.color("#DC7431");
    bottomseries2.strokeWidth = 1;

    series2.dropFromOpenState.properties.fill = am4core.color("#DC7431");
    series2.dropFromOpenState.properties.stroke = am4core.color("#DC7431");
    series2.riseFromOpenState.properties.fill = am4core.color("#DC7431");
    series2.riseFromOpenState.properties.stroke = am4core.color("#DC7431");

    let series3 = chart.series.push(new am4charts.CandlestickSeries());
    series3.dataFields.categoryX = "quarter_period";
    series3.columns.template.width = am4core.percent(60);
    series3.dataFields.valueY = "close3";
    series3.dataFields.openValueY = "open3";
    series3.dataFields.lowValueY = "low3";
    series3.dataFields.highValueY = "high3";
    series3.simplifiedProcessing = true;
    series3.columns.template.dx = -15;
    series3.riseFromopenState = undefined;
    series3.dropFromopenState = undefined;
    series3.columns.template.tooltipText = "[font-size: 12px; #fff]High: [/][font-size: 12px; #fff bold]${high3}[/]\n[font-size: 12px; #fff]Low: [/][font-size: 12px; #fff bold]${low3}[/]\n[font-size: 12px; #fff]Value: [/][font-size: 12px; #fff bold]${close3}[/]\n[font-size: 12px; #fff]Open Value: [/][font-size: 12px; #fff bold]${open3}[/]";

    let topseries3 = chart.series.push(new am4charts.StepLineSeries());
    topseries3.noRisers = true;
    topseries3.startLocation = 0.63;
    topseries3.endLocation = 0.68;
    topseries3.dataFields.valueY = "high3";
    topseries3.dataFields.categoryX = "quarter_period";
    topseries3.stroke = am4core.color("#F0CC71");
    topseries3.strokeWidth = 1;

    let bottomseries3 = chart.series.push(new am4charts.StepLineSeries());
    bottomseries3.noRisers = true;
    bottomseries3.startLocation = 0.63;
    bottomseries3.endLocation = 0.68;
    bottomseries3.dataFields.valueY = "low3";
    bottomseries3.dataFields.categoryX = "quarter_period";
    bottomseries3.stroke = am4core.color("#F0CC71");
    bottomseries3.strokeWidth = 1;

    series3.dropFromOpenState.properties.fill = am4core.color("#F0CC71");
    series3.dropFromOpenState.properties.stroke = am4core.color("#F0CC71");
    series3.riseFromOpenState.properties.fill = am4core.color("#F0CC71");
    series3.riseFromOpenState.properties.stroke = am4core.color("#F0CC71");

    return () => {
      chart.dispose();
    };
  }, [id, data]
  );

  return (
    <div className="box-and-whisker-chart" id={id}></div>
  );
}

export default BoxWhiskerChart;