import React, { useState } from 'react';
import { Container, Row, Col, Card } from 'react-bootstrap';
import { ImArrowUpRight2, ImArrowDownRight2 } from "react-icons/im";
import Legend from '../../components/legend/Legend';
import { BasicDD } from '../../components/customDropdown';
import CustomTable, { formatLongText } from '../../components/customTable/CustomTable';
import StackedColumnChart from '../../components/chart/StackedColumnChart';
import MicroBarChart from '../../components/chart/MicroBarChart';
import DonutChart from '../../components/chart/DonutChart';
import LineChart from '../../components/chart/LineChart';
import KPI_NEW from '../../components/kpi/KPI_NEW';
import OverviewNotableTable from './table/OverviewNotableTable';
import { optionOverviewFilter } from '../../constants/filterOptions';
import { convertToPercent, convertToCount, convertToWholePercent } from '../../constants/formatter';


const OverviewContent = (props) => {
	const { overviewData, selectedRangeValue } = props;
	const [kpiClicked, setkpiClicked] = useState(null);

	// basic dd for column chart values
	const stacked_column_initial_value = optionOverviewFilter ? optionOverviewFilter[0] : null;
	const [stackedColumnSelection, setStackedColumnSelection] = useState(stacked_column_initial_value);

	// Data Distribution
	let overall = overviewData ? overviewData.overall : [];
	let customer_count_by_segment = overviewData ? overviewData.customer_count_by_segment : [];
	let segment_trends_by_segment = overviewData ? overviewData.segment_trends_by_segment : [];
	let segment_revenue_mix = overviewData ? overviewData.segment_revenue_mix : [];
	let segment_details = overviewData ? overviewData.segment_details : [];
	let gross_adds_by_segment = overviewData ? overviewData.gross_adds_by_segment : [];
	let attrition_by_segment = overviewData ? overviewData.attrition_by_segment : [];
	let adds_as_percent_of_attrition_by_segment = overviewData ? overviewData.adds_as_percent_of_attrition_by_segment : [];
	let notable_adds = overviewData ? overviewData.notable_adds : [];
	let notable_attritions = overviewData ? overviewData.notable_attritions : [];

	let customer_count_by_segment_in_p4q_percentage = overviewData ? overviewData.customer_count_by_segment_in_p4q_percentage : [];
	let customer_count_by_segment_in_quarter_percentage = overviewData ? overviewData.customer_count_by_segment_in_quarter_percentage : [];
	let customer_count_by_segment_in_year_percentage = overviewData ? overviewData.customer_count_by_segment_in_year_percentage : [];

	// Table Config
	const indicatorPercent = (cell, row) => {
		let bgColor = cell >= 0.5 ? 'indicator-up' : cell <= -0.5 ? 'indicator-down' : 'indicator-middle';
		let icon = cell >= 0.5 ? <ImArrowUpRight2 /> : cell <= -0.5 ? <ImArrowDownRight2 /> : <span className="mr-3"></span>;
		return (
			<span className={`indicator ${bgColor}`}>
				{icon}
				{convertToPercent(cell)}
			</span>
		);
	};

	const segmentFormat = (cell, row) => {
		return formatLongText(cell, row)
	};

	const microBarChart = (cell, row) => {
		return <MicroBarChart loadedData={cell} id={"micro-bar-chart-" + row._id} />
	}

	const formatToPercent = (cell, row) => {
		return <span>{convertToWholePercent(cell)}</span>
	};

	const formatToCount = (cell, row) => {
		return <span>{convertToCount(cell)}</span>
	};

	const defaultSorted = [{
		dataField: '_id',
		order: 'asc',
	}];

	const columns = [
		{
			dataField: '_id',
			text: 'id',
			hidden: true
		},
		{
			dataField: 'segment',
			text: 'SEGMENT',
			sort: true,
			formatter: segmentFormat
		},
		{
			dataField: 'customer_count',
			text: 'CUSTOMER #',
			sort: true,
			classes: 'right',
			headerClasses: 'right',
			formatter: formatToCount
		},
		{
			dataField: 'mix_percent',
			text: 'MIX %',
			sort: true,
			formatter: formatToPercent,
			classes: 'center',
			headerClasses: 'center'
		},
		{
			dataField: 'q_by_q_growth_percent',
			text: 'Q/Q % GROWTH',
			sort: true,
			formatter: indicatorPercent,
			classes: 'with-ellipsis center',
			headerClasses: 'center'
		},
		{
			dataField: 'micro_column_bar_data',
			text: '',
			sort: true,
			formatter: microBarChart,
			classes: 'sparkline-cell center',
			headerClasses: 'sparkline-cell center'
		},
		{
			dataField: 'q_to_q_growth_count',
			text: 'Q-Q # GROWTH',
			sort: true,
			classes: 'right',
			headerClasses: 'right',
			formatter: formatToCount
		},
		{
			dataField: 'y_by_y_growth_percent',
			text: 'Y/Y % GROWTH',
			sort: true,
			formatter: indicatorPercent,
			classes: 'with-ellipsis center',
			headerClasses: 'center'
		},
		{
			dataField: 'y_to_y_growth_count',
			text: 'Y-Y # GROWTH',
			sort: true,
			classes: 'right',
			headerClasses: 'right',
			formatter: formatToCount
		},
	];

	return (
		<Container fluid>
			<Row className="contentDivider pb-30">
				<div className="fix-width">
					<Card className="revenue" >
						<Card.Body>
							<Card.Title>CUSTOMER COUNT</Card.Title>
							<KPI_NEW
								totalValue={overall.total.value}
								totalUnit={overall.total.unit}
								details={overall.details}
								setkpiClicked={setkpiClicked}
								kpiClicked={kpiClicked}
							/>
						</Card.Body>
					</Card>
				</div>
				<Col>
					<Card className="revenue">
						<Card.Body>
							<Card.Title>{kpiClicked ? kpiClicked : 'CUSTOMER COUNT'} BY SEGMENT</Card.Title>
							{kpiClicked === '% of Budget'
								? <>
									<Legend legend={customer_count_by_segment_in_p4q_percentage.legend} />
									<div style={{ height: "92%" }}>
										<LineChart
											id={`customer_count_by_segment_in_p4q_percentage-line_chart-segment`}
											data={customer_count_by_segment_in_p4q_percentage.data}
											legend={customer_count_by_segment_in_p4q_percentage.legend}
											isAnimationEnabled={false}
										/>
									</div>
								</>
								: kpiClicked === 'Q/Q Growth %'
									? <>
										<Legend legend={customer_count_by_segment_in_quarter_percentage.legend} />
										<div style={{ height: "92%" }}>
											<LineChart
												id={`customer_count_by_segment_in_quarter_percentage-line_chart-segment`}
												data={customer_count_by_segment_in_quarter_percentage.data}
												legend={customer_count_by_segment_in_quarter_percentage.legend}
												isAnimationEnabled={false}
											/>
										</div>
									</>
									: kpiClicked === 'Y/Y Growth %'
										? <>
											<Legend legend={customer_count_by_segment_in_year_percentage.legend} />
											<div style={{ height: "92%" }}>
												<LineChart
													id={`customer_count_by_segment_in_year_percentage-line_chart-segment`}
													data={customer_count_by_segment_in_year_percentage.data}
													legend={customer_count_by_segment_in_year_percentage.legend}
													isAnimationEnabled={false}
												/>
											</div>
										</>
										: <>
											<div className="d-flex">
												<Legend legend={customer_count_by_segment.legend} />
												{/* {kpiClicked === null
													?  */}
												<BasicDD
													className="ml-auto"
													id={`stacked_column_dd`}
													options={optionOverviewFilter}
													value={stackedColumnSelection}
													setValue={setStackedColumnSelection}
												/>
												{/* : null
												} */}
											</div>
											<div style={{ height: "85%", marginTop: "1rem" }}>
												<StackedColumnChart
													id={`stacked_column_chart`}
													hiddenValueAxis={false}
													isOneHundredPercent={false}
													isPercentage={false}
													largerWidth={true}
													hideLabelBullet={false}
													isOverviewChart={true}
													isGrowthPercentToggable={true}
													stackedColumnSelection={stackedColumnSelection.key}
													selectedRangeValue={'last_completed_quarter'}
													data={customer_count_by_segment.data}
													legend={customer_count_by_segment.legend}
													isAnimationEnabled={false}
												/>
											</div>
										</>
							}
						</Card.Body>
					</Card>
				</Col>
				<Col style={{ maxWidth: "400px" }}>
					<Card className="Segment revenue">
						<Card.Body>
							<Card.Title>CUSTOMER COUNT MIX % BY SEGMENT</Card.Title>
							<Legend legend={segment_revenue_mix.legend} />
							<div style={{ height: "350px" }}>
								<DonutChart
									id="overview-donut_chart"
									totalTitle="CUSTOMER COUNT"
									total={segment_revenue_mix.customer_count}
									data={segment_revenue_mix.data}
									legend={segment_revenue_mix.legend}
									isDollar={false}
									isCount={true}
									isAnimationEnabled={true}
								/>
							</div>
						</Card.Body>
					</Card>
				</Col>
			</Row>
			<Row className="contentDivider pb-30">
				<Col>
					<Card className="revenue">
						<Card.Body>
							<Card.Title>SEGMENT DETAILS</Card.Title>
							<CustomTable
								tableName='segment-details scrollable'
								data={segment_details ? segment_details : []}
								defaultSorted={defaultSorted}
								columns={columns}
							/>
						</Card.Body>
					</Card>
				</Col>
				<Col style={{ maxWidth: "400px" }}>
					<Card className="revenue">
						<Card.Body>
							<Card.Title>Y/Y % GROWTH BY SEGMENT</Card.Title>
							<Legend legend={segment_trends_by_segment.legend} />
							<div style={{ height: "300px" }}>
								<LineChart
									id="overview-line_chart"
									data={segment_trends_by_segment.data}
									legend={segment_trends_by_segment.legend}
									isAnimationEnabled={true}
								/>
							</div>
						</Card.Body>
					</Card>
				</Col>
			</Row>
			<Row className="contentDivider pb-30">
				<Col lg={4} md={12}>
					<Card className="revenue">
						<Card.Body>
							<Card.Title>GROSS ADDS BY segment</Card.Title>
							<Legend legend={gross_adds_by_segment.legend} />
							<div style={{ height: '300px' }}>
								<StackedColumnChart
									id={`gross-stacked_column_chart`}
									hiddenValueAxis={true}
									isOneHundredPercent={false}
									isPercentage={false}
									largerWidth={true}
									hideLabelBullet={false}
									data={gross_adds_by_segment.data}
									legend={gross_adds_by_segment.legend}
									isAnimationEnabled={false}
								/>
							</div>
						</Card.Body>
					</Card>
				</Col>
				<Col lg={4} md={12}>
					<Card className="revenue">
						<Card.Body>
							<Card.Title>ATTRITION BY segment</Card.Title>
							<Legend legend={attrition_by_segment.legend} />
							<div style={{ height: '300px' }}>
								<StackedColumnChart
									id={`attrition-stacked_column_chart`}
									hiddenValueAxis={true}
									isOneHundredPercent={false}
									isPercentage={false}
									largerWidth={true}
									hideLabelBullet={false}
									data={attrition_by_segment.data}
									legend={attrition_by_segment.legend}
									isAnimationEnabled={false}
								/>
							</div>
						</Card.Body>
					</Card>
				</Col>
				<Col lg={4} md={12}>
					<Card className="revenue">
						<Card.Body>
							<Card.Title>ADDS AS % OF ATTRITION BY segment</Card.Title>
							<Legend legend={adds_as_percent_of_attrition_by_segment.legend} />
							<div style={{ height: '300px' }}>
								<LineChart
									id={`adds_attrition-line_chart`}
									data={adds_as_percent_of_attrition_by_segment.data}
									legend={adds_as_percent_of_attrition_by_segment.legend}
									isAnimationEnabled={false}
								/>
							</div>
						</Card.Body>
					</Card>
				</Col>
			</Row>
			<Row className="contentDivider">
				<Col lg={6} md={12}>
					<Card className="revenue">
						<Card.Body>
							<OverviewNotableTable data={notable_adds} tableTitle='NOTABLE ADDS' />
						</Card.Body>
					</Card>
				</Col>
				<Col lg={6} md={12}>
					<Card className="revenue">
						<Card.Body>
							<OverviewNotableTable data={notable_attritions} tableTitle='NOTABLE ATTRITION' />
						</Card.Body>
					</Card>
				</Col>
			</Row>
		</Container>
	);
};

export default OverviewContent;