import React, { useState } from 'react';
import { Container, Row, Col, Button } from 'react-bootstrap';
import { Link } from 'react-router-dom';
import Monitor from '../../assets/images/laptop-data-mart-generator.png';
import { ReactComponent as Person1 } from '../../assets/images/boy-shuffle-reports.svg';
import { ReactComponent as Person2 } from '../../assets/images/girl-using-laptop.svg';
import { motion } from 'framer-motion';
import './helpCenterDataMart.scss';
import GeneratorModal from './GeneratorModal';

const animateContent = {
  hidden: {
    opacity: 0,
    x: '-100px',
  },
  visible: {
    opacity: 1,
    x: '0',
    transition: { duration: 1 }
  }
}

const animateMonitor = {
  hidden: {
    opacity: 0,
    x: '100px',
  },
  visible: {
    opacity: 1,
    x: '0',
    transition: { duration: 1 }
  }
}

const animatePerson = {
  hidden: {
    opacity: 0,
    y: '50px',
  },
  visible: {
    opacity: 1,
    y: '0',
    transition: { duration: .5, delay: .6 }
  }
}

const Generator = () => {
  const [showGeneratorModal, setShowGeneratorModal] = useState(false);

  const onCancelModal = () => setShowGeneratorModal(false);
  const onShowFilter = () => setShowGeneratorModal(true);

  return (
    <>
      <div className={`page-content`}>
        <Container className="generator-page" fluid>
          <Row>
            <Col xl={7}>
              <div className="inner-wrap">
                <div className="block content-block">
                  <motion.div
                    variants={animateContent}
                    initial='hidden'
                    animate='visible'
                  >
                    <h2 className="title">Bespoke reports at your fingertips</h2>
                    <p className="text">Create custom reports and access an abundant source of high-quality data that has been curated and
                    democratized for your unique requirements.</p>
                    <Button onClick={onShowFilter} className="btn-blank-report">Use the Wizard</Button>
                    <Link to='/self-service-rerport/blank-report' className="btn btn-primary">Create a Blank Report</Link>
                  </motion.div>
                </div>
              </div>
            </Col>
            <Col xl={5}>
              <div className="inner-wrap">
                <div className="block image-block">
                  <motion.img
                    src={Monitor}
                    alt=""
                    className="img-fluid"
                    variants={animateMonitor}
                    initial='hidden'
                    animate='visible'
                  />
                  <motion.div
                    className="person person-01"
                    variants={animatePerson}
                    initial='hidden'
                    animate='visible'
                  >
                    <Person1 />
                  </motion.div>
                  <motion.div
                    className="person person-02"
                    variants={animatePerson}
                    initial='hidden'
                    animate='visible'
                  >
                    <Person2 />
                  </motion.div>
                </div>
              </div>
            </Col>
          </Row>
        </Container>

        <GeneratorModal
          showGeneratorModal={showGeneratorModal}
          onCancelModal={onCancelModal}
        />
      </div>
    </>
  )
};

export default Generator;
