import React, { useState, useEffect } from 'react';
import { Container, Row, Col, Card } from 'react-bootstrap';
import { ImArrowUpRight2, ImArrowDownRight2 } from "react-icons/im";
import axiosRetry from 'axios-retry';
import axios from 'axios';
import MetroModal from './MetroModal';
import {RadioDD} from '../../components/customDropdown';
import CustomTable from '../../components/customTable/CustomTable';
import SparklineCustom from '../../components/chart/SparklineCustom';
import StackedColumnChart from '../../components/chart/StackedColumnChart';
import StackedColumnBar from '../../components/chart/StackedColumnBar';
import MapChart from '../../components/chart/MapChart';
import LineChart from '../../components/chart/LineChart';
import Legend from '../../components/legend/Legend';

import { API_BASE_URL } from '../../constants/config';
import { convertToUSDGrowth, convertToPercent, convertToWholePercent } from '../../constants/formatter';


const group_by = [
  {
    key: 'hq_region',
    text: 'HQ Region'
  },
  {
    key: 'vertical',
    text: 'Vertical'
  }
]

const MetroContent = (props) => {
  const { metroData, modalShow, modalData, setModalShow, setModalData, setReferenceID } = props;

  const [selectedMetro, setSelectedMetro] = useState(null);

  const byRank = metroData ? metroData.metro_ranking.by_rank : [];
  const byTopRank = metroData ? metroData.metro_ranking.by_top_rank : [];
  const byRankId = metroData ? metroData.metro_ranking.by_rank_id : [];
  const deploymentMap = metroData ? metroData.deployment_map : [];
  const contribution_to_total_usd_by_metro_tier = metroData ? metroData.contribution_to_total_usd_by_metro_tier : [];
  const y_by_y_percent_growth_by_metro_tier = metroData ? metroData.y_by_y_percent_growth_by_metro_tier : [];
  const metro_number_by_mrc_size = metroData ? metroData.metro_number_by_mrc_size : [];


  const [isLoading, setIsLoading] = useState(true);
  const [microStackedChart, setData] = useState(null);
  const [hqStackedChart, setHQRegionStackedChart] = useState(null);

  // radio for vertical values
  const vertical_initial_value = group_by ? group_by[0] : null;
  const [vertical_onApply, set_vertical_onApply] = useState(vertical_initial_value);
  const [vertical_val, set_vertical_val] = useState(vertical_initial_value);

  const onApplyVertical = () => {
    set_vertical_onApply(vertical_val);
    if (vertical_val === 'vertical') {
      if (microStackedChart) {
        if (byRank) {
          setHQRegionStackedChart(byRank.data[0].stacked_column_bar_data)
          byRank.data.map(items => {
            items.stacked_column_bar_data = microStackedChart;
          })
        }
      }
    } else if (vertical_val === 'hq_region') {
      if (microStackedChart) {
        if (byRank) {
          byRank.data.map(items => {
            items.stacked_column_bar_data = hqStackedChart;
          })
        }
      }
    }
  };
  const onCancelVertical = () => {
    set_vertical_val(vertical_onApply);
  };




  // Axios call
  const fetchData = () => {
    let url = `${API_BASE_URL}/charts/micro_stacked_column_chart/5-segments.json?print=pretty`;
    // Uncomment only when API is now available and working
    // const token = localStorage.getItem('token');
    // let url = `${API_BASE}/orbis/api/totalrevenuemetro`;
    // let defaultQueryParameters = `?measure=${measure_val_onApply.key}&range=${range_val_onApply.key}`;
    // let regionParameter = region_val_onApply.length > 0 ? `&region=${region_val_onApply.join(",").toString()}` : '';

    setIsLoading(true);

    axiosRetry(axios, { retries: 3 });

    axios({
      method: 'GET',
      url: url
      // Uncomment only when API is now available and working
      // url: url + defaultQueryParameters + regionParameter,
      // headers: {
      //   'Content-Type': 'application/json',
      //   "Authorization": `bearer ${token}`
      // }
    }).then(result => {
      // Uncomment only if for API testing purposes
      // console.log(result.config.url);
      setData(result.data);
      setIsLoading(false);
    }).catch(error => {
      console.log(error);
      setData(null);
      setIsLoading(false);
    });
  };

  useEffect(() => {
    fetchData();
  },
    []
    // Uncomment only when API is now available and working
    // [
    //   region_val_onApply,
    //   measure_val_onApply,
    //   range_val_onApply 
    // ]
  );

  const indicatorPercent = (cell, row) => {
    let bgColor = cell >= 0.5 ? 'indicator-up' : cell <= -0.5 ? 'indicator-down' : 'indicator-middle';
    let icon = cell >= 0.5 ? <ImArrowUpRight2 /> : cell <= -0.5 ? <ImArrowDownRight2 /> : <span>&nbsp;</span>;

    return (
      <>
        <span className={`indicator ${bgColor}`}>
          {icon}
          {convertToPercent(cell)}
        </span>
      </>
    );
  };

  const formatUSDGrowth = (cell, row) => {
    return (
      <span>
        {convertToUSDGrowth(cell)}
      </span>
    );
  };

  const formatWholePercent = (cell, row) => {
    return (
      <span>
        {convertToWholePercent(cell)}
      </span>
    );
  };
  const barchart = (cell, row) => {
    return (
      <StackedColumnBar data={cell} />
    )
  }

  const sparkline = (cell, row) => {
    return (
      <SparklineCustom loadedData={cell} />
    );
  }

  const metro = (cell, row) => {
    return (
      <>
        <img className="flag-icon" src={cell.image_url} alt={cell.name} />
        <span className="body-name">{cell.name}</span>
      </>

    )
  }

  const rankFormat = (cell, row, rowIndex, formatExtraData) => {
    return (
      <span>{rowIndex + 1}</span>
    );
  }

  const handleOnSelect = (row, isSelect, rowIndex) => {
    let modalDataByRank;
    let metroName = row.metro.name;

    setSelectedMetro(metroName);

    for (const key in byRankId) {
      const element = byRankId[key];

      if (element._id === row._id) {
        modalDataByRank = element
      }
    }

    if (isSelect) {
      setModalShow(true);
      setReferenceID(row._id);
      setModalData(modalDataByRank);
    }
  };

  const selectRow = {
    mode: 'radio',
    clickToSelect: true,
    hideSelectColumn: true,
    onSelect: handleOnSelect,
    classes: 'selected-row',
    bgColor: '#ecf2ff'
  };

  const rowClasses = () => {
    let classes = 'selectable';
    return classes
  };

  const defaultSorted = [
    {
      dataField: 'mix_percent',
      order: 'desc',
    }
  ];

  const columns = [
    {
      dataField: '_id',
      text: 'id',
      hidden: true
    },
    {
      dataField: '',
      text: 'Rank',
      classes: 'rank center',
      headerClasses: 'rank center',
      formatter: rankFormat,
    },
    {
      dataField: 'metro',
      text: 'METRO',
      
      
      classes: 'segment',
      headerClasses: 'segment',
      formatter: metro
    },
    {
      dataField: 'stacked_column_bar_data',
      text: '',
      classes: 'sparkline-cell center',
      headerClasses: 'sparkline-cell center',
      formatter: barchart
    },
    {
      dataField: 'mrc_usd',
      text: 'MRC$',
      sort: true,
      classes: 'right',
      headerClasses: 'right',
      formatter: formatUSDGrowth
    },
    {
      dataField: 'mix_percent',
      text: 'MIX %',
      sort: true,
      classes: 'right',
      headerClasses: 'right',
      formatter: formatWholePercent
    },
    {
      dataField: 'q_by_q_growth_percent',
      text: 'Q/Q % GROWTH',
      sort: true,
      classes: 'with-ellipsis right',
      headerClasses: 'right',
      formatter: indicatorPercent,
    },
    {
      dataField: 'q_to_q_usd_growth',
      text: 'Q-Q $GROWTH',
      sort: true,
      classes: 'right',
      headerClasses: 'right',
      formatter: formatUSDGrowth
    },
    {
      dataField: 'y_by_y_growth_percent',
      text: 'Y/Y % GROWTH',
      sort: true,
      classes: 'with-ellipsis right',
      headerClasses: 'right',
      formatter: indicatorPercent,
    },
    {
      dataField: 'y_to_y_usd_growth',
      text: 'Y-Y $GROWTH',
      sort: true,
      classes: 'right',
      headerClasses: 'right',
      formatter: formatUSDGrowth
    }
  ];


  // TODO - ADD AXIOS CALL WHEN API FOR MAP FILTERING IS READY
  return (
    <Container fluid className="metro">
      <MetroModal
        show={modalShow}
        onHide={() => setModalShow(false)}
        data={modalData}
        selectedMetro={selectedMetro}
      />

      <Row className="pb-30">
        <Col md={4}>
          <Card className="revenue">
            <Card.Body>
              <Card.Title>deployment map</Card.Title>
              <Legend legend={deploymentMap.legend} />
              <div style={{ height: "400px ", paddingTop: "2rem" }}>
                <MapChart
                  id={"total_revenue_map_chart"}
                  largerWidth={true}
                  data={deploymentMap.data}
                />
              </div>
            </Card.Body>
          </Card>
        </Col>
        <Col md={8}>
          <Card className="revenue">
            <Card.Body>
              <Card.Title>metro ranking</Card.Title>
              <RadioDD
                title="Vertical"
                id="vertical"
                options={group_by}
                radioVal={vertical_val}
                setRadioVal={set_vertical_val}
                onApply={onApplyVertical}
                onCancel={onCancelVertical}
              />
              <CustomTable
                tableName='metro-ranking scrollable'
                data={byRank === undefined ? [] : byRank.data}
                selectRow={selectRow} // For Selecting Row ===
                defaultSorted={defaultSorted} 
                rowClasses={rowClasses}
                columns={columns}
              />
              {/* <Table responsive className="separate-table">
                <tbody>
                  {
                    byTopRank.map((data, i) => {
                      return (
                        <tr key={i} className={i === 0 ? 'border-head height1' : 'height1'}>
                          <td className="rank"></td>
                          <td className="metro">{data.metro}</td>
                          <td className="sparkline-cell">
                            <StackedColumnBar data={data.stacked_column_bar_data} />
                          </td>
                          <td className="text-right" >{formatUSDGrowth(data.bkgs)}</td>
                          <td className="text-right" >{formatWholePercent(data.mix_percent)}</td>
                          <td className="text-right">
                            {indicatorPercent(data.y_by_y_growth_percent)}
                          </td>
                          <td className="text-right">{formatUSDGrowth(data.y_to_y_usd_growth)}</td>
                          <td className="text-right">{formatWholePercent(data.percent_total_usd_growth)}</td>
                        </tr>
                      )
                    })
                  }
                </tbody>
              </Table> */}

            </Card.Body>
          </Card>
        </Col>
      </Row>
      <Row className="contentDivider">
        <Col md={4} xs={12}>
          <Card className="revenue">
            <Card.Body>
              <Card.Title>top 10 metros contribution total</Card.Title>
              <Legend legend={contribution_to_total_usd_by_metro_tier.legend} />
              <div style={{ height: '250px' }}>
                <StackedColumnChart
                  id="contribution_to_total_usd_by_metro_tier_column_chart"
                  largerWidth={true}
                  isOneHundredPercent={true}
                  isPercentage={true}
                  insideLabel={true}
                  percentLabel={true}
                  hiddenValueAxis={true}
                  data={contribution_to_total_usd_by_metro_tier.data}
                  legend={contribution_to_total_usd_by_metro_tier.legend}
                  isAnimationEnabled={true}
                />
              </div>
            </Card.Body>
          </Card>
        </Col>
        <Col md={4} xs={12}>
          <Card className="revenue">
            <Card.Body>
              <Card.Title>Metro # By MRC Size</Card.Title>
              <Legend legend={y_by_y_percent_growth_by_metro_tier.legend} />
              <div style={{ height: "300px" }}>
                <LineChart
                  id={"metro_number_by_growth_line_chart"}
                  data={y_by_y_percent_growth_by_metro_tier.data}
                  legend={y_by_y_percent_growth_by_metro_tier.legend}
                  isAnimationEnabled={true}
                />
              </div>
            </Card.Body>
          </Card>
        </Col>
        <Col md={4} xs={12}>
          <Card className="revenue">
            <Card.Body>
              <Card.Title>Metro # By MRC Size</Card.Title>
              <Legend legend={metro_number_by_mrc_size.legend} />
              <div style={{ height: "250px" }}>
                <StackedColumnChart
                  id="total_revenue_metro_number_by_mrc_size"
                  largerWidth={true}
                  hiddenValueAxis={true}
                  isOneHundredPercent={false}
                  isPercentage={false}
                  insideLabel={true}
                  hideLabelBullet={false}
                  data={metro_number_by_mrc_size.data}
                  legend={metro_number_by_mrc_size.legend}
                  isAnimationEnabled={true}
                />
              </div>
            </Card.Body>
          </Card>
        </Col>
      </Row>

    </Container>
  );
};

export default MetroContent;