import React, { useState, useEffect } from 'react';

import PageHeader from '../../components/pageHeader/PageHeader';

import Overview from './Overview';
import Destination from './Destination';
import Customer from './Customer';

import './globalPlatform.scss';


const GlobalPlatform = (props) => {
  let path = props.location.pathname;
  const currentPath = path.substring(path.lastIndexOf('/') + 1);
  const [activePage, setActivePage] = useState(currentPath);

  const handleActivePage = (page) => {
    setActivePage(page)
  }

  const handleBreadcrubsPreviousLink = () => {
    setActivePage('Overview')
  }

  useEffect(() => {
    setActivePage(currentPath)
  }, [currentPath])

  const breadcrumbs = [
    {
      path: '/analytics-dashboard/page',
      label: 'Analytics Dashboard '
    },
    {
      path: path === '/analytics-dashboard/global-platform/overview' ? null : '/analytics-dashboard/global-platform/overview',
      label: 'Global Platform'
    },
    {
      label: activePage,
    }
  ]

  const innerPage = [
    {
      path: '/analytics-dashboard/global-platform/overview',
      label: 'Overview'
    },
    // {
    //   path: '/analytics-dashboard/global-platform/growth',
    //   label: 'Growth'
    // },
    {
      path: '/analytics-dashboard/global-platform/destination',
      label: 'Destination'
    },
    {
      path: '/analytics-dashboard/global-platform/customer',
      label: 'Customer'
    }
  ]

  const [activeTab, setActiveTab] = useState('multi-metro');

  return (
    <div className="global-platform-page">
      <PageHeader
        title="GLOBAL PLATFORM"
        color="dark-orange"
        innerPage={innerPage}
        breadcrumbs={breadcrumbs}
        previousPage="/analytics-dashboard/page"
        handleActivePage={handleActivePage}
        handleBreadcrubsPreviousLink={handleBreadcrubsPreviousLink}
      />
      <div className="page-wrapper">
        {currentPath === 'overview' && <Overview activeTab={activeTab} setActiveTab={setActiveTab} />}
        {currentPath === 'destination' && <Destination activeTab={activeTab} setActiveTab={setActiveTab} />}
        {currentPath === 'customer' && <Customer activeTab={activeTab} setActiveTab={setActiveTab} />}
      </div>
    </div>
  );
}

export default GlobalPlatform;
