import React, { useState, useEffect } from 'react';
import { useHistory } from 'react-router-dom';
import PageHeader from '../../components/pageHeader/PageHeader';

import Navigator from './Navigator';
import Financials from './Financials';
import Deployments from './Deployments';
import Ecosystem from './Ecosystem';
import NPS from './NPS';
import Tickets from './Tickets';
import GeneralInfo from './GeneralInfo';

import './accountNavigator.scss';


const CustomerNavigator = (props) => {
  let path = props.location.pathname;
  let history = useHistory();
  const search = history.location.search ? history.location.search.replace("?customer=", "") : false;
  const currentPath = path.substring(path.lastIndexOf('/') + 1);
  const [activePage, setActivePage] = useState(currentPath);

  const handleActivePage = (page) => {
    setActivePage(page)
  }

  const handleBreadcrubsPreviousLink = () => {
    setActivePage('Navigator')
  }

  useEffect(() => {
    setActivePage(currentPath)
  }, [currentPath])

  let innerPage, breadcrumbs = [];

  if (currentPath === 'navigator') {
    breadcrumbs = [
      {
        path: path === '/account-navigator/navigator' ? null : '/account-navigator/navigator',
        label: 'Account Navigator'
      },
      {
        label: activePage,
      }
    ]
    innerPage = [
      {
        path: '/account-navigator/navigator',
        label: 'Navigator'
      }
    ]
  } else {
    breadcrumbs = [
      {
        path: path === '/account-navigator/navigator' ? null : '/account-navigator/navigator',
        label: 'Account Navigator'
      },
      {
        label: 'Account Details',
      },
      {
        label: activePage,
      }
    ]
    innerPage = [
      {
        path: '/account-navigator/navigator/financials?customer=' + search.replace(/%20/g, " "),
        label: 'Financials'
      },
      {
        path: '/account-navigator/navigator/deployments?customer=' + search.replace(/%20/g, " "),
        label: 'Deployments'
      },
      {
        path: '/account-navigator/navigator/ecosystem?customer=' + search.replace(/%20/g, " "),
        label: 'Ecosystem'
      },
      {
        path: '/account-navigator/navigator/nps?customer=' + search.replace(/%20/g, " "),
        label: 'NPS'
      },
      {
        path: '/account-navigator/navigator/tickets?customer=' + search.replace(/%20/g, " "),
        label: 'Tickets'
      },
      {
        path: '/account-navigator/navigator/general-info?customer=' + search.replace(/%20/g, " "),
        label: 'General Info'
      }
    ]
  }

  return (
    <div className="account-navigator-page">
      <PageHeader
        title={currentPath === 'navigator' ? 'ACCOUNT NAVIGATOR' : search ? search.replace(/%20/g, " ") : 'CUSTOMER NAME'}
        color="dark-orange"
        innerPage={innerPage}
        breadcrumbs={breadcrumbs}
        previousPage="/account-navigator/navigator"
        handleActivePage={handleActivePage}
        handleBreadcrubsPreviousLink={handleBreadcrubsPreviousLink}
      />
      <div className="page-wrapper">
        {currentPath === 'navigator' && <Navigator />}
        {currentPath === 'financials' && <Financials />}
        {currentPath === 'deployments' && <Deployments />}
        {currentPath === 'ecosystem' && <Ecosystem />}
        {currentPath === 'nps' && <NPS />}
        {currentPath === 'tickets' && <Tickets />}
        {currentPath === 'general-info' && <GeneralInfo />}
      </div>
    </div>
  );
}

export default CustomerNavigator;
