import React, { useState } from 'react';
import { Modal, Button, Form } from 'react-bootstrap';
import { motion, AnimatePresence } from 'framer-motion';
import { BasicDD } from '../../components/customDropdown';

const productOption = [
  {
    key: 'all_dashboards',
    text: 'All Dashboards'
  },
  {
    key: 'rev',
    text: 'Revenue'
  },
  {
    key: 'bkgs',
    text: 'Bookings'
  },
  {
    key: 'churn',
    text: 'Churn'
  },
  {
    key: 'pricing',
    text: 'Pricing'
  },
  {
    key: 'cbt',
    text: 'Cross Border'
  },
  {
    key: 'cp',
    text: 'Channel Performance'
  },
  {
    key: 'nlae',
    text: 'New Logos Extension'
  },
  {
    key: 'sp',
    text: 'Sales Performance (Team)'
  },
  {
    key: 'rp',
    text: 'Rep Performance'
  },
  {
    key: 'cc',
    text: 'Customer Count'
  },
  {
    key: 'gp',
    text: 'Global Platform'
  },
  {
    key: 'eco',
    text: 'Ecosystem'
  },
  {
    key: 'cust',
    text: 'Customer NPS'
  },
  {
    key: 'cm',
    text: 'Colo Market Growth'
  },
  {
    key: 'ms',
    text: 'Market Share'
  }
]

const AddMetricsModal = (props) => {
  const { showBuilderModal, onCancelModal, selectedItem, setSelectedItem, setNextPage, stepList } = props;
  const [checkedList, setCheckedList] = useState(stepList);

  // Basic DD
  const product_initial_value = productOption ? productOption[0] : null;
  const [productSelection, setProductSelection] = useState(product_initial_value);

  const onCancel = () => {
    onCancelModal()
  }

  const checkButton = (item) => {
    var newArray = selectedItem.slice();
    var finalArray = [];
    var arr = stepList.map((val) => {
      if (val.isChecked === true && val.key === item.key) {
        val.isChecked = false;
        finalArray = selectedItem.filter(data => {
          return data.key != item.key
        })
        setSelectedItem(finalArray)
      } else if (val.key === item.key) {
        val.isChecked = true;
        newArray.push(item);
        setSelectedItem(newArray)
      }
      return val
    })

    setCheckedList(arr);
  }

  return (
    <>
      <Modal
        show={showBuilderModal}
        onHide={onCancel}
        className="product-modal"
        backdrop="static"
        keyboard={false}
        centered
      >
        <div className="to-prevent-glitch">prevent glitch</div>
        <div className="content-wrapper">

          <AnimatePresence exitBeforeEnter>
            <ContentContainer
              modalTitle='CHOOSE ADD METRICS TO ADD'
              modalRight={
                <div className="filters custom-scroll">
                  <BasicDD
                    title="All Categories"
                    id="all_categories"
                    addTitleOnWide
                    value={productSelection}
                    setValue={setProductSelection}
                    options={productOption}
                  />
                </div>
              }
            >
              <Modal.Body>
                <Form className="product-form">
                  <div className="product-container">
                    {productSelection.key === 'all_dashboards'
                      ? <>
                        {checkedList && checkedList.sort(function (a, b) {
                          if (a.text < b.text) { return -1; }
                          if (a.text > b.text) { return 1; }
                          return 0;
                        }).map((item) => {
                          return (
                            <div className="btn-container" key={item.key}>
                              <Button
                                variant="link"
                                id={item.key}
                                className={`checkbox-${item.isChecked}`}
                                onClick={() => checkButton(item)}
                              >
                                {item.icon}{item.text}
                              </Button>
                            </div>
                          )
                        })}
                      </>
                      : <>
                        {checkedList && checkedList.sort(function (a, b) {
                          if (a.text < b.text) { return -1; }
                          if (a.text > b.text) { return 1; }
                          return 0;
                        }).map((item) => {
                          return (
                            <div className="btn-container" key={item.key}>
                              <Button
                                variant="link"
                                id={item.key}
                                className={`checkbox-${item.isChecked}`}
                                onClick={() => checkButton(item)}
                              >
                                {item.icon}{item.text}
                              </Button>
                            </div>
                          )
                        }).filter(
                          (child) => !productSelection.key || child.key.toLowerCase().split('_')[0] === productSelection.key
                        )}
                      </>
                    }
                  </div>
                </Form>
              </Modal.Body>

              <Modal.Footer>
                <Button variant="light" onClick={() => onCancel()}>Cancel</Button>
                {/* <Button variant="primary" type="submit" onClick={() => goTo(selectedItem)} > Next</Button> */}
                <Button variant="primary" type="submit" onClick={() => setNextPage(true)} > Next</Button>
              </Modal.Footer>
            </ContentContainer>
          </AnimatePresence>
        </div>
      </Modal>
    </>
  )
};

const ContentContainer = ({
  modalTitle,
  modalRight,
  children
}) => {
  return (
    <motion.div className={`content-container`}
      animate='visible'
    >
      {(modalTitle && modalRight) &&
        <Modal.Header>
          <Modal.Title>{modalTitle}</Modal.Title>
          {modalRight}
        </Modal.Header>
      }
      {children}
    </motion.div>
  )
}


export default AddMetricsModal;