
import React, { useState, useEffect } from 'react';
import axiosRetry from 'axios-retry';
import axios from 'axios';
import { Container, Row, Col, Card } from 'react-bootstrap';
import { ImArrowUpRight2, ImArrowDownRight2 } from "react-icons/im";

import PartnerModal from './PartnerModal';

import CustomTable, { formatLongText } from '../../components/customTable/CustomTable';
import Legend from '../../components/legend/Legend';
import SparklineCustom from '../../components/chart/SparklineCustom';
import StackedColumnChart from '../../components/chart/StackedColumnChart';
import MapChart from '../../components/chart/MapChart';
import LineChart from '../../components/chart/LineChart';
import Loader from '../../components/loader/Loader';

import { API_BASE } from '../../constants/config';
import { convertToUSDGrowth, convertToPercent, convertToWholePercent } from '../../constants/formatter';


const ExportContent = (props) => {
  const { customerData, modalShow, modalData, setModalShow, setModalData, setReferenceID, measure_val_onApply, range_val_onApply, vertical_val_onApply, hyperscaler_val_onApply, region_val_onApply, metro_val_onApply, rankingVal_onApply } = props;
  const [map_region_val, set_map_region_val] = useState(null);
  const [isRankingTableFiltered, setIsRankingTableFiltered] = useState(false);
  const [selectedCustomer, setCustomerSelected] = useState(null);
  const [isLoading, setIsLoading] = useState(true);
  const [tableData, setTableData] = useState([]);
  const [mdalRankingById, setModalRankingById] = useState([]);
  const [selectedMetro, setSelectedMetro] = useState(null);
  const [toggleSelectedRow, setToggleSelectedRow] = useState();
  console.log(customerData)

  let topLabelTitle = rankingVal_onApply ? `${rankingVal_onApply.topBottom.split('_')[1]} ${rankingVal_onApply.numberInput} ` : '';
  const y_by_y_percent_growth_by_metro_tier = customerData ? customerData.y_by_y_percent_growth_by_customer_tier : [];
  const contribution_to_total_usd_by_metro_tier = customerData ? customerData.contribution_to_total_usd_by_customer_tier : [];
  const deploymentMap = customerData ? customerData.deployment_map : [];
  const byRank = customerData ? customerData.customer_ranking.by_rank : [];
  const byRankId = customerData ? customerData.customer_ranking.by_rank_id : [];

  const indicatorPercent = (cell, row) => {
    let bgColor = cell >= 0.5 ? 'indicator-up' : cell <= -0.5 ? 'indicator-down' : 'indicator-middle';
    let icon = cell >= 0.5 ? <ImArrowUpRight2 /> : cell <= -0.5 ? <ImArrowDownRight2 /> : <span>&nbsp;</span>;

    return (
      <>
        <span className={`indicator ${bgColor}`}>
          {icon}
          {convertToPercent(cell)}
        </span>
      </>
    );
  };

  const formatUSDGrowth = (cell, row) => {
    let new_cell = convertToUSDGrowth(cell);
    return formatLongText(new_cell, row)
  };

  const formatWholePercent = (cell, row) => {
    return (
      <span>
        {convertToWholePercent(cell)}
      </span>
    );
  };

  const formatToPercent = (cell, row) => {
    return (
      <span>
        {convertToPercent(cell)}
      </span>
    );
  };

  const textFormat = (cell, row) => {
    return formatLongText(cell, row)
  };

  const sparkline = (cell, row) => {
    return (
      <SparklineCustom loadedData={cell} />
    );
  };


  const rankFormat = (cell, row, rowIndex, formatExtraData) => {
    return (
      <span>{rowIndex + 1}</span>
    );
  };


  // For Selecting Row ===
  const handleOnSelect = (row, isSelect, rowIndex) => {
    let modalDataByRank;

    setCustomerSelected(row.customer);

    for (const key in mdalRankingById) {
      const element = mdalRankingById[key];

      if (element._id === row._id) {
        modalDataByRank = element
      }
    }

    if (isSelect) {
      setModalShow(true);
      setReferenceID(row._id);
      setModalData(modalDataByRank);
    }
  };

  const selectRow = {
    mode: 'radio',
    clickToSelect: true,
    hideSelectColumn: true,
    onSelect: handleOnSelect,
    classes: 'selected-row',
    bgColor: '#ecf2ff'
  };

  const rowClasses = () => {
    let classes = 'selectable';
    return classes
  };
  // For Selecting Row ===


  const defaultSorted = [
    {
      dataField: 'mrr_mix_percent',
      order: 'desc',
    }
  ];


  const columns = [
    {
      dataField: '_id',
      text: 'id',
      hidden: true
    },
    {
      dataField: '',
      text: 'Rank',
      classes: 'rank center',
      headerClasses: 'rank center',
      formatter: rankFormat,
    },
    {
      dataField: 'customer',
      text: 'CUSTOMER',
      classes: 'customer',
      headerClasses: 'customer',
      formatter: textFormat
    },
    {
      dataField: 'hq_region',
      text: 'HQ Region',
      sort: true,
      formatter: textFormat
    },
    {
      dataField: 'hq_country',
      text: 'HQ Country',
      sort: true,

    },
    {
      dataField: 'vertical',
      text: 'vertical',

    },
    {
      dataField: 'mrc_usd',
      text: 'MRC$',
      classes: 'right',
      headerClasses: 'right',
      formatter: formatUSDGrowth
    },
    {
      dataField: 'mix_percent',
      text: 'MIX %',
      classes: 'right',
      headerClasses: 'right',
      formatter: formatToPercent
    },
    {
      dataField: 'q_by_q_growth_percent',
      text: 'Q/Q % GROWTH',
      sort: true,
      formatter: indicatorPercent,
      classes: 'with-ellipsis center',
      headerClasses: 'center',
    },
    {
      dataField: 'p4q_growth_percent',
      text: 'P4Q % GROWTH',
      sort: true,
      formatter: indicatorPercent,
      classes: 'with-ellipsis center',
      headerClasses: 'center',
    },
    {
      dataField: 'y_by_y_growth_percent',
      text: 'Y/Y % GROWTH',
      sort: true,
      formatter: indicatorPercent,
      classes: 'with-ellipsis center',
      headerClasses: 'center',
    },
  ];

  const onCloseModal = async () => {
    await setModalShow(false)
    await setSelectedMetro(null);
    await setReferenceID(null);
    await setToggleSelectedRow()

    setTimeout(() => {
      setModalData(null);
    }, 1000);
  };

  const onRankingTableFiltered = () => {
    // const token = localStorage.getItem('token');
    // let url = `${API_BASE}/orbis/api/customerrevenuecustomer`;
    // let showValueParameter = `&showvalue=${rankingVal_onApply.numberInput}`;
    // let showParameter = `&show=${rankingVal_onApply.topBottom.split('_')[1]}`;
    // let regionParameter = region_val_onApply.length > 0 ? `&region=${region_val_onApply.join(",").toString()}` : '';
    // let metroParameter = metro_val_onApply.length > 0 ? `&rmetro=${metro_val_onApply.join(",").toString()}` : '';
    // let mapRegionParameter = map_region_val != null ? `&mapregion=${map_region_val}` : '';

    setIsLoading(true);

    if (isRankingTableFiltered === true) {
      axiosRetry(axios, { retries: 3 });

      // axios({
      //   method: 'GET',
      //   url: url  + showValueParameter + showParameter + regionParameter + metroParameter + mapRegionParameter,
      //   headers: {
      //     'Content-Type': 'application/json',
      //     "Authorization": `bearer ${token}`
      //   }
      // }).then(result => {
      //   let customer_ranking_table = result.data.customer_ranking;
      //   // Uncomment only if for API testing purposes
      //   // console.log(result.config.url);
      //   setIsLoading(false);
      //   setTableData(customer_ranking_table.by_rank.data);
      //   setModalRankingById(customer_ranking_table.by_rank_id);
      // }).catch(error => {
      //   setIsLoading(false);
      //   console.log(error);
      //   setTableData([]);
      // });

    } else {
      setIsLoading(false);
      setTableData(byRank.data);
      setModalRankingById(byRankId);
    }
  };

  useEffect(() => {
    onRankingTableFiltered();
  }, [map_region_val, onRankingTableFiltered]);

  return (
    <Container fluid>
      <PartnerModal
        show={modalShow}
        onClose={onCloseModal}
        data={modalData}
        selectedCustomer={selectedCustomer}
      />
      <Row className="contentDivider" style={{ paddingBottom: '30px' }}>
        <Col md={6}>
          <Row>
            <Col md={12}>
              <Card className="revenue-card">
                <Card.Body>
                  <Card.Title>export map</Card.Title>
                  <Legend legend={deploymentMap.legend} />
                  <div style={{ height: "450px ", paddingTop: "2rem" }}>
                    <MapChart
                      id={"cross_border_map_chart"}
                      largerWidth={true}
                      data={deploymentMap.data}
                      setMapRegion={set_map_region_val}
                      setIsRankingTableFiltered={setIsRankingTableFiltered}
                    />
                  </div>
                  {/* </> */}
                </Card.Body>
              </Card>
            </Col>
          </Row>
          <Row className="contentDivider pt-30">
            <Col md={6}>
              <Card className="revenue-card">
                <Card.Body>
                  <Card.Title>contribution to total $ by customer tier</Card.Title>
                  <Legend legend={contribution_to_total_usd_by_metro_tier.legend} />
                  <div style={{ height: '450px' }}>
                    <StackedColumnChart
                      id="contribution_to_total_usd_by_metro_tier_column_chart"
                      largerWidth={true}
                      isOneHundredPercent={true}
                      isPercentage={true}
                      insideLabel={true}
                      percentLabel={true}
                      hiddenValueAxis={true}
                      data={contribution_to_total_usd_by_metro_tier.data}
                      legend={contribution_to_total_usd_by_metro_tier.legend}
                    />
                  </div>
                </Card.Body>
              </Card>
            </Col>
            <Col md={6}>
              <Card className="revenue-card">
                <Card.Body>
                  <Card.Title>y/y % growth by customer tier</Card.Title>
                  <Legend legend={y_by_y_percent_growth_by_metro_tier.legend} />
                  <div style={{ height: "450px" }}>
                    <LineChart
                      id={"cross_border_overview_line_chart"}
                      data={y_by_y_percent_growth_by_metro_tier.data}
                      legend={y_by_y_percent_growth_by_metro_tier.legend}
                    />
                  </div>
                </Card.Body>
              </Card>
            </Col>
          </Row>

        </Col>
        <Col md={6}>
          <Card className="revenue-card">
            <Card.Body>
              <Card.Title>customer ranking</Card.Title>
              {isLoading ? <Loader /> :
                <>
                  <CustomTable
                    tableName='customer-ranking scrollable'
                    data={byRank.data === undefined ? [] : tableData}
                    selectRow={selectRow} // For Selecting Row ===
                    defaultSorted={defaultSorted}
                    rowClasses={rowClasses}
                    columns={columns}
                  />
                </>}
            </Card.Body>
          </Card>
        </Col>
      </Row>

    </Container>
  );
};

export default ExportContent;