import React, { useState } from 'react';
import { ImArrowDownRight2, ImArrowUpRight2 } from "react-icons/im";
import { Modal, Row, Col } from 'react-bootstrap';
import KPI_NEW from '../../components/kpi/KPI_NEW';
import Legend from '../../components/legend/Legend';
import CustomTable, { formatLongText } from '../../components/customTable/CustomTable';
import MicroBarChart from '../../components/chart/MicroBarChart';
import StackedColumnChart from '../../components/chart/StackedColumnChart';
import ChordDiagramChart from '../../components/chart/ChordDiagramChart';
import SparklineCustom from '../../components/chart/SparklineCustom';
import { convertToUSDGrowth, convertToPercent, convertToWholePercent, convertToCount } from '../../constants/formatter';
import { optionGroupEcosystem } from '../../constants/filterOptions';
import DonutChart from '../../components/chart/DonutChart';
import { RadioDD } from '../../components/customDropdown';

const MetroModal = (props) => {
	const { show, onClose, data, data2, selectedMetro } = props;
	console.log(data2)

	// Data Distribution
	let overall = data ? data.overall : [];
	let tableDetails = data ? data.vertical_details : [];
	let stackedChartData = data ? data.interconnection_count_by_vertical : [];
	let revenueMix = data ? data.vertical_revenue_mix : [];

	let overall2 = data2 ? data2.overall : [];
	let tableDetails2 = data2 ? data2.segment_details : [];
	let stackedChartData2 = data2 ? data2.interconnection_by_segment : [];
	let revenueMix2 = data2 ? data2.mix_percent_by_interconnection : [];


	// radio for group-by values
	const group_initial_value = optionGroupEcosystem ? optionGroupEcosystem[0] : null;
	const [group_val_onApply, set_group_val_onApply] = useState(group_initial_value);
	const [group_val, set_group_val] = useState(group_initial_value);
	const onApplyGroup = () => {
		set_group_val_onApply(group_val);
	};
	const onCancelGroup = () => {
		set_group_val(group_val_onApply);
	};

	// Table Config
	const indicatorPercent = (cell, row) => {
		let bgColor = cell >= 0.5 ? 'indicator-up' : cell <= -0.5 ? 'indicator-down' : 'indicator-middle';
		let icon = cell >= 0.5 ? <ImArrowUpRight2 /> : cell <= -0.5 ? <ImArrowDownRight2 /> : <span>&nbsp;</span>;
		return (
			<span className={`indicator ${bgColor}`}>
				{icon}
				{convertToPercent(cell)}
			</span>
		);
	};


	const formatToCount = (cell, row) => {
		return <span>{convertToCount(cell)}</span>
	};

	const formatUSDGrowth = (cell, row) => {
		let new_cell = convertToUSDGrowth(cell);
		return formatLongText(new_cell, row)
	};

	const formatWholePercent = (cell, row) => {
		return <span>{convertToWholePercent(cell)}</span>
	};

	const sparkline = (cell, row) => {
		return (
			<SparklineCustom loadedData={cell} />
		);
	}
	const microBarChart = (cell, row) => {
		return <MicroBarChart loadedData={cell} id={"micro-bar-chart-" + row._id} />
	};

	const defaultSorted = [{
		dataField: 'segment',
		order: 'desc',
	}];

	const columns = [
		{
			dataField: '_id',
			text: 'id',
			hidden: true
		},
		{
			dataField: 'segment',
			text: 'SEGMENT',


			classes: 'segment',
			headerClasses: 'segment',
		},
		{
			dataField: 'micro_column_bar_data',
			text: '',
			classes: 'right',
			headerClasses: 'right',
			classes: 'sparkline-cell center',
			headerClasses: 'sparkline-cell center',
			formatter: microBarChart
		},
		{
			dataField: 'ic_count',
			text: 'IC #',
			sort: true,
			classes: 'right',
			headerClasses: 'right',
			formatter: formatToCount
		},
		{
			dataField: 'mix_percent',
			text: 'MIX%',
			sort: true,
			classes: 'right',
			headerClasses: 'right',
		},

		{
			dataField: 'q_by_q_growth_percent',
			text: 'Q/Q % GROWTH',
			sort: true,
			formatter: indicatorPercent,
			classes: 'with-ellipsis right',
			headerClasses: 'right'
		},
		{
			dataField: 'q_to_q_growth_count',
			text: 'Q-Q $ GROWTH',
			sort: true,
			classes: 'right',
			headerClasses: 'right',
			formatter: formatUSDGrowth
		},
		{
			dataField: 'p4q_growth_percent',
			text: 'P4Q % GROWTH',
			sort: true,
			formatter: indicatorPercent,
			classes: 'with-ellipsis right',
			headerClasses: 'right'
		},
		{
			dataField: 'p4q_growth_count',
			text: 'P4Q $ GROWTH',
			sort: true,
			classes: 'right',
			headerClasses: 'right',
			formatter: formatUSDGrowth
		},
		{
			dataField: 'y_by_y_growth_percent',
			text: 'Y/Y % GROWTH',
			sort: true,
			formatter: indicatorPercent,
			classes: 'with-ellipsis right',
			headerClasses: 'right'
		},
		{
			dataField: 'y_to_y_growth_count',
			text: 'Y-Y $ GROWTH',
			sort: true,
			classes: 'right',
			headerClasses: 'right',
			formatter: formatUSDGrowth
		}

	];

	const defaultSorted2 = [{
		dataField: 'segment',
		order: 'desc',
	}];

	const columns2 = [
		{
			dataField: '_id',
			text: 'id',
			hidden: true
		},
		{
			dataField: 'segment',
			text: 'SEGMENT',
			classes: 'segment',
			headerClasses: 'segment',
		},
		{
			dataField: 'cc_count',
			text: 'CC #',
			sort: true,
			classes: 'right',
			headerClasses: 'right',
			formatter: formatToCount
		},
		{
			dataField: 'vc_count',
			text: 'VC #',
			sort: true,
			classes: 'right',
			headerClasses: 'right',
			formatter: formatToCount
		},
		{
			dataField: 'ic_count',
			text: 'IC #',
			sort: true,
			classes: 'right',
			headerClasses: 'right',
			formatter: formatToCount
		},
		{
			dataField: 'mrr_usd',
			text: 'MRR $',
			sort: true,
			classes: 'right',
			headerClasses: 'right',
			formatter: formatUSDGrowth
		},
		{
			dataField: 'mix_percent',
			text: 'MIX%',
			sort: true,
			classes: 'right',
			headerClasses: 'right',
		},
		{
			dataField: 'y_by_y_growth_percent',
			text: 'Y/Y % GROWTH',
			sort: true,
			formatter: indicatorPercent,
			classes: 'with-ellipsis right',
			headerClasses: 'right'
		},
		{
			dataField: 'y_to_y_usd_growth',
			text: 'Y-Y $ GROWTH',
			sort: true,
			classes: 'right',
			headerClasses: 'right',
			formatter: formatUSDGrowth
		},
		{
			dataField: 'percent_of_total_usd_growth',
			text: '% OF TOTAL $ GROWTH',
			sort: true,
			classes: 'right',
			headerClasses: 'right',
			formatter: formatToCount
		}

	];
	return (
		<Modal
			id="metro-modal"
			className="metro-modal "
			show={show}
			onHide={onClose}
			dialogClassName="custom-scroll"
			aria-labelledby="contained-modal-title-vcenter"
			centered
		>
			<Modal.Header>
				<Modal.Title id="contained-modal-title-vcenter">
					{selectedMetro} interconnection #
				</Modal.Title>
				<button type="button" className="close" onClick={onClose}>
					<span aria-hidden="true">×</span><span className="sr-only">
						Close
          </span>
				</button>
				<div className="filters custom-scroll">
					<RadioDD
						id="group_by_modal"
						options={optionGroupEcosystem}
						radioVal={group_val}
						setRadioVal={set_group_val}
						onApply={onApplyGroup}
						onCancel={onCancelGroup}
						initialValue={group_initial_value}
					/>

				</div>
			</Modal.Header>
			<Modal.Body>
				{data ?
					<>
						<Row>
							<Col md={3} className="fix-width" style={{ paddingRight: "30px" }}>
								{group_val_onApply.key === 'a_to_z_combination' ?
									<KPI_NEW
										totalValue={overall2.total.value}
										totalUnit={overall2.total.unit}
										details={overall2.details}
										noHover={true}
									/>
									:
									<KPI_NEW
										totalValue={overall.total.value}
										totalUnit={overall.total.unit}
										details={overall.details}
										noHover={true}
									/>
								}

							</Col>
							<Col>
								<Legend legend={stackedChartData.legend} />
								{group_val_onApply.key === 'a_to_z_combination' ?
									<StackedColumnChart
										id="ecosystem_metro_stacked_column_chart"
										largerWidth={true}
										hiddenValueAxis={false}
										isOneHundredPercent={false}
										isPercentage={false}
										hideLabelBullet={false}
										data={stackedChartData2.data}
										legend={stackedChartData2.legend}
										isAnimationEnabled={true}
									/>
									:
									<StackedColumnChart
										id="ecosystem_metro_stacked_column_chart"
										largerWidth={true}
										hiddenValueAxis={false}
										isOneHundredPercent={false}
										isPercentage={false}
										hideLabelBullet={false}
										data={stackedChartData.data}
										legend={stackedChartData.legend}
										isAnimationEnabled={true}
									/>
								}

							</Col>
							<Col style={{ paddingLeft: "30px" }}>
								{group_val_onApply.key === 'a_to_z_combination' ?
									<ChordDiagramChart
										id={"ecosystem_metro_modal_chord_chart"}
										data={revenueMix2.data}
										legend={revenueMix2.legend}
									/>
									:
									<DonutChart
										id={"ecosystem_metro_modal_donut_chart"}
										totalTitle={'INTERCONNECTION #'}
										total={revenueMix.interconnection_count}
										data={revenueMix.data}
										isCount={true}
										legend={revenueMix.legend}
										isAnimationEnabled={false}
									/>

								}

							</Col>
						</Row>
						<Row>
							<Col md={12} style={{ paddingTop: '1.5rem' }}>
								{group_val_onApply.key === 'a_to_z_combination' ?
									<CustomTable
										tableName='a-to-z-metro-product-details scrollable'
										data={tableDetails2 === undefined ? [] : tableDetails2}
										defaultSorted={defaultSorted2}
										columns={columns2}
									/>
									:
									<CustomTable
										tableName='metro-product-details scrollable'
										data={tableDetails === undefined ? [] : tableDetails}
										defaultSorted={defaultSorted}
										columns={columns}
									/>
								}

							</Col>
						</Row>
					</>
					: <div>No data available.</div>
				}
			</Modal.Body>
		</Modal>
	);
}

export default MetroModal;