import React, { useState, useEffect } from 'react';
import axiosRetry from 'axios-retry';
import axios from 'axios';
import { Container, ButtonGroup, Button } from 'react-bootstrap';
import { FilterContainer, FilterModal } from '../../components/filter';
import { RadioDD, CheckboxDD, SearchCheckboxDD, Separator } from '../../components/customDropdown';
import { optionRegion, optionCountry, optionMetro, optionProduct, optionGroup9 } from '../../constants/filterOptions';
import { comments } from '../../constants/comments';
import { InternalServerError, ResourceNotFoundError } from '../../components/error/Error';
import { API_BASE_URL } from '../../constants/config';
import Comment from '../../components/comment/Comment';
import Loader from '../../components/loader/Loader';
import LighthouseContent from './LighthouseContent';


const Lighthouse = ({ activeTab, setActiveTab }) => {
	const [isLoading, setIsLoading] = useState(true)
	const [g2000Data, setG2000Data] = useState([]);
	const [f500Data, setF500Data] = useState([]);
	const [starData, setStarData] = useState([]);

	// show all filters
	const [showFilter, setShowFilter] = useState(false);
	const onShowFilter = () => {
		setShowFilter(true)
	}
	const onCloseFilter = async () => {
		await onCancelRegion();
		await onCancelCountry();
		await onCancelMetro();
		await onCancelHQRegion();
		await onCancelHQCountry();
		await onCancelProduct();
		await onCancelGroup();
		setShowFilter(false);
	}
	const onApplyFilter = async () => {
		await onApplyRegion();
		await onApplyCountry();
		await onApplyMetro();
		await onApplyHQRegion();
		await onApplyHQCountry();
		await onApplyProduct();
		await onApplyGroup();
		setShowFilter(false);
	}

	// comment area
	const [showComment, setShowComment] = useState(false);
	const onCloseComment = () => {
		setShowComment(false);
	}
	const onShowComment = () => {
		setShowComment(true)
	};

	// checkbox for region values
	const [region_val_onApply, set_region_val_onApply] = useState([]);
	const [region_val, set_region_val] = useState([]);
	const onApplyRegion = () => {
		set_region_val_onApply(region_val);
	};
	const onCancelRegion = () => {
		set_region_val(region_val_onApply);
	};

	// searchCheckbox for country values
	const [country_val_onApply, set_country_val_onApply] = useState([]);
	const [country_val, set_country_val] = useState([]);
	const onApplyCountry = () => {
		set_country_val_onApply(country_val);
	};
	const onCancelCountry = () => {
		set_country_val(country_val_onApply);
	};

	// searchCheckbox for metro values
	const [metro_val_onApply, set_metro_val_onApply] = useState([]);
	const [metro_val, set_metro_val] = useState([]);
	const onApplyMetro = () => {
		set_metro_val_onApply(metro_val);
	};
	const onCancelMetro = () => {
		set_metro_val(metro_val_onApply);
	};

	// checkbox for HQ region values
	const [hqregion_val_onApply, set_hqregion_val_onApply] = useState([]);
	const [hqregion_val, set_hqregion_val] = useState([]);
	const onApplyHQRegion = () => {
		set_hqregion_val_onApply(hqregion_val);
	};
	const onCancelHQRegion = () => {
		set_hqregion_val(hqregion_val_onApply);
	};

	// searchCheckbox for HQ country values
	const [hqcountry_val_onApply, set_hqcountry_val_onApply] = useState([]);
	const [hqcountry_val, set_hqcountry_val] = useState([]);
	const onApplyHQCountry = () => {
		set_hqcountry_val_onApply(hqcountry_val);
	};
	const onCancelHQCountry = () => {
		set_hqcountry_val(hqcountry_val_onApply);
	};

	// checkbox for product values
	// const product_initial_value = optionProduct ? optionProduct[0] : null;
	const [product_val_onApply, set_product_val_onApply] = useState([]);
	const [product_val, set_product_val] = useState([]);
	const onApplyProduct = () => {
		set_product_val_onApply(product_val);
	};
	const onCancelProduct = () => {
		set_product_val(product_val_onApply);
	};

	// radio for group-by values
	const group_initial_value = optionGroup9 ? optionGroup9[0] : null;
	const [group_val_onApply, set_group_val_onApply] = useState(group_initial_value);
	const [group_val, set_group_val] = useState(group_initial_value);
	const onApplyGroup = () => {
		set_group_val_onApply(group_val);
	};
	const onCancelGroup = () => {
		set_group_val(group_val_onApply);
	};

	// Axios call
	const fetchData = async () => {
		let url = `${API_BASE_URL}/customer-count/lighthouse.json`;

		setIsLoading(true);
		axiosRetry(axios, { retries: 3 });

		await axios({
			method: 'GET',
			url: url
		}).then(result => {
			let data = result.data;
			// setG2000Data(data['g2000']);
			// setF500Data(data['f500']);
			// setStarData(data['star']);
			setG2000Data(data);
			setF500Data(data);
			setStarData(data);
			setIsLoading(false);
		}).catch(error => {
			console.log(error);
			setG2000Data([]);
			setF500Data([]);
			setStarData([]);
			setIsLoading(false);
		});
	};

	useEffect(() => {
		fetchData();
	}, []);

	if (isLoading) return <Loader withContainer />;

	return (
		<>
			<div className={`page-content ${showComment ? 'open' : 'closed'}`}>
				{(g2000Data || f500Data || starData) ?
					(g2000Data.segment_details.length > 0 || f500Data.segment_details.length > 0 || starData.segment_details.length > 0) ?
						<>
							<FilterContainer
								showComment={showComment}
								onShowFilter={onShowFilter}
								onShowComment={onShowComment}
							>
								<div className="filters custom-scroll">
									<ButtonGroup className="sub-tab">
										<Button
											variant='light'
											className={activeTab === 'g2000' ? 'active' : ''}
											onClick={() => { setActiveTab('g2000') }}>
											G2000
											</Button>
										<Button
											variant='light'
											className={activeTab === 'f500' ? 'active' : ''}
											onClick={() => { setActiveTab('f500') }}>
											F500
											</Button>
										<Button
											variant='light'
											className={activeTab === 'star' ? 'active' : ''}
											onClick={() => { setActiveTab('star') }}>
											STAR
											</Button>
									</ButtonGroup>
									<Separator height='39px' />
									<CheckboxDD
										title="Deploy Region"
										id="region"
										options={optionRegion}
										valueApplied={region_val_onApply}
										checkVal={region_val}
										setCheckVal={set_region_val}
										onApply={onApplyRegion}
										onCancel={onCancelRegion}
									/>
									<SearchCheckboxDD
										title="Deploy Country"
										id="country"
										options={optionCountry}
										checkVal={country_val}
										setCheckVal={set_country_val}
										onApply={onApplyCountry}
										onCancel={onCancelCountry}
									/>
									<SearchCheckboxDD
										title="Deploy Metro"
										id="metro"
										options={optionMetro}
										valueApplied={metro_val_onApply}
										checkVal={metro_val}
										setCheckVal={set_metro_val}
										onApply={onApplyMetro}
										onCancel={onCancelMetro}
									/>
									<CheckboxDD
										title="HQ Region"
										id="hq-region"
										options={optionRegion}
										checkVal={hqregion_val}
										setCheckVal={set_hqregion_val}
										onApply={onApplyHQRegion}
										onCancel={onCancelHQRegion}
									/>
									<SearchCheckboxDD
										title="HQ Country"
										id="hq-country"
										options={optionCountry}
										checkVal={hqcountry_val}
										setCheckVal={set_hqcountry_val}
										onApply={onApplyHQCountry}
										onCancel={onCancelHQCountry}
									/>
									<CheckboxDD
										title="Product"
										id="product"
										options={optionProduct}
										checkVal={product_val}
										setCheckVal={set_product_val}
										onApply={onApplyProduct}
										onCancel={onCancelProduct}
									/>
									<RadioDD
										title="Group by"
										id="group"
										options={optionGroup9}
										radioVal={group_val}
										setRadioVal={set_group_val}
										onApply={onApplyGroup}
										onCancel={onCancelGroup}
										initialValue={group_initial_value}
									/>
								</div>
								<FilterModal
									title="Overview"
									setShowFilter={setShowFilter}
									showFilter={showFilter}
									onCloseFilter={onCloseFilter}
									onApplyFilter={onApplyFilter}
								>
									<CheckboxDD
										title="Deploy Region"
										id="region-modal"
										visibleInModal
										options={optionRegion}
										checkVal={region_val}
										setCheckVal={set_region_val}
									/>
									<SearchCheckboxDD
										title="Deploy Country"
										id="country-modal"
										visibleInModal
										options={optionCountry}
										checkVal={country_val}
										setCheckVal={set_country_val}
									/>
									<SearchCheckboxDD
										title="Deploy Metro"
										id="metro-modal"
										visibleInModal
										options={optionMetro}
										checkVal={metro_val}
										setCheckVal={set_metro_val}
									/>
									<CheckboxDD
										title="HQ Region"
										id="hq-region-modal"
										visibleInModal
										options={optionRegion}
										checkVal={hqregion_val}
										setCheckVal={set_hqregion_val}
									/>
									<SearchCheckboxDD
										title="HQ Country"
										id="hq-country-modal"
										visibleInModal
										options={optionCountry}
										checkVal={hqcountry_val}
										setCheckVal={set_hqcountry_val}
									/>
									<CheckboxDD
										title="Product"
										id="product-modal"
										visibleInModal
										options={optionProduct}
										checkVal={product_val}
										setCheckVal={set_product_val}
									/>
									<RadioDD
										title="Group by"
										id="group-modal"
										visibleInModal
										options={optionGroup9}
										radioVal={group_val}
										setRadioVal={set_group_val}
									/>
								</FilterModal>
							</FilterContainer>
							{/* ============= Your Component here! ============= */}
							<Container fluid className={`${activeTab === 'g2000' ? 'd-block' : 'd-none'}`}>
								<LighthouseContent
									activeTab={activeTab}
									lighthouseData={g2000Data}
									tab_name='G2000'
									tab_id="g2000-lighthouse" />
							</Container>
							<Container fluid className={`${activeTab === 'f500' ? 'd-block' : 'd-none'}`}>
								<LighthouseContent
									activeTab={activeTab}
									lighthouseData={f500Data}
									tab_name='F500'
									tab_id="f500-lighthouse" />
							</Container>
							<Container fluid className={`${activeTab === 'star' ? 'd-block' : 'd-none'}`}>
								<LighthouseContent
									activeTab={activeTab}
									lighthouseData={starData}
									tab_name='STAR'
									tab_id="star-lighthouse" />
							</Container>
						</>
						: <ResourceNotFoundError redirectURL={"/executive-cockpit/customer-count/lighthouse"} />
					: <InternalServerError redirectURL={"/executive-cockpit/customer-count/lighthouse"} />}
			</div>
			<Comment
				data={comments}
				onCloseComment={onCloseComment}
				showComment={showComment}
			/>
		</>
	);
};

export default Lighthouse;
