import React, { useState } from 'react';
import { Container, Row, Col, Card } from 'react-bootstrap';
import { ImArrowUpRight2, ImArrowDownRight2 } from "react-icons/im";
import Legend from '../../components/legend/Legend';
import {BasicDD} from '../../components/customDropdown';
import CustomTable from '../../components/customTable/CustomTable';
import StackedColumnChart from '../../components/chart/StackedColumnChart';
import DonutChart from '../../components/chart/DonutChart';
import LineChart from '../../components/chart/LineChart';
import WaterFallChart from '../../components/chart/WaterfallChart';
import KPI_NEW from '../../components/kpi/KPI_NEW';
import { optionOverviewFilter } from '../../constants/filterOptions';
import { convertToUSDGrowth, convertToPercent } from '../../constants/formatter';


const OverviewContent = (props) => {
  const { overviewData, selectedRangeValue, activeTab } = props;
  const stacked_column_initial_value = optionOverviewFilter ? optionOverviewFilter[0] : null;
  const [stackedColumnSelection, setStackedColumnSelection] = useState(stacked_column_initial_value);

  //by segment
  const overallSegment = overviewData ? overviewData.net_bookings_by_vertical.overall : [];
  const netBookingsByVerticalSegment = overviewData ? overviewData.net_bookings_by_vertical.net_bookings_by_segment ? overviewData.net_bookings_by_vertical.net_bookings_by_segment : overviewData.net_bookings_by_vertical.net_bookings_by_segment : [];
  // const segmentTrendsSegment = overviewData ? overviewData.gross_bookings_by_vertical.segment_ ? overviewData.gross_bookings_by_vertical.segment_trends : overviewData.gross_bookings_by_vertical.segment_trends : [];
  const segmentRevenueMixSegment = overviewData ? overviewData.net_bookings_by_vertical.segment_revenue_mix : [];
  const segmentDetailsSegment = overviewData ? overviewData.net_bookings_by_vertical.segment_details : [];
  // //by vertical
  const overallVertical = overviewData ? overviewData.gross_bookings_by_vertical.overall : [];
  const grossNetBkgBrideBySegment = overviewData ? overviewData.gross_bookings_by_vertical.gross_net_bkg_bride_by_segment ? overviewData.gross_bookings_by_vertical.gross_net_bkg_bride_by_segment : overviewData.gross_bookings_by_vertical.gross_net_bkg_bride_by_segment : [];
  const grossNetBkgBridgeTrends = overviewData ? overviewData.gross_bookings_by_vertical.gross_net_bkg_bridge_trends ? overviewData.gross_bookings_by_vertical.gross_net_bkg_bridge_trends : overviewData.gross_bookings_by_vertical.gross_net_bkg_bridge_trends : [];
  const netPercentOfGrossBySegment = overviewData ? overviewData.gross_bookings_by_vertical.net_percent_of_gross_by_segment : [];
  const netPercentOfGrossTrends = overviewData ? overviewData.gross_bookings_by_vertical.net_percent_of_gross_trends : [];
  const grossNetBkgWaterfall = overviewData ? overviewData.gross_bookings_by_vertical.gross_net_bkg_waterfall : [];

  const indicatorPercent = (cell, row) => {
    let bgColor = cell >= 0.5 ? 'indicator-up' : cell <= -0.5 ? 'indicator-down' : 'indicator-middle';
    let icon = cell >= 0.5 ? <ImArrowUpRight2 /> : cell <= -0.5 ? <ImArrowDownRight2 /> : <span className="mr-3"></span>;

    return (
      <>
        <span className={`indicator ${bgColor}`}>
          {icon}
          {convertToPercent(cell)}
        </span>
      </>
    );
  };

  const formatUSDGrowth = (cell, row) => {
    return (
      <span>
        {convertToUSDGrowth(cell)}
      </span>
    );
  };

  const formatToPercent = (cell, row) => {
    return (
      <span>
        {convertToPercent(cell)}
      </span>
    );
  };

  const defaultSorted = [
    {
      dataField: 'mix_percent',
      order: 'desc',
    }
  ];

  const columns = [
    {
      dataField: '_id',
      text: 'id',
      hidden: true
    },
    {
      dataField: 'segment',
      text: 'SEGMENT',
      
      
      classes: 'segment-th',
      headerClasses: 'segment-td'
    },
    {
      dataField: 'revenue',
      text: 'REVENUE',
      sort: true,
      formatter: formatUSDGrowth,
      classes: 'right',
      headerClasses: 'right'
    },
    {
      dataField: 'mix_percent',
      text: 'MIX%',
      sort: true,
      formatter: formatToPercent,
      classes: 'right',
      headerClasses: 'right'
    },
    {
      dataField: 'q_by_q_growth_percent',
      text: 'Q/Q % GROWTH',
      sort: true,
      formatter: indicatorPercent,
      classes: 'with-ellipsis center',
      headerClasses: 'center'
    },
    {
      dataField: 'q_to_q_growth_usd',
      text: 'Q-Q $ GROWTH',
      sort: true,
      formatter: formatUSDGrowth,
      classes: 'center',
      headerClasses: 'center'
    },
    {
      dataField: 'y_by_y_growth_percent',
      text: 'Y/Y % GROWTH',
      sort: true,
      formatter: indicatorPercent,
      classes: 'with-ellipsis center',
      headerClasses: 'center'
    },
    {
      dataField: 'y_to_y_growth_usd',
      text: 'Y-Y $ GROWTH',
      sort: true,
      formatter: formatUSDGrowth,
      classes: 'center',
      headerClasses: 'center'
    },
    {
      dataField: 'percent_mpq',
      text: '%MPQ',
      sort: true,
      classes: 'right',
      headerClasses: 'right'
    },
    {
      dataField: 'percent_budget',
      text: '%BUDGET',
      sort: true,
      classes: 'right',
      headerClasses: 'right'
    }
  ];

  const defaultSortedVertical = [
    {
      dataField: 'mix_percent',
      order: 'desc',
    }
  ];

  const columnsVertical = [
    {
      dataField: '_id',
      text: 'id',
      hidden: true
    },
    {
      dataField: 'segment',
      text: 'SEGMENT',
      
      
      classes: 'segment-th',
      headerClasses: 'segment-td'
    },
    {
      dataField: 'net_as_percent_of_gross',
      text: 'NET AS % OF GROWTH',
      sort: true,
      formatter: formatUSDGrowth,
      classes: 'right',
      headerClasses: 'right'
    },
    {
      dataField: 'q_to_q_growth',
      text: 'Q-Q GROWTH',
      sort: true,
      formatter: indicatorPercent,
      classes: 'with-ellipsis center',
      headerClasses: 'center'
    },
    {
      dataField: 'q_by_q_growth_percent',
      text: 'P4QGROWTH',
      sort: true,
      formatter: indicatorPercent,
      classes: 'with-ellipsis center',
      headerClasses: 'center'
    },
    {
      dataField: 'y_to_y_growth',
      text: 'Q-Q GROWTH',
      sort: true,
      formatter: indicatorPercent,
      classes: 'with-ellipsis center',
      headerClasses: 'center'
    },
  ];

  return (
    <Container fluid>
      <Row className="contentDivider pb-30">
        <Col style={{ maxWidth: "400px" }}>
          <Card className="TotalRevenue revenue" >
            <Card.Body>
              <Card.Title>net bookings</Card.Title>
              <KPI_NEW
                totalValue={overallSegment.total.value}
                totalUnit={overallSegment.total.unit}
                details={overallSegment.details}
              />
            </Card.Body>
          </Card>
        </Col>
        <Col>
          <Card className="RevenueByType revenue">
            <Card.Body>
              <Card.Title>net bookings by vertical</Card.Title>
              <div className="d-flex">
                <Legend legend={netBookingsByVerticalSegment.legend} />
                {
                  selectedRangeValue === "last_completed_quarter" ?
                    <BasicDD
                      className=" ml-auto"
                      id="overviewStackedColumn"
                      options={optionOverviewFilter}
                      value={stackedColumnSelection}
                      setValue={setStackedColumnSelection}
                    /> : null
                }
              </div>
              <div style={{ height: "400px" }}>
                <StackedColumnChart
                  id="customer_revenue_by_revenue_type_stacked_column_chart"
                  hiddenValueAxis={false}
                  isOneHundredPercent={false}
                  isPercentage={false}
                  largerWidth={true}
                  hideLabelBullet={false}
                  isOverviewChart={true}
                  isGrowthPercentToggable={true}
                  stackedColumnSelection={stackedColumnSelection.key}
                  selectedRangeValue={selectedRangeValue}
                  data={netBookingsByVerticalSegment.data}
                  legend={netBookingsByVerticalSegment.legend}
                  isAnimationEnabled={true}
                />
              </div>
            </Card.Body>
          </Card>
        </Col>
        <Col sm={12} md={3} lg={3}>
          <Card className="Segment revenue">
            <Card.Body>
              <Card.Title>net bookings mix % by segment</Card.Title>
              <Legend legend={segmentRevenueMixSegment.legend} />
              <div style={{ height: "350px" }}>
                <DonutChart
                  id={"customer_revenue_overview_donut_chart"}
                  totalTitle={"NET BOOKINGS"}
                  total={segmentRevenueMixSegment.revenue}
                  data={segmentRevenueMixSegment.data}
                  legend={segmentRevenueMixSegment.legend}
                  isDollar={true}
                  isAnimationEnabled={true}
                />
              </div>
            </Card.Body>
          </Card>
        </Col>
      </Row>
      <Row className="contentDivider">
        <Col>
          <Card className="SegmentDetails revenue">
            <Card.Body>
              <Card.Title>segment details</Card.Title>
              <CustomTable
                tableName='segment-details scrollable'
                data={segmentDetailsSegment === undefined ? [] : segmentDetailsSegment}
                defaultSorted={defaultSorted}
                columns={columns}
              />
            </Card.Body>
          </Card>
        </Col>
      </Row>
      {activeTab === 'Gross-Net' ?
        <>
          <Row className="contentDivider" style={{ paddingTop: "30px", paddingBottom: "30px" }}>
            <Col style={{ maxWidth: "400px" }}>
              <Card className="TotalRevenue revenue" >
                <Card.Body>
                  <Card.Title>gross-net bkg bridge</Card.Title>
                  {/* <Kpi
                data={overall}
                total={overall.total}
                withPercent={true}
              /> */}
                  <KPI_NEW
                    totalValue={overallVertical.total.value}
                    totalUnit={overallVertical.total.unit}
                    details={overallVertical.details}
                  />
                </Card.Body>
              </Card>
            </Col>
            <Col>
              <Card className="RevenueByType revenue">
                <Card.Body>
                  <Card.Title>gross-net bkg bridge trends</Card.Title>
                  <div className="d-flex">
                    <Legend legend={grossNetBkgBrideBySegment.legend} />
                    {
                      selectedRangeValue === "last_completed_quarter" ?
                        <BasicDD
                          className=" ml-auto"
                          id="overviewStackedColumnVertical"
                          options={optionOverviewFilter}
                          value={stackedColumnSelection}
                          setValue={setStackedColumnSelection}
                        /> : null
                    }
                  </div>
                  <div style={{ height: "400px" }}>
                    <StackedColumnChart
                      id="customer_revenue_by_revenue_type_stacked_column_chart_segment"
                      hiddenValueAxis={false}
                      isOneHundredPercent={false}
                      isPercentage={false}
                      largerWidth={true}
                      hideLabelBullet={false}
                      isOverviewChart={true}
                      isGrowthPercentToggable={true}
                      stackedColumnSelection={stackedColumnSelection.key}
                      selectedRangeValue={selectedRangeValue}
                      data={grossNetBkgBridgeTrends.data}
                      legend={grossNetBkgBridgeTrends.legend}
                      isAnimationEnabled={true}
                    />
                  </div>
                </Card.Body>
              </Card>
            </Col>
            <Col sm={12} md={3} lg={3}>
              <Card className="Segment revenue">
                <Card.Body>
                  <Card.Title>gross-net bkg waterfall</Card.Title>
                  <Legend legend={grossNetBkgWaterfall.legend} />
                  <div style={{ height: "450px" }}>
                    <WaterFallChart
                      id={"net_bookings_waterfall"}
                      data={grossNetBkgWaterfall.data}
                      legend={grossNetBkgWaterfall.legend}
                      isAnimationEnabled={true}
                    />
                  </div>
                </Card.Body>
              </Card>
            </Col>
          </Row>
          <Row className="contentDivider">
            <Col sm={12} md={6} lg={6}>
              <Card className="SegmentDetails revenue">
                <Card.Body>
                  <Card.Title>net % of gross by segment</Card.Title>
                  <CustomTable
                    tableName='segment-details-vertical scrollable'
                    data={netPercentOfGrossBySegment === undefined ? [] : netPercentOfGrossBySegment}
                    defaultSorted={defaultSortedVertical}
                    columns={columnsVertical}
                  />
                </Card.Body>
              </Card>
            </Col>
            <Col sm={12} md={3} lg={3}>
              <Card className="RevenueByType revenue">
                <Card.Body>
                  <Card.Title>gross net bkg bridge by segment</Card.Title>
                  <div style={{ height: "300px" }}>
                    <StackedColumnChart
                      id="customer_revenue_by_revenue_type_stacked_column_chart_vertical"
                      hiddenValueAxis={false}
                      isOneHundredPercent={false}
                      isPercentage={false}
                      largerWidth={true}
                      hideLabelBullet={false}
                      isOverviewChart={true}
                      isGrowthPercentToggable={true}
                      stackedColumnSelection={stackedColumnSelection.key}
                      selectedRangeValue={selectedRangeValue}
                      data={grossNetBkgBrideBySegment.data}
                      legend={grossNetBkgBrideBySegment.legend}
                      isAnimationEnabled={true}
                    />
                  </div>
                </Card.Body>
              </Card>
            </Col>
            <Col sm={12} md={3} lg={3}>
              <Card className="SegmentTrends revenue">
                <Card.Body>
                  <Card.Title>net % of gross trends</Card.Title>
                  <Legend legend={netPercentOfGrossTrends.legend} />
                  <div style={{ height: "300px" }}>
                    <LineChart
                      id={"gross_trends_line_chart"}
                      data={netPercentOfGrossTrends.data}
                      legend={netPercentOfGrossTrends.legend}
                      isAnimationEnabled={true}
                    />
                  </div>
                </Card.Body>
              </Card>
            </Col>
          </Row>
        </> : null}
    </Container>
  );
};

export default OverviewContent;