import React, { useLayoutEffect } from 'react';
import * as am4core from "@amcharts/amcharts4/core";
import * as am4charts from "@amcharts/amcharts4/charts";
import am4themes_animated from "@amcharts/amcharts4/themes/animated";

import { convertToUSDGrowth, convertToCount, convertToWholePercent } from '../../constants/formatter';

am4core.useTheme(am4themes_animated);


const DonutChart = (props) => {
  const { data, id, isTotalPercent, isCount, legend, total, totalTitle, isDollar, isAnimationEnabled } = props

  useLayoutEffect(() => {
    let chart = am4core.create(id, am4charts.PieChart);
    chart.width = am4core.percent(100);
    chart.height = am4core.percent(100);
    chart.padding(50, 0, 0, 0);
    chart.margin(0);
    chart.data = data;
    chart.innerRadius = am4core.percent(50);

    let pieSeries = chart.series.push(new am4charts.PieSeries());
    pieSeries.dataFields.value = "value";
    pieSeries.dataFields.category = "segment";
    pieSeries.hiddenState.properties.opacity = 1;
    pieSeries.hiddenState.properties.endAngle = -90;
    pieSeries.hiddenState.properties.startAngle = -90;
    pieSeries.ticks.template.disabled = true;
    pieSeries.alignLabels = false;
    pieSeries.labels.template.text = "[font-size: 12px]{value.percent.formatNumber('#.0')}%[/]";
    pieSeries.labels.template.radius = am4core.percent(-20);
    pieSeries.labels.template.fill = am4core.color("white");
    pieSeries.slices.template.tooltipText = "[#fff]{category}: [#fff font-weight: bold]{value.formatNumber('#.0')}%[/][/]";
    pieSeries.colors.list = legend.sort((first, second) => (first._id - second._id)).map(legend => (am4core.color(legend.color)));
    pieSeries.hiddenState.transitionDuration = 0;
    pieSeries.defaultState.transitionDuration = isAnimationEnabled ? 1000 : 0;

    let label = pieSeries.createChild(am4core.Label);
    label.text = isTotalPercent === true ? `${convertToWholePercent(total)}%` : isDollar === true ? `${convertToUSDGrowth(total)}` : isCount === true ? `${convertToCount(total)}` : `${total}`;
    label.fontSize = 20;
    label.isMeasured = false;
    label.y = 8;
    label.horizontalCenter = "middle";
    label.verticalCenter = "middle";

    let titleLabel = pieSeries.createChild(am4core.Label);
    titleLabel.text = totalTitle;
    titleLabel.horizontalCenter = "middle";
    titleLabel.verticalCenter = "middle";
    titleLabel.fontSize = 12;
    titleLabel.isMeasured = false;
    titleLabel.x = 0;
    titleLabel.y = -15;

    chart.responsive.enabled = true;

    return () => {
      chart.dispose();
    };
  }, [data, id, isTotalPercent, isDollar, legend, total, totalTitle, isAnimationEnabled, isCount]);

  return (
    <div id={id} className="donut-chart" style={{ width: "100%", height: "100%" }}></div>
  );
}

export default DonutChart;