import React, { useState } from 'react';
import { Container, Row, Col, Button } from 'react-bootstrap';
import Monitor from '../../assets/images/laptop-snapshot-builder.png';
import { ReactComponent as Person } from '../../assets/images/boy-orange-back.svg';
import { motion } from 'framer-motion';
import './snapshot.scss';
import BuilderModal from './BuildModal';

const animateContent = {
  hidden: {
    opacity: 0,
    x: '-100px',
  },
  visible: {
    opacity: 1,
    x: '0',
    transition: { duration: 1 }
  }
}

const animateMonitor = {
  hidden: {
    opacity: 0,
    x: '100px',
  },
  visible: {
    opacity: 1,
    x: '0',
    transition: { duration: 1 }
  }
}

const animatePerson = {
  hidden: {
    opacity: 0,
    y: '50px',
  },
  visible: {
    opacity: 1,
    y: '0',
    transition: { duration: .5, delay: .6 }
  }
}

const Builder = () => {
  const [showBuilderModal, setShowBuilderModal] = useState(false);

  const onCancelModal = () => setShowBuilderModal(false);
  const onShowFilter = () => setShowBuilderModal(true);

  return (
    <>
      <div className={`page-content`}>
        <Container className="builder-page" fluid>
          <Row>
            <Col xl={7}>
              <div className="inner-wrap">
                <div className="block content-block">
                  <motion.div
                    variants={animateContent}
                    initial='hidden'
                    animate='visible'
                  >
                    <h2 className="title">Mindfully-designed templates that save time</h2>
                    <p className="text">Dodge the repetitive process of slide production with our built-in templates and pre-loaded data sets,
                  ensuring both speed and quality of your analysis.</p>
                    <Button onClick={onShowFilter}>Create a Snapshot</Button>
                  </motion.div>
                </div>
              </div>
            </Col>
            <Col xl={5}>
              <div className="inner-wrap">
                <div className="block image-block">
                  <motion.img
                    src={Monitor}
                    alt=""
                    className="img-fluid"
                    variants={animateMonitor}
                    initial='hidden'
                    animate='visible'
                  />
                  <motion.div
                    className="person"
                    variants={animatePerson}
                    initial='hidden'
                    animate='visible'
                  >
                    <Person />
                  </motion.div>
                </div>
              </div>
            </Col>
          </Row>
        </Container>
        <BuilderModal
          showBuilderModal={showBuilderModal}
          onCancelModal={onCancelModal}
        />
      </div>
    </>
  )
};

export default Builder;
