import React, { useState, useEffect } from 'react';

import PageHeader from '../../components/pageHeader/PageHeader';

import Overview from './Overview';
import Growth from './Growth';
import LFP from './LFP';
import Customer from './Customer';

import './pricing.scss';


const Pricing = (props) => {
	let path = props.location.pathname;
	const currentPath = path.substring(path.lastIndexOf('/') + 1);
	const [activePage, setActivePage] = useState(currentPath);

	const handleActivePage = (page) => {
		setActivePage(page)
	}

	const handleBreadcrubsPreviousLink = () => {
		setActivePage('Overview')
	}

	useEffect(() => {
		setActivePage(currentPath)
	}, [currentPath])

	const breadcrumbs = [
		{
			path: '/analytics-dashboard/page',
			label: 'ANALYTICS DASHBOARD'
		},
		{
			path: path === '/analytics-dashboard/pricing/overview' ? null : '/analytics-dashboard/pricing/overview',
			label: 'Pricing'
		},
		{
			label: activePage,
		}
	]

	const innerPage = [
		{
			path: '/analytics-dashboard/pricing/overview',
			label: 'Overview'
		},
		{
			path: '/analytics-dashboard/pricing/growth',
			label: 'Growth'
		},
		{
			path: '/analytics-dashboard/pricing/lfp',
			label: 'LFP'
		},
		{
			path: '/analytics-dashboard/pricing/customer',
			label: 'Customer'
		}
	]

	return (
		<div className="pricing-page">
			<PageHeader
				title="PRICING"
				color="dark-orange"
				innerPage={innerPage}
				breadcrumbs={breadcrumbs}
				previousPage="/analytics-dashboard/page"
				handleActivePage={handleActivePage}
				handleBreadcrubsPreviousLink={handleBreadcrubsPreviousLink}
			/>
			<div className="page-wrapper">
				{currentPath === 'overview' && <Overview />}
				{currentPath === 'growth' && <Growth />}
				{currentPath === 'lfp' && <LFP />}
				{currentPath === 'customer' && <Customer />}
			</div>
		</div>
	);
}

export default Pricing;
