import React, { useLayoutEffect } from 'react';
import * as am4core from "@amcharts/amcharts4/core";
import * as am4charts from "@amcharts/amcharts4/charts";
import am4themes_animated from "@amcharts/amcharts4/themes/animated";

am4core.useTheme(am4themes_animated);


const LineChart = (props) => {
  const { id, data, legend, startValue, endValue, hideLabel, hideValueLabel, isAnimationEnabled } = props
  am4core.options.autoSetClassName = true;

  useLayoutEffect(() => {
    let chart = am4core.create(id, am4charts.XYChart);
    chart.width = am4core.percent(100);
    chart.height = am4core.percent(100);
    chart.paddingRight = 0;
    chart.paddingLeft = 0;
    chart.paddingTop = 60;
    chart.paddingBottom = 0;

    // Add data
    chart.data = data;

    // Create axes
    let categoryAxis = chart.xAxes.push(new am4charts.CategoryAxis());
    categoryAxis.dataFields.category = "category";
    categoryAxis.renderer.grid.template.location = 0;
    categoryAxis.renderer.grid.template.strokeWidth = 0;
    categoryAxis.renderer.minGridDistance = 50;
    categoryAxis.id = "myCategoryAxis";
    categoryAxis.renderer.paddingTop = -8;
    categoryAxis.renderer.paddingBottom = -8;


    let valueAxis = chart.yAxes.push(new am4charts.ValueAxis());
    valueAxis.renderer.minGridDistance = 50;
    valueAxis.renderer.grid.template.strokeWidth = 0;
    valueAxis.renderer.labels.template.adapter.add("text", (text, label) => { return label.dataItem.value + "%"; })
    valueAxis.id = "myValueAxis";
    valueAxis.renderer.paddingLeft = -8;
    valueAxis.renderer.paddingRight = -8;
    valueAxis.min = startValue;
    valueAxis.max = endValue;


    if (hideLabel) {
      categoryAxis.renderer.grid.template.disabled = true;
      valueAxis.renderer.grid.template.disabled = true;
      categoryAxis.renderer.labels.template.disabled = true;
      valueAxis.renderer.labels.template.disabled = true;
      chart.paddingTop = 0;
      chart.marginTop = 0;
      chart.valign = 'top';
      chart.contentValign = 'top';
    } else if (hideValueLabel) {
      valueAxis.renderer.grid.template.disabled = true;
      valueAxis.renderer.grid.template.strokeWidth = 0;
      valueAxis.renderer.labels.template.disabled = true;
      chart.paddingTop = 0;
      chart.marginTop = 0;
      chart.valign = 'top';
      chart.contentValign = 'top';
      chart.strokeOpacity = 0;
    }


    // Create series
    function createSeries(field, name, color) {
      let series = chart.series.push(new am4charts.LineSeries());
      series.dataFields.valueY = field;
      series.dataFields.categoryX = "category";
      series.name = name;
      series.strokeWidth = 2;
      series.stroke = am4core.color(color);
      series.hiddenState.transitionDuration = 0;
      series.defaultState.transitionDuration = isAnimationEnabled ? 1000 : 0;

      let bullet = series.bullets.push(new am4charts.CircleBullet());
      bullet.fill = am4core.color(color); // tooltips grab fill from parent by default
      bullet.tooltipText = "[#fff]{name} in {categoryX}: [/][#fff font-weight: bold]{valueY.formatNumber('#.0')}%[/] [#fff]{additional}[/]";

      let circle = bullet.createChild(am4core.Circle);
      circle.radius = 4;
      circle.fill = am4core.color("#fff");
      circle.strokeWidth = 1;

      let segment = series.segments.template;
      segment.interactionsEnabled = true;

      let hs = segment.states.create("hover");
      hs.properties.strokeWidth = 5;
      return series;
    }

    for (let i = 0; i < legend.length; i++) {
      createSeries(legend[i].value_name, legend[i].name, legend[i].color);
    }

    return () => {
      chart.dispose();
    };
  }, [id, data, legend, startValue, endValue, hideLabel, hideValueLabel, isAnimationEnabled]);

  return (
    <div className="line-chart" id={id}></div>
  );
}

export default LineChart;