import React, { useState, useEffect } from 'react';
import axiosRetry from 'axios-retry';
import axios from 'axios';
import { FilterContainer, FilterModal } from '../../components/filter';
import { RadioDD, CheckboxDD, SearchCheckboxDD, ShowRankingDD } from '../../components/customDropdown';
import { optionRegion, optionCompanySize, optionGroup11, optionShowBy7, optionQuarter2, optionSTAR } from '../../constants/filterOptions';
import { comments } from '../../constants/comments';
import { InternalServerError, ResourceNotFoundError } from '../../components/error/Error';
import { API_BASE_URL } from '../../constants/config';
import Comment from '../../components/comment/Comment';
import Loader from '../../components/loader/Loader';
import ExpandContent from './ExpandContent';


const Expand = () => {
	const [isLoading, setIsLoading] = useState(true)
	const [expandData, setData] = useState()

	// show all filters
	const [showFilter, setShowFilter] = useState(false);
	const onShowFilter = () => {
		setShowFilter(true)
	}
	const onCloseFilter = async () => {
		await onCancelHQRegion();
		await onCancelCompany();
		await onCancelGroup();
		await onCancelShow();
		setShowFilter(false);
	}
	const onApplyFilter = async () => {
		await onApplyHQRegion();
		await onApplyCompany();
		await onApplyGroup();
		await onApplyShow();
		setShowFilter(false);
	}

	// comment area
	const [showComment, setShowComment] = useState(false);
	const onCloseComment = () => {
		setShowComment(false);
	};
	const onShowComment = () => {
		setShowComment(true);
	};

	// checkbox for quarter values
	// const quarter_initial_value = optionQuarter2 ? optionQuarter2[0] : null;
	const [quarter_val_onApply, set_quarter_val_onApply] = useState([]);
	const [quarter_val, set_quarter_val] = useState([]);
	const onApplyQuarter = () => {
		set_quarter_val_onApply(quarter_val);
	};
	const onCancelQuarter = () => {
		set_quarter_val(quarter_val_onApply);
	};

	// checkbox for HQ region values
	const [hqregion_val_onApply, set_hqregion_val_onApply] = useState([]);
	const [hqregion_val, set_hqregion_val] = useState([]);
	const onApplyHQRegion = () => {
		set_hqregion_val_onApply(hqregion_val);
	};
	const onCancelHQRegion = () => {
		set_hqregion_val(hqregion_val_onApply);
	};

	// checkbox for company-size values
	const [company_val_onApply, set_company_val_onApply] = useState([]);
	const [company_val, set_company_val] = useState([]);
	const onApplyCompany = () => {
		set_company_val_onApply(company_val);
	};
	const onCancelCompany = () => {
		set_company_val(company_val_onApply);
	};


	// checkbox for star values
	const [star_val_onApply, set_star_val_onApply] = useState([]);
	const [star_val, set_star_val] = useState([]);
	const onApplyStar = () => {
		set_star_val_onApply(star_val);
	};
	const onCancelStar = () => {
		set_star_val(star_val_onApply);
	};

	// radio for group1 values
	const group_initial_value = optionGroup11 ? optionGroup11[0] : null;
	const [group_val_onApply, set_group_val_onApply] = useState(group_initial_value);
	const [group_val, set_group_val] = useState(group_initial_value);
	const onApplyGroup = () => {
		set_group_val_onApply(group_val);
	};
	const onCancelGroup = () => {
		set_group_val(group_val_onApply);
	};

	// radio for show values
	const show_initial_value = optionShowBy7 ? optionShowBy7[0] : null;
	const [show_val_onApply, set_show_val_onApply] = useState(show_initial_value);
	const [show_val, set_show_val] = useState(show_initial_value);
	const onApplyShow = () => {
		set_show_val_onApply(show_val);
	};
	const onCancelShow = () => {
		set_show_val(show_val_onApply);
	};

	// Axios call
	const fetchData = () => {
		let url = `${API_BASE_URL}/new-logos-and-extensions/expand.json`;

		setIsLoading(true);
		axiosRetry(axios, { retries: 3 });

		axios({
			method: 'GET',
			url: url
		}).then(result => {
			setData(result.data);
			setIsLoading(false);
		}).catch(error => {
			console.log(error);
			setData(null);
			setIsLoading(false);
		});
	};

	useEffect(() => {
		fetchData();
	}, []);

	if (isLoading) return <Loader withContainer />;

	return (
		<>
			<div className={`page-content ${showComment ? 'open' : 'closed'}`}>
				{expandData ?
					expandData.customer_ranking.by_rank.data.length > 0 ?
						<>
							<FilterContainer
								showComment={showComment}
								onShowFilter={onShowFilter}
								onShowComment={onShowComment}
							>
								<div className="filters custom-scroll">
									<CheckboxDD
										title="New Logo Quarter"
										id="new-logo-quarter"
										addTitleOnWide
										options={optionQuarter2}
										checkVal={quarter_val}
										setCheckVal={set_quarter_val}
										onApply={onApplyQuarter}
										onCancel={onCancelQuarter}
									/>
									<CheckboxDD
										title="HQ Region"
										id="hq-region"
										options={optionRegion}
										checkVal={hqregion_val}
										setCheckVal={set_hqregion_val}
										onApply={onApplyHQRegion}
										onCancel={onCancelHQRegion}
									/>
									<CheckboxDD
										title="Company Size"
										id="company-size"
										options={optionCompanySize}
										checkVal={company_val}
										setCheckVal={set_company_val}
										onApply={onApplyCompany}
										onCancel={onCancelCompany}
									/>
									<CheckboxDD
										title="STAR"
										id="star-accounts"
										options={optionSTAR}
										checkVal={star_val}
										setCheckVal={set_star_val}
										onApply={onApplyStar}
										onCancel={onCancelStar}
									/>
									<RadioDD
										title="Group by"
										id="group"
										addTitleOnWide
										options={optionGroup11}
										radioVal={group_val}
										setRadioVal={set_group_val}
										onApply={onApplyGroup}
										onCancel={onCancelGroup}
										initialValue={group_initial_value}
									/>
									<RadioDD
										title="Show by"
										id="show"
										addTitleOnWide
										options={optionShowBy7}
										radioVal={show_val}
										setRadioVal={set_show_val}
										onApply={onApplyShow}
										onCancel={onCancelShow}
										initialValue={show_initial_value}
									/>
								</div>
								<FilterModal
									title="Expand"
									setShowFilter={setShowFilter}
									showFilter={showFilter}
									onCloseFilter={onCloseFilter}
									onApplyFilter={onApplyFilter}
								>
									<CheckboxDD
										title="New Logo Quarter"
										id="new-logo-quarter-modal"
										addTitleOnWide
										visibleInModal
										options={optionQuarter2}
										checkVal={quarter_val}
										setCheckVal={set_quarter_val}
										onApply={onApplyQuarter}
										onCancel={onCancelQuarter}
									/>
									<CheckboxDD
										title="HQ Region"
										id="hq-region-modal"
										visibleInModal
										options={optionRegion}
										checkVal={hqregion_val}
										setCheckVal={set_hqregion_val}
									/>
									<CheckboxDD
										title="Company Size"
										id="company-size-modal"
										visibleInModal
										options={optionCompanySize}
										checkVal={company_val}
										setCheckVal={set_company_val}
									/>
									<CheckboxDD
										title="STAR"
										id="star-accounts"
										visibleInModal
										options={optionSTAR}
										checkVal={star_val}
										setCheckVal={set_star_val}
										onApply={onApplyStar}
										onCancel={onCancelStar}
									/>
									<RadioDD
										title="Group by"
										id="group-modal"
										options={optionGroup11}
										visibleInModal
										radioVal={group_val}
										setRadioVal={set_group_val}
									/>
									<RadioDD
										title="Show by"
										id="show-modal"
										options={optionShowBy7}
										visibleInModal
										radioVal={show_val}
										setRadioVal={set_show_val}
									/>
								</FilterModal>
							</FilterContainer>
							{/* ============= Your Component here! ============= */}
							<ExpandContent expandData={expandData} />
						</>
						: <ResourceNotFoundError redirectURL={"/executive-cockpit/new-logos-and-extensions/expand"} />
					: <InternalServerError redirectURL={"/executive-cockpit/new-logos-and-extensions/expand"} />}
			</div>
			<Comment
				data={comments}
				onCloseComment={onCloseComment}
				showComment={showComment}
			/>
		</>
	);
};

export default Expand;
