
import React, { useState, useEffect } from 'react';
import axiosRetry from 'axios-retry';
import axios from 'axios';
import GrowthContent from './GrowthContent';
import {RadioDD} from '../../components/customDropdown';
import {FilterContainer, FilterModal} from '../../components/filter';
import Comment from '../../components/comment/Comment';
import Loader from '../../components/loader/Loader';
import { InternalServerError, ResourceNotFoundError } from '../../components/error/Error';
import { optionMeasure, optionRange, optionVertical, optionSize } from '../../constants/filterOptions';
import { comments } from '../../constants/comments';
import { API_BASE } from '../../constants/config';


const Growth = () => {
  const [isLoading, setIsLoading] = useState(true)
  const [growthData, setData] = useState(null);
  const [resultData, setResultData] = useState(null);
  const [selectedData, setSelectedData] = useState(null);
  const [selectedBubbleData, setSelectedBubbleData] = useState(null);
  const [idState, setReferenceID] = useState(null);
  const [bubbleName, setBubbleName] = useState(null);

  if (idState) {
    selectedBubbleData.map(item => {
      if (item._id !== idState) {
        item.color = 'rgba(234, 240, 244, 0.2)';
      }

      return item;
    });
  }

  // show all filters
  const [showFilter, setShowFilter] = useState(false);
  const onShowFilter = () => {
    setShowFilter(true);
  };
  const onCloseFilter = async () => {
    await onCancelMeasure();
    await onCancelRange();
    await onCancelVertical();
    await onCancelSize();
    setShowFilter(false);
  };
  const onApplyFilter = async () => {
    await onApplyMeasure();
    await onApplyRange();
    await onApplyVertical();
    await onApplySize();
    setShowFilter(false);
  };

  // comment area
  const [showComment, setShowComment] = useState(false);
  const onCloseComment = () => {
    setShowComment(false);
  };
  const onShowComment = () => {
    setShowComment(true);
  };

  // radio for measure values
  const measure_initial_value = optionMeasure ? optionMeasure[0] : null;
  const [measure_val_onApply, set_measure_val_onApply] = useState(measure_initial_value);
  const [measure_val, set_measure_val] = useState(measure_initial_value);
  const onApplyMeasure = () => {
    set_measure_val_onApply(measure_val);
  };
  const onCancelMeasure = () => {
    set_measure_val(measure_val_onApply);
  };

  // radio for range values
  const range_initial_value = optionRange ? optionRange[0] : null;
  const [range_val_onApply, set_range_val_onApply] = useState(range_initial_value);
  const [range_val, set_range_val] = useState(range_initial_value);
  const onApplyRange = () => {
    set_range_val_onApply(range_val);
  };
  const onCancelRange = () => {
    set_range_val(range_val_onApply);
  };

  // radio for vertical values
  const vertical_initial_value = optionVertical ? optionVertical[0] : null;
  const [vertical_val_onApply, set_vertical_val_onApply] = useState(vertical_initial_value);
  const [vertical_val, set_vertical_val] = useState(vertical_initial_value);
  const onApplyVertical = () => {
    set_vertical_val_onApply(vertical_val)
  };
  const onCancelVertical = () => {
    set_vertical_val(vertical_val_onApply)
  };

  // radio for show by values
  const size_initial_value = optionSize ? optionSize[0] : null;
  const [size_val_onApply, set_size_val_onApply] = useState(size_initial_value);
  const [size_val, set_size_val] = useState(size_initial_value);
  const onApplySize = () => {
    set_size_val_onApply(size_val);
  };
  const onCancelSize = () => {
    set_size_val(size_val_onApply);
  };

  // Axios call
  const fetchData = async () => {
    const token = localStorage.getItem('token');
    let url = `${API_BASE}/orbis/api/customerrevenuegrowth`;
    let defaultQueryParameters = `?measure=${measure_val_onApply.key}&range=${range_val_onApply.key}&vertical=${vertical_val_onApply.key}&showby=${size_val_onApply.key}`;

    setIsLoading(true);

    axiosRetry(axios, { retries: 3 });

    axios({
      method: 'GET',
      url: url + defaultQueryParameters,
      headers: {
        'Content-Type': 'application/json',
        "Authorization": `bearer ${token}`
      }
    }).then(result => {
      const data = result.data;
      const resultSelected = data.segment_ranking;
      // Uncomment only if for API testing purposes
      // console.log(result.config.url);
      setData(data);
      setResultData(resultSelected);
      setIsLoading(false);
    }).catch(error => {
      console.log(error);
      setData(null);
      setIsLoading(false);
    });
  };

  useEffect(() => {
    fetchData();
  }, [measure_val_onApply, range_val_onApply, vertical_val_onApply, size_val_onApply, fetchData]
  );

  if (isLoading) return <Loader withContainer />;

  return (
    <>
      <div className={`page-content ${showComment ? 'open' : 'closed'}`}>
        { growthData ?  
        growthData.segment_ranking.by_rank.length > 0 ?
        <>
          <FilterContainer
            showComment={showComment}
            onShowFilter={onShowFilter}
            onShowComment={onShowComment}
          >
            <div className="filters custom-scroll">
              <RadioDD
                title="Measure"
                id="measure"
                options={optionMeasure}
                radioVal={measure_val}
                setRadioVal={set_measure_val}
                onApply={onApplyMeasure}
                onCancel={onCancelMeasure}
              />
              
              <RadioDD
                title="Time Period"
                id="range"
                options={optionRange}
                radioVal={range_val}
                setRadioVal={set_range_val}
                onApply={onApplyRange}
                onCancel={onCancelRange}
                initialValue={range_initial_value}
              />
              
              <RadioDD
                id="vertical"
                options={optionVertical}
                radioVal={vertical_val}
                setRadioVal={set_vertical_val}
                onApply={onApplyVertical}
                onCancel={onCancelVertical}
                initialValue={vertical_initial_value}
              />
              
              <RadioDD
                title="Show By"
                id="show"
                options={optionSize}
                radioVal={size_val}
                setRadioVal={set_size_val}
                onApply={onApplySize}
                onCancel={onCancelSize}
                initialValue={size_initial_value}
              />
            </div>

            <FilterModal
              title="Growth"
              setShowFilter={setShowFilter}
              showFilter={showFilter}
              onCloseFilter={onCloseFilter}
              onApplyFilter={onApplyFilter}
            >
              <RadioDD
                title="Measure"
                id="measure-modal"
                options={optionMeasure}
                visibleInModal
                radioVal={measure_val}
                setRadioVal={set_measure_val}
              />
              <RadioDD
                title="Time Period"
                id="range-modal"
                options={optionRange}
                visibleInModal
                radioVal={range_val}
                setRadioVal={set_range_val}
              />
              <RadioDD
                title="Vertical"
                id="vertical-modal"
                options={optionVertical}
                visibleInModal
                radioVal={vertical_val}
                setRadioVal={set_vertical_val}
              />
              <RadioDD
                title="Show By"
                id="show-modal"
                options={optionSize}
                visibleInModal
                radioVal={size_val}
                setRadioVal={set_size_val}
              />
            </FilterModal>
          </FilterContainer>
          < GrowthContent
            bubbleName={bubbleName}
            selectedData={selectedData}
            idState={idState}
            resultData={resultData}
            selectedBubbleData={selectedBubbleData}
            setSelectedData={setSelectedData}
            setSelectedBubbleData={setSelectedBubbleData}
            setBubbleName={setBubbleName}
            setReferenceID={setReferenceID}
            growthData={growthData}
          />
        </> 
        : <ResourceNotFoundError redirectURL={"/executive-cockpit/customer-revenue-live/growth"} />
        : <InternalServerError redirectURL={"/executive-cockpit/customer-revenue-live/growth"}/> }
      </div>
      <Comment
        data={comments}
        onCloseComment={onCloseComment}
        showComment={showComment}
      />
    </>
  );
};

export default Growth;
