import React, { useState } from 'react';
import { Container, Row, Col, Card } from 'react-bootstrap';
import { ImArrowUpRight2, ImArrowDownRight2 } from "react-icons/im";
import NestedDonutChart from '../../components/chart/NestedDonutChart';
import Legend from '../../components/legend/Legend';
import { BasicDD } from '../../components/customDropdown';
import CustomTable from '../../components/customTable/CustomTable';
import StackedColumnChart from '../../components/chart/StackedColumnChart';
import DonutChart from '../../components/chart/DonutChart';
import LineChart from '../../components/chart/LineChart';
import KPI_NEW from '../../components/kpi/KPI_NEW';

import { optionOverviewFilter, optionOverviewFilter1 } from '../../constants/filterOptions';
import { convertToUSDGrowth, convertToPercent, convertToCount } from '../../constants/formatter';


const OverviewContent = (props) => {
  const { overviewData, selectedRangeValue, activeTab } = props;
  const [kpiClicked, setkpiClicked] = useState(null);

  const stacked_column_initial_value = optionOverviewFilter ? optionOverviewFilter[0] : null;
  const [stackedColumnSelection, setStackedColumnSelection] = useState(stacked_column_initial_value);

  //by segment
  const overall = overviewData ? overviewData.overall : [];
  const percent_of_total_by_segment = overviewData ? overviewData.percent_of_total_by_segment : [];
  const mix_percent_by_deployment_status = overviewData ? overviewData.mix_percent_by_deployment_status : [];
  const segment_details = overviewData ? overviewData.segment_details : [];
  const multi_metro_by_segment = overviewData ? overviewData.multi_metro_by_segment : [];
  
  let multi_metro_by_segment_in_quarter_net_adds_count = overviewData ? overviewData.multi_metro_by_segment_in_quarter_net_adds_count : [];
  let multi_metro_by_segment_in_year_net_adds_count = overviewData ? overviewData.multi_metro_by_segment_in_year_net_adds_count : [];
  let multi_metro_by_segment_in_quarter_percentage = overviewData ? overviewData.multi_metro_by_segment_in_quarter_percentage : [];
  let multi_metro_by_segment_in_year_percentage = overviewData ? overviewData.multi_metro_by_segment_in_year_percentage : [];

  const indicatorPercent = (cell, row) => {
    let bgColor = cell >= 0.5 ? 'indicator-up' : cell <= -0.5 ? 'indicator-down' : 'indicator-middle';
    let icon = cell >= 0.5 ? <ImArrowUpRight2 /> : cell <= -0.5 ? <ImArrowDownRight2 /> : <span className="mr-3"></span>;

    return (
      <>
        <span className={`indicator ${bgColor}`}>
          {icon}
          {convertToPercent(cell)}
        </span>
      </>
    );
  };

  const formatUSDGrowth = (cell, row) => {
    return (
      <span>
        {convertToUSDGrowth(cell)}
      </span>
    );
  };

  const formatToPercent = (cell, row) => {
    return (
      <span>
        {convertToPercent(cell)}
      </span>
    );
  };

  const formatToCount = (cell, row) => {
    return (
      <span>
        {convertToCount(cell)}
      </span>
    );
  };

  const defaultSorted = [
    {
      dataField: '_id',
      order: 'asc',
    }
  ];

  const columns = [
    {
      dataField: '_id',
      text: 'id',
      hidden: true
    },
    {
      dataField: 'segment',
      text: 'SEGMENT',
      classes: 'segment-th',
      headerClasses: 'segment-td'
    },
    {
      dataField: 'customer_count',
      text: 'CUSTOMER #',
      sort: true,
      formatter: formatToCount,
      classes: 'right',
      headerClasses: 'right'
    },
    {
      dataField: 'mix_percent',
      text: 'MIX%',
      sort: true,
      classes: 'right',
      headerClasses: 'right'
    },
    {
      dataField: 'q_by_q_growth_percent',
      text: 'Q/Q % GROWTH',
      sort: true,
      formatter: indicatorPercent,
      classes: 'with-ellipsis center',
      headerClasses: 'center',
    },
    {
      dataField: 'q_to_q_growth_count',
      text: 'Q-Q # GROWTH',
      sort: true,
      formatter: formatToCount,
      classes: 'right',
      headerClasses: 'right',
    },
    {
      dataField: 'y_by_y_growth_percent',
      text: 'Y/Y % GROWTH',
      sort: true,
      formatter: indicatorPercent,
      classes: 'with-ellipsis center',
      headerClasses: 'center',
    },
    {
      dataField: 'y_to_y_growth_count',
      text: 'Y-Y # GROWTH',
      sort: true,
      classes: 'right border-right',
      headerClasses: 'right border-right',
    },
    {
      dataField: 'percent_multi_metro',
      text: '% MULTI-METRO',
      sort: true,
      classes: 'right',
      headerClasses: 'right'
    },
    {
      dataField: 'multi_metro_q_by_q_growth_percent',
      text: 'Q-Q % GROWTH',
      sort: true,
      classes: 'right',
      headerClasses: 'right'
    },
    {
      dataField: 'multi_metro_y_by_y_growth_percent',
      text: 'Y-Y % GROWTH',
      sort: true,
      classes: 'right',
      headerClasses: 'right'
    }
  ];

  return (
    <Container fluid>
      {overviewData ?
        <>
          <Row className="contentDivider pb-30">
            <div className="fix-width">
              <Card className="TotalRevenue revenue " >
                <Card.Body>
                  <Card.Title>{activeTab} count</Card.Title>
                  <KPI_NEW
                    totalValue={overall.total.value}
                    totalUnit={overall.total.unit}
                    details={overall.details}
                    setkpiClicked={setkpiClicked}
                    kpiClicked={kpiClicked}
                  />

                </Card.Body>
              </Card>
            </div>
            <Col>
              <Card className="RevenueByType revenue">
                <Card.Body>
                  <Card.Title>{activeTab} count by segment</Card.Title>

                  {kpiClicked === 'Q-Q Net Adds #' ?
                    <>
                      <Legend legend={multi_metro_by_segment_in_quarter_net_adds_count.legend} />
                      <div style={{ height: "92%" }}>
                        <LineChart
                          id={`multi_metro_by_segment_in_quarter_net_adds_count-line_chart-segment`}
                          data={multi_metro_by_segment_in_quarter_net_adds_count.data}
                          legend={multi_metro_by_segment_in_quarter_net_adds_count.legend}
                          isAnimationEnabled={false}
                        />
                      </div>
                    </>
                    :

                    kpiClicked === 'Y-Y Net Adds #' ?
                      <>
                        <Legend legend={multi_metro_by_segment_in_year_net_adds_count.legend} />
                        <div style={{ height: "92%" }}>
                          <LineChart
                            id={`multi_metro_by_segment_in_year_net_adds_count-line_chart-segment`}
                            data={multi_metro_by_segment_in_year_net_adds_count.data}
                            legend={multi_metro_by_segment_in_year_net_adds_count.legend}
                            isAnimationEnabled={false}
                          />
                        </div>
                      </>
                      :
                      kpiClicked === 'Q/Q Growth %' ?
                        <>
                          <Legend legend={multi_metro_by_segment_in_quarter_percentage.legend} />
                          <div style={{ height: "92%" }}>
                            <LineChart
                              id={`multi_metro_by_segment_in_quarter_percentage-line_chart-segment`}
                              data={multi_metro_by_segment_in_quarter_percentage.data}
                              legend={multi_metro_by_segment_in_quarter_percentage.legend}
                              isAnimationEnabled={false}
                            />
                          </div>
                        </>
                        :
                        kpiClicked === 'Y/Y Growth %' ?
                          <>
                            <Legend legend={multi_metro_by_segment_in_year_percentage.legend} />
                            <div style={{ height: "92%" }}>
                              <LineChart
                                id={`multi_metro_by_segment_in_year_percentage-line_chart-segment`}
                                data={multi_metro_by_segment_in_year_percentage.data}
                                legend={multi_metro_by_segment_in_year_percentage.legend}
                                isAnimationEnabled={false}
                              />
                            </div>
                          </>

                          :
                          <>
                            <div className="d-flex">
                              <Legend legend={multi_metro_by_segment.legend} />
                              {/* {selectedRangeValue === "last_completed_quarter" ? */}
                              <BasicDD
                                className="ml-auto"
                                id={`stacked_column_dd`}
                                options={optionOverviewFilter1}
                                value={stackedColumnSelection}
                                setValue={setStackedColumnSelection}
                              />
                              {/* : null
                          } */}
                            </div>
                            <div style={{ height: "85%", marginTop: "1rem" }}>
                              <StackedColumnChart
                                id={`stacked_column_chart`}
                                hiddenValueAxis={false}
                                isOneHundredPercent={false}
                                isPercentage={false}
                                largerWidth={true}
                                hideLabelBullet={false}
                                isOverviewChart={true}
                                isGrowthPercentToggable={true}
                                stackedColumnSelection={stackedColumnSelection.key}
                                selectedRangeValue={'last_completed_quarter'}
                                data={multi_metro_by_segment.data}
                                legend={multi_metro_by_segment.legend}
                                isAnimationEnabled={false}
                              />
                            </div>
                          </>
                  }


                </Card.Body>
              </Card>
            </Col>
            <Col sm={12} md={3} lg={3}>
              <Card className="Segment revenue">
                <Card.Body>
                  <Card.Title>% of total by segment</Card.Title>
                  <Legend legend={percent_of_total_by_segment.legend} />
                  <div style={{ height: "92%" }}>
                    <LineChart
                      id={`percent_of_total_by_segment-line_chart-segment`}
                      data={percent_of_total_by_segment.data}
                      legend={percent_of_total_by_segment.legend}
                      isAnimationEnabled={false}
                    />
                  </div>
                </Card.Body>
              </Card>
            </Col>
          </Row>
          <Row className="contentDivider">
            <Col>
              <Card className="SegmentDetails revenue">
                <Card.Body>
                  <Card.Title>segment details</Card.Title>
                  <CustomTable
                    tableName='segment-details'
                    data={segment_details === undefined ? [] : segment_details}
                    defaultSorted={defaultSorted}
                    columns={columns}
                  />
                </Card.Body>
              </Card>
            </Col>
            <Col sm={12} md={3} lg={3}>
              <Card className="SegmentTrends revenue">
                <Card.Body>
                  <Card.Title>mix % by deployment status</Card.Title>
                  <Legend legend={mix_percent_by_deployment_status.legend} />
                  <div style={{ height: "400px", marginTop: '3rem' }}>
                    <NestedDonutChart
                      id={`overview-nested-donut-chart`}
                      insideValueName={"company_name"}
                      insideValue={"company_percentage"}
                      insideCategory={"hq_region"}
                      outsideValueName={"others_name"}
                      outsideValue={"others_percentage"}
                      outsideCategory={"hq_region"}
                      loadedData={mix_percent_by_deployment_status.data}
                      isAnimationEnabled={true}
                    />
                  </div>
                </Card.Body>
              </Card>
            </Col>
          </Row>

        </>
        : null}
    </Container>
  );
};

export default OverviewContent;