// loop
const step1List = [
  {
    icon: <Dollar className="icon" />,
    key: 'global_report',
    label: 'CAMPAIGN TO OPPORTUNITY',
  },
  {
    icon: <Shakehands className="icon" />,
    key: 'regional_report',
    label: 'OPPORTUNITY TO CASH',
  },
  {
    icon: <NoEmail className="icon" />,
    key: 'country_report',
    label: 'ISSUE TO RESOLUTION',
  },
  {
    icon: <Server className="icon" />,
    key: 'metro_report',
    label: 'CAPACITY MANAGEMENT',
  },
  {
    icon: <Team className="icon" />,
    key: 'sales_team_report',
    label: 'PEOPLE',
  }
]

// state
const initialState = {
  step1: '',
  step2: '',
}

const GeneratorModal = (props) => {
  const { showGeneratorModal, onCancelModal } = props;
  let history = useHistory();
  const [stepOpen, setStepOpen] = useState(1);
  const [clickBack, setClickBack] = useState(false);
  const [clickNext, setClickNext] = useState(false);
  const [generating, setGenerating] = useState(false);
  const [stepsSelectedValue, setStepsSelectedValue] = useState(initialState)
  const [showValidate, setShowValidate] = useState(false);
  // checkbox for period values
  const [new_val, set_new_val] = useState([]);
  // data table
  const [dataTable, setDataTable] = useState();
  const [tableError, setTableError] = useState(false);

  const onSelectValueStep1 = (e) => {
    setShowValidate(false)
    setStepsSelectedValue({
      ...stepsSelectedValue,
      step1: e.target.id,
    })
  }

  const [dragState, setDragState] = useState(draggableState)

  const onCancel = async () => {
    await setClickBack(false)
    await setClickNext(false)
    await onCancelModal()
    setStepsSelectedValue(initialState)
    setShowValidate(false)
    setTimeout(()=>{
      setStepOpen(1)
    }, 200)
  }

  const onBack = async () => {
    await setClickNext(false)
    await setClickBack(true)
    setShowValidate(false)
    setStepOpen(stepOpen - 1)
  }

  const onNext = async () => {
    await setClickBack(false)
    await setClickNext(true)
    if (stepOpen === 1) {
      if (stepsSelectedValue.step1 !== '') setStepOpen(stepOpen + 1)
      else setShowValidate(true)
    }
    if (stepOpen === 2) {
      setStepOpen(stepOpen + 1)
      showPreview();
    }
    // if (stepOpen === 3) {
    //   if (new_val.length > 0) {
    //     showPreview();
    //     setStepOpen(stepOpen + 1)
    //   } else setShowValidate(true)
    // }
  }

  const showPreview = () => {
    // setLoading(true);

    axios({
      method: 'GET',
      url: `${API_BASE_URL}/ssrg/data.json?print=pretty`,
    }).then(result => {
      setDataTable(result.data)
    }).catch(error => {
      console.log(error)
      setTableError(true)
    })
  }

  const runReport = () => {
    setGenerating(true);
    setStepOpen(4)
    // API here
    setTimeout(async () => {
      setGenerating(false)
    }, 5000)
  }

  const initialAnimate = clickBack ? 'backEnter' : clickNext ? 'nextEnter' : false;
  const exitAnimate = clickBack ? 'backExit' : clickNext ? 'nextExit' : false;

  const viewReport = () => {
    history.push('/self-service-rerport/blank-report/generated-report')
  }



  
  return (
    <>
      <Modal
        show={showGeneratorModal}
        onHide={onCancel}
        className="generator-modal--on-help-center"
        backdrop="static"
        keyboard={false}
        centered
      >
        <div className="to-prevent-glitch">prevent glitch</div>
        <div className="content-wrapper">
          <AnimatePresence exitBeforeEnter>
            {stepOpen === 1 && (
              <ContentContainer
                stepOpen={stepOpen}
                initialAnimate={initialAnimate}
                exitAnimate='nextExit'
                modalTitle='STEP 1: CHOOSE A DATA UNIVERSE'
              >
                <Modal.Body>
                  {showValidate && <Alert variant="danger" className="w-100 text-center">Please select before you proceed.</Alert>}
                  {step1List && step1List.map((item) => {
                    return (
                      <div className="btn-container" key={`step-${stepOpen}-${item.key}`}>
                        <Button
                          variant="link"
                          onClick={(e) => onSelectValueStep1(e)}
                          id={item.key}
                          className={`steps-btn ${stepsSelectedValue.step1 === item.key ? 'active' : ''}`}
                        >
                          {item.icon}{item.label}
                        </Button>
                      </div>
                    )
                  })}
                </Modal.Body>
                <Modal.Footer>
                  <Button variant="light" onClick={() => onCancel()}>Cancel</Button>
                  <Button variant="info" onClick={() => onNext()}>Next</Button>
                </Modal.Footer>
              </ContentContainer>
            )}
            {stepOpen === 2 && (
              <ContentContainer
                stepOpen={stepOpen}
                initialAnimate={initialAnimate}
                exitAnimate={exitAnimate}
                modalTitle='STEP 2: SELECT GLOBAL FILTER FIELDS'
              >
                <Modal.Body>
                  {/* <DraggableComponent
                    buttonList={buttonList}
                    dragState={dragState}
                    grabableList={grabableList}
                    setDragState={setDragState}
                  /> */}
                  <FieldList/>
                </Modal.Body>
                <Modal.Footer>
                  <Button variant="light" onClick={() => onBack()}>Back</Button>
                  <Button variant="light" onClick={() => onCancel()}>Cancel</Button>
                  <Button variant="info" onClick={() => onNext()}>Next</Button>
                </Modal.Footer>
              </ContentContainer>
            )}
            {/* {stepOpen === 3 && (
              <ContentContainer
                stepOpen={stepOpen}
                initialAnimate={initialAnimate}
                exitAnimate={exitAnimate}
                modalTitle='STEP 3: SET FILTERS'
              >
                <Modal.Body>
                  {showValidate && <Alert variant="danger" className="w-100 text-center">Please select before you proceed.</Alert>}
                  <CheckboxList
                    options={period}
                    id="new_customer_quarter"
                    title="New Customer Quarter"
                    setCheckVal={set_new_val}
                    checkVal={new_val}
                  />
                </Modal.Body>
                <Modal.Footer>
                  <Button variant="light" onClick={() => onBack()}>Back</Button>
                  <Button variant="light" onClick={() => onCancel()}>Cancel</Button>
                  <Button variant="info" onClick={() => onNext()}>Next</Button>
                </Modal.Footer>
              </ContentContainer>
            )} */}
            {stepOpen === 3 && (
              <ContentContainer
                stepOpen={stepOpen}
                initialAnimate={initialAnimate}
                exitAnimate={exitAnimate}
                modalTitle='STEP 3: PREVIEW'
              >
                <Modal.Body>
                  {dataTable
                    ? tableError
                      ? <Alert variant="danger" className="w-100 text-center">Something went wrong. Can't fetch data.</Alert>
                      : <>
                        {/* <table className="fake-table-head">
                          <tr>
                            <th></th>
                            <th></th>
                            <th></th>
                            <th></th>
                            <th></th>
                            <th className="width-x-9">#-New Customers</th>
                            <th className="width-x-9">Customer MRR</th>
                          </tr>
                        </table>
                        <CustomTable
                          tableName='preview-table'
                          data={dataTable ? dataTable : []}
                          defaultSorted={defaultSorted}
                          columns={columns}
                          rowsPerPage={rowsPerPage}
                          addPaginate
                        /> */}
                        <PivotView/>
                      </>
                    : <LoadingIcon className="icon mb-4" />
                  }
                </Modal.Body>
                <Modal.Footer>
                  <Button variant="light" onClick={() => onBack()}>Back</Button>
                  <Button variant="light" onClick={() => onCancel()}>Cancel</Button>
                  <Button variant="info" onClick={() => runReport()}>Run Report</Button>
                </Modal.Footer>
              </ContentContainer>
            )}
            {stepOpen === 4 && (
              <ContentContainer
                stepOpen={stepOpen}
                initialAnimate={initialAnimate}
                exitAnimate={exitAnimate}
              >
                <Modal.Body>
                  {generating
                    ? <motion.div className="body-content"
                      key={generating}
                      variants={animateGenerate}
                      initial='hidden'
                      animate='visible'
                    >
                      <LoadingIcon className="icon mb-4" />
                      <h5 className="title">Generating Report…</h5>
                      <p className="note">We’ll send the report to your email after we finish.</p>
                    </motion.div>
                    : <motion.div className="body-content"
                      key={generating}
                      variants={animateGenerate}
                      initial='hidden'
                      animate='visible'
                    >
                      <h5 className="title">Finished!</h5>
                      <Button variant="info" onClick={() => viewReport()}>View Report</Button>
                    </motion.div>
                  }
                </Modal.Body>
              </ContentContainer>
            )}
          </AnimatePresence>
        </div>
      </Modal>
    </>
  )
};

const ContentContainer = ({
  stepOpen,
  initialAnimate,
  exitAnimate,
  modalTitle,
  children,
}) => {
  return (
    <motion.div className={`content-container step-${stepOpen}`}
      key={stepOpen}
      variants={animateStep}
      initial={initialAnimate}
      animate='visible'
      exit={exitAnimate}
    >
      {modalTitle &&
        <Modal.Header>
          <Modal.Title>{modalTitle}</Modal.Title>
        </Modal.Header>
      }
      {children}
    </motion.div>
  )
}


export default GeneratorModal;