import React, { useLayoutEffect } from 'react';
import * as am4core from "@amcharts/amcharts4/core";
import * as am4charts from "@amcharts/amcharts4/charts";
import am4themes_animated from "@amcharts/amcharts4/themes/animated";

am4core.useTheme(am4themes_animated);


const ClusteredColumnChart = (props) => {
  const { id, isAnimationEnabled, isHorizontal } = props;

  useLayoutEffect(() => {
    var chart = am4core.create(id, am4charts.XYChart)
    chart.width = am4core.percent(100);
    chart.height = am4core.percent(100);

    chart.paddingRight = 0;
    chart.paddingLeft = -10;
    chart.paddingTop = 50;
    chart.paddingBottom = 0;

    // chart.colors.step = 2;
    let seriesColors = [];
    for (let i = 0; i < props.legend.length; i++) {
      seriesColors.push(am4core.color(props.legend[i].color));
    }

    chart.colors.list = seriesColors;

    chart.data = props.data;


    var xAxis, yAxis;
    if (isHorizontal) {
      xAxis = chart.yAxes.push(new am4charts.CategoryAxis())
      xAxis.dataFields.category = 'category';
      xAxis.renderer.cellStartLocation = 0.1;
      xAxis.renderer.cellEndLocation = 0.9;
      xAxis.renderer.grid.template.location = 0;;
      xAxis.renderer.labels.template.fill = am4core.color('#748A9D');
      xAxis.renderer.grid.template.location = 0;
      xAxis.renderer.grid.template.strokeWidth = 0;

      yAxis = chart.xAxes.push(new am4charts.ValueAxis());
      yAxis.min = 0;
      yAxis.renderer.labels.template.fill = am4core.color('#748A9D');
      yAxis.renderer.grid.template.strokeWidth = 0.5;
    } else {
      xAxis = chart.xAxes.push(new am4charts.CategoryAxis())
      xAxis.dataFields.category = 'category';
      xAxis.renderer.cellStartLocation = 0.1;
      xAxis.renderer.cellEndLocation = 0.9;
      xAxis.renderer.grid.template.location = 0;;
      xAxis.renderer.labels.template.fill = am4core.color('#748A9D');
      xAxis.renderer.grid.template.location = 0;
      xAxis.renderer.grid.template.strokeWidth = 0;

      yAxis = chart.yAxes.push(new am4charts.ValueAxis());
      yAxis.min = 0;
      yAxis.renderer.labels.template.fill = am4core.color('#748A9D');
      yAxis.renderer.grid.template.strokeWidth = 0.5;
    }

    function createSeries(value, name) {
      var series = chart.series.push(new am4charts.ColumnSeries())
      if (isHorizontal) {
        series.dataFields.categoryY = 'category';
        series.dataFields.valueX = value;
      } else {
        series.dataFields.valueY = value;
        series.dataFields.categoryX = 'category';
      }

      series.name = name
      series.hiddenState.transitionDuration = 0;
      series.defaultState.transitionDuration = isAnimationEnabled ? 1000 : 0;
      series.events.on("hidden", arrangeColumns);
      series.events.on("shown", arrangeColumns);

      // var bullet = series.bullets.push(new am4charts.LabelBullet())
      // bullet.interactionsEnabled = false
      // bullet.dy = 30;

      // if(isHorizontal){
      //   bullet.label.text = '{valueX}';
      // }else{
      //   bullet.label.text = '{valueY}'
      // }

      // bullet.label.fill = am4core.color('#ffffff')

      return series;
    }

    createSeries('value_one', 'The First');
    createSeries('value_two', 'The Second');

    function arrangeColumns() {

      var series = chart.series.getIndex(0);

      var w = 1 - xAxis.renderer.cellStartLocation - (1 - xAxis.renderer.cellEndLocation);
      if (series.dataItems.length > 1) {
        var x0 = xAxis.getX(series.dataItems.getIndex(0), "categoryX");
        var x1 = xAxis.getX(series.dataItems.getIndex(1), "categoryX");
        var delta = ((x1 - x0) / chart.series.length) * w;
        if (am4core.isNumber(delta)) {
          var middle = chart.series.length / 2;

          var newIndex = 0;
          chart.series.each(function (series) {
            if (!series.isHidden && !series.isHiding) {
              series.dummyData = newIndex;
              newIndex++;
            }
            else {
              series.dummyData = chart.series.indexOf(series);
            }
          })
          var visibleCount = newIndex;
          var newMiddle = visibleCount / 2;

          chart.series.each(function (series) {
            var trueIndex = chart.series.indexOf(series);
            var newIndex = series.dummyData;

            var dx = (newIndex - trueIndex + middle - newMiddle) * delta

            series.animate({ property: "dx", to: dx }, series.interpolationDuration, series.interpolationEasing);
            series.bulletsContainer.animate({ property: "dx", to: dx }, series.interpolationDuration, series.interpolationEasing);
          })
        }
      }
    }

  });
  return (
    <div className="clustered-column-chart" id={id}></div>
  );
}

export default ClusteredColumnChart;