import React, { useState, useEffect, useRef } from 'react';
import axiosRetry from 'axios-retry';
import axios from 'axios';

import Loader from '../../components/loader/Loader';
import { InternalServerError } from '../../components/error/Error';

import { API_BASE_URL } from '../../constants/config';

import GeneralInfoContent from './GeneralInfoContent';


const GeneralInfo = () => {
  const [generalInfoData, setGeneralInfoData] = useState([]);
  const [isLoading, setIsLoading] = useState(true);

  // Axios call
  const fetchData = () => {
    let url = `${API_BASE_URL}/account-navigator/general_info.json`;
    // Uncomment only when API is now available and working
    // const token = localStorage.getItem('token');
    // TODO: Add url to call Filter API calls

    setIsLoading(true);

    axiosRetry(axios, { retries: 3 });

    axios({
      method: 'GET',
      url: url
      // Uncomment only when API is now available and working
      // url: url,
      // headers: {
      //   'Content-Type': 'application/json',
      //   "Authorization": `bearer ${token}`
      // }
    }).then(result => {
      // Uncomment only if for API testing purposes
      // console.log(result.config.url);
      setGeneralInfoData(result.data);
      setIsLoading(false);
    }).catch(error => {
      console.log(error);
      setGeneralInfoData(null);
      setIsLoading(false);
    });
  };

  useEffect(() => {
    fetchData();
  }, []);

  // dropdowns
  const cardRef = useRef(null);

  if (isLoading) return <Loader withContainer />

  return (
    <>
      <div className={`page-content`} >
        {
          generalInfoData ? 
          <>
            {/* ============= Your Component here! ============= */}
            <GeneralInfoContent generalInfoData={generalInfoData ? generalInfoData[0] : []} />
          </>
          : <InternalServerError redirectURL={`/account-navigator/navigator/general-info${window.location.search}`} ref={cardRef} />
        }
      </div>

    </>
  );
};

export default GeneralInfo;
