import React, { useState, useEffect } from 'react';

import PageHeader from '../../components/pageHeader/PageHeader';

import Overview from './Overview';
import Growth from './Growth';
import Metro from './Metro';
import Partner from './Partner';

import './channelPerformance.scss';


const ChannelPerformance = (props) => {
  let path = props.location.pathname;
  const currentPath = path.substring(path.lastIndexOf('/') + 1);
  const [activePage, setActivePage] = useState(currentPath);

  const handleActivePage = (page) => {
    setActivePage(page)
  }

  const handleBreadcrubsPreviousLink = () => {
    setActivePage('Overview')
  }

  useEffect(() => {
    setActivePage(currentPath)
  }, [currentPath])

  const breadcrumbs = [
    {
      path: '/analytics-dashboard/page',
      label: 'ANALYTICS DASHBOARD'
    },
    {
      path: path === '/analytics-dashboard/channel-performance/overview' ? null : '/analytics-dashboard/channel-performance/overview',
      label: 'Channel Performance'
    },
    {
      label: activePage,
    }
  ]

  const innerPage = [
    {
      path: '/analytics-dashboard/channel-performance/overview',
      label: 'Overview'
    },
    {
      path: '/analytics-dashboard/channel-performance/growth',
      label: 'Growth'
    },
    {
      path: '/analytics-dashboard/channel-performance/metro',
      label: 'Metro'
    },
    {
      path: '/analytics-dashboard/channel-performance/partner',
      label: 'Partner'
    }
  ]

  return (
    <div className="channel-performance-page">
      <PageHeader
        title="CHANNEL PERFORMANCE"
        color="dark-orange"
        innerPage={innerPage}
        breadcrumbs={breadcrumbs}
        previousPage="/analytics-dashboard/page"
        handleActivePage={handleActivePage}
        handleBreadcrubsPreviousLink={handleBreadcrubsPreviousLink}
      />
      <div className="page-wrapper">
        {currentPath === 'overview' && <Overview />}
        {currentPath === 'growth' && <Growth />}
        {currentPath === 'metro' && <Metro />}
        {currentPath === 'partner' && <Partner />}
      </div>
    </div>
  );
}

export default ChannelPerformance;
