import React, { useLayoutEffect } from 'react';
import * as am4core from "@amcharts/amcharts4/core";
import * as am4charts from "@amcharts/amcharts4/charts";
import { roundUpToTenth, convertToUSD } from '../../constants/formatter';
import am4themes_animated from "@amcharts/amcharts4/themes/animated";

am4core.useTheme(am4themes_animated);


function BubbleChart(props) {
  const { bubbleSelection, data, id, legend, totalRevenue, isAnimationEnabled, xAxisLabel, yAxisLabel } = props;
  console.log(props)
  useLayoutEffect(() => {
    var chart = am4core.create(id, am4charts.XYChart);
    chart.width = am4core.percent(100);
    chart.height = am4core.percent(100);

    chart.paddingRight = 0;
    chart.paddingLeft = 0;
    chart.paddingTop = 30;
    chart.paddingBottom = 0;

    chart.data = data;

    var xAxis = chart.xAxes.push(new am4charts.ValueAxis());
    xAxis.renderer.minGridDistance = 50;
    xAxis.renderer.grid.template.location = 0;
    xAxis.renderer.grid.template.strokeWidth = 0.5;
    xAxis.title.text = xAxisLabel;
    xAxis.title.rotation = 0;
    xAxis.title.align = "center";
    xAxis.title.valign = "top";


    xAxis.renderer.minGridDistance = 80;
    xAxis.id = "myCategoryAxis";

    var yAxis = chart.yAxes.push(new am4charts.ValueAxis());
    yAxis.renderer.minGridDistance = 50;
    yAxis.renderer.grid.template.location = 0;
    yAxis.renderer.grid.template.strokeWidth = 0.5;
    yAxis.title.text = yAxisLabel;
    yAxis.title.rotation = 0;
    yAxis.title.align = "center";
    yAxis.title.valign = "top";

    yAxis.renderer.minGridDistance = 80;
    yAxis.id = "myValueAxis";

    var series = chart.series.push(new am4charts.LineSeries());
    series.strokeOpacity = 0;
    series.sequencedInterpolation = true;
    series.tooltip.pointerOrientation = "vertical";
    series.hiddenState.transitionDuration = 0;
    series.defaultState.transitionDuration = isAnimationEnabled ? 1000 : 0;

    var bullet = series.bullets.push(new am4charts.CircleBullet())

    if (bubbleSelection === 'y-y_growth') {
      series.dataFields.valueX = "total_revenue";
      series.dataFields.valueY = "y_by_y_growth_percent";
      yAxis.renderer.labels.template.adapter.add("text", (text, label) => { return `${roundUpToTenth(label.dataItem.value)}%`; });
      xAxis.renderer.labels.template.adapter.add("text", (text, label) => { return convertToUSD(label.dataItem.value) });
      series.dataFields.value = "y_to_y_usd_growth";
      bullet.tooltipText = "[#fff]Y-Y $ Growth:[/] [#fff {less_y_to_y_usd_growth} font-weight:bold]${y_to_y_usd_growth}[/]\n[#fff]Y/Y Growth %:[/] [#fff font-weight:bold]{y_by_y_growth_percent.formatNumber('#.0')}%[/]\n[#fff]Revenue:[/] [#fff {less_rev_dollar} font-weight:bold]${total_revenue}[/]";
    } else if (bubbleSelection === 'mrc_dollar') {
      series.dataFields.valueX = "y_to_y_usd_growth";
      series.dataFields.valueY = "y_by_y_growth_percent";
      xAxis.renderer.labels.template.adapter.add("text", (text, label) => { return convertToUSD(label.dataItem.value) });
      yAxis.renderer.labels.template.adapter.add("text", (text, label) => { return `${roundUpToTenth(label.dataItem.value)}%`; });
      series.dataFields.value = "mrc_usd";
      bullet.tooltipText = "[#fff]Y-Y $ Growth:[/] [#fff {less_y_to_y_usd_growth} font-weight:bold]${y_to_y_usd_growth}[/]\n[#fff]Y/Y Growth %:[/] [#fff font-weight:bold]{y_by_y_growth_percent.formatNumber('#.0')}%[/]\n[#fff]Revenue:[/] [#fff {less_rev_dollar} font-weight:bold]${mrc_usd}[/]";
    } else if (bubbleSelection === 'y_to_y_usd_growth_with_mrc') {
      series.dataFields.valueX = "mrc_usd";
      series.dataFields.valueY = "y_by_y_growth_percent";
      yAxis.renderer.labels.template.adapter.add("text", (text, label) => { return `${roundUpToTenth(label.dataItem.value)}%`; });
      xAxis.renderer.labels.template.adapter.add("text", (text, label) => { return convertToUSD(label.dataItem.value) });
      series.dataFields.value = "y_to_y_usd_growth";
      bullet.tooltipText = "[#fff]Y-Y $ Growth:[/] [#fff {less_y_to_y_usd_growth} font-weight:bold]${y_to_y_usd_growth}[/]\n[#fff]Y/Y Growth %:[/] [#fff font-weight:bold]{y_by_y_growth_percent.formatNumber('#.0')}%[/]\n[#fff]Revenue:[/] [#fff {less_rev_dollar} font-weight:bold]${mrc_usd}[/]";
    } else if (bubbleSelection === 'mw_sold') {
      series.dataFields.valueX = "mw_sold_p4q_percent_growth";
      series.dataFields.valueY = "usd_per_kva_p4q_percent_growth";
      yAxis.renderer.labels.template.adapter.add("text", (text, label) => { return `${roundUpToTenth(label.dataItem.value)}%`; });
      xAxis.renderer.labels.template.adapter.add("text", (text, label) => { return convertToUSD(label.dataItem.value) });
      series.dataFields.value = "mw_sold";
      bullet.tooltipText = "[#fff]MW Sold:[/] [#fff font-weight:bold]${mw_sold}[/]\n[#fff]$ per KVA:[/] [#fff font-weight:bold]{usd_per_kva.formatNumber('#.##')}%[/]\n[#fff]Deploy Count:[/] [#fff font-weight:bold]${deploy_count}[/]";
    } else if (bubbleSelection === 'usd_per_kva') {
      series.dataFields.valueX = "mw_sold_p4q_percent_growth";
      series.dataFields.valueY = "usd_per_kva_p4q_percent_growth";
      yAxis.renderer.labels.template.adapter.add("text", (text, label) => { return `${roundUpToTenth(label.dataItem.value)}%`; });
      xAxis.renderer.labels.template.adapter.add("text", (text, label) => { return convertToUSD(label.dataItem.value) });
      series.dataFields.value = "usd_per_kva";
      bullet.tooltipText = "[#fff]MW Sold:[/] [{mw_sold} font-weight:bold]${mw_sold}[/]\n[#fff]$ per KVA:[/] [#fff font-weight:bold]{usd_per_kva.formatNumber('#.##')}%[/]\n[#fff]Deploy Count:[/] [{deploy_count} font-weight:bold]${deploy_count}[/]";
    } else if (bubbleSelection === 'deployment_num') {
      series.dataFields.valueX = "mw_sold_p4q_percent_growth";
      series.dataFields.valueY = "usd_per_kva_p4q_percent_growth";
      yAxis.renderer.labels.template.adapter.add("text", (text, label) => { return `${roundUpToTenth(label.dataItem.value)}%`; });
      xAxis.renderer.labels.template.adapter.add("text", (text, label) => { return convertToUSD(label.dataItem.value) });
      series.dataFields.value = "deployment_num";
      bullet.tooltipText = "[#fff]MW Sold:[/] [{mw_sold} font-weight:bold]${mw_sold}[/]\n[#fff]$ per KVA:[/] [#fff font-weight:bold]{usd_per_kva.formatNumber('#.##')}%[/]\n[#fff]Deploy Count:[/] [{deploy_count} font-weight:bold]${deploy_count}[/]";
    } else if (bubbleSelection === 'deal_mw') {
      series.dataFields.valueX = "density";
      series.dataFields.valueY = "usd_per_kva_p4q_percent_growth";
      yAxis.renderer.labels.template.adapter.add("text", (text, label) => { return `${roundUpToTenth(label.dataItem.value)}%`; });
      xAxis.renderer.labels.template.adapter.add("text", (text, label) => { return convertToUSD(label.dataItem.value) });
      series.dataFields.value = "mrc_usd";
      bullet.tooltipText = "[#fff]Y-Y $ Growth:[/] [#fff {less_y_to_y_usd_growth} font-weight:bold]${y_to_y_usd_growth}[/]\n[#fff]Y/Y Growth %:[/] [#fff font-weight:bold]{y_by_y_growth_percent.formatNumber('#.0')}%[/]\n[#fff]Revenue:[/] [#fff {less_rev_dollar} font-weight:bold]${mrc_usd}[/]";
    } else if (bubbleSelection === 'cust_num') {
      series.dataFields.valueX = "y_to_y_growth_count";
      series.dataFields.valueY = "y_by_y_growth_percent";
      yAxis.renderer.labels.template.adapter.add("text", (text, label) => { return `${roundUpToTenth(label.dataItem.value)}%`; });
      xAxis.renderer.labels.template.adapter.add("text", (text, label) => { return convertToUSD(label.dataItem.value) });
      series.dataFields.value = "customer_count";
      bullet.tooltipText = "[#fff]Y-Y $ Growth:[/] [#fff {less_y_to_y_usd_growth} font-weight:bold]${y_to_y_usd_growth}[/]\n[#fff]Y/Y Growth %:[/] [#fff font-weight:bold]{y_by_y_growth_percent.formatNumber('#.0')}%[/]\n[#fff]Customer Count:[/] [#fff {less_rev_dollar} font-weight:bold]${customer_count}[/]";
    } else if (bubbleSelection === 'cust_num_new_logo') {
      series.dataFields.valueX = "y_to_y_number_growth";
      series.dataFields.valueY = "y_by_y_growth_percent";
      yAxis.renderer.labels.template.adapter.add("text", (text, label) => { return `${roundUpToTenth(label.dataItem.value)}%`; });
      xAxis.renderer.labels.template.adapter.add("text", (text, label) => { return convertToUSD(label.dataItem.value) });
      series.dataFields.value = "new_logo_count";
      bullet.tooltipText = "[#fff]Y-Y $ Growth:[/] [#fff {less_y_to_y_usd_growth} font-weight:bold]${y_to_y_usd_growth}[/]\n[#fff]Y/Y Growth %:[/] [#fff font-weight:bold]{y_by_y_growth_percent.formatNumber('#.0')}%[/]\n[#fff]Customer Count:[/] [#fff {less_rev_dollar} font-weight:bold]${customer_count}[/]";
    } else if (bubbleSelection === 'ic_count') {
      series.dataFields.valueX = "y_to_y_growth_count";
      series.dataFields.valueY = "y_by_y_growth_percent";
      yAxis.renderer.labels.template.adapter.add("text", (text, label) => { return `${roundUpToTenth(label.dataItem.value)}%`; });
      xAxis.renderer.labels.template.adapter.add("text", (text, label) => { return convertToUSD(label.dataItem.value) });
      series.dataFields.value = "ic_counr";
      bullet.tooltipText = "[#fff]Y-Y $ Growth:[/] [#fff {less_y_to_y_usd_growth} font-weight:bold]${y_to_y_usd_growth}[/]\n[#fff]Y/Y Growth %:[/] [#fff font-weight:bold]{y_by_y_growth_percent.formatNumber('#.0')}%[/]\n[#fff]IC Count:[/] [#fff {less_rev_dollar} font-weight:bold]${ic_count}[/]";
    } else {
      series.dataFields.valueX = "y_to_y_usd_growth";
      series.dataFields.valueY = "y_by_y_growth_percent";
      xAxis.renderer.labels.template.adapter.add("text", (text, label) => { return convertToUSD(label.dataItem.value) });
      yAxis.renderer.labels.template.adapter.add("text", (text, label) => { return `${roundUpToTenth(label.dataItem.value)}%`; });
      series.dataFields.value = "total_revenue";
      bullet.tooltipText = "[#fff]Y-Y $ Growth:[/] [#fff {less_y_to_y_usd_growth} font-weight:bold]${y_to_y_usd_growth}[/]\n[#fff]Y/Y Growth %:[/] [#fff font-weight:bold]{y_by_y_growth_percent.formatNumber('#.0')}%[/]\n[#fff]Revenue:[/] [#fff {less_rev_dollar} font-weight:bold]${total_revenue}[/]";
      // series.tooltip.getFillFromObject = false;
      // series.tooltip.background.propertyFields.stroke = "borderColor";
    }

    function createSeries(opacity) {
      bullet.autoTextColor = false;
      bullet.propertyFields.fill = "color";
      bullet.strokeOpacity = 0;
      bullet.strokeWidth = 2;
      bullet.hiddenState.properties.opacity = 0;
      bullet.circle.propertyFields.fill = "color";
      bullet.circle.propertyFields.stroke = "color";
      bullet.circle.strokeWidth = 2;
      series.heatRules.push({
        target: bullet.circle,
        min: 15,
        max: 60,
        property: "radius"
      });
    }

    var seriesArr = [];
    data.map(item => {
      seriesArr.push(createSeries(item.opacity))
    })

    const bulletLabel = series.bullets.push(new am4charts.LabelBullet());
    bulletLabel.fill = am4core.color('#ffffff');
    bulletLabel.adapter.add('dy', (dy, bullet) => {
      let radiusDy = dy;
      bullet.dataItem.bullets.iterator()(firstBullet => {
        const circleBullet = firstBullet[1];
        if (bullet.dataItem.dataContext.bubbleLabelText) {
          radiusDy = circleBullet.circle.radius;
          if (bullet.dataItem.values.valueY.value >= 10) {
            radiusDy += 12;
          } else {
            radiusDy = -1 * radiusDy - 12;
          }
        }
      })
      return radiusDy;
    });

    // Hide label for each bubble
    // bulletLabel.label.adapter.add("text", (label, target, key) => {
    //   return target.dataItem.dataContext.segment;
    // });

    chart.responsive.enabled = true;

    return () => {
      chart.dispose();
    };
  }, [bubbleSelection, data, id, legend, totalRevenue, isAnimationEnabled, xAxisLabel, yAxisLabel]);

  return (
    <div id={id} className="bubble-chart" ></div>
  );
}

export default BubbleChart;

