import React, { useState, useEffect } from 'react';
import axiosRetry from 'axios-retry';
import axios from 'axios';
import DestinationContent from './DestinationContent';
import { Button, ButtonGroup, Container } from 'react-bootstrap';
import { Separator, RadioDD, CheckboxDD, SearchCheckboxDD } from '../../components/customDropdown';
import { FilterContainer, FilterModal } from '../../components/filter';
import Comment from '../../components/comment/Comment';
import Loader from '../../components/loader/Loader';
import { InternalServerError, ResourceNotFoundError } from '../../components/error/Error';
import { optionRegion, optionVertical2, optionCountry, optionFocusedList } from '../../constants/filterOptions';
import { API_BASE_URL } from '../../constants/config';
import { comments } from '../../constants/comments';


const Destination = ({ activeTab, setActiveTab }) => {
  const [isLoading, setIsLoading] = useState(true);
  const [idState, setReferenceID] = useState(null);
  const [metroData, setData] = useState(null);
  const [modalShow, setModalShow] = useState(false);
  const [modalData, setModalData] = useState(null);

  // show all filters
  const [showFilter, setShowFilter] = useState(false);
  const onShowFilter = () => {
    setShowFilter(true);
  };
  const onCloseFilter = async () => {
    await onCancelFocused();
    setShowFilter(false);
  };
  const onApplyFilter = async () => {
    await onApplyFocused();
    setShowFilter(false);
  };

  // comment area
  const [showComment, setShowComment] = useState(false);
  const onCloseComment = () => {
    setShowComment(false);
  };
  const onShowComment = () => {
    setShowComment(true);
  };


  // checkbox for region values
  const [hqregion_val_onApply, set_hqregion_val_onApply] = useState([]);
  const [hqregion_val, set_hqregion_val] = useState([]);
  const onApplyHQRegion = () => {
    set_hqregion_val_onApply(hqregion_val);
  };
  const onCancelHQRegion = () => {
    set_hqregion_val(hqregion_val_onApply);
  };

  // checkbox for region values
  const [focused_val_onApply, set_focused_val_onApply] = useState([]);
  const [focused_val, set_focused_val] = useState([]);
  const onApplyFocused = () => {
    set_focused_val_onApply(focused_val);
  };
  const onCancelFocused = () => {
    set_focused_val(focused_val_onApply);
  };

  // searchCheckbox for country values
  const [hq_country_val_onApply, set_hq_country_val_onApply] = useState([]);
  const [hq_country_val, set_hq_country_val] = useState([]);
  const onApplyHQCountry = () => {
    set_hq_country_val_onApply(hq_country_val);
  };
  const onCancelHQCountry = () => {
    set_hq_country_val(hq_country_val_onApply);
  };


  // checkbox for vertical values
  const [vertical_val_onApply, set_vertical_val_onApply] = useState([]);
  const [vertical_val, set_vertical_val] = useState([]);
  const onApplyVertical = () => {
    set_vertical_val_onApply(vertical_val);
  };
  const onCancelVertical = () => {
    set_vertical_val(vertical_val_onApply);
  };

  // Axios call
  const fetchData = () => {
    let url = `${API_BASE_URL}/global-platform/destination.json`;
    // Uncomment only when API is now available and working
    // const token = localStorage.getItem('token');
    // let url = `${API_BASE}/orbis/api/totalrevenuemetro`;
    // let defaultQueryParameters = `?measure=${measure_val_onApply.key}&range=${range_val_onApply.key}`;
    // let regionParameter = region_val_onApply.length > 0 ? `&region=${region_val_onApply.join(",").toString()}` : '';

    setIsLoading(true);

    axiosRetry(axios, { retries: 3 });

    axios({
      method: 'GET',
      url: url
      // Uncomment only when API is now available and working
      // url: url + defaultQueryParameters + regionParameter,
      // headers: {
      //   'Content-Type': 'application/json',
      //   "Authorization": `bearer ${token}`
      // }
    }).then(result => {
      // Uncomment only if for API testing purposes
      // console.log(result.config.url);
      setData(result.data);
      // setImportData(result.data['import-region']);
      // setExportData(result.data['export-region']);
      console.log('exportData', result.data)
      setIsLoading(false);
    }).catch(error => {
      console.log(error);
      setData(null);
      setIsLoading(false);
    });
  };

  useEffect(() => {
    fetchData();
  },
    []
    // Uncomment only when API is now available and working
    // [
    //   region_val_onApply,
    //   measure_val_onApply,
    //   range_val_onApply 
    // ]
  );

  if (isLoading) return <Loader withContainer />;

  return (
    <>
      <div className={`page-content ${showComment ? 'open' : 'closed'}`}>
        {metroData ?
          metroData.metro_ranking.by_rank_id.length > 0 ?
            <>
              <FilterContainer
                showComment={showComment}
                onShowFilter={onShowFilter}
                onShowComment={onShowComment}
              >
                <div className="filters custom-scroll">
                  <ButtonGroup className="sub-tab">
                    <Button
                      variant='light'
                      className={activeTab === 'multi-metro' ? 'active' : ''}
                      onClick={() => { setActiveTab('multi-metro') }}>
                      Multi-Metro
                		</Button>
                    <Button
                      variant='light'
                      className={activeTab === 'multi-country' ? 'active' : ''}
                      onClick={() => { setActiveTab('multi-country') }}>
                      Multi-Country
                		</Button>
                    <Button
                      variant='light'
                      className={activeTab === 'multi-region' ? 'active' : ''}
                      onClick={() => { setActiveTab('multi-region') }}>
                      Multi-Region
                		</Button>
                    <Button
                      variant='light'
                      className={activeTab === 'three-region' ? 'active' : ''}
                      onClick={() => { setActiveTab('three-region') }}>
                      3-Region
                		</Button>
                  </ButtonGroup>
                  <Separator height='39px' />
                  <CheckboxDD
                    title="HQ Region"
                    id="hqregion"
                    options={optionRegion}
                    checkVal={hqregion_val}
                    setCheckVal={set_hqregion_val}
                    onApply={onApplyHQRegion}
                    onCancel={onCancelHQRegion}
                  />
                  <SearchCheckboxDD
                    title="HQ Country"
                    id="country"
                    options={optionCountry}
                    checkVal={hq_country_val}
                    setCheckVal={set_hq_country_val}
                  />
                  <CheckboxDD
                    title="Vertical"
                    id="vertical"
                    options={optionVertical2}
                    checkVal={vertical_val}
                    setCheckVal={set_vertical_val}
                    onApply={onApplyVertical}
                    onCancel={onCancelVertical}
                  />
                  <CheckboxDD
                    title="Focused List"
                    id="focused-list"
                    options={optionFocusedList}
                    checkVal={focused_val}
                    setCheckVal={set_focused_val}
                    onApply={onApplyFocused}
                    onCancel={onCancelFocused}
                  />
                </div>

                <FilterModal
                  title="Destination"
                  setShowFilter={setShowFilter}
                  showFilter={showFilter}
                  onCloseFilter={onCloseFilter}
                  onApplyFilter={onApplyFilter}
                >
                  <CheckboxDD
                    title="HQ Region"
                    id="hqregion-modal"
                    visibleInModal
                    options={optionRegion}
                    checkVal={hqregion_val}
                    setCheckVal={set_hqregion_val}
                    onApply={onApplyHQRegion}
                    onCancel={onCancelHQRegion}
                  />
                  <SearchCheckboxDD
                    title="HQ Country"
                    id="country-modal"
                    visibleInModal
                    options={optionCountry}
                    checkVal={hq_country_val}
                    setCheckVal={set_hq_country_val}
                    onApply={onApplyHQCountry}
                    onCancel={onCancelHQCountry}
                  />
                  <CheckboxDD
                    title="Vertical"
                    id="vertical-modal"
                    visibleInModal
                    options={optionVertical2}
                    checkVal={vertical_val}
                    setCheckVal={set_vertical_val}
                    onApply={onApplyVertical}
                    onCancel={onCancelVertical}
                  />
                  <CheckboxDD
                    title="Focused List"
                    id="focused-list-modal"
                    visibleInModal
                    options={optionFocusedList}
                    checkVal={focused_val}
                    setCheckVal={set_focused_val}
                    onApply={onApplyFocused}
                    onCancel={onCancelFocused}
                  />

                </FilterModal>
              </FilterContainer>
              <DestinationContent
                metroData={metroData}
                modalShow={modalShow}
                modalData={modalData}
                idState={idState}
                setReferenceID={setReferenceID}
                setModalData={setModalData}
                setModalShow={setModalShow}
                activeTab={activeTab === 'three-region' ? '3-region' : activeTab}
              />
            </>
            : <ResourceNotFoundError redirectURL={"/executive-cockpit/gross-bookings-revenue/metro"} />
          : <InternalServerError redirectURL={"/executive-cockpit/gross-bookings-revenue/metro"} />}
      </div>
      <Comment
        data={comments}
        onCloseComment={onCloseComment}
        showComment={showComment}
      />
    </>
  );
};

export default Destination;