import React, { useState } from 'react';
import { ImArrowUpRight2, ImArrowDownRight2 } from "react-icons/im";
import { Modal, Row, Col } from 'react-bootstrap';
import KPI_NEW from '../../components/kpi/KPI_NEW';
import Legend from '../../components/legend/Legend';
import MicroBarChart from '../../components/chart/MicroBarChart';
import CustomTable, { formatLongText } from '../../components/customTable/CustomTable';
import MapModalChart from '../../components/chart/MapModalChart';
import DonutChart from '../../components/chart/DonutChart';
import { convertToPercent, convertToUSDGrowth, convertToCount } from '../../constants/formatter';
import { optionShowBy7 } from '../../constants/filterOptions';
import { RadioDD } from '../../components/customDropdown';

const ExpandModal = (props) => {
	const { data, selectedCustomer, show, onClose } = props;

	// Data Distribution
	let overall = data ? data.overall : [];
	let tableDetails = data ? data.customer_details : [];
	let revenueMix = data ? data.customer_revenue_mix : [];
	let deploymentMap = data ? data.deployment_map : [];

	// radio for show-by values
	const show_initial_value = optionShowBy7 ? optionShowBy7[0] : null;
	const [show_val_onApply, set_show_val_onApply] = useState(show_initial_value);
	const [show_val, set_show_val] = useState(show_initial_value);
	const onApplyShow = () => {
		set_show_val_onApply(show_val);
	};
	const onCancelShow = () => {
		set_show_val(show_val_onApply);
	};

	// Table Config
	const indicatorPercent = (cell, row) => {
		let bgColor = cell >= 0.5 ? 'indicator-up' : cell <= -0.5 ? 'indicator-down' : 'indicator-middle';
		let icon = cell >= 0.5 ? <ImArrowUpRight2 /> : cell <= -0.5 ? <ImArrowDownRight2 /> : <span>&nbsp;</span>;
		return (
			<span className={`indicator ${bgColor}`}>
				{icon}
				{convertToPercent(cell)}
			</span>
		);
	};

	const formatUSDGrowth = (cell, row) => {
		let new_cell = convertToUSDGrowth(cell);
		return formatLongText(new_cell, row)
	};

	const formatToCount = (cell, row) => {
		return <span>{convertToCount(cell)}</span>
	};

	const microBarChart = (cell, row) => {
		return <MicroBarChart loadedData={cell} id={"micro-bar-chart-" + row._id} />
	};

	const metroColumn = (cell, row) => {
		return (
			<>
				<img className="flag-icon" src={cell.image_url} alt={cell.name} />
				<span className="body-name">{cell.name}</span>
			</>
		)
	};

	const rankFormat = (cell, row, rowIndex, formatExtraData) => {
		return <span>{rowIndex + 1}</span>
	};

	const defaultSorted = [{
		dataField: 'mrr_mix_percent',
		order: 'desc',
	}];

	const columns = [
		{
			dataField: '_id',
			text: 'id',
			hidden: true
		},
		{
			dataField: '', // For Ranks
			text: 'Rank',
			classes: 'rank',
			headerClasses: 'rank',
			formatter: rankFormat,
		},
		{
			dataField: 'metro',
			text: 'segment',
			classes: 'metro with-ellipsis',
			headerClasses: 'metro',
			formatter: metroColumn
		},
		// {
		// 	dataField: 'micro_column_bar_data',
		// 	text: '',
		// 	classes: 'center',
		// 	headerClasses: 'center',
		// 	classes: 'sparkline-cell',
		// 	headerClasses: 'sparkline-cell',
		// 	formatter: microBarChart
		// },
		{
			dataField: 'status',
			text: 'STATUS',
			sort: true,
			classes: 'left',
			headerClasses: 'left',
		},
		{
			dataField: 'mrc_usd',
			text: 'MRC $',
			sort: true,
			classes: 'right',
			headerClasses: 'right',
			formatter: formatToCount
		},

		{
			dataField: 'mrc_usd_6_qtr_percent_growth',
			text: '6QTR % GROWTH',
			sort: true,
			classes: 'border-right with-ellipsis center',
			headerClasses: 'border-right center',
			formatter: indicatorPercent
		},
		{
			dataField: 'cabinet_count',
			text: 'CABINET #',
			sort: true,
			classes: 'right',
			headerClasses: 'right',
			formatter: formatToCount
		},

		{
			dataField: 'cabinet_count_6_qtr_percent_growth',
			text: '6QTR % GROWTH',
			sort: true,
			formatter: indicatorPercent,
			classes: 'border-right with-ellipsis center',
			headerClasses: 'border-right center',
		},
		{
			dataField: 'interconnection_count',
			text: 'INTERCONNECTION #',
			sort: true,
			classes: 'right',
			headerClasses: 'right',
			formatter: formatToCount
		},
		{
			dataField: 'interconnection_count_6_qtr_percent_growth',
			text: '6QTR % GROWTH',
			sort: true,
			formatter: indicatorPercent,
			classes: 'with-ellipsis right',
			headerClasses: 'right',
		}
	];

	return (
		<Modal
			id="customer-modal"
			className="customer-modal "
			show={show}
			onHide={onClose}
			aria-labelledby="contained-modal-title-vcenter"
			centered
		>
			<Modal.Header>
				<Modal.Title id="contained-modal-title-vcenter">
					{selectedCustomer} extension trends
				</Modal.Title>
				<button type="button" className="close" onClick={onClose}>
					<span aria-hidden="true">×</span><span className="sr-only">
						Close
          </span>
				</button>
				<div className="filters custom-scroll">
					<RadioDD
						id="show_by_modal"
						options={optionShowBy7}
						radioVal={show_val}
						setRadioVal={set_show_val}
						onApply={onApplyShow}
						onCancel={onCancelShow}
						initialValue={show_initial_value}
					/>
				</div>
			</Modal.Header>
			<Modal.Body>
				{data ?
					<>
						<Row>
							<Col className="fix-width pr-30">
								<KPI_NEW
									totalValue={overall.total.value}
									totalUnit={overall.total.unit}
									details={overall.details}
									noHover={true}
								/>
							</Col>
							<Col md={6} xs={12}>
								<Legend legend={deploymentMap.legend} />
								<div className="pt-30" style={{ height: "350px" }}>
									<MapModalChart
										id={"new_logos_expand_metro_modal_map_chart"}
										largerWidth={false}
										data={deploymentMap.data}
										isAnimationEnabled={false}
									/>
								</div>
							</Col>
							<Col className="pl-30">
								<div style={{ height: "92%" }}>
									<DonutChart
										id={"new_logos_expand_metro_modal_donut_chart"}
										totalTitle={'$ MRC'}
										total={revenueMix.mrc_usd}
										data={revenueMix.data}
										legend={revenueMix.legend}
										isDollar={true}
										isAnimationEnabled={false}
									/>
								</div>
							</Col>
						</Row>
						<Row>
							<Col md={12} className="pt-4">
								<CustomTable
									tableName='vertical-details'
									data={tableDetails === undefined ? [] : tableDetails}
									defaultSorted={defaultSorted}
									columns={columns}
								/>
							</Col>
						</Row>
					</>
					: <div>No data available.</div>
				}
			</Modal.Body>
		</Modal>
	);
}

export default ExpandModal;