import React from 'react';
import { ImArrowUpRight2, ImArrowDownRight2 } from "react-icons/im";
import { Modal, Row, Col } from 'react-bootstrap';
import KPI_NEW from '../../components/kpi/KPI_NEW';
import Legend from '../../components/legend/Legend';
import MicroBarChart from '../../components/chart/MicroBarChart';
import CustomTable, { formatLongText } from '../../components/customTable/CustomTable';
import MapModalChart from '../../components/chart/MapModalChart';
import DonutChart from '../../components/chart/DonutChart';
import { convertToPercent, convertToUSDGrowth, convertToWholePercent } from '../../constants/formatter';


const CustomerModal = (props) => {
	const { data, selectedPartner, show, onClose, activeTab } = props;

	// Data Distribution
	let overall = data ? data.overall : [];
	let tableDetails = data ? data.customer_details : [];
	let revenueMix = data ? data.customer_revenue_mix : [];
	let deploymentMap = data ? data.deployment_map : [];
	// Table Config
	const indicatorPercent = (cell, row) => {
		let bgColor = cell >= 0.5 ? 'indicator-up' : cell <= -0.5 ? 'indicator-down' : 'indicator-middle';
		let icon = cell >= 0.5 ? <ImArrowUpRight2 /> : cell <= -0.5 ? <ImArrowDownRight2 /> : <span>&nbsp;</span>;
		return (
			<span className={`indicator ${bgColor}`}>
				{icon}
				{convertToPercent(cell)}
			</span>
		);
	};

	const formatUSDGrowth = (cell, row) => {
		let new_cell = convertToUSDGrowth(cell);
		return formatLongText(new_cell, row)
	};

	const formatWholePercent = (cell, row) => {
		return <span>{convertToWholePercent(cell)}</span>
	};

	const microBarChart = (cell, row) => {
		return <MicroBarChart loadedData={cell} id={"modal-micro-bar-chart-" + row._id} />
	};

	const metroColumn = (cell, row) => {
		return (
			<>
				<img className="flag-icon" src={cell.image_url} alt={cell.name} />
				<span className="body-name">{cell.name}</span>
			</>
		)
	};

	const statusFormat = (cell, row) => {
		let new_color = cell.color.replace(/#/g, '')
		return <div className={`pill-status color-${new_color}`} >{cell.value}</div>
	}

	const rankFormat = (cell, row, rowIndex, formatExtraData) => {
		return <span>{rowIndex + 1}</span>
	};

	const defaultSorted = [{
		dataField: 'mrr_mix_percent',
		order: 'desc',
	}];

	const columns = [
		{
			dataField: '_id',
			text: 'id',
			hidden: true
		},
		{
			dataField: '', // For Ranks
			text: 'Rank',
			classes: 'center',
			headerClasses: 'center',
			classes: 'rank',
			headerClasses: 'rank',
			formatter: rankFormat,
		},
		{
			dataField: 'metro',
			text: 'segment',
			classes: 'left',
			headerClasses: 'left',
			classes: 'metro with-ellipsis',
			headerClasses: 'metro',
			formatter: metroColumn
		},
		{
			dataField: 'micro_column_bar_data',
			text: '',
			classes: 'center',
			headerClasses: 'center',
			classes: 'sparkline-cell',
			headerClasses: 'sparkline-cell',
			formatter: microBarChart
		},
		{
			dataField: 'mrc_usd',
			text: 'MRC $',
			sort: true,
			classes: 'right',
			headerClasses: 'right',
			formatter: formatUSDGrowth
		},

		{
			dataField: 'percent_customer_total',
			text: '% CUSTOMER TOTAL',
			sort: true,
			classes: 'right',
			headerClasses: 'right',
			formatter: formatWholePercent
		},
		{
			dataField: 'percent_metro_total',
			text: '% METRO TOTAL',
			sort: true,
			classes: 'right',
			headerClasses: 'right',
			formatter: formatWholePercent
		},

		{
			dataField: 'q_by_q_growth_percent',
			text: 'Q/Q GROWTH %',
			sort: true,
			formatter: indicatorPercent,
			classes: 'with-ellipsis center',
			headerClasses: 'center'
		},
		{
			dataField: 'p4q_growth_percent',
			text: 'P4Q GROWTH %',
			sort: true,
			classes: 'with-ellipsis center',
			headerClasses: 'center',
			formatter: indicatorPercent
		},
		{
			dataField: 'y_by_y_growth_percent',
			text: 'Y/Y GROWTH %',
			sort: true,
			formatter: indicatorPercent,
			classes: 'with-ellipsis center',
			headerClasses: 'center',
		}
	];


	const defaultSorted2 = [{
		dataField: 'mrc_usd',
		order: 'desc',
	}];

	const columns2 = [
		{
			dataField: '_id',
			text: 'id',
			hidden: true
		},
		{
			dataField: '', // For Ranks
			text: 'Rank',
			classes: 'center',
			headerClasses: 'center',
			classes: 'rank',
			headerClasses: 'rank',
			formatter: rankFormat,
		},
		{
			dataField: 'oppty_name',
			text: 'OPPTY NAME',
			classes: 'left',
			headerClasses: 'left',
			classes: 'oppty-name with-ellipsis',
			headerClasses: 'oppty-name',
		},
		{
			dataField: 'ibx',
			text: 'IBX',
			classes: 'left',
			headerClasses: 'left',
		},
		{
			dataField: 'status',
			text: 'STATUS',
			classes: 'left',
			headerClasses: 'left',
			formatter: statusFormat
		},
		{
			dataField: 'mrc_usd',
			text: 'MRC $',
			sort: true,
			classes: 'right',
			headerClasses: 'right',
			formatter: formatUSDGrowth
		},
		{
			dataField: 'mix_percent',
			text: 'MIX %',
			sort: true,
			classes: 'right',
			headerClasses: 'right'
		},
	];


	return (
		<Modal
			id="customer-modal"
			className="customer-modal "
			show={show}
			onHide={onClose}
			aria-labelledby="contained-modal-title-vcenter"
			centered
		>
			<Modal.Header>
				<Modal.Title id="contained-modal-title-vcenter">
					{selectedPartner} pipeline
				</Modal.Title>
				<button type="button" className="close" onClick={onClose}>
					<span aria-hidden="true">×</span><span className="sr-only">
						Close
          </span>
				</button>
			</Modal.Header>
			<Modal.Body>
				{data ?
					<>
						<Row>
							<Col className="fix-width pr-30">
								<KPI_NEW
									totalValue={overall.total.value}
									totalUnit={overall.total.unit}
									details={overall.details}
									noHover={true}
								/>
							</Col>
							<Col md={6} xs={12}>
								<Legend legend={deploymentMap.legend} />
								<div className="pt-30" style={{ height: "350px" }}>
									<MapModalChart
										id={"pipeline_metro_modal_map_chart"}
										largerWidth={false}
										data={deploymentMap.data}
										isAnimationEnabled={false}
									/>
								</div>
							</Col>
							<Col className="pl-30">
								<div style={{ height: "350px" }}>
									<DonutChart
										id={"pipeline_metro_modal_donut_chart"}
										totalTitle={'PIPELINE'}
										total={revenueMix.pipeline}
										data={revenueMix.data}
										legend={revenueMix.legend}
										isDollar={true}
										isAnimationEnabled={false}
									/>
								</div>
							</Col>
						</Row>
						<Row>
							<Col md={12} className="pt-4">
								{activeTab === 'customer' ?
									<CustomTable
										tableName='vertical-details'
										data={tableDetails === undefined ? [] : tableDetails}
										defaultSorted={defaultSorted}
										columns={columns}
									/>
									:
									<CustomTable
										tableName='vertical-details2'
										data={tableDetails === undefined ? [] : tableDetails}
										defaultSorted={defaultSorted2}
										columns={columns2}
									/>
								}

							</Col>
						</Row>
					</>
					: <div>No data available.</div>
				}
			</Modal.Body>
		</Modal>
	);
}

export default CustomerModal;