import React, { useState, useEffect } from 'react';
import axiosRetry from 'axios-retry';
import axios from 'axios';
import {RadioDD, CheckboxDD, SearchCheckboxDD} from '../../components/customDropdown';
import {FilterContainer, FilterModal} from '../../components/filter';
import Loader from '../../components/loader/Loader';
import Comment from '../../components/comment/Comment';
import { InternalServerError, ResourceNotFoundError } from '../../components/error/Error';
import { optionMeasure, optionRange, optionGroup1, optionRegion, optionMetro } from '../../constants/filterOptions';
import { API_BASE_URL } from '../../constants/config';
import { comments } from '../../constants/comments';
import DeploymentContent from './DeploymentContent';


const Deployments = () => {
  const [deploymentData, setDeploymentData] = useState([])
  const [isLoading, setIsLoading] = useState(true)

  // show all filters
  const [showFilter, setShowFilter] = useState(false);
  const onShowFilter = () => {
    setShowFilter(true)
  }

  const onCloseFilter = async () => {
    await onCancelRegion();
    await onCancelMetro();
    await onCancelMeasure();
    await onCancelRange();
    await onCancelGroup1();
    setShowFilter(false);
  }
  const onApplyFilter = async () => {
    await onApplyRegion();
    await onApplyMetro();
    await onApplyMeasure();
    await onApplyRange();
    await onApplyGroup1();
    setShowFilter(false);
  }
  // comment area
  const [showComment, setShowComment] = useState(false);
  const onCloseComment = () => {
    setShowComment(false);
  };
  const onShowComment = () => {
    setShowComment(true);
  };

  // checkbox for region values
  const [region_val_onApply, set_region_val_onApply] = useState([]);
  const [region_val, set_region_val] = useState([]);
  const onApplyRegion = () => {
    set_region_val_onApply(region_val);
  };
  const onCancelRegion = () => {
    set_region_val(region_val_onApply);
  };

  // searchCheckbox for metro values
  const [metro_val_onApply, set_metro_val_onApply] = useState([]);
  const [metro_val, set_metro_val] = useState([]);
  const onApplyMetro = () => {
    set_metro_val_onApply(metro_val);
  };
  const onCancelMetro = () => {
    set_metro_val(metro_val_onApply);
  };

  // radio for measure values
  const measure_initial_value = optionMeasure ? optionMeasure[0] : null;
  const [measure_val_onApply, set_measure_val_onApply] = useState(measure_initial_value);
  const [measure_val, set_measure_val] = useState(measure_initial_value);
  const onApplyMeasure = () => {
    set_measure_val_onApply(measure_val);
  };
  const onCancelMeasure = () => {
    set_measure_val(measure_val_onApply);
  };

  // radio for range values
  const range_initial_value = optionRange ? optionRange[0] : null;
  const [range_val_onApply, set_range_val_onApply] = useState(range_initial_value);
  const [range_val, set_range_val] = useState(range_initial_value);
  const onApplyRange = () => {
    set_range_val_onApply(range_val);
  };
  const onCancelRange = () => {
    set_range_val(range_val_onApply);
  };

  // radio for group1 values
  const group1_initial_value = optionGroup1 ? optionGroup1[0] : null;
  const [group1_val_onApply, set_group1_val_onApply] = useState(group1_initial_value);
  const [group1_val, set_group1_val] = useState(group1_initial_value);
  const onApplyGroup1 = () => {
    set_group1_val_onApply(group1_val);
  };
  const onCancelGroup1 = () => {
    set_group1_val(group1_val_onApply);
  };

  // Axios call
  const fetchData = () => {
    let url = `${API_BASE_URL}/account-navigator/deployments.json?print=pretty`;
    // Uncomment only when API is now available and working
    // const token = localStorage.getItem('token');
    // TODO: Add url and defaultQueryParameters to call Filter API calls

    setIsLoading(true);

    axiosRetry(axios, { retries: 3 });

    axios({
      method: 'GET',
      url: url
      // Uncomment only when API is now available and working
      // url: url + defaultQueryParameters,
      // headers: {
      //   'Content-Type': 'application/json',
      //   "Authorization": `bearer ${token}`
      // }
    }).then(result => {
      // Uncomment only if for API testing purposes
      // console.log(result.config.url);
      setDeploymentData(result.data);
      setIsLoading(false);
    }).catch(error => {
      console.log(error);
      setDeploymentData(null);
      setIsLoading(false);
    });
  };

  useEffect(() => {
    fetchData();
  }, 
    []
    // Uncomment only when API is now available and working
    // [
    //   region_val_onApply,
    //   metro_val_onApply,
    //   measure_val_onApply,
    //   range_val_onApply,
    //   group1_val_onApply
    // ]
  );

  if (isLoading) return <Loader withContainer />;

  return (
    <>
      <div className={`page-content ${showComment ? 'open' : 'closed'}`}>
        { deploymentData ? 
          deploymentData.length > 0 ?
          <>
            <FilterContainer
              showComment={showComment}
              onShowFilter={onShowFilter}
              onShowComment={onShowComment}
            >
              <div className="filters custom-scroll">
                <CheckboxDD
                  title="Deploy Region"
                  id="region"
                  options={optionRegion}
                  valueApplied={region_val_onApply}
                  checkVal={region_val}
                  setCheckVal={set_region_val}
                  onApply={onApplyRegion} 
                  onCancel={onCancelRegion}
                />
                <SearchCheckboxDD
                  title="Deploy Metro"
                  id="metro"
                  options={optionMetro}
                  valueApplied={metro_val_onApply}
                  checkVal={metro_val}
                  setCheckVal={set_metro_val}
                  onApply={onApplyMetro}
                  onCancel={onCancelMetro}
                />
                <RadioDD
                  title="Measure"
                  id="measure"
                  options={optionMeasure}
                  radioVal={measure_val}
                  setRadioVal={set_measure_val}
                  onApply={onApplyMeasure}
                  onCancel={onCancelMeasure}
                />
                
                <RadioDD
                  title="Time Period"
                  id="range"
                  options={optionRange}
                  radioVal={range_val}
                  setRadioVal={set_range_val}
                  onApply={onApplyRange}
                  onCancel={onCancelRange}
                  initialValue={range_initial_value}
                />
                
                <RadioDD
                  title="Group by"
                  id="group"
                  addTitleOnWide
                  options={optionGroup1}
                  radioVal={group1_val}
                  setRadioVal={set_group1_val}
                  onApply={onApplyGroup1}
                  onCancel={onCancelGroup1}
                  initialValue={group1_initial_value}
                />
              </div>
              <FilterModal
                title="Deployment"
                setShowFilter={setShowFilter}
                showFilter={showFilter}
                onCloseFilter={onCloseFilter}
                onApplyFilter={onApplyFilter}
              >
                <CheckboxDD
                  title="Deploy Region"
                  id="region-modal"
                  options={optionRegion}
                  visibleInModal
                  checkVal={region_val}
                  setCheckVal={set_region_val}
                />
                <SearchCheckboxDD
                  title="Deploy Metro"
                  id="metro-modal"
                  options={optionMetro}
                  visibleInModal
                  checkVal={metro_val}
                  setCheckVal={set_metro_val}
                />
                <RadioDD
                  title="Measure"
                  id="measure-modal"
                  options={optionMeasure}
                  visibleInModal
                  radioVal={measure_val}
                  setRadioVal={set_measure_val}
                />
                <RadioDD
                  title="Time Period"
                  id="range-modal"
                  options={optionRange}
                  visibleInModal
                  radioVal={range_val}
                  setRadioVal={set_range_val}
                />
                <RadioDD
                  title="Group by"
                  id="group-modal"
                  options={optionGroup1}
                  visibleInModal
                  radioVal={group1_val}
                  setRadioVal={set_group1_val}
                />
              </FilterModal>
            </FilterContainer>
            <DeploymentContent deploymentData={deploymentData[0]} />
          </>
          : <ResourceNotFoundError redirectURL={`/account-navigator/navigator/deployments${window.location.search}`} />
          : <InternalServerError redirectURL={`/account-navigator/navigator/deployments${window.location.search}`}/> 
        }
      </div>
      <Comment
        data={comments}
        onCloseComment={onCloseComment}
        showComment={showComment}
      />
    </>
  );
};

export default Deployments;
