import React, { useState } from 'react';
import { ImArrowDownRight2, ImArrowUpRight2 } from "react-icons/im";
import { Modal, Row, Col } from 'react-bootstrap';
import KPI_NEW from '../../components/kpi/KPI_NEW';
import Legend from '../../components/legend/Legend';
import CustomTable, { formatLongText } from '../../components/customTable/CustomTable';
import MicroBarChart from '../../components/chart/MicroBarChart';
import StackedColumnChart from '../../components/chart/StackedColumnChart';
import DonutChart from '../../components/chart/DonutChart';
import { convertToUSDGrowth, convertToPercent, convertToWholePercent } from '../../constants/formatter';
import { optionGroup3, optionVertical } from '../../constants/filterOptions';
import { RadioDD } from '../../components/customDropdown';


const MetroModal = (props) => {
	const { show, onClose, data, selectedMetro, activeTab } = props;
	let modalName = activeTab;

	// Data Distribution
	let overall = data ? data.overall : [];
	let bookings_details = data ? data.bookings_details : [];
	let revenue_mix = data ? data.revenue_mix : [];
	let bookings_revenue = data ? data.bookings_revenue : [];


	// radio for group-by values
	const group_initial_value = optionGroup3 ? optionGroup3[0] : null;
	const [group_val_onApply, set_group_val_onApply] = useState(group_initial_value);
	const [group_val, set_group_val] = useState(group_initial_value);
	const onApplyGroup = () => {
		set_group_val_onApply(group_val);
	};
	const onCancelGroup = () => {
		set_group_val(group_val_onApply);
	};

	// radio for segmentation values
	const vertical_initial_value = optionVertical ? optionVertical[0] : null;
	const [vertical_val_onApply, set_vertical_val_onApply] = useState(vertical_initial_value);
	const [vertical_val, set_vertical_val] = useState(vertical_initial_value);
	const onApplyVertical = () => {
		set_vertical_val_onApply(vertical_val);
	};
	const onCancelVertical = () => {
		set_vertical_val(vertical_val_onApply);
	};

	// // radio for range values
	// const range_initial_value = optionGroup5 ? optionGroup5[0] : null;
	// const [range_val_onApply, set_range_val_onApply] = useState(range_initial_value);
	// const [range_val, set_range_val] = useState(range_initial_value);
	// const onApplyRange = () => {
	// 	set_range_val_onApply(range_val);
	// };
	// const onCancelRange = () => {
	// 	set_range_val(range_val_onApply);
	// };

	// Table Config
	const indicatorPercent = (cell, row) => {
		let bgColor = cell >= 0.5 ? 'indicator-up' : cell <= -0.5 ? 'indicator-down' : 'indicator-middle';
		let icon = cell >= 0.5 ? <ImArrowUpRight2 /> : cell <= -0.5 ? <ImArrowDownRight2 /> : <span>&nbsp;</span>;
		return (
			<span className={`indicator ${bgColor}`}>
				{icon}
				{convertToPercent(cell)}
			</span>
		);
	};

	const formatUSDGrowth = (cell, row) => {
		let new_cell = convertToUSDGrowth(cell);
		return formatLongText(new_cell, row)
	};

	const formatWholePercent = (cell, row) => {
		return <span>{convertToWholePercent(cell)}</span>
	};

	const formatToPercent = (cell, row) => {
		return <span>{convertToPercent(cell)}</span>
	};


	const microBarChart = (cell, row) => {
		return <MicroBarChart loadedData={cell} id={"micro-bar-chart-" + row._id} />
	};

	const defaultSorted = [{
		dataField: '_id',
		order: 'asc',
	}];

	const columns = [
		{
			dataField: '_id',
			text: 'id',
			hidden: true
		},
		{
			dataField: 'segment',
			text: 'SEGMENT',
			classes: 'segment',
			headerClasses: 'segment'
		},
		{
			dataField: 'micro_column_bar_data',
			text: '',
			sort: true,
			classes: 'sparkline-cell center',
			headerClasses: 'sparkline-cell center',
			formatter: microBarChart
		},
		{
			dataField: 'mrc_usd',
			text: 'MRC $',
			sort: true,
			classes: 'right',
			headerClasses: 'right',
			formatter: formatUSDGrowth
		},
		{
			dataField: 'mix_percent',
			text: 'MIX%',
			sort: true,
			classes: 'center',
			headerClasses: 'center',
			formatter: formatToPercent
		},
		{
			dataField: 'q_by_q_growth_percent',
			text: 'Q/Q % GROWTH',
			sort: true,
			classes: 'with-ellipsis center',
			headerClasses: 'center',
			formatter: indicatorPercent,
		},
		{
			dataField: 'q_to_q_usd_growth',
			text: 'Q-Q $ GROWTH',
			sort: true,
			classes: 'right',
			headerClasses: 'right',
			formatter: formatUSDGrowth
		},
		{
			dataField: 'p4q_growth_percent',
			text: 'P4Q % GROWTH',
			sort: true,
			classes: 'with-ellipsis center',
			headerClasses: 'center',
			formatter: indicatorPercent
		},
		{
			dataField: 'p4q_usd_growth',
			text: 'P4Q $ GROWTH',
			sort: true,
			classes: 'right',
			headerClasses: 'right',
			formatter: formatUSDGrowth
		},
		{
			dataField: 'y_by_y_growth_percent',
			text: 'Y/Y % GROWTH',
			sort: true,
			classes: 'with-ellipsis center',
			headerClasses: 'center',
			formatter: indicatorPercent,
		},
		{
			dataField: 'y_to_y_usd_growth',
			text: 'Y-Y $ GROWTH',
			sort: true,
			classes: 'right',
			headerClasses: 'right',
			formatter: formatUSDGrowth
		}
	];

	if (modalName.includes('gross')) {
		modalName = 'Gross Bookings';
	} else if (modalName.includes('pag')) {
		modalName = 'PAG';
	} else if (modalName.includes('net')) {
		modalName = 'Net Bookings';
	}

	return (
		<Modal
			id="metro-modal"
			className="metro-modal "
			show={show}
			onHide={onClose}
			dialogClassName="custom-scroll"
			aria-labelledby="contained-modal-title-vcenter"
			centered
		>
			<Modal.Header>
				<Modal.Title id="contained-modal-title-vcenter">
					{selectedMetro} {modalName}
				</Modal.Title>

				<button type="button" className="close" onClick={onClose}>
					<span aria-hidden="true">×</span><span className="sr-only">
						Close
          </span>
				</button>
				<div className="filters custom-scroll">
					<RadioDD
						id="group_by"
						options={optionGroup3}
						radioVal={group_val}
						setRadioVal={set_group_val}
						onApply={onApplyGroup}
						onCancel={onCancelGroup}
						initialValue={group_initial_value}
					/>
					<RadioDD
						id="segmentation"
						options={optionVertical}
						radioVal={vertical_val}
						setRadioVal={set_vertical_val}
						onApply={onApplyVertical}
						onCancel={onCancelVertical}
						initialValue={vertical_initial_value}
					/>
				</div>
			</Modal.Header>
			<Modal.Body>
				{data ?
					<>
						<Row>
							<Col className="fix-width pr-30">
								<KPI_NEW
									totalValue={overall.total.value}
									totalUnit={overall.total.unit}
									details={overall.details}
									noHover={true}
								/>
							</Col>
							<Col md={6} xs={12}>
								<Legend legend={bookings_revenue.legend} />
								<div style={{ height: "350px" }}>
									<StackedColumnChart
										id="total_revenue_metro_modal_by_revenue_type_stacked_column_chart"
										largerWidth={true}
										hiddenValueAxis={false}
										isOneHundredPercent={false}
										isPercentage={false}
										hideLabelBullet={false}
										data={bookings_revenue.data}
										legend={bookings_revenue.legend}
										isAnimationEnabled={false}
									/>
								</div>
							</Col>
							<Col className="pl-30">

								<div style={{ height: "300px" }}>
									<DonutChart
										id={"total_revenue_metro_modal_donut_chart"}
										totalTitle={modalName}
										total={revenue_mix.gross_bookings}
										data={revenue_mix.data}
										isDollar={true}
										legend={revenue_mix.legend}
										isAnimationEnabled={false}
									/>
								</div>
							</Col>
						</Row>
						<Row>
							<Col md={12} className="pt-4">
								<CustomTable
									tableName='metro-product-details '
									data={bookings_details ? bookings_details : []}
									defaultSorted={defaultSorted}
									columns={columns}
								/>
							</Col>
						</Row>
					</>
					: <div>No data available.</div>
				}
			</Modal.Body>
		</Modal>
	);
}

export default MetroModal;