import React, { useState } from 'react';
import { Container, Row, Col, Card } from 'react-bootstrap';
import { ImArrowUpRight2, ImArrowDownRight2 } from "react-icons/im";
import Legend from '../../components/legend/Legend';
import { BasicDD } from '../../components/customDropdown';
import CustomTable, { formatLongText } from '../../components/customTable/CustomTable';
import StackedColumnChart from '../../components/chart/StackedColumnChart';
import NestedDonutChart from '../../components/chart/NestedDonutChart';
import HorizontalStackedColumnChart from '../../components/chart/HorizontalStackedColumnChart';
import SparklineChart from '../../components/chart/SparklineChart';
import LineChart from '../../components/chart/LineChart';
import KPI_NEW from '../../components/kpi/KPI_NEW';
import LighthouseNotableTable from './table/LighthouseNotableTable';
import LighthouseListTable from './table/LighthouseListTable';
import { optionOverviewFilter1 } from '../../constants/filterOptions';
import { convertToPercent, convertToCount, convertToWholePercent } from '../../constants/formatter';


const Lighthouse = (props) => {
	const { lighthouseData, selectedRangeValue, tab_name, tab_id } = props;
	const [kpiClicked, setkpiClicked] = useState(null);

	// basic dd for column chart values
	const stacked_column_initial_value = optionOverviewFilter1 ? optionOverviewFilter1[0] : null;
	const [stackedColumnSelection, setStackedColumnSelection] = useState(stacked_column_initial_value);

	// Data Distribution
	let overall = lighthouseData ? lighthouseData.overall_lighthouse_count : [];
	let overall_penetration_percent = lighthouseData ? lighthouseData.overall_penetration_percent : [];
	let segment_details = lighthouseData ? lighthouseData.segment_details : [];
	let gross_adds_by_segment = lighthouseData ? lighthouseData.gross_adds_by_segment : [];
	let attrition_by_segment = lighthouseData ? lighthouseData.attrition_by_segment : [];
	let adds_as_percent_of_attrition_by_segment = lighthouseData ? lighthouseData.adds_as_percent_of_attrition_by_segment : [];
	let y_by_y_percent_growth_by_segment = lighthouseData ? lighthouseData.y_by_y_percent_growth_by_segment : [];
	let lighthouse_count_mix_percent_by_segment = lighthouseData ? lighthouseData.lighthouse_count_mix_percent_by_segment : [];
	let lighthouse_count_by_segment = lighthouseData ? lighthouseData.lighthouse_count_by_segment : [];
	let lighthouse_list = lighthouseData ? lighthouseData.lighthouse_list : [];
	let notable_adds = lighthouseData ? lighthouseData.notable_adds : [];

	let lighthouse_count_by_segment_in_quarter_net_adds_count = lighthouseData ? lighthouseData.lighthouse_count_by_segment_in_quarter_net_adds_count : [];
	let lighthouse_count_by_segment_in_quarter_percentage = lighthouseData ? lighthouseData.lighthouse_count_by_segment_in_quarter_percentage : [];
	let lighthouse_count_by_segment_in_year_net_adds_count = lighthouseData ? lighthouseData.lighthouse_count_by_segment_in_year_net_adds_count : [];
	let lighthouse_count_by_segment_in_year_percentage = lighthouseData ? lighthouseData.lighthouse_count_by_segment_in_year_percentage : [];

	// Table Config
	const indicatorPercent = (cell, row) => {
		let bgColor = cell >= 0.5 ? 'indicator-up' : cell <= -0.5 ? 'indicator-down' : 'indicator-middle';
		let icon = cell >= 0.5 ? <ImArrowUpRight2 /> : cell <= -0.5 ? <ImArrowDownRight2 /> : <span className="mr-3"></span>;
		return (
			<span className={`indicator ${bgColor}`}>
				{icon}
				{convertToPercent(cell)}
			</span>
		);
	};

	const sparkline = (cell, row) => {
		return <SparklineChart loadedData={cell} id={`sparkline-chart-${row._id}`} />
	};

	const segmentFormat = (cell, row) => {
		return formatLongText(cell, row)
	};

	const longNumbersFormat = (cell, row) => {
		return formatLongText(cell, row)
	}

	const formatToPercent = (cell, row) => {
		return <span>{convertToWholePercent(cell)}</span>
	};

	const formatToCount = (cell, row) => {
		return <span>{convertToCount(cell)}</span>
	};

	const defaultSorted = [{
		dataField: '_id',
		order: 'asc',
	}];

	const columns = [
		{
			dataField: '_id',
			text: 'id',
			hidden: true
		},
		{
			dataField: 'segment',
			text: 'SEGMENT',
			sort: true,
			formatter: segmentFormat
		},
		{
			dataField: 'customer_count',
			text: 'CUSTOMER #',
			sort: true,
			classes: 'right',
			headerClasses: 'right',
			formatter: formatToCount
		},
		{
			dataField: 'mix_percent',
			text: 'MIX %',
			sort: true,
			formatter: formatToPercent,
			classes: 'center',
			headerClasses: 'center'
		},
		{
			dataField: 'q_by_q_growth_percent',
			text: 'Q/Q % GROWTH',
			sort: true,
			formatter: indicatorPercent,
			classes: 'with-ellipsis center',
			headerClasses: 'center'
		},
		{
			dataField: 'q_to_q_growth_count',
			text: 'Q-Q # GROWTH',
			sort: true,
			formatter: longNumbersFormat,
			classes: 'right',
			headerClasses: 'right'
		},
		{
			dataField: 'y_to_y_growth_percent',
			text: 'Y/Y % GROWTH',
			sort: true,
			formatter: indicatorPercent,
			classes: 'with-ellipsis center',
			headerClasses: 'center'
		},
		{
			dataField: 'y_to_y_growth_count',
			text: 'Y-Y # GROWTH',
			sort: true,
			formatter: longNumbersFormat,
			classes: 'border-right right',
			headerClasses: 'border-right right'
		},
		// {
		// 	dataField: 'sparkline_data',
		// 	text: '',
		// 	formatter: sparkline,
		// 	classes: 'center',
		// 	headerClasses: 'center'
		// },
		{
			dataField: 'penetration_percent',
			text: 'PENETRATION %',
			sort: true,
			formatter: formatToPercent,
			classes: 'right',
			headerClasses: 'right'
		},
		{
			dataField: 'q_to_q_growth_percent',
			text: 'Q-Q % GROWTH',
			sort: true,
			formatter: formatToPercent,
			classes: 'right',
			headerClasses: 'right'
		},
		{
			dataField: 'y_to_y_growth_percent',
			text: 'Y-Y % GROWTH',
			sort: true,
			formatter: formatToPercent,
			classes: 'right',
			headerClasses: 'right'
		},
	];

	return (
		<Container fluid>
			<Row className="contentDivider pb-30">
				<div className="fix-width">
					<Card className="revenue" >
						<Card.Body>
							<Card.Title>{tab_name} CUSTOMER COUNT</Card.Title>
							<KPI_NEW
								totalValue={overall.total.value}
								totalUnit={overall.total.unit}
								details={overall.details}
								setkpiClicked={setkpiClicked}
								kpiClicked={kpiClicked}
							/>
						</Card.Body>
					</Card>
				</div>
				<Col>
					<Card className="revenue">
						<Card.Body>
							<Card.Title>{tab_name} CUSTOMER COUNT BY SEGMENT</Card.Title>
							{kpiClicked === 'Q/Q Growth %' ?
								<>
									<Legend legend={lighthouse_count_by_segment_in_quarter_percentage.legend} />
									<div style={{ height: "92%" }}>
										<LineChart
											id={`lighthouse_count_by_segment_in_quarter_percentage-line_chart-segment`}
											data={lighthouse_count_by_segment_in_quarter_percentage.data}
											legend={lighthouse_count_by_segment_in_quarter_percentage.legend}
											isAnimationEnabled={false}
										/>
									</div>
								</>
								:
								kpiClicked === 'Q-Q Net Adds #' ?
									<>
										<Legend legend={lighthouse_count_by_segment_in_quarter_net_adds_count.legend} />
										<div style={{ height: "92%" }}>
											<LineChart
												id={`lighthouse_count_by_segment_in_quarter_net_adds_count-line_chart-segment`}
												data={lighthouse_count_by_segment_in_quarter_net_adds_count.data}
												legend={lighthouse_count_by_segment_in_quarter_net_adds_count.legend}
												isAnimationEnabled={false}
											/>
										</div>
									</>
									:
									kpiClicked === 'Y-Y Net Adds #' ?
										<>
											<Legend legend={lighthouse_count_by_segment_in_year_net_adds_count.legend} />
											<div style={{ height: "92%" }}>
												<LineChart
													id={`lighthouse_count_by_segment_in_year_net_adds_count-line_chart-segment`}
													data={lighthouse_count_by_segment_in_year_net_adds_count.data}
													legend={lighthouse_count_by_segment_in_year_net_adds_count.legend}
													isAnimationEnabled={false}
												/>
											</div>
										</>

										:
										kpiClicked === 'Y/Y Growth %' ?
											<>
												<Legend legend={lighthouse_count_by_segment_in_year_percentage.legend} />
												<div style={{ height: "92%" }}>
													<LineChart
														id={`lighthouse_count_by_segment_in_year_percentage-line_chart-segment`}
														data={lighthouse_count_by_segment_in_year_percentage.data}
														legend={lighthouse_count_by_segment_in_year_percentage.legend}
														isAnimationEnabled={false}
													/>
												</div>
											</>

											:
											<>
												<Legend legend={lighthouse_count_by_segment.legend} />
												<div style={{ height: "92%" }}>
													<StackedColumnChart
														id={`${tab_id}-stacked-column-chart`}
														largerWidth={true}
														isOneHundredPercent={true}
														isPercentage={true}
														insideLabel={false}
														percentLabel={false}
														hiddenValueAxis={false}
														data={lighthouse_count_by_segment.data}
														legend={lighthouse_count_by_segment.legend}
														isAnimationEnabled={false}
													/>
												</div>
											</>
							}

						</Card.Body>
					</Card>
				</Col>
				<Col style={{ maxWidth: "400px" }}>
					<Card className="Segment revenue">
						<Card.Body>
							<Card.Title>{tab_name} CUSTOMER COUNT MIX % BY SEGMENT</Card.Title>
							<Legend legend={lighthouse_count_mix_percent_by_segment.legend} />
							<NestedDonutChart
								id={`${tab_id}-nested-donut-chart`}
								insideValueName={"company_name"}
								insideValue={"company_percentage"}
								insideCategory={"hq_region"}
								outsideValueName={"others_name"}
								outsideValue={"others_percentage"}
								outsideCategory={"hq_region"}
								loadedData={lighthouse_count_mix_percent_by_segment.data}
								isAnimationEnabled={true}
							/>
						</Card.Body>
					</Card>
				</Col>
			</Row>
			<Row className="contentDivider pb-30">
				<div className="fix-width" style={{ minWidth: '335px' }}>
					<Card className="revenue" >
						<Card.Body>
							<Card.Title>PENETRATION %</Card.Title>
							<KPI_NEW
								totalValue={overall_penetration_percent.total.value}
								totalUnit={overall_penetration_percent.total.unit}
								details={overall_penetration_percent.details}
								setkpiClicked={setkpiClicked}
								kpiClicked={kpiClicked}
								noHover={true}
							/>
						</Card.Body>
					</Card>
				</div>
				<Col>
					<Card className="revenue">
						<Card.Body>
							<Card.Title>SEGMENT DETAILS</Card.Title>
							<CustomTable
								tableName='segment-details-lighthouse scrollable'
								data={segment_details ? segment_details : []}
								defaultSorted={defaultSorted}
								columns={columns}
							/>
						</Card.Body>
					</Card>
				</Col>
				<Col style={{ maxWidth: "400px" }}>
					<Card className="revenue">
						<Card.Body>
							<Card.Title>Y/Y % GROWTH BY SEGMENT</Card.Title>
							<Legend legend={y_by_y_percent_growth_by_segment.legend} />
							<div style={{ height: "350px" }}>
								<HorizontalStackedColumnChart
									id={`${tab_id}-horizontal-bar-chart`}
									data={y_by_y_percent_growth_by_segment.data}
									legend={y_by_y_percent_growth_by_segment.legend}
									isOneHundredPercent={true}
									largerWidth={true}
									disablePaddingTop={false}
								/>
							</div>
						</Card.Body>
					</Card>
				</Col>
			</Row>
			<Row className="contentDivider pb-30">
				<Col lg={4} md={12}>
					<Card className="revenue">
						<Card.Body>
							<Card.Title>GROSS ADDS BY segment</Card.Title>
							<Legend legend={gross_adds_by_segment.legend} />
							<div style={{ height: '300px' }}>
								<StackedColumnChart
									id={`${tab_id}-gross-stacked_column_chart`}
									hiddenValueAxis={true}
									isOneHundredPercent={false}
									isPercentage={false}
									largerWidth={true}
									hideLabelBullet={false}
									data={gross_adds_by_segment.data}
									legend={gross_adds_by_segment.legend}
									isAnimationEnabled={false}
								/>
							</div>
						</Card.Body>
					</Card>
				</Col>
				<Col lg={4} md={12}>
					<Card className="revenue">
						<Card.Body>
							<Card.Title>ATTRITION BY segment</Card.Title>
							<Legend legend={attrition_by_segment.legend} />
							<div style={{ height: '300px' }}>
								<StackedColumnChart
									id={`${tab_id}-attrition-stacked_column_chart`}
									hiddenValueAxis={true}
									isOneHundredPercent={false}
									isPercentage={false}
									largerWidth={true}
									hideLabelBullet={false}
									data={attrition_by_segment.data}
									legend={attrition_by_segment.legend}
									isAnimationEnabled={false}
								/>
							</div>
						</Card.Body>
					</Card>
				</Col>
				<Col lg={4} md={12}>
					<Card className="revenue">
						<Card.Body>
							<Card.Title>ADDS AS % OF ATTRITION BY segment</Card.Title>
							<Legend legend={adds_as_percent_of_attrition_by_segment.legend} />
							<div style={{ height: '300px' }}>
								<LineChart
									id={`${tab_id}-adds_attrition-line_chart`}
									data={adds_as_percent_of_attrition_by_segment.data}
									legend={adds_as_percent_of_attrition_by_segment.legend}
									isAnimationEnabled={false}
								/>
							</div>
						</Card.Body>
					</Card>
				</Col>
			</Row>
			<Row className="contentDivider">
				<Col lg={6} md={12}>
					<Card className="revenue">
						<Card.Body>
							<LighthouseNotableTable data={notable_adds} tableTitle='NOTABLE ADDS' />
						</Card.Body>
					</Card>
				</Col>
				<Col lg={6} md={12}>
					<Card className="revenue">
						<Card.Body>
							<LighthouseListTable data={lighthouse_list} tableTitle='LIGHTHOUSE LIST' />
						</Card.Body>
					</Card>
				</Col>
			</Row>
		</Container>
	);
};

export default Lighthouse;