import React, { useState, useEffect } from 'react';
import PageHeader from '../../components/pageHeader/PageHeader';

import Generator from './Generator';
import WizardGenerator from './BlankReport';
import WizardGeneratorDummy from './BlankReportDummy';

import './helpCenterDataMart.scss';



const HelpCenterDataMart = (props) => {
  let path = props.location.pathname;
  const currentPath = path.substring(path.lastIndexOf('/') + 1);
  const [activePage, setActivePage] = useState(currentPath);

  const handleActivePage = (page) => {
    setActivePage(page)
  }

  const handleBreadcrubsPreviousLink = () => {
    setActivePage('Generator')
  }

  useEffect(() => {
    setActivePage(currentPath)
  }, [currentPath])

  const breadcrumbs = [
    {
        path: path === '/help-center/generator' ? null : '/help-center/generator',
        label: 'SELF-SERVICE REPORT GENERATOR'
    },
    {
        label: activePage,
    }
  ]

  const innerPage = [
    {
      path: '/help-center/generator',
      label: currentPath === 'blank-report' ? null : 'Generator'
    },
    {
      path: '/help-center/blank-report',
      label: currentPath === 'generator' ? null : 'Blank Report'
    }
  ]

  return (
    <div className="data-mart-page--on-help-center">
      <PageHeader 
        title="HELP CENTER" 
        color="turquoise" 
        innerPage={innerPage} 
        breadcrumbs={breadcrumbs}
        previousPage='/help-center/generator'
        handleActivePage={handleActivePage}
        handleBreadcrubsPreviousLink={handleBreadcrubsPreviousLink}
      />
      <div className="page-wrapper">
        {currentPath === 'generator' && <Generator/>}
        {currentPath === 'blank-report' && <WizardGenerator/>}
        {currentPath === 'generated-report' && <WizardGeneratorDummy/>}
      </div>
    </div>
  );
}

export default HelpCenterDataMart;
