import React, { useState } from 'react';
import { Container, Row, Col, Card } from 'react-bootstrap';
import { ImArrowUpRight2, ImArrowDownRight2 } from "react-icons/im";
import Legend from '../../components/legend/Legend';
import { BasicDD } from '../../components/customDropdown';
import CustomTable, { formatLongText } from '../../components/customTable/CustomTable';
import StackedColumnChart from '../../components/chart/StackedColumnChart';
import DonutChart from '../../components/chart/DonutChart';
import LineChart from '../../components/chart/LineChart';
import ClusteredColumnChart from '../../components/chart/ClusteredColumnChart';
import KPI_NEW from '../../components/kpi/KPI_NEW';
import { optionOverviewFilter } from '../../constants/filterOptions';
import { convertToUSDGrowth, convertToPercent, convertToWholePercent } from '../../constants/formatter';


const OverviewContent = (props) => {
  const { overviewData, selectedRangeValue } = props;
  const [kpiClicked, setkpiClicked] = useState(null);
  const [kpiClicked2, setkpiClicked2] = useState(null);

  // basic dd for column chart values
  const stacked_column_initial_value = optionOverviewFilter ? optionOverviewFilter[0] : null;
  const [stackedColumnSelection, setStackedColumnSelection] = useState(stacked_column_initial_value);

  // Data Distribution
  let overall = overviewData ? overviewData.pipeline_overall : [];
  let pipeline_by_segment = overviewData ? overviewData.pipeline_by_segment : [];
  let y_by_y_percent_growth_by_segment = overviewData ? overviewData.y_by_y_percent_growth_by_segment : [];
  let pipeline_mix_percent_by_segment = overviewData ? overviewData.pipeline_mix_percent_by_segment : [];
  let segment_details = overviewData ? overviewData.segment_details : [];

  let pipeline_by_segment_in_p4q_percentage = overviewData ? overviewData.pipeline_by_segment_in_p4q_percentage : [];
  let pipeline_by_segment_in_quarter_percentage = overviewData ? overviewData.pipeline_by_segment_in_quarter_percentage : [];
  let pipeline_by_segment_in_year_percentage = overviewData ? overviewData.pipeline_by_segment_in_year_percentage : [];


  let overall_conversion = overviewData ? overviewData.pipeline_conversion_overall : [];
  let conversion_pipeline_by_segment = overviewData ? overviewData.conversion_pipeline_by_segment : [];
  let conversion_percent_by_segment = overviewData ? overviewData.conversion_percent_by_segment : [];
  let next_qtr_outlook_vs_average = overviewData ? overviewData.next_qtr_outlook_vs_average : [];
  let pipeline_maturity_by_region = overviewData ? overviewData.pipeline_maturity_by_region : [];
  let closed_won_percent_of_total = overviewData ? overviewData.closed_won_percent_of_total : [];

  let conversion_pipeline_by_segment_in_p4q_percentage = overviewData ? overviewData.conversion_pipeline_by_segment_in_p4q_percentage : [];
  let conversion_pipeline_by_segment_in_quarter_percentage = overviewData ? overviewData.conversion_pipeline_by_segment_in_quarter_percentage : [];
  let conversion_pipeline_by_segment_in_year_percentage = overviewData ? overviewData.conversion_pipeline_by_segment_in_year_percentage : [];

  // Table Config
  const indicatorPercent = (cell, row) => {
    let bgColor = cell >= 0.5 ? 'indicator-up' : cell <= -0.5 ? 'indicator-down' : 'indicator-middle';
    let icon = cell >= 0.5 ? <ImArrowUpRight2 /> : cell <= -0.5 ? <ImArrowDownRight2 /> : <span className="mr-3"></span>;
    return (
      <span className={`indicator ${bgColor}`}>
        {icon}
        {convertToPercent(cell)}
      </span>
    );
  };

  const segmentFormat = (cell, row) => {
    return formatLongText(cell, row)
  };

  const formatUSDGrowth = (cell, row) => {
    let new_cell = convertToUSDGrowth(cell);
    return formatLongText(new_cell, row)
  };

  const formatToPercent = (cell, row) => {
    return <span>{convertToWholePercent(cell)}</span>
  };

  const defaultSorted = [{
    dataField: '_id',
    order: 'asc',
  }];

  const columns = [
    {
      dataField: '_id',
      text: 'id',
      hidden: true
    },
    {
      dataField: 'segment',
      text: 'SEGMENT',
      sort: true,
      formatter: segmentFormat
    },
    {
      dataField: 'mrc_usd',
      text: 'MRC $',
      sort: true,
      formatter: formatUSDGrowth,
      classes: 'right',
      headerClasses: 'right'
    },
    {
      dataField: 'mix_percent',
      text: 'MIX %',
      sort: true,
      formatter: formatToPercent,
      classes: 'center',
      headerClasses: 'center'
    },
    {
      dataField: 'q_by_q_growth_percent',
      text: 'Q/Q % GROWTH',
      sort: true,
      formatter: indicatorPercent,
      classes: 'with-ellipsis center',
      headerClasses: 'center'
    },
    {
      dataField: 'q_to_q_growth_usd',
      text: 'Q-Q $ GROWTH',
      sort: true,
      formatter: formatUSDGrowth,
      classes: 'right',
      headerClasses: 'right'
    },
    {
      dataField: 'y_by_y_growth_percent',
      text: 'Y/Y % GROWTH',
      sort: true,
      formatter: indicatorPercent,
      classes: 'with-ellipsis center',
      headerClasses: 'center'
    },
    {
      dataField: 'y_to_y_growth_usd',
      text: 'Y-Y $ GROWTH',
      sort: true,
      formatter: formatUSDGrowth,
      classes: 'right',
      headerClasses: 'right'
    },
    {
      dataField: 'percent_late_stage',
      text: '% LATE STAGE',
      sort: true,
      formatter: formatToPercent,
      classes: 'right',
      headerClasses: 'right'
    },
    {
      dataField: 'conversion_percent',
      text: 'CONVERSION %',
      sort: true,
      formatter: formatToPercent,
      classes: 'right',
      headerClasses: 'right'
    }
  ];


  const defaultSortedConversion = [{
    dataField: '_id',
		order: 'asc',
  }];

  const columnsConversion = [
    {
      dataField: '_id',
      text: 'id',
      hidden: true
    },
    {
      dataField: 'segment',
      text: 'SEGMENT',
      sort: true,
      formatter: segmentFormat
    },
    {
      dataField: 'conversion_percent',
      text: 'CONVERSION %',
      sort: true,
      formatter: formatToPercent,
      classes: 'center',
      headerClasses: 'center'
    },
    {
      dataField: 'q_to_q_percent_growth',
      text: 'Q-Q % GROWTH',
      sort: true,
      formatter: formatToPercent,
      classes: 'center',
      headerClasses: 'center'
    },
    {
      dataField: 'p4q_percent_growth',
      text: 'P4Q % GROWTH',
      sort: true,
      formatter: formatToPercent,
      classes: 'center',
      headerClasses: 'center'
    }
    ,
    {
      dataField: 'y_to_y_percent_growth',
      text: 'Y-Y % GROWTH',
      sort: true,
      formatter: formatToPercent,
      classes: 'center',
      headerClasses: 'center'
    }
  ];

  return (
    <Container fluid>
      <Row className="contentDivider pb-30">
        <div className="fix-width">
          <Card className="revenue" >
            <Card.Body>
              <Card.Title>PIPELINE</Card.Title>
              <KPI_NEW
                totalValue={overall.total.value}
                totalUnit={overall.total.unit}
                details={overall.details}
                setkpiClicked={setkpiClicked}
                kpiClicked={kpiClicked}
              />
            </Card.Body>
          </Card>
        </div>
        <Col>
          <Card className="revenue">
            <Card.Body>
              <Card.Title>{kpiClicked ? kpiClicked : 'PIPELINE'} BY SEGMENT</Card.Title>
              {kpiClicked === 'P4Q % of Growth' ?
                <>
                  <Legend legend={pipeline_by_segment_in_p4q_percentage.legend} />
                  <div style={{ height: "92%" }}>
                    <LineChart
                      id={`pipeline_by_segment_in_p4q_percentage-line_chart-segment`}
                      data={pipeline_by_segment_in_p4q_percentage.data}
                      legend={pipeline_by_segment_in_p4q_percentage.legend}
                      isAnimationEnabled={false}
                    />
                  </div>
                </>
                :

                kpiClicked === 'Q/Q % Growth' ?
                  <>
                    <Legend legend={pipeline_by_segment_in_quarter_percentage.legend} />
                    <div style={{ height: "92%" }}>
                      <LineChart
                        id={`pipeline_by_segment_in_quarter_percentage-line_chart-segment`}
                        data={pipeline_by_segment_in_quarter_percentage.data}
                        legend={pipeline_by_segment_in_quarter_percentage.legend}
                        isAnimationEnabled={false}
                      />
                    </div>
                  </>
                  :
                  kpiClicked === 'Y/Y % Growth' ?
                    <>
                      <Legend legend={pipeline_by_segment_in_year_percentage.legend} />
                      <div style={{ height: "92%" }}>
                        <LineChart
                          id={`pipeline_by_segment_in_year_percentage-line_chart-segment`}
                          data={pipeline_by_segment_in_year_percentage.data}
                          legend={pipeline_by_segment_in_year_percentage.legend}
                          isAnimationEnabled={false}
                        />
                      </div>
                    </>
                    :
                    <>
                      <div className="d-flex">
                        <Legend legend={pipeline_by_segment.legend} />
                        {/* {selectedRangeValue === "last_completed_quarter" ? */}
                        <BasicDD
                          className="ml-auto"
                          id={`stacked_column_dd`}
                          options={optionOverviewFilter}
                          value={stackedColumnSelection}
                          setValue={setStackedColumnSelection}
                        />
                        {/* : null
                        } */}
                      </div>
                      <div style={{ height: "85%", marginTop: "1rem" }}>
                        <StackedColumnChart
                          id={`stacked_column_chart`}
                          hiddenValueAxis={false}
                          isOneHundredPercent={false}
                          isPercentage={false}
                          largerWidth={true}
                          hideLabelBullet={false}
                          isOverviewChart={true}
                          isGrowthPercentToggable={true}
                          stackedColumnSelection={stackedColumnSelection.key}
                          selectedRangeValue={'last_completed_quarter'}
                          data={pipeline_by_segment.data}
                          legend={pipeline_by_segment.legend}
                          isAnimationEnabled={false}
                        />
                      </div>
                    </>
              }


            </Card.Body>
          </Card>
        </Col>
        <Col style={{ maxWidth: "400px" }}>
          <Card className="Segment revenue">
            <Card.Body>
              <Card.Title>PIPELINE MIX % BY SEGMENT</Card.Title>
              <Legend legend={pipeline_mix_percent_by_segment.legend} />
              <div style={{ height: "92%" }}>
                <DonutChart
                  id="overview-donut_chart"
                  totalTitle="PIPELINE"
                  total={pipeline_mix_percent_by_segment.gross_bookings}
                  data={pipeline_mix_percent_by_segment.data}
                  legend={pipeline_mix_percent_by_segment.legend}
                  isDollar={true}
                  isAnimationEnabled={true}
                />
              </div>
            </Card.Body>
          </Card>
        </Col>
      </Row>
      <Row className="contentDivider">
        <Col>
          <Card className="revenue">
            <Card.Body>
              <Card.Title>SEGMENT DETAILS</Card.Title>
              <CustomTable
                tableName='segment-details scrollable'
                data={segment_details ? segment_details : []}
                defaultSorted={defaultSorted}
                columns={columns}
              />
            </Card.Body>
          </Card>
        </Col>
        <Col style={{ maxWidth: "400px" }}>
          <Card className="revenue">
            <Card.Body>
              <Card.Title>Y/Y % GROWTH BY SEGMENT</Card.Title>
              <Legend legend={y_by_y_percent_growth_by_segment.legend} />
              <div style={{ height: "300px" }}>
                <LineChart
                  id="overview-line_chart"
                  data={y_by_y_percent_growth_by_segment.data}
                  legend={y_by_y_percent_growth_by_segment.legend}
                  isAnimationEnabled={true}
                />
              </div>
            </Card.Body>
          </Card>
        </Col>
      </Row>
      <Row className="contentDivider pt-30 pb-30">
        <div className="fix-width">
          <Card className="revenue" >
            <Card.Body>
              <Card.Title>PIPELINE CONVERSION</Card.Title>
              <KPI_NEW
                totalValue={overall_conversion.total.value}
                totalUnit={overall_conversion.total.unit}
                details={overall_conversion.details}
                setkpiClicked={setkpiClicked2}
                kpiClicked={kpiClicked2}
                isAddText=" of MPQ"
              />
            </Card.Body>
          </Card>
        </div>
        <Col>
          <Card className="revenue">
            <Card.Body>
              <Card.Title>{kpiClicked2 ? kpiClicked2 : 'PIPELINE CONVERSION'}  RATE TRENDS</Card.Title>
              {kpiClicked2 === 'P4Q Growth' ?
                <>
                  <Legend legend={conversion_pipeline_by_segment_in_p4q_percentage.legend} />
                  <div style={{ height: "92%" }}>
                    <LineChart
                      id={`conversion_pipeline_by_segment_in_p4q_percentage-line_chart-segment`}
                      data={conversion_pipeline_by_segment_in_p4q_percentage.data}
                      legend={conversion_pipeline_by_segment_in_p4q_percentage.legend}
                      isAnimationEnabled={false}
                    />
                  </div>
                </>
                :

                kpiClicked2 === 'Q-Q Growth' ?
                  <>
                    <Legend legend={conversion_pipeline_by_segment_in_quarter_percentage.legend} />
                    <div style={{ height: "92%" }}>
                      <LineChart
                        id={`conversion_pipeline_by_segment_in_quarter_percentage-line_chart-segment`}
                        data={conversion_pipeline_by_segment_in_quarter_percentage.data}
                        legend={conversion_pipeline_by_segment_in_quarter_percentage.legend}
                        isAnimationEnabled={false}
                      />
                    </div>
                  </>
                  :
                  kpiClicked2 === 'Y-Y Growth' ?
                    <>
                      <Legend legend={conversion_pipeline_by_segment_in_year_percentage.legend} />
                      <div style={{ height: "92%" }}>
                        <LineChart
                          id={`conversion_pipeline_by_segment_in_year_percentage-line_chart-segment`}
                          data={conversion_pipeline_by_segment_in_year_percentage.data}
                          legend={conversion_pipeline_by_segment_in_year_percentage.legend}
                          isAnimationEnabled={false}
                        />
                      </div>
                    </>
                    :
                    <>

                      <Legend legend={conversion_pipeline_by_segment.legend} />
                      <div style={{ height: "92%" }}>
                        <LineChart
                          id="overview-conversion-pipeline-line_chart"
                          data={conversion_pipeline_by_segment.data}
                          legend={conversion_pipeline_by_segment.legend}
                          isAnimationEnabled={true}
                        />
                      </div>
                    </>
              }

            </Card.Body>
          </Card>
        </Col>
        <Col style={{ maxWidth: "400px" }}>
          <Card className="Segment revenue">
            <Card.Body>
              <Card.Title>NEXT QTR OUTLOOK VS. AVERAGE</Card.Title>
              <Legend legend={next_qtr_outlook_vs_average.legend} />


              <div style={{ height: "92%" }}>
                <ClusteredColumnChart
                  id={"next_qtr_outlook_vs_average_by_segment"}
                  data={next_qtr_outlook_vs_average.data}
                  legend={next_qtr_outlook_vs_average.legend}
                  isAnimationEnabled={true}
                  isHorizontal={true}
                />
              </div>
            </Card.Body>
          </Card>
        </Col>
      </Row>
      <Row className="contentDivider">
        <Col md={6}>
          <Card className="revenue">
            <Card.Body>
              <Card.Title>CONVERSION % BY SEGMENT</Card.Title>
              <CustomTable
                tableName='conversion-percent-by-segment'
                data={conversion_percent_by_segment ? conversion_percent_by_segment : []}
                defaultSorted={defaultSortedConversion}
                columns={columnsConversion}
              />
            </Card.Body>
          </Card>
        </Col>
        <Col>
          <Card className="revenue">
            <Card.Body>
              <Card.Title>PIPELINE MATURITY BY REGION</Card.Title>
              <Legend legend={pipeline_maturity_by_region.legend} />
              <div style={{ height: "92%" }}>
                <StackedColumnChart
                  id="overview-stacked_column_chart"
                  largerWidth={true}
                  hideLabelBullet={true}
                  isOneHundredPercent={true}
                  isPercentage={true}
                  hiddenValueAxis={false}
                  hideLabelBullet={false}
                  isOverviewChart={true}
                  isGrowthPercentToggable={true}
                  // stackedColumnSelection={stackedColumnSelection.key}
                  // selectedRangeValue={selectedRangeValue}
                  data={pipeline_maturity_by_region.data}
                  legend={pipeline_maturity_by_region.legend}
                  isAnimationEnabled={true}
                />
              </div>
            </Card.Body>
          </Card>
        </Col>
        <Col>
          <Card className="revenue">
            <Card.Body>
              <Card.Title>CLOSED-WON % OF TOTAL</Card.Title>
              <Legend legend={closed_won_percent_of_total.legend} />
              <div style={{ height: "92%" }}>
                <LineChart
                  id="overview-conversion-line_chart"
                  data={closed_won_percent_of_total.data}
                  legend={closed_won_percent_of_total.legend}
                  isAnimationEnabled={true}
                />
              </div>
            </Card.Body>
          </Card>
        </Col>
      </Row>

    </Container>
  );
};

export default OverviewContent;