import React, { useState, useEffect } from 'react';
import axiosRetry from 'axios-retry';
import axios from 'axios';
import { Button, ButtonGroup, Container } from 'react-bootstrap';
import { FilterContainer, FilterModal } from '../../components/filter';
import { optionRegion, optionCountry, optionMetro, optionRange } from '../../constants/filterOptions';
import { RadioDD, CheckboxDD, SearchCheckboxDD, Separator } from '../../components/customDropdown';
import { comments } from '../../constants/comments';
import { InternalServerError, ResourceNotFoundError } from '../../components/error/Error';
import { API_BASE_URL } from '../../constants/config';
import Comment from '../../components/comment/Comment';
import Loader from '../../components/loader/Loader';
import LandContent from './LandContent';


const Land = ({ activeTab, setActiveTab }) => {
	const [newData, setNewData] = useState([]);
	const [regionalData, setRegionalData] = useState([]);
	const [countryData, setCountryData] = useState([]);
	const [isLoading, setIsLoading] = useState(true)

	// show all filters
	const [showFilter, setShowFilter] = useState(false);
	const onShowFilter = () => {
		setShowFilter(true)
	}
	const onCloseFilter = async () => {
		await onCancelRegion();
		await onCancelCountry();
		await onCancelMetro();
		await onCancelHQRegion();
		await onCancelRange();
		setShowFilter(false);
	}
	const onApplyFilter = async () => {
		await onApplyRegion();
		await onApplyCountry();
		await onApplyMetro();
		await onApplyHQRegion();
		await onApplyRange();
		setShowFilter(false);
	}

	// comment area
	const [showComment, setShowComment] = useState(false);
	const onCloseComment = () => {
		setShowComment(false);
	}
	const onShowComment = () => {
		setShowComment(true)
	};

	// checkbox for region values
	const [region_val_onApply, set_region_val_onApply] = useState([]);
	const [region_val, set_region_val] = useState([]);
	const onApplyRegion = () => {
		set_region_val_onApply(region_val);
	};
	const onCancelRegion = () => {
		set_region_val(region_val_onApply);
	};

	// searchCheckbox for country values
	const [country_val_onApply, set_country_val_onApply] = useState([]);
	const [country_val, set_country_val] = useState([]);
	const onApplyCountry = () => {
		set_country_val_onApply(country_val);
	};
	const onCancelCountry = () => {
		set_country_val(country_val_onApply);
	};

	// searchCheckbox for metro values
	const [metro_val_onApply, set_metro_val_onApply] = useState([]);
	const [metro_val, set_metro_val] = useState([]);
	const onApplyMetro = () => {
		set_metro_val_onApply(metro_val);
	};
	const onCancelMetro = () => {
		set_metro_val(metro_val_onApply);
	};

	// checkbox for HQ region values
	const [hqregion_val_onApply, set_hqregion_val_onApply] = useState([]);
	const [hqregion_val, set_hqregion_val] = useState([]);
	const onApplyHQRegion = () => {
		set_hqregion_val_onApply(hqregion_val);
	};
	const onCancelHQRegion = () => {
		set_hqregion_val(hqregion_val_onApply);
	};

	// radio for range values
	const range_initial_value = optionRange ? optionRange[0] : null;
	const [range_val_onApply, set_range_val_onApply] = useState(range_initial_value);
	const [range_val, set_range_val] = useState(range_initial_value);
	const onApplyRange = () => {
		set_range_val_onApply(range_val);
	};
	const onCancelRange = () => {
		set_range_val(range_val_onApply);
	};

	// Axios call
	const fetchData = () => {
		let url = `${API_BASE_URL}/new-logos-and-extensions/land.json`;

		setIsLoading(true);
		axiosRetry(axios, { retries: 3 });

		axios({
			method: 'GET',
			url: url
		}).then(result => {
			const data = result.data
			setNewData(data['new-logos']);
			setRegionalData(data['regional-extension']);
			setCountryData(data['country-extension']);
			setIsLoading(false);
		}).catch(error => {
			console.log(error);
			setNewData(null)
			setRegionalData(null)
			setCountryData(null)
			setIsLoading(false);
		});
	};

	useEffect(() => {
		fetchData();
	}, []);

	if (isLoading) return <Loader withContainer />;

	return (
		<>
			<div className={`page-content ${showComment ? 'open' : 'closed'}`}>
				{(newData || regionalData || countryData)
					? newData.metro_ranking.by_top_rank.data.length > 0 || regionalData.metro_ranking.by_top_rank.data.length > 0 || countryData.metro_ranking.by_top_rank.data.length > 0
						? <>
							<FilterContainer
								showComment={showComment}
								onShowFilter={onShowFilter}
								onShowComment={onShowComment}
							>
								<div className="filters custom-scroll">
									<ButtonGroup>
										<Button
											variant='light'
											className={activeTab === 'new' ? 'active' : ''}
											onClick={() => { setActiveTab('new') }}>
											New Logos
										</Button>
										<Button
											variant='light'
											className={activeTab === 'regional' ? 'active' : ''}
											onClick={() => { setActiveTab('regional') }}>
											Regional Ext.
										</Button>
										<Button
											variant='light'
											className={activeTab === 'country' ? 'active' : ''}
											onClick={() => { setActiveTab('country') }}>
											Country Ext.
										</Button>
									</ButtonGroup>
									<Separator height='39px' />
									<CheckboxDD
										title="Deploy Region"
										id="region"
										options={optionRegion}
										valueApplied={region_val_onApply}
										checkVal={region_val}
										setCheckVal={set_region_val}
										onApply={onApplyRegion}
										onCancel={onCancelRegion}
									/>
									<SearchCheckboxDD
										title="Deploy Country"
										id="country"
										options={optionCountry}
										checkVal={country_val}
										setCheckVal={set_country_val}
										onApply={onApplyCountry}
										onCancel={onCancelCountry}
									/>
									<SearchCheckboxDD
										title="Deploy Metro"
										id="metro"
										options={optionMetro}
										valueApplied={metro_val_onApply}
										checkVal={metro_val}
										setCheckVal={set_metro_val}
										onApply={onApplyMetro}
										onCancel={onCancelMetro}
									/>
									<CheckboxDD
										title="HQ Region"
										id="hq-region"
										options={optionRegion}
										checkVal={hqregion_val}
										setCheckVal={set_hqregion_val}
										onApply={onApplyHQRegion}
										onCancel={onCancelHQRegion}
									/>
									<RadioDD
										title="Time Period"
										id="range"
										options={optionRange}
										radioVal={range_val}
										setRadioVal={set_range_val}
										onApply={onApplyRange}
										onCancel={onCancelRange}
										initialValue={range_initial_value}
									/>
								</div>
								<FilterModal
									title="Overview"
									setShowFilter={setShowFilter}
									showFilter={showFilter}
									onCloseFilter={onCloseFilter}
									onApplyFilter={onApplyFilter}
								>
									<CheckboxDD
										title="Deploy Region"
										id="region-modal"
										visibleInModal
										options={optionRegion}
										checkVal={region_val}
										setCheckVal={set_region_val}
									/>
									<SearchCheckboxDD
										title="Deploy Country"
										id="country-modal"
										visibleInModal
										options={optionCountry}
										checkVal={country_val}
										setCheckVal={set_country_val}
									/>
									<SearchCheckboxDD
										title="Deploy Metro"
										id="metro-modal"
										visibleInModal
										options={optionMetro}
										checkVal={metro_val}
										setCheckVal={set_metro_val}
									/>
									<CheckboxDD
										title="HQ Region"
										id="hq-region-modal"
										visibleInModal
										options={optionRegion}
										checkVal={hqregion_val}
										setCheckVal={set_hqregion_val}
									/>
									<RadioDD
										title="Time Period"
										id="range-modal"
										options={optionRange}
										visibleInModal
										radioVal={range_val}
										setRadioVal={set_range_val}
									/>
								</FilterModal>
							</FilterContainer>
							{/* ============= Your Component here! ============= */}
							<Container fluid className={`${activeTab === 'new' ? 'd-block' : 'd-none'}`}>
								<LandContent
									activeTab={activeTab}
									landData={newData}
									tab_name='NEW LOGOS'
									tab_id="new-overview" />
							</Container>
							<Container fluid className={`${activeTab === 'regional' ? 'd-block' : 'd-none'}`}>
								<LandContent
									activeTab={activeTab}
									landData={regionalData}
									tab_name='REGIONAL EXTENSION'
									tab_id="regional-overview" />
							</Container>
							<Container fluid className={`${activeTab === 'country' ? 'd-block' : 'd-none'}`}>
								<LandContent
									activeTab={activeTab}
									landData={countryData}
									tab_name='COUNTRY EXTENSION'
									tab_id="country-overview" />
							</Container>
						</>
						: <ResourceNotFoundError redirectURL={"/executive-cockpit/new-logos-and-extensions/land"} />
					: <InternalServerError redirectURL={"/executive-cockpit/new-logos-and-extensions/land"} />
				}
			</div>
			<Comment
				data={comments}
				onCloseComment={onCloseComment}
				showComment={showComment}
			/>
		</>
	);
};

export default Land;
