import React, { useState, useContext } from 'react';
import { NavLink, Link, useHistory } from "react-router-dom";

// SVG icons
import { ReactComponent as BrandLogo } from '../../assets/images/orbis-logo.svg';
import { ReactComponent as Hamburger } from '../../assets/icons/hamburger.svg';
import { ReactComponent as Plane } from '../../assets/icons/plane.svg';
import { ReactComponent as Shutter } from '../../assets/icons/shutter.svg';
import { ReactComponent as Flare } from '../../assets/icons/flare.svg';
import { ReactComponent as Add } from '../../assets/icons/add.svg';
import { ReactComponent as Info } from '../../assets/icons/info.svg';
import { ReactComponent as Settings } from '../../assets/icons/settings.svg';
import { ReactComponent as Logout } from '../../assets/icons/logout.svg';
import { ReactComponent as Database } from '../../assets/icons/database.svg';
import { ReactComponent as Blocks } from '../../assets/icons/blocks.svg';
import { ReactComponent as AppIcon } from '../../assets/icons/app.svg';

import { AuthContext } from '../../context/AuthContext';
import ProductModal from './ProductModal';

import './sidebar.scss';

const Sidebar = (props) => {
	const { open, toggleOpen } = props;
	let history = useHistory();
	const { dispatch } = useContext(AuthContext)
	const [modalShow, setModalShow] = useState(false);
	const onCancelModal = () => setModalShow(false);
	let pathName = history.location.pathname ? history.location.pathname : '/404';
	let firstValue = pathName.split('/')[1];

	const menuTop = [
		{
			path: "/executive-cockpit/metrics",
			icon: <Plane className="icon" />,
			label: "Executive Cockpit",
		},
		{
			path: "/snapshot-builder/builder",
			icon: <Shutter className="icon" />,
			label: "Snapshot Builder",
		},
		{
			path: "/account-navigator/navigator",
			icon: <Flare className="icon" />,
			label: "Account Navigator",
		},
		{
			path: "/self-service-rerport/generator",
			icon: <Database className="icon" />,
			label: "Self-Service Reports",
		},
		{
			path: "/analytics-dashboard/page",
			icon: <Blocks className="icon" />,
			label: "Analytics Dashboard",
		},
	];

	const onLogout = () => {
		dispatch({
			type: "LOGOUT"
		})
		// history.push("#/login");
	}

	return (
		<div className={`sidebar ${open === true ? 'open' : 'closed'}`}>
			<nav className={`menu ${open === true ? 'open' : 'closed'}`}>
				<ProductModal
					showBuilderModal={modalShow}
					onCancelModal={onCancelModal}
				/>
				<div className="header">
					<Link to="/" className="logo">
						<BrandLogo />
					</Link>
					<div className="hamburger" onClick={() => toggleOpen()}>
						<Hamburger />
					</div>
				</div>
				<div className="content">
					<ul className="top">
						{menuTop && menuTop.map((item, index) => {
							let labelFirstValue = item.path.split('/')[1];
							return (
								<li key={index} className="item">
									<NavLink exact to={item.path} className={firstValue === labelFirstValue ? 'active' : ''}>
										{item.icon}
										<span>{item.label}</span>
									</NavLink>
								</li>
							)
						})}

					</ul>

					<ul className="bottom">
						<li className="item">
							<a
								onClick={() => {
									setModalShow(true);
								}}
								> 
								<AppIcon className="icon" />
								<span>App Marketplace</span>
							</a>
						</li>
						<li className="item">
							<NavLink to="/help-center">
								<Info className="icon" />
								<span>Help Center</span>
							</NavLink>
						</li>
						<li className="item">
							<NavLink to="/settings">
								<Settings className="icon" />
								<span>Settings</span>
							</NavLink>
						</li>
						<li className="item">
							<a href="#/login" onClick={() => onLogout()}>
								<Logout className="icon" />
								<span>Logout</span>
							</a>
						</li>
					</ul>
				</div>
			</nav>
		</div >
	);
}

export default Sidebar;