import React, { useState } from 'react';
import { Row, Col, Card } from 'react-bootstrap';
import { ImArrowUpRight2, ImArrowDownRight2 } from "react-icons/im";
import Legend from '../../components/legend/Legend';
import { BasicDD } from '../../components/customDropdown';
import CustomTable, { formatLongText } from '../../components/customTable/CustomTable';
import StackedColumnChart from '../../components/chart/StackedColumnChart';
import NestedDonutChart from '../../components/chart/NestedDonutChart';
import LineChart from '../../components/chart/LineChart';
import KPI_NEW from '../../components/kpi/KPI_NEW';
import { optionOverviewFilter } from '../../constants/filterOptions';
import { convertToUSDGrowth, convertToPercent, convertToWholePercent } from '../../constants/formatter';


const OverviewContentType1 = (props) => {
	const { overviewData, selectedRangeValue, tab_name, tab_id } = props;

	// basic dd for column chart values
	const stacked_column_initial_value = optionOverviewFilter ? optionOverviewFilter[0] : null;
	const [stackedColumnSelection, setStackedColumnSelection] = useState(stacked_column_initial_value);

	// Data Distribution
	const overallSegment = overviewData ? overviewData.overall : [];
	const grossBookingsByVerticalSegment = overviewData ? overviewData.gross_bookings_by_segment : [];
	const segmentTrendsSegment = overviewData ? overviewData.segment_trends_by_segment : [];
	const segmentDetailsSegment = overviewData ? overviewData.segment_details : [];
	const nested_segment_revenue_mix = selectedRangeValue === "last_completed_quarter" ? overviewData.nested_segment_revenue_mix_lcq : selectedRangeValue === "last_4_quarters" ? overviewData.nested_segment_revenue_mix_l4q : overviewData.nested_segment_revenue_mix_ytd || [];

	const grossBookingsInBudgetPercentage = overviewData ? overviewData.gross_bookings_by_segment_in_budget_percentage : [];
	const grossBookingsBySegmentInMpqPercentage = overviewData ? overviewData.gross_bookings_by_segment_in_mpq_percentage : [];
	const grossBookingsBySegmentInQuarterPercentage = overviewData ? overviewData.gross_bookings_by_segment_in_quarter_percentage : [];
	const grossBookingsBySegmentInYearPercentage = overviewData ? overviewData.gross_bookings_by_segment_in_year_percentage : [];

	const [kpiClicked, setkpiClicked] = useState(null);

	// Table Config
	const indicatorPercent = (cell, row) => {
		let bgColor = cell >= 0.5 ? 'indicator-up' : cell <= -0.5 ? 'indicator-down' : 'indicator-middle';
		let icon = cell >= 0.5 ? <ImArrowUpRight2 /> : cell <= -0.5 ? <ImArrowDownRight2 /> : <span className="mr-3"></span>;
		return (
			<span className={`indicator ${bgColor}`}>
				{icon}
				{convertToPercent(cell)}
			</span>
		);
	};

	const segmentFormat = (cell, row) => {
		return formatLongText(cell, row)
	};

	const formatUSDGrowth = (cell, row) => {
		let new_cell = convertToUSDGrowth(cell);
		return formatLongText(new_cell, row)
	};

	const formatToPercent = (cell, row) => {
		return <span>{convertToPercent(cell)}</span>
	};

	const formatToWholePercent = (cell, row) => {
		return <span>{convertToWholePercent(cell)}</span>
	};

	const defaultSorted = [{
		dataField: '_id',
		order: 'asc',
	}];

	const columns = [
		{
			dataField: '_id',
			text: 'id',
			hidden: true
		},
		{
			dataField: 'segment',
			text: 'SEGMENT',
			sort: true,
			formatter: segmentFormat
		},
		{
			dataField: 'mrc_usd',
			text: 'MRC $',
			sort: true,
			formatter: formatUSDGrowth,
			classes: 'right',
			headerClasses: 'right'
		},
		{
			dataField: 'mix_percent',
			text: 'MIX %',
			sort: true,
			formatter: formatToPercent,
			classes: 'center',
			headerClasses: 'center',
		},
		{
			dataField: 'q_by_q_growth_percent',
			text: 'Q/Q % GROWTH',
			sort: true,
			formatter: indicatorPercent,
			classes: 'with-ellipsis center',
			headerClasses: 'center'
		},
		{
			dataField: 'q_to_q_growth_usd',
			text: 'Q-Q $ GROWTH',
			sort: true,
			formatter: formatUSDGrowth,
			classes: 'center',
			headerClasses: 'center'
		},
		{
			dataField: 'y_by_y_growth_percent',
			text: 'Y/Y % GROWTH',
			sort: true,
			formatter: indicatorPercent,
			classes: 'with-ellipsis center',
			headerClasses: 'center'
		},
		{
			dataField: 'y_to_y_growth_usd',
			text: 'Y-Y $ GROWTH',
			sort: true,
			formatter: formatUSDGrowth,
			classes: 'center',
			headerClasses: 'center'
		},
		{
			dataField: 'percent_mpq',
			text: '% MPQ',
			sort: true,
			formatter: formatToPercent,
			classes: 'center',
			headerClasses: 'center',
		},
		{
			dataField: 'percent_budget',
			text: '% BUDGET',
			sort: true,
			formatter: formatToPercent,
			classes: 'right',
			headerClasses: 'right',
		}
	];

	return (
		<>
			<Row className="contentDivider pb-30">
				<div className="fix-width">
					<Card className="revenue" >
						<Card.Body>
							<Card.Title>{tab_name}</Card.Title>
							<KPI_NEW
								totalValue={overallSegment.total.value}
								totalUnit={overallSegment.total.unit}
								details={overallSegment.details}
								setkpiClicked={setkpiClicked}
								kpiClicked={kpiClicked}
							/>
						</Card.Body>
					</Card>
				</div>
				<Col>
					<Card className="revenue">
						<Card.Body>
							{/* <Card.Title>{tab_name} BY SEGMENT</Card.Title> */}
							<Card.Title>	{kpiClicked ? kpiClicked : tab_name} BY SEGMENT	</Card.Title>
							{kpiClicked === '% of MPQ' ?
								<>
									<Legend legend={grossBookingsBySegmentInMpqPercentage.legend} />
									<div style={{ height: "92%" }}>
										<LineChart
											id={`${tab_id}-grossBookingsBySegmentInMpqPercentage-line_chart-segment`}
											data={grossBookingsBySegmentInMpqPercentage.data}
											legend={grossBookingsBySegmentInMpqPercentage.legend}
											isAnimationEnabled={false}
										/>
									</div>
								</>
								:

								kpiClicked === '% of Budget' ?
									<>
										<Legend legend={grossBookingsInBudgetPercentage.legend} />
										<div style={{ height: "92%" }}>
											<LineChart
												id={`${tab_id}-grossBookingsInBudgetPercentage-line_chart-segment`}
												data={grossBookingsInBudgetPercentage.data}
												legend={grossBookingsInBudgetPercentage.legend}
												isAnimationEnabled={false}
											/>
										</div>
									</>
									:
									kpiClicked === 'Q/Q Growth %' ?
										<>
											<Legend legend={grossBookingsBySegmentInQuarterPercentage.legend} />
											<div style={{ height: "92%" }}>
												<LineChart
													id={`${tab_id}-grossBookingsBySegmentInQuarterPercentage-line_chart-segment`}
													data={grossBookingsBySegmentInQuarterPercentage.data}
													legend={grossBookingsBySegmentInQuarterPercentage.legend}
													isAnimationEnabled={false}
												/>
											</div>
										</>
										:
										kpiClicked === 'Y/Y Growth %' ?
											<>
												<Legend legend={grossBookingsBySegmentInYearPercentage.legend} />
												<div style={{ height: "92%" }}>
													<LineChart
														id={`${tab_id}-grossBookingsBySegmentInYearPercentage-line_chart-segment`}
														data={grossBookingsBySegmentInYearPercentage.data}
														legend={grossBookingsBySegmentInYearPercentage.legend}
														isAnimationEnabled={false}
													/>
												</div>
											</>
											:
											<>
												<div className="d-flex">
													<Legend legend={grossBookingsByVerticalSegment.legend} />
													{/* {selectedRangeValue === "last_completed_quarter" ? */}
													<BasicDD
														className="ml-auto"
														id={`${tab_id}-stacked_column_dd`}
														options={optionOverviewFilter}
														value={stackedColumnSelection}
														setValue={setStackedColumnSelection}
													/>
													{/* : null
													} */}
												</div>
												<div style={{ height: "85%", marginTop: "1rem" }}>
													<StackedColumnChart
														id={`${tab_id}-stacked_column_chart`}
														hiddenValueAxis={false}
														isOneHundredPercent={false}
														isPercentage={false}
														largerWidth={true}
														hideLabelBullet={false}
														isOverviewChart={true}
														isGrowthPercentToggable={true}
														stackedColumnSelection={stackedColumnSelection.key}
														selectedRangeValue={'last_completed_quarter'}
														data={grossBookingsByVerticalSegment.data}
														legend={grossBookingsByVerticalSegment.legend}
														isAnimationEnabled={false}
													/>
												</div>
											</>
							}

						</Card.Body>
					</Card>
				</Col>
				<Col style={{ maxWidth: "400px" }}>
					<Card className="Segment revenue">
						<Card.Body>
							<Card.Title>{tab_name} MIX % BY SEGMENT</Card.Title>
							<Legend legend={nested_segment_revenue_mix.legend} />
							<div style={{ height: "92%" }}>
								<NestedDonutChart
									id={`${tab_id}-nested-donut-chart`}
									insideValueName={"inner_quarter"}
									insideValue={"inner_quarter_percentage"}
									insideCategory={"hq_region"}
									outsideValueName={"outer_quarter"}
									outsideValue={"outer_quarter_percentage"}
									outsideCategory={"hq_region"}
									loadedData={nested_segment_revenue_mix.data}
									isAnimationEnabled={true}
								/>
							</div>
						</Card.Body>
					</Card>
				</Col>
			</Row>
			<Row className="contentDivider">
				<Col>
					<Card className="revenue">
						<Card.Body>
							<Card.Title>SEGMENT DETAILS</Card.Title>
							<CustomTable
								tableName='segment-details scrollable'
								data={segmentDetailsSegment ? segmentDetailsSegment : []}
								defaultSorted={defaultSorted}
								columns={columns}
							/>
						</Card.Body>
					</Card>
				</Col>
				<Col style={{ maxWidth: "400px" }}>
					<Card className="revenue">
						<Card.Body>
							<Card.Title>Y/Y % GROWTH BY SEGMENT</Card.Title>
							<Legend legend={segmentTrendsSegment.legend} />
							<div style={{ height: "300px" }}>
								<LineChart
									id={`${tab_id}-line_chart-segment`}
									data={segmentTrendsSegment.data}
									legend={segmentTrendsSegment.legend}
									isAnimationEnabled={false}
								/>
							</div>
						</Card.Body>
					</Card>
				</Col>
			</Row>
		</>
	);
};

export default OverviewContentType1;