import React, { useLayoutEffect } from 'react';
import * as am4core from "@amcharts/amcharts4/core";
import * as am4charts from "@amcharts/amcharts4/charts";
import am4themes_animated from "@amcharts/amcharts4/themes/animated";

am4core.useTheme(am4themes_animated);


const NestedDonutChart = (props) => {
  const { id, insideValueName, insideValue, insideCategory, outsideValueName, outsideValue, outsideCategory, loadedData, isAnimationEnabled } = props;

  useLayoutEffect(() => {
    let chart = am4core.create(id, am4charts.PieChart);
    chart.data = loadedData;
    chart.padding(0);
    chart.innerRadius = am4core.percent(40);

    let firstPieSeries = chart.series.push(new am4charts.PieSeries());
    firstPieSeries.dataFields.value = insideValue;
    firstPieSeries.dataFields.category = insideCategory;
    firstPieSeries.dataFields.valueName = insideValueName;
    firstPieSeries.slices.radius = 10;
    firstPieSeries.slices.template.strokeWidth = 0;
    firstPieSeries.slices.template.propertyFields.fill = "color";
    firstPieSeries.slices.template.fillOpacity = 1;
    firstPieSeries.innerRadius = am4core.percent(-40);
    firstPieSeries.ticks.template.disabled = true;
    firstPieSeries.alignLabels = false;
    firstPieSeries.labels.template.text = "[font-size: 12px]{valueName}\n{value.formatNumber('#.0')}%[/]";
    firstPieSeries.labels.template.radius = am4core.percent(-30);
    firstPieSeries.labels.template.fill = am4core.color("white");
    firstPieSeries.slices.template.tooltipText = "[#fff]{category}: [#fff font-weight: bold]{value.formatNumber('#.0')}%[/][/]";
    firstPieSeries.slices.template.states.getKey("hover").properties.scale = 1;
    firstPieSeries.slices.template.states.getKey("active").properties.shiftRadius = 0;
    firstPieSeries.defaultState.transitionDuration = isAnimationEnabled ? 1000 : 0;
    firstPieSeries.hiddenState.transitionDuration = 0;

    let secondPieSeries = chart.series.push(new am4charts.PieSeries());
    secondPieSeries.dataFields.value = outsideValue;
    secondPieSeries.dataFields.category = outsideCategory;
    secondPieSeries.dataFields.valueName = outsideValueName;
    secondPieSeries.slices.template.strokeWidth = 0;
    secondPieSeries.radius = am4core.percent(150);
    secondPieSeries.slices.template.propertyFields.fill = "color";
    secondPieSeries.slices.template.fillOpacity = 0.2 ;
    secondPieSeries.ticks.template.disabled = true;
    secondPieSeries.alignLabels = false;
    secondPieSeries.labels.template.text = "[font-size: 12px]{valueName}\n{value.formatNumber('#.0')}%[/]";
    secondPieSeries.labels.template.radius = am4core.percent(-20);
    secondPieSeries.labels.template.fill = am4core.color("#748a9d");
    secondPieSeries.slices.template.tooltipText = "[#fff]{category}: [#fff font-weight: bold]{value.formatNumber('#.0')}%[/][/]";
    secondPieSeries.slices.template.states.getKey("hover").properties.scale = 1;
    secondPieSeries.slices.template.states.getKey("active").properties.shiftRadius = 0;
    secondPieSeries.defaultState.transitionDuration = isAnimationEnabled ? 1000 : 0;
    secondPieSeries.hiddenState.transitionDuration = 0;

    return () => {
      chart.dispose();
    };
  }, [id, insideValueName, insideValue, insideCategory, outsideValueName, outsideValue, outsideCategory, loadedData, isAnimationEnabled]);

  return (
    <div className="nested-donut-chart" id={id}></div>
  );
}

export default NestedDonutChart;
