import React, { useState } from 'react';
import { Row, Col, Card } from 'react-bootstrap';
import { ImArrowUpRight2, ImArrowDownRight2 } from "react-icons/im";
import Legend from '../../components/legend/Legend';
import CustomTable, { formatLongText } from '../../components/customTable/CustomTable';
import StackedColumnChart from '../../components/chart/StackedColumnChart';
import LineChart from '../../components/chart/LineChart';
import WaterFallChart from '../../components/chart/WaterfallChart';
import KPI_NEW from '../../components/kpi/KPI_NEW';
import { optionOverviewFilter } from '../../constants/filterOptions';
import { convertToUSDGrowth, convertToPercent, convertToWholePercent } from '../../constants/formatter';
import OverviewContentType1 from './OverviewContentType1';


const OverviewContentType2 = (props) => {
	const { overviewData, overviewData2, selectedRangeValue, tab_id, tab_name, activeTab } = props;

	// basic dd for column chart values
	const stacked_column_initial_value = optionOverviewFilter ? optionOverviewFilter[0] : null;
	const [stackedColumnSelection, setStackedColumnSelection] = useState(stacked_column_initial_value);

	// Data Distribution
	const overallVertical = overviewData ? overviewData.overall : [];
	const grossNetBkgBrideBySegment = overviewData ? overviewData.gross_to_net_bkg_bridge_by_segment : [];
	const grossNetBkgBridgeTrends = overviewData ? overviewData.gross_net_bkg_bridge_trends : [];
	const netPercentOfGrossBySegment = overviewData ? overviewData.net_percent_of_gross_by_segment : [];
	const netPercentOfGrossTrends = overviewData ? overviewData.net_percent_of_gross_trends : [];
	const grossNetBkgWaterfall = overviewData ? overviewData.gross_net_bkg_waterfall : [];

	// Table Config
	const indicatorPercent = (cell, row) => {
		let bgColor = cell >= 0.5 ? 'indicator-up' : cell <= -0.5 ? 'indicator-down' : 'indicator-middle';
		let icon = cell >= 0.5 ? <ImArrowUpRight2 /> : cell <= -0.5 ? <ImArrowDownRight2 /> : <span className="mr-3"></span>;
		return (
			<span className={`indicator ${bgColor}`}>
				{icon}
				{convertToPercent(cell)}
			</span>
		);
	};

	const segmentFormat = (cell, row) => {
		return formatLongText(cell, row)
	};

	const formatUSDGrowth = (cell, row) => {
		let new_cell = convertToUSDGrowth(cell);
		return formatLongText(new_cell, row)
	};

	const formatToPercent = (cell, row) => {
		return <span>{convertToPercent(cell)}</span>
	};

	const formatToWholePercent = (cell, row) => {
		return <span>{convertToWholePercent(cell)}</span>
	};


	const defaultSorted = [{
		dataField: '_id',
		order: 'asc',
	}];

	const columns = [
		{
			dataField: '_id',
			text: 'id',
			hidden: true
		},
		{
			dataField: 'segment',
			text: 'SEGMENT',
			sort: true,
			formatter: segmentFormat
		},
		{
			dataField: activeTab === 'net' ? 'net_as_percent_of_gross' : 'pag_as_percent_of_gross',
			text: activeTab === 'net' ? 'NET AS % OF GROSS' : 'PAG AS % OF GROSS',
			sort: true,
			classes: 'right',
			headerClasses: 'right',
			formatter: formatToPercent,
		},
		{
			dataField: 'q_to_q_growth',
			text: 'Q-Q GROWTH',
			sort: true,
			formatter: indicatorPercent,
			classes: 'with-ellipsis center',
			headerClasses: 'center'
		},
		{
			dataField: 'p4q_growth',
			text: 'P4Q GROWTH',
			sort: true,
			formatter: indicatorPercent,
			classes: 'with-ellipsis center',
			headerClasses: 'center'
		},
		{
			dataField: 'y_to_y_growth',
			text: 'Y-Y GROWTH',
			sort: true,
			formatter: indicatorPercent,
			classes: 'with-ellipsis center',
			headerClasses: 'center'
		}
	];

	return (
		<>
			<OverviewContentType1 overviewData={overviewData2} tab_name={tab_name} tab_id={tab_id} selectedRangeValue={selectedRangeValue} />
			<Row className="contentDivider py-30">
				<Col style={{ maxWidth: "400px" }}>
					<Card className="revenue" >
						<Card.Body>
							<Card.Title>gross-{activeTab} {activeTab === 'pag' ? '' : 'bkg'} bridge</Card.Title>
							<KPI_NEW
								totalValue={overallVertical.total.value}
								totalUnit={overallVertical.total.unit}
								details={overallVertical.details}
								isAddText={' of Gross'}
								noHover={true}
							/>
						</Card.Body>
					</Card>
				</Col>
				<Col>
					<Card className="revenue">
						<Card.Body>
							<Card.Title>gross-{activeTab} {activeTab === 'pag' ? '' : 'bkg'} bridge trends</Card.Title>
							<Legend legend={grossNetBkgBridgeTrends.legend} />
							<div style={{ height: "400px" }}>
								<StackedColumnChart
									id={`${tab_id}-stacked_column_chart-segment`}
									hiddenValueAxis={false}
									isOneHundredPercent={false}
									isPercentage={false}
									largerWidth={true}
									hideLabelBullet={false}
									isOverviewChart={true}
									stackedColumnSelection={stackedColumnSelection.key}
									data={grossNetBkgBridgeTrends.data}
									legend={grossNetBkgBridgeTrends.legend}
									isAnimationEnabled={false}
									withNegativeValues={true}
								/>
							</div>
						</Card.Body>
					</Card>
				</Col>
				<Col sm={12} md={3} lg={3}>
					<Card className="revenue">
						<Card.Body>
							<Card.Title>gross-{activeTab} {activeTab === 'pag' ? '' : 'bkg'} waterfall</Card.Title>
							<Legend legend={grossNetBkgWaterfall.legend} />
							<div style={{ height: "450px" }}>
								<WaterFallChart
									id={`${tab_id}-watterfall_chart`}
									data={grossNetBkgWaterfall.data}
									legend={grossNetBkgWaterfall.legend}
									isAnimationEnabled={false}
								/>
							</div>
						</Card.Body>
					</Card>
				</Col>
			</Row>
			<Row className="contentDivider">
				<Col sm={12} md={6} lg={6}>
					<Card className="revenue">
						<Card.Body>
							<Card.Title>{activeTab} % of gross by segment</Card.Title>
							<CustomTable
								tableName='segment-details-vertical scrollable'
								data={netPercentOfGrossBySegment ? netPercentOfGrossBySegment : []}
								defaultSorted={defaultSorted}
								columns={columns}
							/>
						</Card.Body>
					</Card>
				</Col>
				<Col sm={12} md={3} lg={3}>
					<Card className="revenue">
						<Card.Body>
							<Card.Title>gross-{activeTab} {activeTab === 'pag' ? '' : 'bkg'} bridge by segment</Card.Title>
							<Legend legend={grossNetBkgBrideBySegment.legend} />
							<div style={{ height: "300px" }}>
								<StackedColumnChart
									id={`${tab_id}-stacked_column_chart-vertical`}
									hiddenValueAxis={false}
									isOneHundredPercent={false}
									isPercentage={false}
									largerWidth={false}
									hideLabelBullet={false}
									isOverviewChart={true}
									data={grossNetBkgBrideBySegment.data}
									legend={grossNetBkgBrideBySegment.legend}
									isAnimationEnabled={false}
									withNegativeValues={true}
								/>
							</div>
						</Card.Body>
					</Card>
				</Col>
				<Col sm={12} md={3} lg={3}>
					<Card className="revenue">
						<Card.Body>
							<Card.Title>{activeTab} % of gross trends</Card.Title>
							<Legend legend={netPercentOfGrossTrends.legend} />
							<div style={{ height: "300px" }}>
								<LineChart
									id={`${tab_id}-line_chart-trends`}
									data={netPercentOfGrossTrends.data}
									legend={netPercentOfGrossTrends.legend}
									isAnimationEnabled={false}
								/>
							</div>
						</Card.Body>
					</Card>
				</Col>
			</Row>
		</>
	);
};

export default OverviewContentType2;