import React, { useState, useEffect } from 'react';
import axiosRetry from 'axios-retry';
import axios from 'axios';
import { FilterContainer, FilterModal } from '../../components/filter';
import { optionRegion, optionMetro, optionGroup4, optionOwnerTerritory, optionForecastStatus, optionGroupPipelineOverview } from '../../constants/filterOptions';
import { RadioDD, CheckboxDD, SearchCheckboxDD } from '../../components/customDropdown';
import { comments } from '../../constants/comments';
import { InternalServerError, ResourceNotFoundError } from '../../components/error/Error';
import { API_BASE_URL } from '../../constants/config';
import Comment from '../../components/comment/Comment';
import Loader from '../../components/loader/Loader';
import OverviewContent from './OverviewContent';


const Overview = () => {
  const [overviewData, setData] = useState([]);
  const [isLoading, setIsLoading] = useState(true)

  // show all filters
  const [showFilter, setShowFilter] = useState(false);
  const onShowFilter = () => {
    setShowFilter(true)
  }
  const onCloseFilter = async () => {
    await onCancelRegion();
    await onCancelMetro();
    await onCancelForecast();
    await onCancelGroup();
    setShowFilter(false);
  }
  const onApplyFilter = async () => {
    await onApplyRegion();
    await onApplyMetro();
    await onApplyForecast();
    await onApplyGroup();
    setShowFilter(false);
  }

  // comment area
  const [showComment, setShowComment] = useState(false);
  const onCloseComment = () => {
    setShowComment(false);
  }
  const onShowComment = () => {
    setShowComment(true)
  };

  // checkbox for region values
  const [region_val_onApply, set_region_val_onApply] = useState([]);
  const [region_val, set_region_val] = useState([]);
  const onApplyRegion = () => {
    set_region_val_onApply(region_val);
  };
  const onCancelRegion = () => {
    set_region_val(region_val_onApply);
  };

  // searchCheckbox for metro values
  const [metro_val_onApply, set_metro_val_onApply] = useState([]);
  const [metro_val, set_metro_val] = useState([]);
  const onApplyMetro = () => {
    set_metro_val_onApply(metro_val);
  };
  const onCancelMetro = () => {
    set_metro_val(metro_val_onApply);
  };

  // checkbox for forecast values
  const [forecast_val_onApply, set_forecast_val_onApply] = useState([]);
  const [forecast_val, set_forecast_val] = useState([]);
  const onApplyForecast = () => {
    set_forecast_val_onApply(forecast_val);
  };
  const onCancelForecast = () => {
    set_forecast_val(forecast_val_onApply);
  };

  // checkbox for owner territory values
  const [territory_val_onApply, set_territory_val_onApply] = useState([]);
  const [territory_val, set_territory_val] = useState([]);
  const onApplyTerritory = () => {
    set_territory_val_onApply(territory_val);
  };
  const onCancelTerritory = () => {
    set_territory_val(territory_val_onApply);
  };

  // radio for group-by values
  const group_initial_value = optionGroupPipelineOverview ? optionGroupPipelineOverview[0] : null;
  const [group_val_onApply, set_group_val_onApply] = useState(group_initial_value);
  const [group_val, set_group_val] = useState(group_initial_value);
  const onApplyGroup = () => {
    set_group_val_onApply(group_val);
  };
  const onCancelGroup = () => {
    set_group_val(group_val_onApply);
  };

  // Axios call
  const fetchData = () => {
    let url = `${API_BASE_URL}/pipeline/overview.json`;

    setIsLoading(true);
    axiosRetry(axios, { retries: 3 });

    axios({
      method: 'GET',
      url: url
    }).then(result => {
      setData(result.data);
      setIsLoading(false);
    }).catch(error => {
      console.log(error);
      setData(null);
      setIsLoading(false);
    });
  };

  useEffect(() => {
    fetchData();
  }, []);

  if (isLoading) return <Loader withContainer />;

  return (
    <>
      <div className={`page-content ${showComment ? 'open' : 'closed'}`}>
        {overviewData ?
          overviewData.segment_details.length > 0 ?
            <>
              <FilterContainer
                showComment={showComment}
                onShowFilter={onShowFilter}
                onShowComment={onShowComment}
              >
                <div className="filters custom-scroll">
                  <CheckboxDD
                    title="Deploy Region"
                    id="region"
                    options={optionRegion}
                    valueApplied={region_val_onApply}
                    checkVal={region_val}
                    setCheckVal={set_region_val}
                    onApply={onApplyRegion}
                    onCancel={onCancelRegion}
                  />
                  <SearchCheckboxDD
                    title="Deploy Metro"
                    id="metro"
                    options={optionMetro}
                    valueApplied={metro_val_onApply}
                    checkVal={metro_val}
                    setCheckVal={set_metro_val}
                    onApply={onApplyMetro}
                    onCancel={onCancelMetro}
                  />
                  <CheckboxDD
                    id="forecast"
                    title="Forecast Status"
                    options={optionForecastStatus}
                    checkVal={forecast_val}
                    setCheckVal={set_forecast_val}
                    onApply={onApplyForecast}
                    onCancel={onCancelForecast}
                  />
                  <CheckboxDD
                    id="territory"
                    title="Owner Territory"
                    options={optionOwnerTerritory}
                    checkVal={territory_val}
                    setCheckVal={set_territory_val}
                    onApply={onApplyTerritory}
                    onCancel={onCancelTerritory}
                  />
                  <RadioDD
                    id="group_by"
                    options={optionGroupPipelineOverview}
                    radioVal={group_val}
                    setRadioVal={set_group_val}
                    onApply={onApplyGroup}
                    onCancel={onCancelGroup}
                    initialValue={group_initial_value}
                  />
                </div>
                <FilterModal
                  title="Overview"
                  setShowFilter={setShowFilter}
                  showFilter={showFilter}
                  onCloseFilter={onCloseFilter}
                  onApplyFilter={onApplyFilter}
                >
                  <CheckboxDD
                    title="Deploy Region"
                    id="region-modal"
                    visibleInModal
                    options={optionRegion}
                    checkVal={region_val}
                    setCheckVal={set_region_val}
                  />
                  <SearchCheckboxDD
                    title="Deploy Metro"
                    id="metro-modal"
                    visibleInModal
                    options={optionMetro}
                    checkVal={metro_val}
                    setCheckVal={set_metro_val}
                  />
                  <CheckboxDD
                    id="forecast-modal"
                    title="Forecast Status"
                    visibleInModal
                    options={optionForecastStatus}
                    checkVal={forecast_val}
                    setCheckVal={set_forecast_val}
                    onApply={onApplyForecast}
                    onCancel={onCancelForecast}
                  />
                  <CheckboxDD
                    id="territory-modal"
                    title="Owner Territory"
                    visibleInModal
                    options={optionOwnerTerritory}
                    checkVal={territory_val}
                    setCheckVal={set_territory_val}
                    onApply={onApplyTerritory}
                    onCancel={onCancelTerritory}
                  />
                  <RadioDD
                    title="Group by"
                    id="group_by-modal"
                    visibleInModal
                    options={optionGroupPipelineOverview}
                    radioVal={group_val}
                    setRadioVal={set_group_val}
                  />
                </FilterModal>
              </FilterContainer>
              {/* ============= Your Component here! ============= */}
              <OverviewContent
                overviewData={overviewData}
              />
            </>
            : <ResourceNotFoundError redirectURL={"/executive-cockpit/channel-bookings-revenue/overview"} />
          : <InternalServerError redirectURL={"/executive-cockpit/channel-bookings-revenue/overview"} />
        }
      </div>
      <Comment
        data={comments}
        onCloseComment={onCloseComment}
        showComment={showComment}
      />
    </>
  );
};

export default Overview;