import React, { useState, useEffect } from 'react';
import axiosRetry from 'axios-retry';
import axios from 'axios';
import { Button, ButtonGroup, Container } from 'react-bootstrap';
import { FilterContainer, FilterModal } from '../../components/filter';
import {
	optionRegion, optionCompanySize, optionFlag, optionCountry,
	optionVertical2, optionRange, optionLighthouseFlag, optionMetro,
	optionTerritory, optionProgram, optionChannelInfluence, optionHyperscaler,
	optionQuarter, optionSubsegment, optionVertical, optionDeaveraging
} from '../../constants/filterOptions';
import { Separator, RadioDD, CheckboxDD, SearchCheckboxDD } from '../../components/customDropdown';
import { comments } from '../../constants/comments';
import { InternalServerError, ResourceNotFoundError } from '../../components/error/Error';
import { API_BASE_URL } from '../../constants/config';
import Comment from '../../components/comment/Comment';
import Loader from '../../components/loader/Loader';
import MetroContent from './MetroContent';


const Metro = ({ activeTab, setActiveTab }) => {
	const [isLoading, setIsLoading] = useState(true);
	const [grossData, setGrossData] = useState([]);
	const [pagData, setPAGData] = useState([]);
	const [netData, setNetData] = useState([]);

	// show all filters
	const [showFilter, setShowFilter] = useState(false);
	const onShowFilter = () => {
		setShowFilter(true);
	};
	const onCloseFilter = async () => {
		await onCancelRegion();
		await onCancelRange();
		await onCancelHyperscaler();
		setShowFilter(false);
	};
	const onApplyFilter = async () => {
		await onApplyRegion();
		await onApplyRange();
		await onApplyHyperscaler();
		setShowFilter(false);
	};

	// comment area
	const [showComment, setShowComment] = useState(false);
	const onCloseComment = () => {
		setShowComment(false);
	};
	const onShowComment = () => {
		setShowComment(true);
	};

	// radio for range values
	const range_initial_value = optionRange ? optionRange[0] : null;
	const [range_val_onApply, set_range_val_onApply] = useState(range_initial_value);
	const [range_val, set_range_val] = useState(range_initial_value);
	const onApplyRange = () => {
		set_range_val_onApply(range_val);
	};
	const onCancelRange = () => {
		set_range_val(range_val_onApply);
	};

	// radio for vertical values
	const vertical_initial_value = optionVertical ? optionVertical[0] : null;
	const [vertical_val_onApply, set_vertical_val_onApply] = useState(vertical_initial_value);
	const [vertical_val, set_vertical_val] = useState(vertical_initial_value);
	const onApplyVertical = () => {
		set_vertical_val_onApply(vertical_val)
	};
	const onCancelVertical = () => {
		set_vertical_val(vertical_val_onApply)
	};

	// radio for hyperscaler values
	const hyperscaler_initial_value = optionHyperscaler ? optionHyperscaler[0] : null;
	const [hyperscaler_val_onApply, set_hyperscaler_val_onApply] = useState(hyperscaler_initial_value);
	const [hyperscaler_val, set_hyperscaler_val] = useState(hyperscaler_initial_value);
	const onApplyHyperscaler = () => {
		set_hyperscaler_val_onApply(hyperscaler_val);
	};
	const onCancelHyperscaler = () => {
		set_hyperscaler_val(hyperscaler_val_onApply);
	};

	// searchCheckbox for country values
	const [hq_country_val_onApply, set_hq_country_val_onApply] = useState([]);
	const [hq_country_val, set_hq_country_val] = useState([]);
	const onApplyHQCountry = () => {
		set_hq_country_val_onApply(hq_country_val);
	};
	const onCancelHQCountry = () => {
		set_hq_country_val(hq_country_val_onApply);
	};

	// searchCheckbox for country values
	const [country_val_onApply, set_country_val_onApply] = useState([]);
	const [country_val, set_country_val] = useState([]);
	const onApplyCountry = () => {
		set_country_val_onApply(country_val);
	};
	const onCancelCountry = () => {
		set_country_val(country_val_onApply);
	};

	// searchCheckbox for quarter values
	const [quarter_val_onApply, set_quarter_val_onApply] = useState([]);
	const [quarter_val, set_quarter_val] = useState([]);
	const onApplyQuarter = () => {
		set_quarter_val_onApply(quarter_val);
	};
	const onCancelQuarter = () => {
		set_quarter_val(quarter_val_onApply);
	};

	// searchCheckbox for subsegment values
	const [subsegment_val_onApply, set_subsegment_val_onApply] = useState([]);
	const [subsegment_val, set_subsegment_val] = useState([]);
	const onApplySubsegment = () => {
		set_subsegment_val_onApply(subsegment_val);
	};
	const onCancelSubsegment = () => {
		set_subsegment_val(subsegment_val_onApply);
	};

	// radio for flag values
	const flag_initial_value = optionFlag ? optionFlag[0] : null;
	const [flag_val_onApply, set_flag_val_onApply] = useState(flag_initial_value);
	const [flag_val, set_flag_val] = useState(flag_initial_value);
	const onApplyFlag = () => {
		set_flag_val_onApply(flag_val);
	};
	const onCancelFlag = () => {
		set_flag_val(flag_val_onApply);
	};

	// checkbox for deaveraging values
	const [deaveraging_val_onApply, set_deaveraging_val_onApply] = useState([]);
	const [deaveraging_val, set_deaveraging_val] = useState([]);
	const onApplyDeaveraging = () => {
		set_deaveraging_val_onApply(deaveraging_val);
	};
	const onCancelDeaveraging = () => {
		set_deaveraging_val(deaveraging_val_onApply);
	};

	// checkbox for company values
	const [company_val_onApply, set_company_val_onApply] = useState([]);
	const [company_val, set_company_val] = useState([]);
	const onApplyCompany = () => {
		set_company_val_onApply(company_val);
	};
	const onCancelCompany = () => {
		set_company_val(company_val_onApply);
	};

	// checkbox for region values
	const [region_val_onApply, set_region_val_onApply] = useState([]);
	const [region_val, set_region_val] = useState([]);
	const onApplyRegion = () => {
		set_region_val_onApply(region_val);
	};
	const onCancelRegion = () => {
		set_region_val(region_val_onApply);
	};

	// checkbox for hqregion values
	const [hqregion_val_onApply, set_hqregion_val_onApply] = useState([]);
	const [hqregion_val, set_hqregion_val] = useState([]);
	const onApplyHQRegion = () => {
		set_hqregion_val_onApply(hqregion_val);
	};
	const onCancelHQRegion = () => {
		set_hqregion_val(hqregion_val_onApply);
	};

	// checkbox for vertical2 values
	const [vertical2_val_onApply, set_vertical2_val_onApply] = useState([]);
	const [vertical2_val, set_vertical2_val] = useState([]);
	const onApplyVertical2 = () => {
		set_vertical2_val_onApply(vertical2_val);
	};
	const onCancelVertical2 = () => {
		set_vertical2_val(vertical2_val_onApply);
	};


	// checkbox for lighthouse values
	const [lighthouse_val_onApply, set_lighthouse_val_onApply] = useState([]);
	const [lighthouse_val, set_lighthouse_val] = useState([]);
	const onApplyLighthouse = () => {
		set_lighthouse_val_onApply(lighthouse_val);
	};
	const onCancelLighthouse = () => {
		set_lighthouse_val(lighthouse_val_onApply);
	};

	// searchCheckbox for metro values
	const [metro_val_onApply, set_metro_val_onApply] = useState([]);
	const [metro_val, set_metro_val] = useState([]);
	const onApplyMetro = () => {
		set_metro_val_onApply(metro_val);
	};
	const onCancelMetro = () => {
		set_metro_val(metro_val_onApply);
	};

	// checkbox for territory values
	const [territory_val_onApply, set_territory_val_onApply] = useState([]);
	const [territory_val, set_territory_val] = useState([]);
	const onApplyTerritory = () => {
		set_territory_val_onApply(territory_val);
	};
	const onCancelTerritory = () => {
		set_territory_val(territory_val_onApply);
	};

	// checkbox for program values
	const [program_val_onApply, set_program_val_onApply] = useState([]);
	const [program_val, set_program_val] = useState([]);
	const onApplyProgram = () => {
		set_program_val_onApply(program_val);
	};
	const onCancelProgram = () => {
		set_program_val(program_val_onApply);
	};

	// radio for channel values
	const channel_initial_value = optionChannelInfluence ? optionChannelInfluence[0] : null;
	const [channel_val_onApply, set_channel_val_onApply] = useState(channel_initial_value);
	const [channel_val, set_channel_val] = useState(channel_initial_value);
	const onApplyChannel = () => {
		set_channel_val_onApply(channel_val);
	};
	const onCancelChannel = () => {
		set_channel_val(channel_val_onApply);
	};

	// Axios call
	const fetchData = () => {
		let url = `${API_BASE_URL}/bookings/metro.json?print=pretty`;

		setIsLoading(true);
		axiosRetry(axios, { retries: 3 });

		axios({
			method: 'GET',
			url: url
		}).then(result => {
			let data = result.data;
			setGrossData(data['gross']);
			setPAGData(data['pag']);
			setNetData(data['net']);
			setIsLoading(false);
		}).catch(error => {
			console.log(error);
			setGrossData([]);
			setPAGData([]);
			setNetData([]);
			setIsLoading(false);
		});
	};

	useEffect(() => {
		fetchData();
	}, []);

	if (isLoading) return <Loader withContainer />;

	return (
		<>
			<div className={`page-content ${showComment ? 'open' : 'closed'}`}>
				{(grossData || pagData || netData) ?
					(grossData || pagData || netData).metro_ranking.by_top_rank.data.length > 0 ?
						<>
							<FilterContainer
								showComment={showComment}
								onShowFilter={onShowFilter}
								onShowComment={onShowComment}
							>
								<div className="filters custom-scroll">
									<ButtonGroup className="sub-tab">
										<Button
											variant='light'
											className={activeTab === 'gross' ? 'active' : ''}
											onClick={() => { setActiveTab('gross') }}>
											Gross Bookings
                		</Button>
										<Button
											variant='light'
											className={activeTab === 'pag' ? 'active' : ''}
											onClick={() => { setActiveTab('pag') }}>
											PAG
                		</Button>
										<Button
											variant='light'
											className={activeTab === 'net' ? 'active' : ''}
											onClick={() => { setActiveTab('net') }}>
											Net Bookings
                		</Button>
									</ButtonGroup>
									<Separator height='39px' />
									{/* PRIMARY FILTERS */}
									<CheckboxDD
										title="HQ Region"
										id="hqregion"
										options={optionRegion}
										checkVal={hqregion_val}
										setCheckVal={set_hqregion_val}
										onApply={onApplyHQRegion}
										onCancel={onCancelHQRegion}
									/>
									<CheckboxDD
										title="Vertical"
										id="vertical"
										options={optionVertical2}
										checkVal={vertical2_val}
										setCheckVal={set_vertical2_val}
										onApply={onApplyVertical2}
										onCancel={onCancelVertical2}
									/>
									<RadioDD
										id="hyperscaler"
										options={optionHyperscaler}
										radioVal={hyperscaler_val}
										setRadioVal={set_hyperscaler_val}
										onApply={onApplyHyperscaler}
										onCancel={onCancelHyperscaler}
									/>
									<CheckboxDD
										title="Deploy Region"
										id="region"
										options={optionRegion}
										checkVal={region_val}
										setCheckVal={set_region_val}
										onApply={onApplyRegion}
										onCancel={onCancelRegion}
									/>
									{/* OTHER FEATURES */}

									<RadioDD
										id="range"
										options={optionRange}
										radioVal={range_val}
										setRadioVal={set_range_val}
										onApply={onApplyRange}
										onCancel={onCancelRange}
										initialValue={range_initial_value}
									/>
									<RadioDD
										id="segment"
										options={optionVertical}
										radioVal={vertical_val}
										setRadioVal={set_vertical_val}
										onApply={onApplyVertical}
										onCancel={onCancelVertical}
										initialValue={vertical_initial_value}
									/>

								</div>
								<FilterModal
									title="Metro"
									setShowFilter={setShowFilter}
									showFilter={showFilter}
									onCloseFilter={onCloseFilter}
									onApplyFilter={onApplyFilter}
								>
									<CheckboxDD
										title="HQ Region"
										id="hqregion-modal"
										visibleInModal
										options={optionRegion}
										checkVal={hqregion_val}
										setCheckVal={set_hqregion_val}
									/>
									<CheckboxDD
										title="Vertical"
										id="vertical-2-modal"
										visibleInModal
										options={optionVertical2}
										checkVal={vertical2_val}
										setCheckVal={set_vertical2_val}
									/>
									<RadioDD
										title="Hyperscaler Flag"
										id="hyperscaler-modal"
										visibleInModal
										options={optionHyperscaler}
										radioVal={hyperscaler_val}
										setRadioVal={set_hyperscaler_val}
									/>
											<CheckboxDD
										title="Deploy Region"
										id="region-modal"
										visibleInModal
										options={optionRegion}
										checkVal={region_val}
										setCheckVal={set_region_val}
									/>
									<SearchCheckboxDD
										title="HQ Country"
										id="country-modal"
										visibleInModal
										options={optionCountry}
										checkVal={hq_country_val}
										setCheckVal={set_hq_country_val}
									/>

									<RadioDD
										title="MNC Flag"
										id="flag-modal"
										visibleInModal
										options={optionFlag}
										radioVal={flag_val}
										setRadioVal={set_flag_val}
									/>
									<CheckboxDD
										title="Company Size"
										id="company-modal"
										visibleInModal
										options={optionCompanySize}
										checkVal={company_val}
										setCheckVal={set_company_val}
									/>

									<SearchCheckboxDD
										title="EQIX Sub Segment"
										id="subsegment-modal"
										visibleInModal
										options={optionSubsegment}
										valueApplied={subsegment_val_onApply}
										checkVal={subsegment_val}
										setCheckVal={set_subsegment_val}
									/>
									<CheckboxDD
										title="Deaveraging Segment"
										id="deaveraging-modal"
										visibleInModal
										options={optionDeaveraging}
										checkVal={deaveraging_val}
										setCheckVal={set_deaveraging_val}
									/>

									<CheckboxDD
										title="Lighthouse Flag"
										id="lighthouse-modal"
										visibleInModal
										options={optionLighthouseFlag}
										checkVal={lighthouse_val}
										setCheckVal={set_lighthouse_val}
									/>
							
									<SearchCheckboxDD
										title="Deploy Country"
										id="country-modal"
										visibleInModal
										options={optionCountry}
										checkVal={country_val}
										setCheckVal={set_country_val}
									/>
									<SearchCheckboxDD
										title="Deploy Metro"
										id="metro-modal"
										visibleInModal
										options={optionMetro}
										checkVal={metro_val}
										setCheckVal={set_metro_val}
									/>
									<SearchCheckboxDD
										title="Customer's Joining Qtr"
										id="quarter-modal"
										visibleInModal
										options={optionQuarter}
										checkVal={quarter_val}
										setCheckVal={set_quarter_val}
									/>
									<CheckboxDD
										title="Sales Territory"
										id="territory-modal"
										visibleInModal
										options={optionTerritory}
										checkVal={territory_val}
										setCheckVal={set_territory_val}
									/>
									<CheckboxDD
										title="Sales Program Type"
										id="program-modal"
										visibleInModal
										options={optionProgram}
										checkVal={program_val}
										setCheckVal={set_program_val}
									/>
									<RadioDD
										title="Channel Influenced"
										id="channel-modal"
										visibleInModal
										options={optionChannelInfluence}
										radioVal={channel_val}
										setRadioVal={set_channel_val}
									/>
								</FilterModal>
							</FilterContainer>
							{/* ============= Your Component here! ============= */}
							<Container fluid className={`${activeTab === 'gross' ? 'd-block' : 'd-none'}`}>
								<MetroContent metroData={grossData} tab_id="gross-metro" />
							</Container>
							<Container fluid className={`${activeTab === 'pag' ? 'd-block' : 'd-none'}`}>
								<MetroContent metroData={pagData} tab_id="pag-metro" />
							</Container>
							<Container fluid className={`${activeTab === 'net' ? 'd-block' : 'd-none'}`}>
								<MetroContent metroData={netData} tab_id="net-metro" />
							</Container>
						</>
						: <ResourceNotFoundError redirectURL={"/executive-cockpit/bookings/metro"} />
					: <InternalServerError redirectURL={"/executive-cockpit/bookings/metro"} />}
			</div>
			<Comment
				data={comments}
				onCloseComment={onCloseComment}
				showComment={showComment}
			/>
		</>
	);
};

export default Metro;