import React, { useState } from 'react';
import { optionMetrics } from '../../constants/filterOptions';
import { SearchCheckboxDD } from '../../components/customDropdown';

import ViewReportContent from './ViewReportContent'
import './snapshot.scss';



const ViewReport = () => {

    // searchCheckbox for metric values
    const [metrics_val_onApply, set_metrics_val_onApply] = useState([]);
    const [metrics_val, set_metrics_val] = useState([]);
    const onApplyMetrics = () => {
        set_metrics_val_onApply(metrics_val)
    }
    const onCancelMetrics = () => {
        set_metrics_val(metrics_val_onApply)
    }

    return (
        <>
            <div className={`page-content view-report-content-page`}>
                <SearchCheckboxDD
                    title="Metric"
                    id="metric"
                    options={optionMetrics}
                    valueApplied={metrics_val_onApply}
                    checkVal={metrics_val}
                    setCheckVal={set_metrics_val}
                    onApply={onApplyMetrics}
                    onCancel={onCancelMetrics}
                />
                <ViewReportContent />
            </div>
        </>
    );
}

export default ViewReport;
