import React, { useState } from 'react';
import { Dropdown, Form, Button } from 'react-bootstrap';
import { BiChevronDown } from 'react-icons/bi'
import './customDropdown.scss';


const RadioDD = ({ 
  options, 
  className, id, 
  title, 
  visibleInModal,
  setRadioVal,
  radioVal,
  onApply,
  onCancel
}) => {
  const [show, setShow] = useState(false);

  const handleSelect = (key, text) => {
    setRadioVal({key, text})
  }

  const handleToggle = () => {
    setShow(!show)
    onCancel();
  }

  return (
    <>
      {visibleInModal
        ? <div className={`radio-in-modal narrow-list filter-modal ${className ? className : ''}`}>
          <div className="title">{title ? title : radioVal.text}</div>
          <Form>
            {options && options.map(option => (
              <Form.Check
                key={option.key}
                custom
                type='radio'
                id={`modal_radio-${option.key}`}
                checked={radioVal.key === option.key}
                label={option.text}
                onChange={() => handleSelect(option.key, option.text)}
              />
            ))}
          </Form>
        </div>
        : <Dropdown
          show={show}
          onToggle={handleToggle}
          className={`radio-dd narrow-list ${className ? className : ''}`}
        >
          <Dropdown.Toggle id={`radio_dd-${id}`} variant='light' className={radioVal.key ? 'hasPick' : ''}>
            {radioVal.text ? radioVal.text : ''} <BiChevronDown viewBox="6 8 12 12" />
          </Dropdown.Toggle>

          <Dropdown.Menu show={show}>
            {options && options.map(option => (
              <Form.Check
                key={option.key}
                custom
                type='radio'
                id={`raido-${option.key}`}
                checked={radioVal.key === option.key}
                label={option.text}
                onChange={() => handleSelect(option.key, option.text)}
              />
            ))}
            <div className="btn-block">
              <Button variant="link" onClick={() => handleToggle()}>Cancel</Button>
              <Button variant="primary" onClick={() => {onApply(); setShow(!show);}}>Apply</Button>
            </div>
          </Dropdown.Menu>
        </Dropdown>
      }
    </>
  );
}

export default RadioDD;
