import React, { useState } from 'react';
import { Container, Row, Col, Card } from 'react-bootstrap';
import { ImArrowUpRight2, ImArrowDownRight2 } from "react-icons/im";
import Legend from '../../components/legend/Legend';
import { BasicDD } from '../../components/customDropdown';
import CustomTable, { formatLongText } from '../../components/customTable/CustomTable';
import StackedColumnChart from '../../components/chart/StackedColumnChart';
import DonutChart from '../../components/chart/DonutChart';
import SparklineChart from '../../components/chart/SparklineChart';
import LineChart from '../../components/chart/LineChart';
import BoxWhiskerChart from '../../components/chart/BoxWhiskerChart';
import KPI_NEW from '../../components/kpi/KPI_NEW';
import { optionOverviewFilter1 } from '../../constants/filterOptions';
import { convertToUSDGrowth, convertToPercent } from '../../constants/formatter';


const OverviewContent = (props) => {
	const { overviewData, selectedRangeValue } = props;
	const [kpiClicked, setkpiClicked] = useState(null);
	const [kpiClicked2, setkpiClicked2] = useState(null);

	// basic dd for column chart values
	const stacked_column_initial_value = optionOverviewFilter1 ? optionOverviewFilter1[0] : null;
	const [stackedColumnSelection, setStackedColumnSelection] = useState(stacked_column_initial_value);

	// Data Distribution
	let overall = overviewData ? overviewData.overall_pricing : [];
	let overall_mw_sold = overviewData ? overviewData.overall_mw_sold : [];
	let mw_sold_by_segment = overviewData ? overviewData.mw_sold_by_segment : [];
	let segment_details = overviewData ? overviewData.segment_details : [];
	let pricing_range_by_segment = overviewData ? overviewData.pricing_range_by_segment : [];
	let pricing_trend_by_segments = overviewData ? overviewData.pricing_trend_by_segments : [];
	let mw_sold_mix_percent_by_segment = overviewData ? overviewData.mw_sold_mix_percent_by_segment : [];


	let pricing_trend_by_segments_in_p4q_percentage = overviewData ? overviewData.pricing_trend_by_segments_in_p4q_percentage : [];
	let pricing_trend_by_segments_in_quarter_percentage = overviewData ? overviewData.pricing_trend_by_segments_in_quarter_percentage : [];
	let pricing_trend_by_segments_in_year_percentage = overviewData ? overviewData.pricing_trend_by_segments_in_year_percentage : [];

	let mw_sold_by_segment_in_p4q_percentage = overviewData ? overviewData.mw_sold_by_segment_in_p4q_percentage : [];
	let mw_sold_by_segment_in_quarter_percentage = overviewData ? overviewData.mw_sold_by_segment_in_quarter_percentage : [];
	let mw_sold_by_segment_in_year_percentage = overviewData ? overviewData.mw_sold_by_segment_in_year_percentage : [];

	// Table Config
	const indicatorPercent = (cell, row) => {
		let bgColor = cell >= 0.5 ? 'indicator-up' : cell <= -0.5 ? 'indicator-down' : 'indicator-middle';
		let icon = cell >= 0.5 ? <ImArrowUpRight2 /> : cell <= -0.5 ? <ImArrowDownRight2 /> : <span className="mr-3"></span>;
		return (
			<span className={`indicator ${bgColor}`}>
				{icon}
				{convertToPercent(cell)}
			</span>
		);
	};

	const segmentFormat = (cell, row) => {
		return formatLongText(cell, row)
	};

	const formatUSDGrowth = (cell, row) => {
		let new_cell = convertToUSDGrowth(cell);
		return formatLongText(new_cell, row)
	};

	const formatToPercent = (cell, row) => {
		return <span>{convertToPercent(cell)}</span>
	};

	const sparklineKVA = (cell, row) => {
		console.log(cell)
		return <SparklineChart loadedData={cell} id={`sparkline-chart-kva-${row._id}`} />
	};

	const sparklineMV = (cell, row) => {
		return <SparklineChart loadedData={cell} id={`sparkline-chart-mv-${row._id}`} />
	};

	const sparklineDeploy = (cell, row) => {
		return <SparklineChart loadedData={cell} id={`sparkline-chart-deploy-${row._id}`} />
	};

	const defaultSorted = [{
		dataField: '_id',
		order: 'asc',
	}];

	const columns = [
		{
			dataField: '_id',
			text: 'id',
			hidden: true
		},
		{
			dataField: 'segment',
			text: 'SEGMENT',
			sort: true,
			formatter: segmentFormat
		},
		{
			dataField: 'usd_per_kva',
			text: '$ / KVA',
			sort: true,
			formatter: formatUSDGrowth,
			classes: 'center',
			headerClasses: 'center'
		},
		{
			dataField: 'usd_per_kva_sparkline_data',
			text: '',
			formatter: sparklineKVA,
			classes: 'center',
			headerClasses: 'center'
		},
		{
			dataField: 'usd_per_kva_p4q_percent_growth',
			text: 'p4q % growth',
			sort: true,
			formatter: indicatorPercent,
			classes: 'with-ellipsis center border-right',
			headerClasses: 'center border-right',
		},
		{
			dataField: 'mw',
			text: 'mw',
			sort: true,
			classes: 'right',
			headerClasses: 'right'
		},
		{
			dataField: 'mw_sparkline_data',
			text: '',
			formatter: sparklineMV,
			classes: 'center',
			headerClasses: 'center'
		},
		{
			dataField: 'mw_p4q_percent_growth',
			text: 'p4q % growth',
			sort: true,
			formatter: indicatorPercent,
			classes: 'with-ellipsis center border-right',
			headerClasses: 'center border-right',
		},
		{
			dataField: 'deploy_count',
			text: 'deploy #',
			sort: true,
			classes: 'right',
			headerClasses: 'right'
		},
		{
			dataField: 'deploy_count_sparkline_data',
			text: '',
			formatter: sparklineDeploy,
			classes: 'center',
			headerClasses: 'center'
		},
		{
			dataField: 'deploy_count_p4q_percent_growth',
			text: 'p4q % growth',
			sort: true,
			formatter: indicatorPercent,
			classes: 'with-ellipsis right',
			headerClasses: 'right',
		},
	];

	return (
		<Container fluid>
			<Row className="contentDivider pb-30">
				<div className="fix-width">
					<Card className="revenue" >
						<Card.Body>
							<Card.Title>PRICING</Card.Title>
							<KPI_NEW
								totalValue={overall.total.value}
								totalUnit={overall.total.unit}
								details={overall.details}
								setkpiClicked={setkpiClicked}
								kpiClicked={kpiClicked}
								isAddText={'/kVa'}
							/>
						</Card.Body>
					</Card>
				</div>
				<Col>
					<Card className="revenue">
						<Card.Body>
							<Card.Title>PRICING TREND BY SEGMENT</Card.Title>

							{kpiClicked === 'P4Q % Growth' ?
								<>
									<Legend legend={pricing_trend_by_segments_in_p4q_percentage.legend} />
									<div style={{ height: "92%" }}>
										<LineChart
											id={`pricing_trend_by_segments_in_p4q_percentage-line_chart-segment`}
											data={pricing_trend_by_segments_in_p4q_percentage.data}
											legend={pricing_trend_by_segments_in_p4q_percentage.legend}
											isAnimationEnabled={false}
										/>
									</div>
								</>
								:
								kpiClicked === 'Y/Y Growth %' ?
									<>
										<Legend legend={pricing_trend_by_segments_in_year_percentage.legend} />
										<div style={{ height: "92%" }}>
											<LineChart
												id={`pricing_trend_by_segments_in_year_percentage-line_chart-segment`}
												data={pricing_trend_by_segments_in_year_percentage.data}
												legend={pricing_trend_by_segments_in_year_percentage.legend}
												isAnimationEnabled={false}
											/>
										</div>
									</>
									:
									kpiClicked === 'Q/Q Growth %' ?
										<>
											<Legend legend={pricing_trend_by_segments_in_quarter_percentage.legend} />
											<div style={{ height: "92%" }}>
												<LineChart
													id={`pricing_trend_by_segments_in_quarter_percentage-line_chart-segment`}
													data={pricing_trend_by_segments_in_quarter_percentage.data}
													legend={pricing_trend_by_segments_in_quarter_percentage.legend}
													isAnimationEnabled={false}
												/>
											</div>
										</>

										:
										<div style={{ height: "92%" }}>
											<Legend legend={pricing_trend_by_segments.legend} />
											<LineChart
												id={'overview-line-chart'}
												data={pricing_trend_by_segments.data}
												legend={pricing_trend_by_segments.legend}
												hideValueLabel={false}
											/>
										</div>
							}

						</Card.Body>
					</Card>
				</Col>
				<Col style={{ maxWidth: "400px" }}>
					<Card className="Segment revenue">
						<Card.Body>
							<Card.Title>PRICING RANGE BY SEGMENT</Card.Title>
							<Legend legend={pricing_range_by_segment.legend} />
							<div style={{ height: "92%" }}>
								<BoxWhiskerChart
									id='pricing-box-whisker-chart'
									data={pricing_range_by_segment.data} />
							</div>
						</Card.Body>
					</Card>
				</Col>
			</Row>
			<Row className="contentDivider pb-30">
				<div className="fix-width" style={{ minWidth: '335px' }}>
					<Card className="revenue" >
						<Card.Body>
							<Card.Title>MW SOLD</Card.Title>
							<KPI_NEW
								totalValue={overall_mw_sold.total.value}
								totalUnit={overall_mw_sold.total.unit}
								details={overall_mw_sold.details}
								setkpiClicked={setkpiClicked2}
								kpiClicked={kpiClicked2}
							/>
						</Card.Body>
					</Card>
				</div>
				<Col>
					<Card className="revenue">
						<Card.Body>
							<Card.Title>MW SOLD BY SEGMENT</Card.Title>

							{kpiClicked2 === 'P4Q % Growth' ?
								<>
									<Legend legend={mw_sold_by_segment_in_p4q_percentage.legend} />
									<div style={{ height: "92%" }}>
										<LineChart
											id={`mw_sold_by_segment_in_p4q_percentage-line_chart-segment`}
											data={mw_sold_by_segment_in_p4q_percentage.data}
											legend={mw_sold_by_segment_in_p4q_percentage.legend}
											isAnimationEnabled={false}
										/>
									</div>
								</>
								:
								kpiClicked2 === 'Y/Y Growth %' ?
									<>
										<Legend legend={mw_sold_by_segment_in_year_percentage.legend} />
										<div style={{ height: "92%" }}>
											<LineChart
												id={`mw_sold_by_segment_in_year_percentage-line_chart-segment`}
												data={mw_sold_by_segment_in_year_percentage.data}
												legend={mw_sold_by_segment_in_year_percentage.legend}
												isAnimationEnabled={false}
											/>
										</div>
									</>
									:
									kpiClicked2 === 'Q/Q Growth %' ?
										<>
											<Legend legend={mw_sold_by_segment_in_quarter_percentage.legend} />
											<div style={{ height: "92%" }}>
												<LineChart
													id={`mw_sold_by_segment_in_quarter_percentage-line_chart-segment`}
													data={mw_sold_by_segment_in_quarter_percentage.data}
													legend={mw_sold_by_segment_in_quarter_percentage.legend}
													isAnimationEnabled={false}
												/>
											</div>
										</>

										:
										<>
											<Legend legend={mw_sold_by_segment.legend} />
											<div style={{ height: "92%", marginTop: '2rem' }}>
												<StackedColumnChart
													id={`stacked_column_chart`}
													hiddenValueAxis={false}
													isOneHundredPercent={false}
													isPercentage={false}
													largerWidth={true}
													hideLabelBullet={false}
													isOverviewChart={true} 
													isGrowthPercentToggable={true}
													stackedColumnSelection={stackedColumnSelection.key}
													selectedRangeValue={selectedRangeValue}
													data={mw_sold_by_segment.data}
													legend={mw_sold_by_segment.legend}
													isAnimationEnabled={false}
												/>
											</div>
										</>
							}

						</Card.Body>
					</Card>
				</Col>
				<Col style={{ maxWidth: "400px" }}>
					<Card className="revenue">
						<Card.Body>
							<Card.Title>MW SOLD MIX % BY SEGMENT</Card.Title>
							<Legend legend={mw_sold_mix_percent_by_segment.legend} />
							<DonutChart
								id={"pricing_metro_modal_donut_chart"}
								totalTitle={"MW SOLD"}
								total={mw_sold_mix_percent_by_segment.gross_bkgs}
								data={mw_sold_mix_percent_by_segment.data}
								legend={mw_sold_mix_percent_by_segment.legend}
								isDollar={true}
								isAnimationEnabled={true}
							/>
						</Card.Body>
					</Card>
				</Col>
			</Row>
			<Row className="contentDivider">
				<Col>
					<Card className="revenue">
						<Card.Body>
							<Card.Title>SEGMENT DETAILS</Card.Title>
							<CustomTable
								tableName='segment-details scrollable'
								data={segment_details ? segment_details : []}
								defaultSorted={defaultSorted}
								columns={columns}
							/>
						</Card.Body>
					</Card>
				</Col>
			</Row>
		</Container>
	);
};

export default OverviewContent;