import React, { useState, useEffect } from 'react';
import axiosRetry from 'axios-retry';
import axios from 'axios';
import { Button, ButtonGroup, Container } from 'react-bootstrap';
import { FilterContainer, FilterModal } from '../../components/filter';
import { optionRegion, optionVertical2, optionCompanySize, optionChannel, optionRange2, optionGroup10, optionChannelInfluence } from '../../constants/filterOptions';
import { RadioDD, CheckboxDD, Separator } from '../../components/customDropdown';
import { comments } from '../../constants/comments';
import { InternalServerError, ResourceNotFoundError } from '../../components/error/Error';
import { API_BASE_URL } from '../../constants/config';
import Comment from '../../components/comment/Comment';
import Loader from '../../components/loader/Loader';
import OverviewContent from './OverviewContent';


const Overview = ({ activeTab, setActiveTab }) => {
	const [newData, setNewData] = useState([]);
	const [regionalData, setRegionalData] = useState([]);
	const [countryData, setCountryData] = useState([]);
	const [isLoading, setIsLoading] = useState(true)

	// show all filters
	const [showFilter, setShowFilter] = useState(false);
	const onShowFilter = () => {
		setShowFilter(true)
	}
	const onCloseFilter = async () => {
		await onCancelHQRegion();
		await onCancelVertical();
		await onCancelCompany();
		await onCancelChannel();
		await onCancelRange();
		await onCancelGroup();
		setShowFilter(false);
	}
	const onApplyFilter = async () => {
		await onApplyHQRegion();
		await onApplyVertical();
		await onApplyCompany();
		await onApplyChannel();
		await onApplyRange();
		await onApplyGroup();
		setShowFilter(false);
	}

	// comment area
	const [showComment, setShowComment] = useState(false);
	const onCloseComment = () => {
		setShowComment(false);
	}
	const onShowComment = () => {
		setShowComment(true)
	};

	// checkbox for HQ region values
	const [hqregion_val_onApply, set_hqregion_val_onApply] = useState([]);
	const [hqregion_val, set_hqregion_val] = useState([]);
	const onApplyHQRegion = () => {
		set_hqregion_val_onApply(hqregion_val);
	};
	const onCancelHQRegion = () => {
		set_hqregion_val(hqregion_val_onApply);
	};

	// checkbox for vertical values
	// const vertical_initial_value = optionVertical2 ? optionVertical2[0] : null;
	const [vertical_val_onApply, set_vertical_val_onApply] = useState([]);
	const [vertical_val, set_vertical_val] = useState([]);
	const onApplyVertical = () => {
		set_vertical_val_onApply(vertical_val)
	};
	const onCancelVertical = () => {
		set_vertical_val(vertical_val_onApply)
	};

	// checkbox for company-size values
	const [company_val_onApply, set_company_val_onApply] = useState([]);
	const [company_val, set_company_val] = useState([]);
	const onApplyCompany = () => {
		set_company_val_onApply(company_val);
	};
	const onCancelCompany = () => {
		set_company_val(company_val_onApply);
	};

	// radio for channel values
	const channel_initial_value = optionChannelInfluence ? optionChannelInfluence[0] : null;
	const [channel_val_onApply, set_channel_val_onApply] = useState(channel_initial_value);
	const [channel_val, set_channel_val] = useState(channel_initial_value);
	const onApplyChannel = () => {
		set_channel_val_onApply(channel_val);
	};
	const onCancelChannel = () => {
		set_channel_val(channel_val_onApply);
	};

	// radio for range values
	const range_initial_value = optionRange2 ? optionRange2[0] : null;
	const [range_val_onApply, set_range_val_onApply] = useState(range_initial_value);
	const [range_val, set_range_val] = useState(range_initial_value);
	const onApplyRange = () => {
		set_range_val_onApply(range_val);
	};
	const onCancelRange = () => {
		set_range_val(range_val_onApply);
	};

	// radio for group values
	const group_initial_value = optionGroup10 ? optionGroup10[0] : null;
	const [group_val_onApply, set_group_val_onApply] = useState(group_initial_value);
	const [group_val, set_group_val] = useState(group_initial_value);
	const onApplyGroup = () => {
		set_group_val_onApply(group_val);
	};
	const onCancelGroup = () => {
		set_group_val(group_val_onApply);
	};

	// Axios call
	const fetchData = () => {
		let url = `${API_BASE_URL}/new-logos-and-extensions/overview.json`;

		setIsLoading(true);
		axiosRetry(axios, { retries: 3 });

		axios({
			method: 'GET',
			url: url
		}).then(result => {
			const data = result.data
			setNewData(data['new-logos']);
			setRegionalData(data['regional-extension']);
			setCountryData(data['country-extension']);
			setIsLoading(false);
		}).catch(error => {
			console.log(error);
			setNewData(null)
			setRegionalData(null)
			setCountryData(null)
			setIsLoading(false);
		});
	};

	useEffect(() => {
		fetchData();
	}, []);

	if (isLoading) return <Loader withContainer />;

	return (
		<>
			<div className={`page-content ${showComment ? 'open' : 'closed'}`}>
				{(newData || regionalData || countryData)
					? newData.segment_details.length > 0 || regionalData.segment_details.length > 0 || countryData.segment_details.length > 0
						? <>
							<FilterContainer
								showComment={showComment}
								onShowFilter={onShowFilter}
								onShowComment={onShowComment}
							>
								<div className="filters custom-scroll">
									<ButtonGroup className="sub-tab">
										<Button
											variant='light'
											className={activeTab === 'new' ? 'active' : ''}
											onClick={() => { setActiveTab('new') }}>
											New Logos
										</Button>
										<Button
											variant='light'
											className={activeTab === 'regional' ? 'active' : ''}
											onClick={() => { setActiveTab('regional') }}>
											Regional Ext.
										</Button>
										<Button
											variant='light'
											className={activeTab === 'country' ? 'active' : ''}
											onClick={() => { setActiveTab('country') }}>
											Country Ext.
										</Button>
									</ButtonGroup>
									<Separator height='39px' />
									<CheckboxDD
										title="HQ Region"
										id="hq-region"
										options={optionRegion}
										checkVal={hqregion_val}
										setCheckVal={set_hqregion_val}
										onApply={onApplyHQRegion}
										onCancel={onCancelHQRegion}
									/>
									<CheckboxDD
										title="Vertical"
										id="vertical"
										options={optionVertical2}
										checkVal={vertical_val}
										setCheckVal={set_vertical_val}
										onApply={onApplyVertical}
										onCancel={onCancelVertical}
									/>
									<CheckboxDD
										title="Company Size"
										id="company-size"
										options={optionCompanySize}
										checkVal={company_val}
										setCheckVal={set_company_val}
										onApply={onApplyCompany}
										onCancel={onCancelCompany}
									/>
									<RadioDD
										title="Channel Influence"
										id="channel-modal"
										options={optionChannelInfluence}
										radioVal={channel_val}
										setRadioVal={set_channel_val}
										onApply={onApplyChannel}
										onCancel={onCancelChannel}
										initialValue={channel_initial_value}
									/>
									<RadioDD
										title="Time Period"
										id="range"
										options={optionRange2}
										radioVal={range_val}
										setRadioVal={set_range_val}
										onApply={onApplyRange}
										onCancel={onCancelRange}
										initialValue={range_initial_value}
									/>
									<RadioDD
										title="Group by"
										id="group"
										addTitleOnWide
										options={optionGroup10}
										radioVal={group_val}
										setRadioVal={set_group_val}
										onApply={onApplyGroup}
										onCancel={onCancelGroup}
										initialValue={group_initial_value}
									/>
								</div>
								<FilterModal
									title="Overview"
									setShowFilter={setShowFilter}
									showFilter={showFilter}
									onCloseFilter={onCloseFilter}
									onApplyFilter={onApplyFilter}
								>
									<CheckboxDD
										title="HQ Region"
										id="hq-region-modal"
										visibleInModal
										options={optionRegion}
										checkVal={hqregion_val}
										setCheckVal={set_hqregion_val}
									/>
									<CheckboxDD
										title="Vertical"
										id="vertical-modal"
										visibleInModal
										options={optionVertical2}
										checkVal={vertical_val}
										setCheckVal={set_vertical_val}
									/>
									<CheckboxDD
										title="Company Size"
										id="company-size-modal"
										visibleInModal
										options={optionCompanySize}
										checkVal={company_val}
										setCheckVal={set_company_val}
									/>
									<RadioDD
										title="Channel Influence"
										id="channel-modal"
										visibleInModal
										options={optionChannelInfluence}
										radioVal={channel_val}
										setRadioVal={set_channel_val}
									/>
									<RadioDD
										title="Time Period"
										id="range-modal"
										options={optionRange2}
										visibleInModal
										radioVal={range_val}
										setRadioVal={set_range_val}
									/>
									<RadioDD
										title="Group by"
										id="group-modal"
										options={optionGroup10}
										visibleInModal
										radioVal={group_val}
										setRadioVal={set_group_val}
									/>
								</FilterModal>
							</FilterContainer>
							{/* ============= Your Component here! ============= */}
							<Container fluid className={`${activeTab === 'new' ? 'd-block' : 'd-none'}`}>
								<OverviewContent
									activeTab={activeTab}
									overviewData={newData}
									tab_name='NEW LOGOS'
									selectedRangeValue={range_val_onApply.key}
									tab_id="new-overview" />
							</Container>
							<Container fluid className={`${activeTab === 'regional' ? 'd-block' : 'd-none'}`}>
								<OverviewContent
									activeTab={activeTab}
									overviewData={regionalData}
									tab_name='REGIONAL EXTENSIONS'
									selectedRangeValue={range_val_onApply.key}
									tab_id="regional-overview" />
							</Container>
							<Container fluid className={`${activeTab === 'country' ? 'd-block' : 'd-none'}`}>
								<OverviewContent
									activeTab={activeTab}
									overviewData={countryData}
									tab_name='COUNTRY EXTENSIONS'
									selectedRangeValue={range_val_onApply.key}
									tab_id="country-overview" />
							</Container>
						</>
						: <ResourceNotFoundError redirectURL={"/executive-cockpit/new-logos-and-extensions/overview"} />
					: <InternalServerError redirectURL={"/executive-cockpit/new-logos-and-extensions/overview"} />
				}
			</div>
			<Comment
				data={comments}
				onCloseComment={onCloseComment}
				showComment={showComment}
			/>
		</>
	);
};

export default Overview;