import React, { useState, useEffect } from 'react';

import PageHeader from '../../components/pageHeader/PageHeader';

import Overview from './Overview';
import Growth from './Growth';
import Land from './Land';
import Expand from './Expand';

import './newlogosandextensions.scss';


const NewLogosAndExtensions = (props) => {
	let path = props.location.pathname;
	const currentPath = path.substring(path.lastIndexOf('/') + 1);
	const [activePage, setActivePage] = useState(currentPath);

	const handleActivePage = (page) => {
		setActivePage(page)
	}

	const handleBreadcrubsPreviousLink = () => {
		setActivePage('Overview')
	}

	useEffect(() => {
		setActivePage(currentPath)
	}, [currentPath])

	const breadcrumbs = [
		{
			path: '/analytics-dashboard/page',
			label: 'ANALYTICS DASHBOARD'
		},
		{
			path: path === '/analytics-dashboard/new-logos-and-extensions/overview' ? null : '/analytics-dashboard/pipenew-logos-and-extensionsline/overview',
			label: 'New Logos and Extensions'
		},
		{
			label: activePage,
		}
	]

	const innerPage = [
		{
			path: '/analytics-dashboard/new-logos-and-extensions/overview',
			label: 'Overview'
		},
		{
			path: '/analytics-dashboard/new-logos-and-extensions/growth',
			label: 'Growth'
		},
		{
			path: '/analytics-dashboard/new-logos-and-extensions/land',
			label: 'Land'
		},
		{
			path: '/analytics-dashboard/new-logos-and-extensions/expand',
			label: 'Expand'
		}
	]

	const [activeTab, setActiveTab] = useState('new');

	return (
		<div className="new-logos-and-extensions-page">
			<PageHeader
				title="NEW LOGOS AND EXTENSIONS"
				color="dark-orange"
				innerPage={innerPage}
				breadcrumbs={breadcrumbs}
				previousPage="/analytics-dashboard/page"
				handleActivePage={handleActivePage}
				handleBreadcrubsPreviousLink={handleBreadcrubsPreviousLink}
			/>
			<div className="page-wrapper">
				{currentPath === 'overview' && <Overview activeTab={activeTab} setActiveTab={setActiveTab} />}
				{currentPath === 'growth' && <Growth activeTab={activeTab} setActiveTab={setActiveTab} />}
				{currentPath === 'land' && <Land activeTab={activeTab} setActiveTab={setActiveTab} />}
				{currentPath === 'expand' && <Expand />}
			</div>
		</div>
	);
}

export default NewLogosAndExtensions;
