import React, { useState, useEffect } from 'react';
import axiosRetry from 'axios-retry';
import axios from 'axios';
import { FilterContainer, FilterModal } from '../../components/filter';
import { ButtonGroup, Button } from 'react-bootstrap';
import { RadioDD, CheckboxDD, SearchCheckboxDD, ShowRankingDD, Separator } from '../../components/customDropdown';
import { optionRange, optionRegion, optionMetro, optionPartner, optionOwnerTerritory, optionForecastStatus } from '../../constants/filterOptions';
import { comments } from '../../constants/comments';
import { InternalServerError, ResourceNotFoundError } from '../../components/error/Error';
import { API_BASE_URL } from '../../constants/config';
import Comment from '../../components/comment/Comment';
import Loader from '../../components/loader/Loader';
import CustomerContent from './CustomerContent';


const Customer = () => {
	const [isLoading, setIsLoading] = useState(true)
	const [partnerData, setData] = useState()
	const [customersData, setCustomer] = useState([]);
	const [opportunitiesData, setOpportunities] = useState([]);
	const [activeTab, setActiveTab] = useState('customer');

	// show all filters
	const [showFilter, setShowFilter] = useState(false);
	const onShowFilter = () => {
		setShowFilter(true)
	}
	const onCloseFilter = async () => {
		await onCancelRegion();
		await onCancelMetro();
		await onCancelForecast();
		await onCancelShowRanking();
		setShowFilter(false);
	}
	const onApplyFilter = async () => {
		await onApplyRegion();
		await onApplyMetro();
		await onApplyForecast();
		await onApplyShowRanking();
		setShowFilter(false);
	}

	// comment area
	const [showComment, setShowComment] = useState(false);
	const onCloseComment = () => {
		setShowComment(false);
	};
	const onShowComment = () => {
		setShowComment(true);
	};

	// checkbox for region values
	const [region_val_onApply, set_region_val_onApply] = useState([]);
	const [region_val, set_region_val] = useState([]);
	const onApplyRegion = () => {
		set_region_val_onApply(region_val);
	};
	const onCancelRegion = () => {
		set_region_val(region_val_onApply);
	};

	// searchCheckbox for metro values
	const [metro_val_onApply, set_metro_val_onApply] = useState([]);
	const [metro_val, set_metro_val] = useState([]);
	const onApplyMetro = () => {
		set_metro_val_onApply(metro_val);
	};
	const onCancelMetro = () => {
		set_metro_val(metro_val_onApply);
	};

	// checkbox for forecast values
	const [forecast_val_onApply, set_forecast_val_onApply] = useState([]);
	const [forecast_val, set_forecast_val] = useState([]);
	const onApplyForecast = () => {
		set_forecast_val_onApply(forecast_val);
	};
	const onCancelForecast = () => {
		set_forecast_val(forecast_val_onApply);
	};

	// checkbox for owner territory values
	const [territory_val_onApply, set_territory_val_onApply] = useState([]);
	const [territory_val, set_territory_val] = useState([]);
	const onApplyTerritory = () => {
		set_territory_val_onApply(territory_val);
	};
	const onCancelTerritory = () => {
		set_territory_val(territory_val_onApply);
	};


	// for show ranking values
	const [rankingVal_onApply, setRankingVal_onApply] = useState({
		topBottom: 'radio_Top',
		numberInput: 10,
		rankingPercentile: 'radio_Ranking'
	});
	const [rankingVal, setRankingVal] = useState({
		topBottom: 'radio_Top',
		numberInput: 10,
		rankingPercentile: 'radio_Ranking'
	});
	const onApplyShowRanking = () => {
		setRankingVal_onApply(rankingVal);
	};
	const onCancelShowRanking = () => {
		setRankingVal(rankingVal_onApply);
	};

	// Axios call
	const fetchData = () => {
		let url = `${API_BASE_URL}/pipeline/customer.json`;

		setIsLoading(true);
		axiosRetry(axios, { retries: 3 });

		axios({
			method: 'GET',
			url: url
		}).then(result => {
			setData(result.data);
			setOpportunities(result.data.opportunities);
			setCustomer(result.data.customers);
			setIsLoading(false);
		}).catch(error => {
			console.log(error);
			setData(null);
			setIsLoading(false);
		});
	};

	useEffect(() => {
		fetchData();
	}, []);

	if (isLoading) return <Loader withContainer />;

	return (
		<>
			<div className={`page-content ${showComment ? 'open' : 'closed'}`}>
				{partnerData ?
					partnerData.customers.customer_ranking.by_rank.data.length > 0 ?
						<>
							<FilterContainer
								showComment={showComment}
								onShowFilter={onShowFilter}
								onShowComment={onShowComment}
							>
								<div className="filters custom-scroll">
									<ButtonGroup className="sub-tab">
										<Button
											variant='light'
											className={activeTab === 'customer' ? 'active' : ''}
											onClick={() => { setActiveTab('customer') }}>
											Customer
											</Button>
										<Button
											variant='light'
											className={activeTab === 'opportunities' ? 'active' : ''}
											onClick={() => { setActiveTab('opportunities') }}>
											Opportunities
											</Button>

									</ButtonGroup>
									<Separator height='39px' />
									<CheckboxDD
										title="Deploy Region"
										id="region"
										options={optionRegion}
										valueApplied={region_val_onApply}
										checkVal={region_val}
										setCheckVal={set_region_val}
										onApply={onApplyRegion}
										onCancel={onCancelRegion}
									/>
									<SearchCheckboxDD
										title="Deploy Metro"
										id="metro"
										options={optionMetro}
										valueApplied={metro_val_onApply}
										checkVal={metro_val}
										setCheckVal={set_metro_val}
										onApply={onApplyMetro}
										onCancel={onCancelMetro}
									/>
						  <CheckboxDD
                    id="forecast"
                    title="Forecast Status"
                    options={optionForecastStatus}
                    checkVal={forecast_val}
                    setCheckVal={set_forecast_val}
                    onApply={onApplyForecast}
                    onCancel={onCancelForecast}
                  />
                  <CheckboxDD
                    id="territory"
                    title="Owner Territory"
                    options={optionOwnerTerritory}
                    checkVal={territory_val}
                    setCheckVal={set_territory_val}
                    onApply={onApplyTerritory}
                    onCancel={onCancelTerritory}
                  />

									<ShowRankingDD
										id="show_ranking"
										rankingVal={rankingVal}
										setRankingVal={setRankingVal}
										onApply={onApplyShowRanking}
										onCancel={onCancelShowRanking}
									/>
								</div>
								<FilterModal
									title="Partner"
									setShowFilter={setShowFilter}
									showFilter={showFilter}
									onCloseFilter={onCloseFilter}
									onApplyFilter={onApplyFilter}
								>
									<CheckboxDD
										visibleInModal
										title="Deploy Region"
										id="region-modal"
										options={optionRegion}
										checkVal={region_val}
										setCheckVal={set_region_val}
									/>
									<SearchCheckboxDD
										visibleInModal
										title="Deploy Metro"
										id="metro-modal"
										options={optionMetro}
										checkVal={metro_val}
										setCheckVal={set_metro_val}
									/>
								 <CheckboxDD
                    id="forecast-modal"
                    title="Forecast Status"
                    visibleInModal
                    options={optionForecastStatus}
                    checkVal={forecast_val}
                    setCheckVal={set_forecast_val}
                    onApply={onApplyForecast}
                    onCancel={onCancelForecast}
                  />
                  <CheckboxDD
                    id="territory-modal"
                    title="Owner Territory"
                    visibleInModal
                    options={optionOwnerTerritory}
                    checkVal={territory_val}
                    setCheckVal={set_territory_val}
                    onApply={onApplyTerritory}
                    onCancel={onCancelTerritory}
                  />
									<ShowRankingDD
										visibleInModal
										title="Show Ranking"
										id="show_ranking-modal"
										rankingVal={rankingVal}
										setRankingVal={setRankingVal}
									/>
								</FilterModal>
							</FilterContainer>
							{/* ============= Your Component here! ============= */}
							<CustomerContent activeTab={activeTab} customerData={activeTab === 'customer' ? customersData : opportunitiesData} />
						</>
						: <ResourceNotFoundError redirectURL={"/executive-cockpit/channel-bookings-revenue/partner"} />
					: <InternalServerError redirectURL={"/executive-cockpit/channel-bookings-revenue/partner"} />}
			</div>
			<Comment
				data={comments}
				onCloseComment={onCloseComment}
				showComment={showComment}
			/>
		</>
	);
};

export default Customer;
