import React, {useState} from 'react';
import CustomTable, { formatLongText } from '../../../components/customTable/CustomTable';
import { BasicDD } from '../../../components/customDropdown';


const LighthouseListTable = ({
  data,
  tableTitle
}) => {

  // basic for show-by values
	const option_show_by = [{
		key: 'show_g2000',
		text: 'Show G2000'
	},
	{
		key: 'show_f500',
		text: 'Show F500'
	}];
	const show_initial_value = option_show_by ? option_show_by[0] : null;
	const [show_val, set_show_val] = useState(show_initial_value);

  // table options
	const textFormat = (cell, row) => {
		return formatLongText(cell, row)
  };

  const rankFormat = (cell, row, rowIndex, formatExtraData) => {
    return (
      <span>{rowIndex + 1}</span>
    );
  }
  
  const tagsFormat = (cell, row) => {
		return (
      <ul className="tags-list">
        {cell && cell.map((item)=>{
          let new_color = item.color.replace(/#/g, '')
          return(
            <li key={item.color} className={`color-${new_color}`}>
              {item.value}
            </li>
          )
        })}
      </ul>
    )
	};

	const defaultSorted = [{
		dataField: 'customer',
		order: 'desc',
	}];

	const columns = [
		{
			dataField: '_id',
			text: 'id',
			hidden: true
    },
    {
			dataField: '',
			text: 'RANK',
      formatter: rankFormat,
      classes: 'rank center',
      headerClasses: 'rank center'
    },
		{
			dataField: 'customer',
			text: 'CUSTOMER',
      sort: true,
      formatter: textFormat,
      classes: 'customer',
      headerClasses: 'customer'
    },
    {
			dataField: 'vertical',
			text: 'VERTICAL',
      sort: true,
      formatter: textFormat,
		},
		{
			dataField: 'hq_region',
			text: 'HQ REGION',
      sort: true,
      formatter: textFormat,
    },
    {
			dataField: 'type',
			text: 'TYPE',
      sort: true,
      formatter: textFormat,
		},
    {
			dataField: 'tags',
			text: 'TAGS',
      formatter: tagsFormat,
      classes: 'tags-cell',
      headerClasses: 'tags-cell'
		},
	];

	return (
    <>
        <CustomTable
          tableName='notable-table scrollable'
          className='notable-table-area'
          tableTitle={tableTitle}
          addSearch
          addFilters={<>
            <BasicDD
              id="show-notable"
              options={option_show_by}
              value={show_val}
              setValue={set_show_val}
            />
          </>}
          data={data ? data : []}
          defaultSorted={defaultSorted}
          columns={columns}
        />  
    </>
	);
};

export default LighthouseListTable;