import React, { useState } from 'react';
import { Container, Row, Col, Card } from 'react-bootstrap';
import { ImArrowUpRight2, ImArrowDownRight2 } from "react-icons/im";
import { convertToPercent, convertToCount, convertToUSDGrowth } from '../../constants/formatter';
import CustomTable, { formatLongText } from '../../components/customTable/CustomTable';
import Legend from '../../components/legend/Legend';
import KPI_NEW from '../../components/kpi/KPI_NEW';
import LineChart from '../../components/chart/LineChart';
import ExpandModal from './ExpandModal';

const ExpandContent = (props) => {
	const { expandData } = props;
	const [idState, setReferenceID] = useState(null);
	const [modalShow, setModalShow] = useState(false);
	const [modalData, setModalData] = useState(null);
	const [toggleSelectedRow, setToggleSelectedRow] = useState();
	const [selectedCustomer, setCustomerSelected] = useState(null);

	// Data Distribution
	const overall = expandData ? expandData.overall : [];
	const by_rank = expandData ? expandData.customer_ranking.by_rank : [];
	const by_rank_id = expandData ? expandData.customer_ranking.by_rank_id : [];
	const segment_details = expandData ? expandData.segment_details : [];
	const multi_metro_count_by_segment = expandData ? expandData.multi_metro_count_by_segment : [];

	const [kpiClicked, setkpiClicked] = useState(null);

	// Table Config
	const indicatorPercent = (cell, row) => {
		let bgColor = cell >= 0.5 ? 'indicator-up' : cell <= -0.5 ? 'indicator-down' : 'indicator-middle';
		let icon = cell >= 0.5 ? <ImArrowUpRight2 /> : cell <= -0.5 ? <ImArrowDownRight2 /> : <span>&nbsp;</span>;
		return (
			<span className={`indicator ${bgColor}`}>
				{icon}
				{convertToPercent(cell)}
			</span>
		);
	};

	const customerFormat = (cell, row) => {
		return formatLongText(cell, row)
	}

	const rankFormat = (cell, row, rowIndex, formatExtraData) => {
		return <span>{rowIndex + 1}</span>
	};

	const formatToCount = (cell, row) => {
		return <span>{convertToCount(cell)}</span>
	};

	const formatUSDGrowth = (cell, row) => {
		return (
			<span>
				{convertToUSDGrowth(cell)}
			</span>
		);
	};

	const handleOnSelect = (row, isSelect, rowIndex) => {
		let modalDataby_rank;

		setToggleSelectedRow(row._id)
		setCustomerSelected(row.customer);

		for (const key in by_rank_id) {
			const element = by_rank_id[key];
			if (element._id === row._id) {
				modalDataby_rank = element
			}
		}

		if (isSelect) {
			setModalShow(true);
			setReferenceID(row._id);
			setModalData(modalDataby_rank);
		}
	};

	const selectRow = {
		mode: 'radio',
		clickToSelect: true,
		hideSelectColumn: true,
		onSelect: handleOnSelect,
		classes: 'selected',
		bgColor: '#ecf2ff'
	};

	const onCloseModal = async () => {
		await setModalShow(false)
		await setCustomerSelected(null);
		await setReferenceID(null);
		await setToggleSelectedRow()

		setTimeout(() => {
			setModalData(null);
		}, 1000);
	};

	const rowClasses = (row) => {
		let classes = row._id === toggleSelectedRow ? 'selected' : 'selectable';
		return classes
	};

	const defaultSorted = [{
		dataField: 'initial_metro_count',
		order: 'desc',
	}];

	const columns = [
		{
			dataField: '_id',
			text: 'id',
			hidden: true
		},
		{
			dataField: '',
			text: 'Rank',
			formatter: rankFormat,
			classes: 'rank center',
			headerClasses: 'rank center',
		},
		{
			dataField: 'customer',
			text: 'CUSTOMER',
			sort: true,
			formatter: customerFormat,
		},
		{
			dataField: 'initial_metro_count',
			text: 'INITAL METRO #',
			sort: true,
			classes: 'right',
			headerClasses: 'right',
			formatter: formatToCount
		},
		{
			dataField: 'ending_metro_count',
			text: 'ENDING METRO #',
			sort: true,
			formatter: formatToCount,
			classes: 'center',
			headerClasses: 'center'
		},
		{
			dataField: '4_qtr_percent_growth',
			text: '4QTR % GROWTH',
			sort: true,
			formatter: indicatorPercent,
			classes: 'with-ellipsis center',
			headerClasses: 'center',
		},
		{
			dataField: '4_qtr_usd_growth',
			text: '4QTR $ GROWTH',
			sort: true,
			formatter: formatUSDGrowth,
			classes: 'center',
			headerClasses: 'center'
		},
		{
			dataField: '6_qtr_percent_growth',
			text: '6QTR % GROWTH',
			sort: true,
			formatter: indicatorPercent,
			classes: 'with-ellipsis center',
			headerClasses: 'center',
		},
		{
			dataField: '6_qtr_usd_growth',
			text: '6QTR $ GROWTH',
			sort: true,
			formatter: formatUSDGrowth,
			classes: 'right',
			headerClasses: 'right'
		},
	];

	const columnsSegment = [
		{
			dataField: '_id',
			text: 'id',
			hidden: true
		},
		{
			dataField: 'segment',
			text: 'SEGMENT',
			sort: true,
			formatter: customerFormat,
		},

		{
			dataField: 'new_logos_count',
			text: 'NEW LOGOS #',
			sort: true,
			classes: 'right',
			headerClasses: 'right',
			formatter: formatToCount
		},

		{
			dataField: 'mix_percent',
			text: 'MIX %',
			sort: true,
			classes: 'center',
			headerClasses: 'center'
		},
		{
			dataField: 'initial_metro_count',
			text: 'INITAL METRO #',
			sort: true,
			classes: 'center',
			headerClasses: 'center'
		},
		{
			dataField: 'ending_metro_count',
			text: 'ENDING METRO #',
			sort: true,
			classes: 'center',
			headerClasses: 'center'
		},
		{
			dataField: '4_qtr_percent_growth',
			text: '4QTR % GROWTH',
			sort: true,
			formatter: indicatorPercent,
			classes: 'with-ellipsis center',
			headerClasses: 'center',
		},
		{
			dataField: '4_qtr_usd_growth',
			text: '4QTR $ GROWTH',
			sort: true,
			classes: 'right',
			headerClasses: 'right',
			formatter: formatUSDGrowth
		},
		{
			dataField: '6_qtr_percent_growth',
			text: '6QTR % GROWTH',
			sort: true,
			formatter: indicatorPercent,
			classes: 'with-ellipsis center',
			headerClasses: 'center',
		},
		{
			dataField: '6_qtr_usd_growth',
			text: '6QTR $ GROWTH',
			sort: true,
			classes: 'right',
			headerClasses: 'right',
			formatter: formatUSDGrowth
		},
	];

	return (
		<Container fluid>
			<ExpandModal
				show={modalShow}
				onClose={onCloseModal}
				data={modalData}
				selectedCustomer={selectedCustomer}
			/>
			<Row className="contentDivider">
				<Col md={6}>
					<Row className="pb-30">
						<div className="fix-width">
							<Card className="revenue-card">
								<Card.Body>
									<Card.Title>EXPAND $ MRC</Card.Title>
									<KPI_NEW
										totalValue={overall.total.value}
										details={overall.details}
										setkpiClicked={setkpiClicked}
										kpiClicked={kpiClicked}
										totalUnit={overall.total.unit}
									/>
								</Card.Body>
							</Card>
						</div>
						<Col>
							<Card className="revenue-card">
								<Card.Body>
									<Card.Title>MULTI-METRO COUNT BY SEGMENT</Card.Title>
									<>
										<Legend legend={multi_metro_count_by_segment.legend} />
										<div style={{ height: "92%" }}>
											<LineChart
												id={`multi_metro_count_by_segment-line_chart-segment`}
												data={multi_metro_count_by_segment.data}
												legend={multi_metro_count_by_segment.legend}
												isAnimationEnabled={false}
											/>
										</div>
									</>
								</Card.Body>
							</Card>
						</Col>
					</Row>
					<Row>
						<Col md={12}>
							<Card className="revenue-card">
								<Card.Body>
									<Card.Title>SEGMENT DETAILS</Card.Title>
									<div style={{ height: '250px' }}>
										<CustomTable
											tableName='segment-details-expand scrollable'
											data={segment_details ? segment_details : []}
											defaultSorted={defaultSorted}
											columns={columnsSegment}
										/>
									</div>
								</Card.Body>
							</Card>
						</Col>
					</Row>
				</Col>
				<Col md={6}>
					<Card className="revenue-card">
						<Card.Body>
							<CustomTable
								tableTitle="CUSTOMER RANKING"
								tableName='customer-ranking scrollable'
								data={by_rank.data ? by_rank.data : []}
								defaultSorted={defaultSorted}
								rowClasses={rowClasses}
								selectRow={selectRow}
								columns={columns}
								addHover
								addSearch
							/>
						</Card.Body>
					</Card>
				</Col>
			</Row>
		</Container>
	);
};

export default ExpandContent;