import React from 'react';
import { Container, Row, Col, Card } from 'react-bootstrap';
import { ImArrowUpRight2, ImArrowDownRight2 } from "react-icons/im";

import StackedColumnChart from '../../components/chart/StackedColumnChart';
import SparklineCustom from '../../components/chart/SparklineCustom';
import DonutChart from '../../components/chart/DonutChart';
import LineChart from '../../components/chart/LineChart';
import MapChart from '../../components/chart/MapChart';
import KPI_NEW from '../../components/kpi/KPI_NEW';
import Legend from '../../components/legend/Legend';

import CustomTable from '../../components/customTable/CustomTable';
import { convertToUSDGrowth, convertToPercent } from '../../constants/formatter';


const InterconnectionContent = (props) => {
  const { interconnectionData } = props;

  const formatToPercent = (cell, row) => {
    return (
      <span>
        {convertToPercent(cell)}
      </span>
    );
  }

  const formatUSDGrowth = (cell, row) => {
    return (
      <span>
        {convertToUSDGrowth(cell)}
      </span>
    );
  };
  // Start Table
  const indicatorUp = (cell, row) => {
    return (
      <span className="indicator indicator-up">
        <ImArrowUpRight2 />
        {cell}
      </span>
    );
  }

  const indicatorDown = (cell, row) => {
    return (
      <span className="indicator indicator-down">
        <ImArrowDownRight2 />
        {cell}
      </span>
    );
  }

  const defaultSorted = [
    {
      dataField: 'segment',
      order: 'desc',
    }
  ];

  const columns = [
    {
      dataField: '_id',
      text: 'id',
      hidden: true
    },
    {
      dataField: 'segment',
      text: 'SEGMENT',
      
      
      classes: 'segment-th',
      headerClasses: 'segment-td'
    },
    {
      dataField: 'ic_billing_usd',
      text: 'IC BILLING $',
      sort: true,
      classes: 'right',
      headerClasses: 'right',
      formatter: formatUSDGrowth
    },
    {
      dataField: 'mix_percent',
      text: 'MRR MIX%',
      sort: true,
      classes: 'right',
      headerClasses: 'right',
      formatter: formatToPercent
    },
    {
      dataField: 'y_by_y_growth_percent',
      text: 'Y/Y % GROWTH',
      sort: true,
      formatter: indicatorUp,
      classes: 'right',
      headerClasses: 'right'
    },
    {
      dataField: 'y_to_y_growth_usd',
      text: 'Y-Y $ GROWTH',
      sort: true,
      classes: 'right',
      headerClasses: 'right',
      formatter: formatUSDGrowth
    },
    {
      dataField: 'q_by_q_growth_percent',
      text: 'Q/Q % GROWTH',
      sort: true,
      formatter: indicatorDown,
      classes: 'right',
      headerClasses: 'right'
    },
    {
      dataField: 'q_to_q_growth_usd',
      text: 'Q-Q $ GROWTH',
      sort: true,
      classes: 'right',
      headerClasses: 'right',
      formatter: formatUSDGrowth
    }
  ]
  // End Table
  // Start Table

  const sparkline = (cell, row) => {
    return (
      <SparklineCustom loadedData={cell} />
    );
  };

  
  const rankFormat = (cell, row, rowIndex, formatExtraData) => {
    return (
      <span>{rowIndex + 1}</span>
    );
  };
  

  // TODO - Handle row click for modal window pop-up feature
  // For Selecting Row ===
  // const handleOnSelect = (row, isSelect, rowIndex) => {
  //   let modalDataByRank;
  //   for (const key in by_rank_id) {
  //     const element = by_rank_id[key];
  //     if (element._id === row._id) {
  //       modalDataByRank = element
  //     }
  //   }
  //   if (isSelect) {
  //     setModalShow(true);
  //     setReferenceID(row._id);
  //     setModalData(modalDataByRank);
  //   }
  // }
  // const selectRowMetro = {
  //   mode: 'radio',
  //   clickToSelect: true,
  //   hideSelectColumn: true,
  //   onSelect: handleOnSelect,
  //   classes: 'selected-row',
  //   bgColor: '#ecf2ff'
  // };

  const metro = (cell, row) => {
    return (
      <>
        <img className="flag-icon" src={cell.image_url} alt="sample flag" />
        <span className="body-name">{cell.name}</span>
      </>
    );
  };

  const rowClasses = () => {
    let classes = 'selectable';
    return classes
  };
  // For Selecting Row ===

  
  const defaultSortedMetro = [
    {
      dataField: 'mix_percent',
      order: 'desc',
    }
  ];
  

  const metroColumns = [
    {
      dataField: '_id',
      text: 'id',
      hidden: true
    },
    {
      dataField: '',
      text: 'Rank',
      classes: 'rank center',
      headerClasses: 'rank center',
      formatter: rankFormat,
    },
    {
      dataField: 'metro',
      text: 'Metro',
      
      
      classes: 'metro',
      headerClasses: 'metro',
      formatter: metro
    },
    {
      dataField: 'ic_billing_usd',
      text: 'IC BILLING $',
      sort: true,
      classes: 'right',
      headerClasses: 'right',
      formatter: formatUSDGrowth
    },
    {
      dataField: 'mix_percent',
      text: 'MIX%',
      sort: true,
      classes: 'right',
      headerClasses: 'right',
      formatter: formatToPercent
    },
    {
      dataField: 'sparkline_data',
      text: '',
      classes: 'sparkline-cell center',
      headerClasses: 'sparkline-cell center',
      formatter: sparkline
    },
    {
      dataField: 'y_by_y_growth_percent',
      text: 'Y/Y % GROWTH',
      sort: true,
      formatter: indicatorUp,
      classes: 'right',
      headerClasses: 'right'
    },
    {
      dataField: 'y_to_y_usd_growth',
      text: 'Y-Y $GROWTH',
      sort: true,
      classes: 'right',
      headerClasses: 'right',
      formatter: formatUSDGrowth
    },
    {
      dataField: 'usd_per_ic',
      text: '$ PER IC',
      sort: true,
      classes: 'right',
      headerClasses: 'right',
      formatter: formatUSDGrowth
    }
  ]
  // End Table

  return (
    <Container className="interconnection" fluid>
      <Row className="contentDivider pb-30">
        <div className="fix-width">
          <Card className="TotalRevenue revenue" >
            <Card.Body>
                <Card.Title>{interconnectionData.overall.total.label}</Card.Title>
                <KPI_NEW
                    totalValue={interconnectionData.overall.total.value}
                    totalUnit={interconnectionData.overall.total.unit}
                    details={interconnectionData.overall.details}
                />
            </Card.Body>
          </Card>
        </div>
        <Col>
          <Card className="RevenueByType revenue">
            <Card.Body>
              <Card.Title>total interconnections by segment</Card.Title>
              {interconnectionData.total_interconnections_by_segment ?
                <>
                  <Legend legend={interconnectionData.total_interconnections_by_segment.legend} />
                  <div style={{ height: "400px" }}>
                    <StackedColumnChart
                      id="total_interconnections_by_segment_stacked_column_chart"
                      hiddenValueAxis={false}
                      isOneHundredPercent={false}
                      isPercentage={false}
                      largerWidth={true}
                      hideLabelBullet={false}
                      data={interconnectionData.total_interconnections_by_segment.data}
                      legend={interconnectionData.total_interconnections_by_segment.legend}
                      isAnimationEnabled={true}
                    />
                  </div>
                </>
                : null}
            </Card.Body>
          </Card>
        </Col>
        <Col style={{ maxWidth: "400px" }}>
          <Card className="Segment revenue">
            <Card.Body>
              <Card.Title>total interconnections mix</Card.Title>
              {interconnectionData.total_interconnections_mix ? <>
                <Legend legend={interconnectionData.total_interconnections_mix.legend} />
                <div style={{ height: "350px" }}>
                  <DonutChart
                    id={"total_interconnections_mix_donut_chart"}
                    totalTitle={"TOTAL IC $"}
                    total={interconnectionData.total_interconnections_mix.total_ic_usd}
                    data={interconnectionData.total_interconnections_mix.data}
                    legend={interconnectionData.total_interconnections_mix.legend}
                    isDollar={true}
                    isAnimationEnabled={true}
                  />
                </div>
              </> : null}
            </Card.Body>
          </Card>
        </Col>
      </Row>
      <Row className="contentDivider" style={{ paddingBottom: '30px' }}>
        <Col>
          <Card className="SegmentDetails revenue">
            <Card.Body>
              <Card.Title>segment details</Card.Title>
              {interconnectionData.segment_details ?
                <CustomTable
                  tableName='segment-details'
                  data={interconnectionData.segment_details === undefined ? [] : interconnectionData.segment_details}
                  defaultSorted={defaultSorted}
                  columns={columns}
                />
                : null}
            </Card.Body>
          </Card>
        </Col>
        <Col style={{ maxWidth: "400px" }}>
          <Card className="SegmentTrends revenue">
            <Card.Body>
              <Card.Title>segment y/y growth trends</Card.Title>
              {interconnectionData.segment_y_by_y_growth_trends ? <>
                <Legend legend={interconnectionData.segment_y_by_y_growth_trends.legend} />
                <div style={{ height: "300px" }}>
                  <LineChart
                    id={"segment_y_by_y_growth_trends_line_chart"}
                    data={interconnectionData.segment_y_by_y_growth_trends.data}
                    legend={interconnectionData.segment_y_by_y_growth_trends.legend}
                    isAnimationEnabled={true}
                  />
                </div>
              </> : null}
            </Card.Body>
          </Card>
        </Col>
      </Row>
      <Row className="contentDivider" style={{ paddingBottom: '30px' }}>
        <Col>
          <Card className="RevenueByType revenue">
            <Card.Body>
              <Card.Title>average ic per cab</Card.Title>
              {interconnectionData.average_ic_per_cab ? <>
                <Legend legend={interconnectionData.average_ic_per_cab.legend} />
                <div style={{ height: "300px" }}>
                  <LineChart
                    id={"average_ic_per_cab_line_chart"}
                    data={interconnectionData.average_ic_per_cab.data}
                    legend={interconnectionData.average_ic_per_cab.legend}
                    isAnimationEnabled={true}
                  />
                </div>
              </> : null}
            </Card.Body>
          </Card>
        </Col>
        <Col>
          <Card className="RevenueByType revenue">
            <Card.Body>
              <Card.Title>average $ per ic</Card.Title>
              {interconnectionData.average_usd_per_ic ? <>
                <Legend legend={interconnectionData.average_usd_per_ic.legend} />
                <div style={{ height: "300px" }}>
                  <LineChart
                    id={"average_usd_per_ic_line_chart"}
                    data={interconnectionData.average_usd_per_ic.data}
                    legend={interconnectionData.average_usd_per_ic.legend}
                    isAnimationEnabled={true}
                  />
                </div>
              </> : null}
            </Card.Body>
          </Card>
        </Col>
        <Col>
          <Card className="RevenueByType revenue">
            <Card.Body>
              <Card.Title>ic per interconnecting vertical</Card.Title>
              {interconnectionData.ic_per_interconnecting_vertical ? <>
                <Legend legend={interconnectionData.ic_per_interconnecting_vertical.legend} />
                <div style={{ height: "300px" }}>
                  <StackedColumnChart
                    id="ic_per_interconnecting_vertical_stacked_column_chart"
                    hiddenValueAxis={true}
                    isOneHundredPercent={true}
                    isPercentage={true}
                    largerWidth={true}
                    hideLabelBullet={false}
                    data={interconnectionData.ic_per_interconnecting_vertical.data}
                    legend={interconnectionData.ic_per_interconnecting_vertical.legend}
                    isAnimationEnabled={true}
                  />
                </div>
              </> : null}
            </Card.Body>
          </Card>
        </Col>
      </Row>
      <Row className="contentDivider">
        <Col>
          <Card className="revenue">
            <Card.Body>
              <Card.Title>deployment map</Card.Title>
              {interconnectionData.total_interconnections_map ? <>
                <Legend legend={interconnectionData.total_interconnections_map.legend} />
                <div style={{ height: "400px ", paddingTop: "2rem" }}>
                  <MapChart
                    id={"total_interconnections_map_chart"}
                    largerWidth={true}
                    data={interconnectionData.total_interconnections_map.data}
                  />
                </div>
              </> : null}
            </Card.Body>
          </Card>
        </Col>
        <Col>
          <Card className="revenue">
            <Card.Body>
              <Card.Title>metro ranking</Card.Title>
              {interconnectionData.metro_ranking ?
                <CustomTable
                  tableName='metro-ranking-interconnection scrollable'
                  data={interconnectionData.metro_ranking.by_rank === undefined ? [] : interconnectionData.metro_ranking.by_rank}
                  // TODO - Handle row click for modal window pop-up feature
                  // selectRow={selectRowMetro} // For Selecting Row ===
                  defaultSorted={defaultSortedMetro} 
                  rowClasses={rowClasses}
                  columns={metroColumns}
                />
                : null}
            </Card.Body>
          </Card>
        </Col>
      </Row>
    </Container>
  );
};


export default InterconnectionContent;