import React from 'react';

import { Table } from 'react-bootstrap';
import { convertToCount } from '../../constants/formatter';
import './kpi.scss';

const KpiHorizontal = (props) => {
  const { details } = props;

  return(
    <Table responsive className="metro-table-modal scrollable">
      < thead >
        <tr>
          {details.map((item, i) => (<th key={i}>{ item.label }</th>))}
        </tr>
      </thead>
      <tbody>
        <tr>
          {details.map((item, i) => (<td key={i}>{ convertToCount(item.value) }</td>))}
        </tr>
      </tbody>
    </Table>
  );
}
export default KpiHorizontal;