import React from 'react';
import { Container, Row, Col, Card } from 'react-bootstrap';
import { ImArrowUpRight2, ImArrowDownRight2 } from "react-icons/im";

import CustomTable from '../../components/customTable/CustomTable';
import StackedColumnChart from '../../components/chart/StackedColumnChart';
import DonutChart from '../../components/chart/DonutChart';
import LineChart from '../../components/chart/LineChart';
import KPI_NEW from '../../components/kpi/KPI_NEW';
import Legend from '../../components/legend/Legend';

import { convertToPercent, convertToUSDGrowth } from '../../constants/formatter';


const BookingsContent = (props) => {
  const { bookingsData } = props;

  let by_segment = bookingsData.by_segment;
  let by_vertical = bookingsData.by_vertical;
  // Start Table
  const indicatorPercent = (cell, row) => {
    let bgColor = cell >= 0.5 ? 'indicator-up' : cell <= -0.5 ? 'indicator-down' : 'indicator-middle';
    let icon = cell >= 0.5 ? <ImArrowUpRight2 /> : cell <= -0.5 ? <ImArrowDownRight2 /> : <span className="mr-3"></span>;

    return (
      <>
        <span className={`indicator ${bgColor}`}>
          {icon}
          {convertToPercent(cell)}
        </span>
      </>
    );
  }

  const formatToPercent = (cell, row) => {
    return (
      <span>
        {convertToPercent(cell)}
      </span>
    );
  }

  const formatToUSDGrowth = (cell, row) => {
    return (
      <span>
        {convertToUSDGrowth(cell)}
      </span>
    );
  }

  const defaultSorted = [
    {
      dataField: 'bkgs',
      order: 'desc',
    }
  ];

  const columns = [
    {
      dataField: '_id',
      text: 'id',
      hidden: true
    },
    {
      dataField: 'segment',
      text: 'SEGMENT',


      classes: 'segment-th',
      headerClasses: 'segment-td'
    },
    {
      dataField: 'bkgs',
      text: 'BKGS',
      sort: true,
      classes: 'right',
      headerClasses: 'right',
      formatter: formatToUSDGrowth
    },
    {
      dataField: 'mix_percent',
      text: 'MRR MIX%',
      sort: true,
      classes: 'right',
      headerClasses: 'right',
      formatter: formatToPercent
    },
    {
      dataField: 'y_by_y_growth_percent',
      text: 'Y/Y % GROWTH',
      sort: true,
      formatter: indicatorPercent,
      classes: 'with-ellipsis right',
      headerClasses: 'right'
    },
    {
      dataField: 'y_to_y_growth_usd',
      text: 'Y-Y $ GROWTH',
      sort: true,
      classes: 'right',
      headerClasses: 'right',
      formatter: formatToUSDGrowth
    },
    {
      dataField: 'q_by_q_growth_percent',
      text: 'Q/Q % GROWTH',
      sort: true,
      formatter: indicatorPercent,
      classes: 'with-ellipsis right',
      headerClasses: 'right'
    },
    {
      dataField: 'q_to_q_growth_usd',
      text: 'Q-Q $ GROWTH',
      sort: true,
      classes: 'right',
      headerClasses: 'right',
      formatter: formatToUSDGrowth
    }
  ]
  // End Table

  const defaultSortedVertical = [
    {
      dataField: 'bkgs',
      order: 'desc',
    }
  ];

  const columnsVertical = [
    {
      dataField: '_id',
      text: 'id',
      hidden: true
    },
    {
      dataField: 'segment',
      text: 'SEGMENT',


      classes: 'segment-th',
      headerClasses: 'segment-td'
    },
    {
      dataField: 'bkgs',
      text: 'BKGS',
      sort: true,
      classes: 'right',
      headerClasses: 'right',
      formatter: formatToUSDGrowth
    },
    {
      dataField: 'mix_percent',
      text: 'MRR MIX%',
      sort: true,
      classes: 'right',
      headerClasses: 'right',
      formatter: formatToPercent
    },
    {
      dataField: 'y_by_y_growth_percent',
      text: 'Y/Y % GROWTH',
      sort: true,
      formatter: indicatorPercent,
      classes: 'with-ellipsis right',
      headerClasses: 'right'
    },
    {
      dataField: 'y_to_y_growth_usd',
      text: 'Y-Y $ GROWTH',
      sort: true,
      classes: 'right',
      headerClasses: 'right',
      formatter: formatToUSDGrowth
    },
    {
      dataField: 'q_by_q_growth_percent',
      text: 'Q/Q % GROWTH',
      sort: true,
      formatter: indicatorPercent,
      classes: 'with-ellipsis right',
      headerClasses: 'right'
    },
    {
      dataField: 'q_to_q_growth_usd',
      text: 'Q-Q $ GROWTH',
      sort: true,
      classes: 'right',
      headerClasses: 'right',
      formatter: formatToUSDGrowth
    }
  ]

  return (
    <Container className="bookings" fluid>
      <Row className="contentDivider pb-30">
        <div className="fix-width">
          <Card className="TotalRevenue revenue" >
            <Card.Body>
              <Card.Title>{by_segment.overall.total.label}</Card.Title>
              <KPI_NEW
                totalValue={by_segment.overall.total.value}
                totalUnit={by_segment.overall.total.unit}
                details={by_segment.overall.details}
              />
            </Card.Body>
          </Card>
        </div>
        <Col>
          <Card className="RevenueByType revenue">
            <Card.Body>
              <Card.Title>gross bookings by segment</Card.Title>
              <Legend legend={by_segment.total_revenue_by_revenue_type.legend} />
              <div style={{ height: "400px" }}>
                <StackedColumnChart
                  id="total_revenue_by_revenue_type_stacked_column_chart"
                  hiddenValueAxis={false}
                  isOneHundredPercent={false}
                  isPercentage={false}
                  largerWidth={true}
                  hideLabelBullet={false}
                  data={by_segment.total_revenue_by_revenue_type.data}
                  legend={by_segment.total_revenue_by_revenue_type.legend}
                  isAnimationEnabled={true}
                />
              </div>
            </Card.Body>
          </Card>
        </Col>
        <Col style={{ maxWidth: "400px" }}>
          <Card className="Segment revenue">
            <Card.Body>
              <Card.Title>gross bookings mix</Card.Title>
              <Legend legend={by_segment.segment_revenue_mix.legend} />
              <div style={{ height: "350px" }}>
                <DonutChart
                  id={"total_revenue_overview_donut_chart"}
                  totalTitle={"GROSS BOOKINGS"}
                  total={by_segment.segment_revenue_mix.customer_revenue}
                  data={by_segment.segment_revenue_mix.data}
                  legend={by_segment.segment_revenue_mix.legend}
                  isDollar={true}
                  isAnimationEnabled={true}
                />
              </div>
            </Card.Body>
          </Card>
        </Col>
      </Row>
      <Row className="contentDivider">
        <Col>
          <Card className="SegmentDetails revenue">
            <Card.Body>
              <Card.Title>segment details</Card.Title>
              <CustomTable
                tableName='segment-details'
                data={by_segment.segment_details === undefined ? [] : by_segment.segment_details}
                defaultSorted={defaultSorted}
                columns={columns}
              />
            </Card.Body>
          </Card>
        </Col>
        <Col style={{ maxWidth: "400px" }}>
          <Card className="SegmentTrends revenue">
            <Card.Body>
              <Card.Title>segment y/y growth trends</Card.Title>
              <Legend legend={by_segment.segment_trends.legend} />
              <div style={{ height: "300px" }}>
                <LineChart
                  id={"total_revenue_overview_line_chart"}
                  data={by_segment.segment_trends.data}
                  legend={by_segment.segment_trends.legend}
                  isAnimationEnabled={true}
                />
              </div>
            </Card.Body>
          </Card>
        </Col>
      </Row>

      <Row className="contentDivider" style={{ paddingBottom: "30px", paddingTop: "30px" }}>
        <div className="fix-width" style={{ minWidth: '335px' }}>
          <Card className="TotalRevenue revenue" >
            <Card.Body>
              <Card.Title>{by_vertical.overall.total.label}</Card.Title>
              <KPI_NEW
                totalSubLabel={by_vertical.overall.total.sub_label}
                totalValue={by_vertical.overall.total.value}
                totalUnit={by_vertical.overall.total.unit}
                details={by_vertical.overall.details}
              />
            </Card.Body>
          </Card>
        </div>
        <Col>
          <Card className="RevenueByType revenue">
            <Card.Body>
              <Card.Title> bookings by segment</Card.Title>
              <Legend legend={by_vertical.total_revenue_by_revenue_type.legend} />
              <div style={{ height: "400px" }}>
                <StackedColumnChart
                  id="by_vertical_total_revenue_by_revenue_type_stacked_column_chart"
                  hiddenValueAxis={false}
                  isOneHundredPercent={false}
                  isPercentage={false}
                  largerWidth={true}
                  hideLabelBullet={true}
                  data={by_vertical.total_revenue_by_revenue_type.data}
                  legend={by_vertical.total_revenue_by_revenue_type.legend}
                  isAnimationEnabled={true}
                />
              </div>
            </Card.Body>
          </Card>
        </Col>
        <Col style={{ maxWidth: "400px" }}>
          <Card className="Segment revenue">
            <Card.Body>
              <Card.Title>bookings mix</Card.Title>
              <Legend legend={by_vertical.segment_revenue_mix.legend} />
              <div style={{ height: "350px" }}>
                <DonutChart
                  id={"by_vertical_total_revenue_overview_donut_chart"}
                  totalTitle={"% OF NET BOOKINGS"}
                  total={by_vertical.segment_revenue_mix.percent_of_net_bookings}
                  data={by_vertical.segment_revenue_mix.data}
                  isTotalPercent={true}
                  legend={by_vertical.segment_revenue_mix.legend}
                  isAnimationEnabled={true}
                />
              </div>
            </Card.Body>
          </Card>
        </Col>
      </Row>
      <Row className="contentDivider">
        <Col>
          <Card className="SegmentDetails revenue">
            <Card.Body>
              <Card.Title>bookings details</Card.Title>
              <CustomTable
                tableName='by_vertical_segment-details'
                data={by_vertical.bookings_detail === undefined ? [] : by_vertical.bookings_detail}
                defaultSorted={defaultSortedVertical}
                columns={columnsVertical}
              />
            </Card.Body>
          </Card>
        </Col>
      </Row>
    </Container>
  );
};

export default BookingsContent;