import React, { useState, useRef } from 'react';
import { v4 as uuidv4 } from 'uuid';
import './draggableComponent.scss';
import { useWidowResize } from '../../constants/utilityHooks';
import { DragDropContext, Droppable, Draggable } from "react-beautiful-dnd";
import { FormControl } from 'react-bootstrap';
import { Dropdown } from 'react-bootstrap';
import { BiChevronDown, BiChevronRight } from 'react-icons/bi';
import { FiMoreHorizontal } from "react-icons/fi";

const reorder = (list, startIndex, endIndex) => {
	const result = Array.from(list);
	const [removed] = result.splice(startIndex, 1);
	result.splice(endIndex, 0, removed);

	return result;
};

const move = (source, destination, droppableSource, droppableDestination) => {
	const sourceClone = Array.from(source);
	const destClone = Array.from(destination);
	const [removed] = sourceClone.splice(droppableSource.index, 1);

	destClone.splice(droppableDestination.index, 0, removed);

	const result = {};
	result[droppableSource.droppableId] = sourceClone;
	result[droppableDestination.droppableId] = destClone;

	return result;
};

const copy = (source, destination, droppableSource, droppableDestination) => {
	const sourceClone = Array.from(source);
	const destClone = Array.from(destination);
	const item = sourceClone[droppableSource.index];

	destClone.splice(droppableDestination.index, 0, { ...item, key: uuidv4() });
	return destClone;
};

const DraggableComponent = ({ buttonList, grabableList, dragState, setDragState, children, withPreview, narrow_view }) => {
	const [searchVal, setSearchVal] = useState('');
	const [focusSearch, setFocusSearch] = useState(false);

	// maxHeight drag component
	const dragRef = useRef(null);
	const { height } = useWidowResize(dragRef);

	// multi menu settings
	const [activeMenus, setActiveMenus] = useState([]);

	const onClickMenu = (menuName, data) => {
		let newActiveMenus = [...activeMenus];
		if (newActiveMenus.includes(menuName)) {
			var index = newActiveMenus.indexOf(menuName);
			if (index > -1) {
				newActiveMenus.splice(index, 1);
			}
		} else {
			newActiveMenus.push(menuName);
		}
		setActiveMenus(newActiveMenus);
	};

	const ListMenu = ({ dept, parent_key, data, hasSubMenu, menuName, menuIndex, hasDraggableMenu }) => {
		return (
			<li key={menuName} >
				<div dept={dept}>
					<span className="field-label" onClick={() => onClickMenu(menuName, data)}>{data.menuLabel} </span>
					{hasSubMenu || hasDraggableMenu
						? activeMenus.includes(menuName) || focusSearch || searchVal !== ''
							? <BiChevronDown className="open-indicator" />
							: <BiChevronRight className="open-indicator" />
						: null
					}
				</div>
				{hasDraggableMenu && (
					<DraggableMenu
						dept={dept}
						parent_key={parent_key}
						data={data.draggableMenu}
						toggle={activeMenus.includes(menuName)}
						menuIndex={menuIndex}
					/>
				)}
				{hasSubMenu && (
					<SubMenu
						dept={dept}
						parent_key={parent_key}
						data={data.submenu}
						toggle={activeMenus.includes(menuName)}
						menuIndex={menuIndex}
					/>
				)}
			</li>
		);
	}

	const SubMenu = ({ dept, parent_key, data, toggle, menuIndex }) => {
		if (!toggle && searchVal === '') {
			return null
		}

		dept = dept + 1;

		return (
			<ul className="field-list">
				{data.map((menu, index) => {
					const menuName = `sidebar-submenu-${dept}-${menuIndex}-${index}`;
					return (
						<ListMenu
							dept={dept}
							data={menu}
							parent_key={`${parent_key}----${menu.menuKey}`}
							hasSubMenu={menu.submenu}
							hasDraggableMenu={menu.draggableMenu}
							menuName={menuName}
							key={menuName}
							menuIndex={index}
						/>
					)
				})}
			</ul>
		);
	};

	const DraggableMenu = ({ dept, parent_key, data, toggle, menuIndex }) => {
		if (!toggle && searchVal === '') {
			return null
		}

		dept = dept + 1;

		return (
			<ul className="field-list drag-drop-list">
				{data.map((menu, index) => {
					const menuName = `sidebar-dragmenu-${dept}-${menuIndex}-${index}`;
					return (
						<li key={menuName} id={menu.key} value={menu.label}>
							<div dept={dept}>
								<Droppable droppableId="grabableList" isDropDisabled={true}>
									{(provided, snapshot) => {
										return (
											<div
												className={`item-container ${snapshot.isDraggingOver ? 'isDraggingOver' : ''}`}
												ref={provided.innerRef}
											// isDraggingOver={snapshot.isDraggingOver}
											>
												<Draggable
													key={menu.key}
													draggableId={`${parent_key}----${menu.key}`}
													index={index}
													value={menu.label}
												>
													{(provided, snapshot) => (
														<React.Fragment>
															<div className={`item ${snapshot.isDragging ? 'isDragging' : ''}`}
																ref={provided.innerRef}
																{...provided.draggableProps}
																{...provided.dragHandleProps}
																// isDragging={snapshot.isDragging}
																style={
																	provided
																		.draggableProps
																		.style
																}>
																{menu.label}
																<Dropdown className="dropdown-option ml-auto">
																	<Dropdown.Toggle id="dropdown-option-toggle" variant="link">
																		<FiMoreHorizontal />
																	</Dropdown.Toggle>
																	<Dropdown.Menu>
																		<Dropdown.Item>Add to Columns</Dropdown.Item>
																		<Dropdown.Item>Add to Rows</Dropdown.Item>
																		<Dropdown.Item>Add to Filters</Dropdown.Item>
																	</Dropdown.Menu>
																</Dropdown>
															</div>
															{snapshot.isDragging && <div className="clone">{menu.label}</div>}
														</React.Fragment>
													)}
												</Draggable>
											</div>
										)
									}}
								</Droppable>
							</div>
						</li>
					)
				}).filter(
					(child) => {
						return !searchVal || child.props.value.toLowerCase().includes(searchVal.toLowerCase());
					})
				}
			</ul>
		)
	};

	// drag settings
	const onDragEnd = (result) => {
		const { source, destination, draggableId } = result;

		// dropped outside the list
		if (!destination) {
			return;
		}

		switch (source.droppableId) {
			case 'grabableList':
				let key_array = draggableId.split("----");
				let grandparent_key = key_array[0];
				let find_grandparent = buttonList.filter(data => data.menuKey === grandparent_key);
				let parent_key = key_array[1];
				let get_submenu = find_grandparent.length > 0 ? find_grandparent[0].submenu : [];
				let find_parent = get_submenu.filter(data => data.menuKey === parent_key);
				let get_draggable_menu = find_parent.length > 0 ? find_parent[0].draggableMenu : [];

				setDragState({
					...dragState,
					[destination.droppableId]: {
						...dragState[destination.droppableId],
						items: copy(
							get_draggable_menu,
							dragState[destination.droppableId].items,
							source,
							destination
						)
					}
				});
				break;
			// case destination.droppableId:
			//   setDragState({
			//     ...dragState,
			//     [destination.droppableId]: {
			//       ...dragState[destination.droppableId],
			//       items: reorder(
			//         dragState[source.droppableId],
			//         source.index,
			//         destination.index
			//       )
			//     }
			//   });
			//   break;
			default: console.log('move')
				// setDragState({
				//   ...dragState,
				//   [destination.droppableId]: {
				//     ...dragState[destination.droppableId],
				//     items: move(
				//       dragState[source.droppableId].items,
				//       dragState[destination.droppableId].items,
				//       source,
				//       destination
				//     )
				//   }
				// });
				break;
		}
	};

	// remove Item in fields
	const onRemoveField = (listId, objectID) => {
		let specificList = dragState[listId];
		let data = specificList.items ? specificList.items : [];
		let updated_data_after_delete = data.filter(item => item.key !== objectID);
		setDragState({
			...dragState,
			[listId]: {
				...dragState[listId],
				items: updated_data_after_delete
			}
		})
	};

	const addList = e => {
		setDragState({ ...dragState, [uuidv4()]: [] });
	};

	console.log(height, dragRef)

	return (
		<div 
			className={`draggable-component ${withPreview ? 'with-preview' : ''}`} 
			style={{ maxHeight: height ? height : 'auto' }} 
			ref={dragRef ? dragRef : null}>
			<DragDropContext onDragEnd={onDragEnd}>
				<div className="toggler-wrapper">
					<div className="toggler-cell" style={{ maxHeight: height ? height - 15 : 'auto' }}>
						<div className="toggler-header">
							<h2 className="title">Available Fields</h2>
							<FormControl
								onKeyUp={() => setFocusSearch(true)}
								onBlur={() => setFocusSearch(false)}
								placeholder="Search for fields"
								onChange={(e) => setSearchVal(e.target.value)}
								value={searchVal}
							/>
						</div>
						<div className="custom-scroll">
							<ul className="field-list">
								{buttonList.map((menu, index) => {
									const dept = 1;
									const menuName = `sidebar-menu-${dept}-${index}`;
									return (
										<ListMenu
											dept={dept}
											parent_key={menu.menuKey}
											data={menu}
											hasSubMenu={menu.submenu}
											menuName={menuName}
											key={menuName}
											menuIndex={index}
										/>
									)
								})}
							</ul>
						</div>
					</div>
				</div>

				<div className="content-wrapper">
					<div className={`drag-destination-area ${narrow_view ? 'narrow-view' : ''}`}>
						{Object.entries(dragState).map(([listId, list], i) => {
							return (
								<div className="content-cell" key={`content-cell-${listId}`}>
									<div key={listId} className="content">
										<div className="content-header">
											<h2 className="title">{list.name}</h2>
										</div>
										{list.text && <p className="text">{list.text}</p>}
										<div className="custom-scroll">
											<Droppable droppableId={listId} key={listId}>
												{(provided, snapshot) => (
													<div
														className={`drag-container ${snapshot.isDraggingOver ? 'isDraggingOver' : ''}`}
														ref={provided.innerRef}
													// isDraggingOver={snapshot.isDraggingOver}
													>
														{dragState[listId].items.length > 0
															? dragState[listId].items.map(
																(item, index) => (
																	<Draggable
																		key={item.key}
																		draggableId={item.key}
																		index={index}>
																		{(provided, snapshot) => (
																			<div className={`item  ${item.color ? 'darker' : ''} ${snapshot.isDragging ? 'isDragging' : ''}`}
																				ref={provided.innerRef}
																				{...provided.draggableProps}
																				{...provided.dragHandleProps}
																				// isDragging={snapshot.isDragging}
																				style={
																					provided
																						.draggableProps
																						.style
																				}>
																				{item.label}
																				<Dropdown className="dropdown-option ml-auto">
																					<Dropdown.Toggle id="dropdown-option-toggle" variant="link">
																						<FiMoreHorizontal />
																					</Dropdown.Toggle>
																					<Dropdown.Menu>
																						<Dropdown.Item onClick={(e) => onRemoveField(listId, item.key)}>Remove</Dropdown.Item>
																					</Dropdown.Menu>
																				</Dropdown>
																			</div>
																		)}
																	</Draggable>
																))
															: !provided.placeholder && <div className="notice">Drop items here</div>
														}
														{provided.placeholder}
													</div>
												)}
											</Droppable>
										</div>
									</div>
								</div>
							)
						})}
					</div>
					{withPreview &&
						<div className="preview-area">
							{children}
						</div>
					}
				</div>
			</DragDropContext>
		</div>
	);
}

export default DraggableComponent;