import React, { useState, useEffect } from 'react';

import PageHeader from '../../components/pageHeader/PageHeader';

import Overview from './Overview';
import Growth from './Growth';
import Metro from './Metro';
import Customer from './Customer';
import AdditionalResources from './AdditionalResources';

import './revenue.scss';


const Revenue = (props) => {
  let path = props.location.pathname;
  const currentPath = path.substring(path.lastIndexOf('/') + 1);
  const [activePage, setActivePage] = useState(currentPath);

  const handleActivePage = (page) => {
    setActivePage(page)
  }

  const handleBreadcrubsPreviousLink = () => {
    setActivePage('Overview')
  }

  useEffect(() => {
    setActivePage(currentPath)
  }, [currentPath])

  const breadcrumbs = [
    {
      path: '/analytics-dashboard/page',
      label: 'ANALYTICS DASHBOARD'
    },
    {
      path: path === '/analytics-dashboard/revenue/overview' ? null : '/analytics-dashboard/revenue/overview',
      label: 'Revenue'
    },
    {
      label: activePage,
    }
  ]

  const innerPage = [
    {
      path: '/analytics-dashboard/revenue/overview',
      label: 'Overview'
    },
    {
      path: '/analytics-dashboard/revenue/growth',
      label: 'Growth'
    },
    {
      path: '/analytics-dashboard/revenue/metro',
      label: 'Metro'
    },
    {
      path: '/analytics-dashboard/revenue/customer',
      label: 'Customer'
    },
    {
      path: '/analytics-dashboard/revenue/additional-resources',
      label: 'Resources'
    }
  ]

  const [activeTab, setActiveTab] = useState('total');

  return (
    <div className="revenue-page">
      <PageHeader
        title="Revenue"
        color="dark-orange"
        innerPage={innerPage}
        breadcrumbs={breadcrumbs}
        previousPage="/analytics-dashboard/page"
        handleActivePage={handleActivePage}
        handleBreadcrubsPreviousLink={handleBreadcrubsPreviousLink}
      />
      <div className="page-wrapper">
        {currentPath === 'overview' && <Overview activeTab={activeTab} setActiveTab={setActiveTab} />}
        {currentPath === 'growth' && <Growth />}
        {currentPath === 'metro' && <Metro />}
        {currentPath === 'customer' && <Customer />}
        {currentPath === 'additional-resources' && <AdditionalResources activeTab={activeTab} setActiveTab={setActiveTab} />}
      </div>
    </div>
  );
}

export default Revenue;
