import React, { useState } from 'react';
import { Row, Col, Card } from 'react-bootstrap';
import { ImArrowUpRight2, ImArrowDownRight2 } from "react-icons/im";
import CustomerModal from './CustomerModal';
import CustomTable, { formatLongText } from '../../components/customTable/CustomTable';
import Legend from '../../components/legend/Legend';
import StackedColumnChart from '../../components/chart/StackedColumnChart';
import MicroBarChart from '../../components/chart/MicroBarChart';
import MapChart from '../../components/chart/MapChart';
import LineChart from '../../components/chart/LineChart';
import { convertToUSDGrowth, convertToPercent, convertToWholePercent } from '../../constants/formatter';


const CustomerContent = (props) => {
  const { customerData, rankingVal_onApply, tab_id } = props;
  const [idState, setReferenceID] = useState(null);
  const [modalShow, setModalShow] = useState(false);
  const [modalData, setModalData] = useState(null);
  const [toggleSelectedRow, setToggleSelectedRow] = useState();
  const [selectedCustomer, setCustomerSelected] = useState(null);

  // Data Distribution
  let topLabelTitle = rankingVal_onApply ? `${rankingVal_onApply.topBottom.split('_')[1]} ${rankingVal_onApply.numberInput} ` : '';
  const contribution_to_total_usd_by_customer_tier = customerData ? customerData.contribution_to_total_usd_by_customer_tier : [];
  const y_by_y_percent_growth_by_customer_tier = customerData ? customerData.y_by_y_percent_growth_by_customer_tier : [];
  const deploymentMap = customerData ? customerData.deployment_map : [];
  const byRank = customerData ? customerData.customer_ranking.by_rank : [];
  const byRankId = customerData ? customerData.customer_ranking.by_rank_id : [];

  // Table Config
  const indicatorPercent = (cell, row) => {
    let bgColor = cell >= 0.5 ? 'indicator-up' : cell <= -0.5 ? 'indicator-down' : 'indicator-middle';
    let icon = cell >= 0.5 ? <ImArrowUpRight2 /> : cell <= -0.5 ? <ImArrowDownRight2 /> : <span>&nbsp;</span>;
    return (
      <span className={`indicator ${bgColor}`}>
        {icon}
        {convertToPercent(cell)}
      </span>
    );
  };

  const formatToPercent = (cell, row) => {
		return <span>{convertToPercent(cell)}</span>
	};

  const formatWholePercent = (cell, row) => {
		return <span>{convertToWholePercent(cell)}</span>
	};

  const formatUSDGrowth = (cell, row) => {
    let new_cell = convertToUSDGrowth(cell);
    return formatLongText(new_cell, row)
  };

  const customerFormat = (cell, row) => {
    return formatLongText(cell, row)
  }

  const microBarChart = (cell, row) => {
    return <MicroBarChart loadedData={cell} id={tab_id + "-micro-bar-chart-" + row._id} />
  }

  const rankFormat = (cell, row, rowIndex, formatExtraData) => {
    return <span>{rowIndex + 1}</span>
  };

  const handleOnSelect = (row, isSelect, rowIndex) => {
    let modalDataByRank;

    setToggleSelectedRow(row._id)
    setCustomerSelected(row.customer);

    for (const key in byRankId) {
      const element = byRankId[key];
      if (element._id === row._id) {
        modalDataByRank = element
      }
    }

    if (isSelect) {
      setModalShow(true);
      setReferenceID(row._id);
      setModalData(modalDataByRank);
    }
  };

  const selectRow = {
    mode: 'radio',
    clickToSelect: true,
    hideSelectColumn: true,
    onSelect: handleOnSelect,
    classes: 'selected',
    bgColor: '#ecf2ff'
  };

  const onCloseModal = async () => {
    await setModalShow(false)
    await setCustomerSelected(null);
    await setReferenceID(null);
    await setToggleSelectedRow()

    setTimeout(() => {
      setModalData(null);
    }, 1000);
  };

  const rowClasses = (row) => {
    let classes = row._id === toggleSelectedRow ? 'selected' : 'selectable';
    return classes
  };

  const defaultSorted = [{
    dataField: 'mrr_mix_percent',
    order: 'desc',
  }];

  const columns = [
    {
      dataField: '_id',
      text: 'id',
      hidden: true
    },
    {
      dataField: '',
      text: 'Rank',
      formatter: rankFormat,
      classes: 'rank center',
      headerClasses: 'rank center',
    },
    {
      dataField: 'customer',
      text: 'CUSTOMER',
      sort: true,
      formatter: customerFormat,
    },
    {
      dataField: 'vertical',
      text: 'VERTICAL',
      sort: true,
      classes: 'vertical-cell',
      headerClasses: 'vertical-cell',
    },
    {
      dataField: 'micro_column_bar_data',
      text: '',
      sort: true,
      formatter: microBarChart,
      classes: 'sparkline-cell center',
      headerClasses: 'sparkline-cell'
    },
    {
      dataField: 'mrc_usd',
      text: 'MRC $',
      sort: true,
      formatter: formatUSDGrowth,
      classes: 'right',
      headerClasses: 'right',
    },
    {
      dataField: 'mix_percent',
      text: 'MIX %',
      sort: true,
      formatter: formatToPercent,
      classes: 'mix-cell center',
      headerClasses: 'mix-cell center',
    },
    {
      dataField: 'q_by_q_growth_percent',
      text: 'Q/Q % GROWTH',
      sort: true,
      formatter: indicatorPercent,
      classes: 'with-ellipsis center',
      headerClasses: 'center',
    },
    {
      dataField: 'p4q_growth_percent',
      text: 'P4Q % GROWTH',
      sort: true,
      formatter: indicatorPercent,
      classes: 'with-ellipsis center',
      headerClasses: 'center',
    },
    {
      dataField: 'y_by_y_growth_percent',
      text: 'Y/Y % GROWTH',
      sort: true,
      formatter: indicatorPercent,
      classes: 'with-ellipsis center',
      headerClasses: 'center',
    },
  ];

  return (
    <>
      <CustomerModal
        show={modalShow}
        onClose={onCloseModal}
        data={modalData}
        selectedCustomer={selectedCustomer}
        activeTab={tab_id}
      />
      <Row className="contentDivider">
        <Col md={6}>
          <Row className="pb-30">
            <Col md={12}>
              <Card className="revenue-card">
                <Card.Body>
                  <Card.Title>deployment map</Card.Title>
                  <>
                    <Legend legend={deploymentMap.legend} />
                    <div className="pt-30" style={{ height: "450px " }}>
                      <MapChart
                        id={`${tab_id}-map_chart`}
                        largerWidth={true}
                        data={deploymentMap.data}
                      />
                    </div>
                  </>
                </Card.Body>
              </Card>
            </Col>
          </Row>
          <Row>
            <Col md={6}>
              <Card className="revenue-card">
                <Card.Body>
                  <Card.Title>{topLabelTitle} contribution to total $ by customer tier</Card.Title>
                  <Legend legend={contribution_to_total_usd_by_customer_tier.legend} />
                  <div style={{ height: '250px' }}>
                    <StackedColumnChart
                      id={`${tab_id}-contribution_to_total`}
                      largerWidth={true}
                      isOneHundredPercent={true}
                      isPercentage={true}
                      insideLabel={false}
                      percentLabel={false}
                      hiddenValueAxis={false}
                      data={contribution_to_total_usd_by_customer_tier.data}
                      legend={contribution_to_total_usd_by_customer_tier.legend}
                      isAnimationEnabled={false}

                    />
                  </div>
                </Card.Body>
              </Card>
            </Col>
            <Col md={6}>
              <Card className="revenue-card">
                <Card.Body>
                  <Card.Title>Y/Y % growth by customer tier</Card.Title>
                  <Legend legend={y_by_y_percent_growth_by_customer_tier.legend} />
                  <div style={{ height: "250px" }}>
                    <LineChart
                      id={`${tab_id}-percent_growth`}
                      data={y_by_y_percent_growth_by_customer_tier.data}
                      legend={y_by_y_percent_growth_by_customer_tier.legend}
                      isAnimationEnabled={false}
                    />
                  </div>
                </Card.Body>
              </Card>
            </Col>
          </Row>
        </Col>
        <Col md={6}>
          <Card className="revenue-card">
            <Card.Body>
              <>
                <CustomTable
                  tableName='customer-ranking scrollable'
                  data={byRank.data ? byRank.data : []}
                  // rowEvents={rowEvents}
                  tableTitle={`customer ranking`} 
                  defaultSorted={defaultSorted}
                  rowClasses={rowClasses}
                  selectRow={selectRow}
                  columns={columns}
                  addHover
                  addSearch
                />
              </>
            </Card.Body>
          </Card>
        </Col>
      </Row>
    </>
  );
};

export default CustomerContent;