import React from 'react';
import { Container, Row, Col, Card } from 'react-bootstrap';
import { ImArrowUpRight2, ImArrowDownRight2 } from "react-icons/im";

import CustomTable from '../../components/customTable/CustomTable';
import StackedColumnChart from '../../components/chart/StackedColumnChart';
import DonutChart from '../../components/chart/DonutChart';
import LineChart from '../../components/chart/LineChart';
import KPI_NEW from '../../components/kpi/KPI_NEW';
import Legend from '../../components/legend/Legend';

import { convertToPercent, convertToUSDGrowth } from '../../constants/formatter';


const RevenueContent = (props) => {
  const { revenueData } = props;
  // Start Table
  const indicatorPercent = (cell, row) => {
    let bgColor = cell >= 0.5 ? 'indicator-up' : cell <= -0.5 ? 'indicator-down' : 'indicator-middle';
    let icon = cell >= 0.5 ? <ImArrowUpRight2 /> : cell <= -0.5 ? <ImArrowDownRight2 /> : <span className="mr-3"></span>;

    return (
      <>
        <span className={`indicator ${bgColor}`}>
          {icon}
          {convertToPercent(cell)}
        </span>
      </>
    );
  }

  const formatToPercent = (cell, row) => {
    return (
      <span>
        {convertToPercent(cell)}
      </span>
    );
  }

  const formatToUSDGrowth = (cell, row) => {
    return (
      <span>
        {convertToUSDGrowth(cell)}
      </span>
    );
  }
  const defaultSorted = [
    {
      dataField: 'segment',
      order: 'desc',
    }
  ];

  const columns = [
    {
      dataField: '_id',
      text: 'id',
      hidden: true
    },
    {
      dataField: 'segment',
      text: 'SEGMENT',
      
      
      classes: 'segment-th',
      headerClasses: 'segment-td'
    },
    {
      dataField: 'mrr',
      text: 'MRR',
      sort: true,
      classes: 'right',
      headerClasses: 'right',
      formatter: formatToUSDGrowth
    },
    {
      dataField: 'mix_percent',
      text: 'MRR MIX%',
      sort: true,
      classes: 'right',
      headerClasses: 'right',
      formatter: formatToPercent
    },
    {
      dataField: 'y_by_y_growth_percent',
      text: 'Y/Y % GROWTH',
      sort: true,
      formatter: indicatorPercent,
      classes: 'with-ellipsis right',
      headerClasses: 'right'
    },
    {
      dataField: 'y_to_y_growth_usd',
      text: 'Y-Y $ GROWTH',
      sort: true,
      classes: 'right',
      headerClasses: 'right',
      formatter: formatToUSDGrowth
    },
    {
      dataField: 'q_by_q_growth_percent',
      text: 'Q/Q % GROWTH',
      sort: true,
      formatter: indicatorPercent,
      classes: 'with-ellipsis right',
      headerClasses: 'right'
    },
    {
      dataField: 'q_to_q_growth_usd',
      text: 'Q-Q $ GROWTH',
      sort: true,
      classes: 'right',
      headerClasses: 'right',
      formatter: formatToUSDGrowth
    }
  ]
  // End Table

  return (
    <Container className="revenue" fluid>
      <Row className="contentDivider pb-30">
        <div className="fix-width">
          <Card className="TotalRevenue revenue" >
            <Card.Body>
              <Card.Title>{revenueData.overall.total.label}</Card.Title>
              <KPI_NEW
                  totalValue={revenueData.overall.total.value}
                  totalUnit={revenueData.overall.total.unit}
                  details={revenueData.overall.details}
              />
            </Card.Body>
          </Card>
        </div>
        <Col>
          <Card className="RevenueByType revenue">
            <Card.Body>
              <Card.Title>customer revenue by revenue type</Card.Title>
              <Legend legend={revenueData.total_revenue_by_revenue_type.legend} />
              <div style={{ height: "400px" }}>
                <StackedColumnChart
                  id="total_revenue_by_revenue_type_stacked_column_chart"
                  hiddenValueAxis={false}
                  isOneHundredPercent={false}
                  isPercentage={false}
                  largerWidth={true}
                  hideLabelBullet={false}
                  data={revenueData.total_revenue_by_revenue_type.data}
                  legend={revenueData.total_revenue_by_revenue_type.legend}
                  isAnimationEnabled={true}
                />
              </div>
            </Card.Body>
          </Card>
        </Col>
        <Col style={{ maxWidth: "400px" }}>
          <Card className="Segment revenue">
            <Card.Body>
              <Card.Title>customer revenue mix</Card.Title>
              <Legend legend={revenueData.segment_revenue_mix.legend} />
              <div style={{ height: "350px" }}>
                <DonutChart
                  id={"total_revenue_overview_donut_chart"}
                  totalTitle={"CUSTOMER REVENUE"}
                  total={revenueData.segment_revenue_mix.customer_revenue}
                  data={revenueData.segment_revenue_mix.data}
                  legend={revenueData.segment_revenue_mix.legend}
                  isDollar={true}
                  isAnimationEnabled={true}
                />
              </div>
            </Card.Body>
          </Card>
        </Col>
      </Row>
      <Row className="contentDivider">
        <Col>
          <Card className="SegmentDetails revenue">
            <Card.Body>
              <Card.Title>segment details</Card.Title>
              <CustomTable
                tableName='segment-details'
                data={revenueData.segment_details === undefined ? [] : revenueData.segment_details}
                defaultSorted={defaultSorted}
                columns={columns}
              />
            </Card.Body>
          </Card>
        </Col>
        <Col style={{ maxWidth: "400px" }}>
          <Card className="SegmentTrends revenue">
            <Card.Body>
              <Card.Title>segment y/y growth trends</Card.Title>
              <Legend legend={revenueData.segment_trends.legend} />
              <div style={{ height: "300px" }}>
                <LineChart
                  id={"total_revenue_overview_line_chart"}
                  data={revenueData.segment_trends.data}
                  legend={revenueData.segment_trends.legend}
                  isAnimationEnabled={true}
                />
              </div>
            </Card.Body>
          </Card>
        </Col>
      </Row>
    </Container>
  );
};

export default RevenueContent;