import React from 'react';
import { roundUpToTenth } from '../../constants/formatter';

import './chart.scss';


const SparklineCustom = (props) => {
  const { loadedData } = props;

  return (
    <>
      <svg width="100%" height="100%" style={{ padding: '5px', overflow: 'visible', maxHeight: '46px' }} transform="scale(1,-1)">
        {loadedData.map((data, index) => {
          let nextObject = loadedData[index + 1];
          let nextObjectValue = nextObject ? nextObject.value : roundUpToTenth(data.value);
          let xPosition = index * 25;
          let circleRadius = 3;
          return (
            <g key={index}>
              {nextObject &&
                <>
                  {index === 0
                    ? <circle r="3"
                      fill="#fff"
                      strokeWidth="1"
                      stroke={roundUpToTenth(data.value) > nextObjectValue ? '#FF0000' : roundUpToTenth(data.value) < nextObjectValue ? '#00B050' : '#FFC000'}
                      cx={`${xPosition}%`} cy={`${roundUpToTenth(data.value)}%`}>
                    </circle>
                    : null
                  }
                  <circle r={circleRadius}
                    fill="#fff"
                    strokeWidth="1"
                    stroke={roundUpToTenth(data.value) > nextObjectValue ? '#FF0000' : roundUpToTenth(data.value) < nextObjectValue ? '#00B050' : '#FFC000'}
                    cx={`${xPosition + 25}%`} cy={`${nextObjectValue}%`}>
                  </circle>
                  <line
                    x1={`${xPosition + circleRadius}%`} y1={`${roundUpToTenth(data.value) + circleRadius}%`}
                    x2={`${xPosition + 21}%`} y2={`${nextObjectValue}%`}
                    strokeWidth="1"
                    stroke={roundUpToTenth(data.value) > nextObjectValue ? '#FF0000' : roundUpToTenth(data.value) < nextObjectValue ? '#00B050' : '#FFC000'} />
                </>
              }
            </g>
          )
        })}
      </svg>
    </>
  );
}



export default SparklineCustom;
