import React, { useState } from 'react';
import { Container, Row, Col, Card } from 'react-bootstrap';
import { ImArrowUpRight2, ImArrowDownRight2 } from "react-icons/im";
import { convertToUSDGrowth, convertToPercent,convertToWholePercent } from '../../constants/formatter';
import CustomTable, { formatLongText } from '../../components/customTable/CustomTable';
import Legend from '../../components/legend/Legend';
import MicroBarChart from '../../components/chart/MicroBarChart';
import StackedColumnChart from '../../components/chart/StackedColumnChart';
import MapChart from '../../components/chart/MapChart';
import LineChart from '../../components/chart/LineChart';
import CustomerModal from './CustomerModal';
import { CgHello } from 'react-icons/cg';

const CustomerContent = (props) => {
  const { customerData, activeTab } = props;
  const [idState, setReferenceID] = useState(null);
  const [modalShow, setModalShow] = useState(false);
  const [modalData, setModalData] = useState(null);
  const [toggleSelectedRow, setToggleSelectedRow] = useState();
  const [selectedPartner, setPartnerSelected] = useState(null);
  // Data Distribution
  const stackedChart = activeTab === 'customer' && customerData ? customerData.contribution_to_total_usd_by_customer_tier : customerData.contribution_to_total_usd_by_opportunity_tier;
  const deployment_map = customerData ? customerData.deployment_map : [];
  const lineChart = activeTab === 'customer' && customerData ? customerData.y_by_y_percent_growth_by_customer_tier : [];
  const by_rank = activeTab === 'customer' && customerData ? customerData.customer_ranking.by_rank : [];
  const by_rank_id = activeTab === 'customer' && customerData ? customerData.customer_ranking.by_rank_id : [];

  const by_rank_opportunities = activeTab === 'opportunities' && customerData ? customerData.opportunity_ranking.by_rank : [];
  const by_rank_id_opportunities = activeTab === 'opportunities' && customerData ? customerData.opportunity_ranking.by_rank_id : [];

  // Table Config
  const indicatorPercent = (cell, row) => {
    let bgColor = cell >= 0.5 ? 'indicator-up' : cell <= -0.5 ? 'indicator-down' : 'indicator-middle';
    let icon = cell >= 0.5 ? <ImArrowUpRight2 /> : cell <= -0.5 ? <ImArrowDownRight2 /> : <span>&nbsp;</span>;
    return (
      <span className={`indicator ${bgColor}`}>
        {icon}
        {convertToPercent(cell)}
      </span>
    );
  };

  const formatWholePercent = (cell, row) => {
    return <span>{convertToWholePercent(cell)}</span>
  };

  const formatUSDGrowth = (cell, row) => {
    let new_cell = convertToUSDGrowth(cell);
    return formatLongText(new_cell, row)
  };

  const customerFormat = (cell, row) => {
    return formatLongText(cell, row)
  }

  const microBarChart = (cell, row) => {
    return <MicroBarChart loadedData={cell} id={"micro-bar-chart-" + row._id} />
  }

  const rankFormat = (cell, row, rowIndex, formatExtraData) => {
    return <span>{rowIndex + 1}</span>
  };

  const statusFormat = (cell, row) => {
    let new_color = cell.color.replace(/#/g, '')
    return <div className={`pill-status color-${new_color}`} >{cell.value}</div>
  }

  const handleOnSelect = (row, isSelect, rowIndex) => {
    let modalDataby_rank;

    setToggleSelectedRow(row._id)
    setPartnerSelected(row.customer);

    for (const key in by_rank_id) {
      const element = by_rank_id[key];
      if (element._id === row._id) {
        modalDataby_rank = element
      }
    }

    if (isSelect) {
      setModalShow(true);
      setReferenceID(row._id);
      setModalData(modalDataby_rank);
    }
  };

  const handleOnSelectOpportunities = (row, isSelect, rowIndex) => {
    let modalDataby_rank;

    setToggleSelectedRow(row._id)
    setPartnerSelected(row.customer);

    for (const key in by_rank_id_opportunities) {
      const element = by_rank_id_opportunities[key];
      if (element._id === row._id) {
        modalDataby_rank = element
      }
    }

    if (isSelect) {
      setModalShow(true);
      setReferenceID(row._id);
      setModalData(modalDataby_rank);
    }
  };


  const selectRowOpportunities = {
    mode: 'radio',
    clickToSelect: true,
    hideSelectColumn: true,
    onSelect: handleOnSelectOpportunities,
    classes: 'selected',
    bgColor: '#ecf2ff'
  };



  const selectRow = {
    mode: 'radio',
    clickToSelect: true,
    hideSelectColumn: true,
    onSelect: handleOnSelect,
    classes: 'selected',
    bgColor: '#ecf2ff'
  };

  const onCloseModal = async () => {
    await setModalShow(false)
    await setPartnerSelected(null);
    await setReferenceID(null);
    await setToggleSelectedRow()

    setTimeout(() => {
      setModalData(null);
    }, 1000);
  };

  const rowClasses = (row) => {
    let classes = row._id === toggleSelectedRow ? 'selected' : 'selectable';
    return classes
  };

  const defaultSorted = [{
    dataField: 'mrc_usd',
    order: 'desc',
  }];

  const columns = [
    {
      dataField: '_id',
      text: 'id',
      hidden: true
    },
    {
      dataField: '',
      text: 'Rank',
      formatter: rankFormat,
      classes: 'rank',
      headerClasses: 'rank',
    },
    {
      dataField: 'customer',
      text: 'CUSTOMER',
      sort: true,
      formatter: customerFormat,
    },
    {
      dataField: 'vertical',
      text: 'VERTICAL',
      sort: true,
      classes: 'vertical-cell',
      headerClasses: 'vertical-cell',
    },
    {
      dataField: 'micro_column_bar_data',
      text: '',
      sort: true,
      formatter: microBarChart,
      classes: 'sparkline-cell',
      headerClasses: 'sparkline-cell'
    },
    {
      dataField: 'mrc_usd',
      text: 'MRC $',
      sort: true,
      formatter: formatUSDGrowth,
      classes: 'right',
      headerClasses: 'right',
    },
    {
      dataField: 'mix_percent',
      text: 'MIX %',
      sort: true,
      formatter: formatWholePercent,
      classes: 'mix-cell center',
      headerClasses: 'mix-cell center',
    },
    {
      dataField: 'q_by_q_growth_percent',
      text: 'Q/Q % GROWTH',
      sort: true,
      formatter: indicatorPercent,
      classes: 'with-ellipsis center',
      headerClasses: 'center',
    },
    {
      dataField: 'p4q_growth_percent',
      text: 'P4Q % GROWTH',
      sort: true,
      formatter: indicatorPercent,
      classes: 'with-ellipsis center',
      headerClasses: 'center',
    },
    {
      dataField: 'y_by_y_growth_percent',
      text: 'Y/Y % GROWTH',
      sort: true,
      formatter: indicatorPercent,
      classes: 'with-ellipsis center',
      headerClasses: 'center',
    },
  ];


  const defaultSortedOpportunity = [{
    dataField: 'mrc_usd',
    order: 'desc',
  }];
  const columnsOpportunity = [
    {
      dataField: '_id',
      text: 'id',
      hidden: true
    },
    {
      dataField: '',
      text: 'Rank',
      formatter: rankFormat,
      classes: 'rank',
      headerClasses: 'rank',
    },
    {
      dataField: 'opportunity',
      text: 'OPPORTUNITY',
      sort: true,
      formatter: customerFormat,
    },
    {
      dataField: 'customer',
      text: 'CUSTOMER',
      sort: true,
      formatter: customerFormat,
    },
    {
      dataField: 'vertical',
      text: 'VERTICAL',
      sort: true,
      classes: 'vertical-cell',
      headerClasses: 'vertical-cell',
    },
    {
      dataField: 'ibx',
      text: 'IBX',
      sort: true,
    },
    {
      dataField: 'status',
      text: 'STATUS',
      sort: true,
      formatter: statusFormat
    },
    {
      dataField: 'mrc_usd',
      text: 'MRC $',
      sort: true,
      formatter: formatUSDGrowth,
      classes: 'right',
      headerClasses: 'right',
    },
    {
      dataField: 'mix_percent',
      text: 'MIX %',
      sort: true,
      formatter: formatWholePercent,
      classes: 'mix-cell right',
      headerClasses: 'mix-cell right',
    }
  ];

  return (
    <Container fluid>
      <CustomerModal
        show={modalShow}
        onClose={onCloseModal}
        data={modalData}
        selectedPartner={selectedPartner}
        activeTab={activeTab}
      />
      <Row className="contentDivider">
        <Col md={6}>
          <Row className="pb-30">
            <Col md={12}>
              <Card className="revenue-card">
                <Card.Body>
                  <Card.Title>deployment map</Card.Title>
                  <>
                    <Legend legend={deployment_map.legend} />
                    <div className="pt-30" style={{ height: "450px " }}>
                      <MapChart
                        id={`customer-map_chart`}
                        largerWidth={true}
                        data={deployment_map.data}
                      />
                    </div>
                  </>
                </Card.Body>
              </Card>
            </Col>
          </Row>
          <Row>
            <Col md={activeTab === 'customer' ? 6 : 12}>
              <Card className="revenue-card">
                <Card.Body>
                  <Card.Title>contribution to total $ by {activeTab} tier</Card.Title>
                  <Legend legend={stackedChart.legend} />
                  <div style={{ height: '250px' }}>
                    <StackedColumnChart
                      id={`customer-contribution_to_total`}
                      largerWidth={true}
                      isOneHundredPercent={true}
                      isPercentage={true}
                      insideLabel={false}
                      percentLabel={false}
                      hiddenValueAxis={false}
                      data={stackedChart.data}
                      legend={stackedChart.legend}
                      isAnimationEnabled={false}
                    />
                  </div>
                </Card.Body>
              </Card>
            </Col>

            {activeTab === 'customer' ?

              <Col md={6}>
                <Card className="revenue-card">
                  <Card.Body>
                    <Card.Title>Y/Y % growth by {activeTab} tier</Card.Title>
                    {activeTab === 'customer' ?
                      <>
                        <Legend legend={lineChart.legend} />
                        <div style={{ height: "250px" }}>
                          <LineChart
                            id={`customer-percent_growth`}
                            data={lineChart.data}
                            legend={lineChart.legend}
                            isAnimationEnabled={false}
                          />
                        </div>
                      </>
                      : null}

                  </Card.Body>
                </Card>
              </Col>

              : null}

          </Row>
        </Col>
        <Col md={6}>
          <Card className="revenue-card">
            <Card.Body>
              <Card.Title>{activeTab} ranking</Card.Title>
              <>
                {activeTab === 'customer' ?
                  <CustomTable
                    tableName='customer-ranking scrollable'
                    data={by_rank.data ? by_rank.data : []}
                    // rowEvents={rowEvents}
                    defaultSorted={defaultSorted}
                    rowClasses={rowClasses}
                    selectRow={selectRow}
                    columns={columns}
                    addHover
                  />

                  : null}


                {activeTab === 'opportunities' ?
                  <CustomTable
                    tableName='customer-opportunity-ranking scrollable'
                    data={by_rank_opportunities.data ? by_rank_opportunities.data : []}
                    // rowEvents={rowEvents}
                    defaultSorted={defaultSortedOpportunity}
                    rowClasses={rowClasses}
                    selectRow={selectRowOpportunities}
                    columns={columnsOpportunity}
                    addHover
                  />

                  : null}



              </>
            </Card.Body>
          </Card>
        </Col>
      </Row>
    </Container>
  );
};

export default CustomerContent;