import React, { useState, useEffect } from 'react';
import { ButtonGroup, Button } from 'react-bootstrap';
import axiosRetry from 'axios-retry';
import axios from 'axios';
import {Separator, RadioDD, CheckboxDD, SearchCheckboxDD} from '../../components/customDropdown';
import {FilterContainer, FilterModal} from '../../components/filter';
import Loader from '../../components/loader/Loader';
import Comment from '../../components/comment/Comment';
import { InternalServerError, ResourceNotFoundError } from '../../components/error/Error';
import { optionRegion, optionMetro, optionType, optionIC } from '../../constants/filterOptions';
import { API_BASE_URL } from '../../constants/config';
import { comments } from '../../constants/comments';
import InterconnectionContent from './InterconnectionContent';
import PartnersContent from './PartnersContent';
import MatchmakerContent from './MatchmakerContent';


const Ecosystem = () => {
  const [interconnectionData, setInterconnectionData] = useState([])
  const [partnersData, setPartnersData] = useState([]);
  const [matchmakerData, setMatchmakerData] = useState([]);
  const [isLoading, setIsLoading] = useState(true)
  const [activeTab, setActiveTab] = useState('interconnection');

  // show all filters
  const [showFilter, setShowFilter] = useState(false);
  const onShowFilter = () => {
    setShowFilter(true)
  }
  const onCloseFilter = async () => {
    await onCancelRegion();
    await onCancelMetro();
    await onCancelGroup1();
    await onCancelShowby();
    setShowFilter(false);
  }
  const onApplyFilter = async () => {
    await onApplyRegion();
    await onApplyMetro();
    await onApplyGroup1();
    await onApplyShowby();
    setShowFilter(false);
  }

  // comment area
  const [showComment, setShowComment] = useState(false);
  const onCloseComment = () => {
    setShowComment(false);
  };
  const onShowComment = () => {
    setShowComment(true);
  };

  // checkbox for region values
  const [region_val_onApply, set_region_val_onApply] = useState([]);
  const [region_val, set_region_val] = useState([]);
  const onApplyRegion = () => {
    set_region_val_onApply(region_val);
  };
  const onCancelRegion = () => {
    set_region_val(region_val_onApply);
  };

  // searchCheckbox for metro values
  const [metro_val_onApply, set_metro_val_onApply] = useState([]);
  const [metro_val, set_metro_val] = useState([]);
  const onApplyMetro = () => {
    set_metro_val_onApply(metro_val);
  };
  const onCancelMetro = () => {
    set_metro_val(metro_val_onApply);
  };

  // radio for group1 values
  const group1_initial_value = optionType ? optionType[0] : null;
  const [group1_val_onApply, set_group1_val_onApply] = useState(group1_initial_value);
  const [group1_val, set_group1_val] = useState(group1_initial_value);
  const onApplyGroup1 = () => {
    set_group1_val_onApply(group1_val);
  };
  const onCancelGroup1 = () => {
    set_group1_val(group1_val_onApply);
  };

   // radio for showby values
   const showby_initial_value = optionIC ? optionIC[0] : null;
   const [showby_val_onApply, set_showby_val_onApply] = useState(showby_initial_value);
   const [showby_val, set_showby_val] = useState(showby_initial_value);
   const onApplyShowby = () => {
     set_showby_val_onApply(showby_val);
   };
   const onCancelShowby = () => {
     set_showby_val(showby_val_onApply);
   };

  // Axios call
  const fetchData = async () => {
    let url = `${API_BASE_URL}/account-navigator/ecosystems.json?print=pretty`;
    // Uncomment only when API is now available and working
    // const token = localStorage.getItem('token');
    // TODO: Add url and defaultQueryParameters to call Filter API calls

    setIsLoading(true);

    axiosRetry(axios, { retries: 3 });

    await axios({
      method: 'GET',
      url: url
      // Uncomment only when API is now available and working
      // url: url + defaultQueryParameters,
      // headers: {
      //   'Content-Type': 'application/json',
      //   "Authorization": `bearer ${token}`
      // }
    }).then(result => {
      let data = result.data;
      // Uncomment only if for API testing purposes
      // console.log(result.config.url);
      setInterconnectionData(data.interconnection);
      setPartnersData(data.partners);
      setMatchmakerData(data.matchmaker);
      setIsLoading(false);
    }).catch(error => {
      console.log(error);
      setInterconnectionData(null);
      setPartnersData(null);
      setMatchmakerData(null);
      setIsLoading(false);
    });
  };

  useEffect(() => {
    fetchData();
  }, 
    []
    // Uncomment only when API is now available and working
    // [
    //   region_val_onApply,
    //   metro_val_onApply,
    //   measure_val_onApply,
    //   range_val_onApply
    // ]
  );

  if (isLoading) return <Loader withContainer />;

  return (
    <>
      <div className={`page-content ${showComment ? 'open' : 'closed'}`}>
        {
          (interconnectionData  && partnersData && matchmakerData) ? 
          (interconnectionData.length > 0 && partnersData.length > 0 && matchmakerData.length > 0) ?
            <>
              <FilterContainer
                showComment={showComment}
                onShowFilter={onShowFilter}
                onShowComment={onShowComment}
              >
                <div className="filters custom-scroll">
                  <ButtonGroup className="sub-tab">
                    <Button variant='light' className={activeTab === 'interconnection' ? 'active' : ''} onClick={() => {
                      setActiveTab('interconnection')
                    }}>
                      Interconnection
                                  </Button>
                    <Button variant='light' className={activeTab === 'partners' ? 'active' : ''} onClick={() => {
                      setActiveTab('partners')
                    }}>
                      Partners
                                  </Button>
                    <Button variant='light' className={activeTab === 'matchmaker' ? 'active' : ''} onClick={() => {
                      setActiveTab('matchmaker')
                    }}>
                      Matchmaker
                                  </Button>
                  </ButtonGroup>
                  <Separator height='39px' />
                  <CheckboxDD
                    title="Deploy Region"
                    id="region"
                    options={optionRegion}
                    valueApplied={region_val_onApply}
                    checkVal={region_val}
                    setCheckVal={set_region_val}
                    onApply={onApplyRegion}
                    onCancel={onCancelRegion}
                  />
                  <SearchCheckboxDD
                    title="Deploy Metro"
                    id="metro"
                    options={optionMetro}
                    valueApplied={metro_val_onApply}
                    checkVal={metro_val}
                    setCheckVal={set_metro_val}
                    onApply={onApplyMetro}
                    onCancel={onCancelMetro}
                  />
                  
                  <RadioDD
                    title="Group by"
                    id="group"
                    addTitleOnWide
                    options={optionType}
                    radioVal={group1_val}
                    setRadioVal={set_group1_val}
                    onApply={onApplyGroup1}
                    onCancel={onCancelGroup1}
                    initialValue={group1_initial_value}
                  />
                  
                  <RadioDD
                    title="Show by"
                    id="show"
                    addTitleOnWide
                    options={optionIC}
                    radioVal={showby_val}
                    setRadioVal={set_showby_val}
                    onApply={onApplyShowby}
                    onCancel={onCancelShowby}
                    initialValue={showby_initial_value}
                  />
                </div>

                <FilterModal
                  title="Ecosystem"
                  setShowFilter={setShowFilter}
                  showFilter={showFilter}
                  onCloseFilter={onCloseFilter}
                  onApplyFilter={onApplyFilter}
                >
                  <CheckboxDD
                    title="Deploy Region"
                    id="region-modal"
                    options={optionRegion}
                    checkVal={region_val}
                    visibleInModal
                    setCheckVal={set_region_val}
                    onApply={onApplyRegion}
                    onCancel={onCancelRegion}
                  />
                  <SearchCheckboxDD
                    title="Deploy Metro"
                    id="metro-modal"
                    options={optionMetro}
                    checkVal={metro_val}
                    visibleInModal
                    setCheckVal={set_metro_val}
                    onApply={onApplyMetro}
                    onCancel={onCancelMetro}
                  />
                  <RadioDD
                    title="Group by"
                    id="group-modal"
                    addTitleOnWide
                    visibleInModal
                    options={optionType}
                    radioVal={group1_val}
                    setRadioVal={set_group1_val}
                    onApply={onApplyGroup1}
                    onCancel={onCancelGroup1}
                    initialValue={group1_initial_value}
                  />
                  <RadioDD
                    title="Show by"
                    id="show-modal"
                    addTitleOnWide
                    visibleInModal
                    options={optionIC}
                    radioVal={showby_val}
                    setRadioVal={set_showby_val}
                    onApply={onApplyShowby}
                    onCancel={onCancelShowby}
                    initialValue={showby_initial_value}
                  />
                </FilterModal>
              </FilterContainer>
              {/* ============= Your Component here! ============= */}
              {activeTab === 'interconnection' && <InterconnectionContent interconnectionData={interconnectionData ? interconnectionData[0] : []} />}
              {activeTab === 'partners' && <PartnersContent partnersData={partnersData ? partnersData[0] : []} />}
              {activeTab === 'matchmaker' && <MatchmakerContent matchmakerData={matchmakerData ? matchmakerData[0] : []} />}
            </>      
          : <ResourceNotFoundError redirectURL={`/account-navigator/navigator/ecosystems${window.location.search}`} />
          : <InternalServerError redirectURL={`/account-navigator/navigator/ecosystems${window.location.search}`} /> 
        }
      </div>
      <Comment
        data={comments}
        onCloseComment={onCloseComment}
        showComment={showComment}
      />
    </>
  );
};

export default Ecosystem;
