import React, { useState, useEffect } from 'react';
import axiosRetry from 'axios-retry';
import axios from 'axios';
import { FilterContainer, FilterModal } from '../../components/filter';
import { optionRegion, optionCompanySize2, optionTier, optionVertical, optionShowBy4 } from '../../constants/filterOptions';
import { RadioDD, CheckboxDD } from '../../components/customDropdown';
import { comments } from '../../constants/comments';
import { InternalServerError, ResourceNotFoundError } from '../../components/error/Error';
import { API_BASE_URL } from '../../constants/config';
import Comment from '../../components/comment/Comment';
import Loader from '../../components/loader/Loader';
import GrowthContent from './GrowthContent';


const Growth = () => {
	const [isLoading, setIsLoading] = useState(true);
	const [growthData, setData] = useState([]);
	const [selectedData, setSelectedData] = useState(null);

	// show all filters
	const [showFilter, setShowFilter] = useState(false);
	const onShowFilter = () => {
		setShowFilter(true);
	};
	const onCloseFilter = async () => {
		await onCancelRegion();
		await onCancelSize();
		await onCancelTier();
		await onCancelVertical();
		await onCancelShow();
		setShowFilter(false);
	};
	const onApplyFilter = async () => {
		await onApplyRegion();
		await onApplySize();
		await onApplyTier();
		await onApplyVertical();
		await onApplyShow();
		setShowFilter(false);
	};

	// comment area
	const [showComment, setShowComment] = useState(false);
	const onCloseComment = () => {
		setShowComment(false);
	};
	const onShowComment = () => {
		setShowComment(true);
	};

	// checkbox for region values
	const [region_val, set_region_val] = useState([]);
	const [region_val_onApply, set_region_val_onApply] = useState([]);
	const onApplyRegion = () => {
		set_region_val_onApply(region_val);
	};
	const onCancelRegion = () => {
		set_region_val(region_val_onApply);
	};

	// radio for size values
	const size_initial_value = optionCompanySize2 ? optionCompanySize2[0] : null;
	const [size_val_onApply, set_size_val_onApply] = useState(size_initial_value);
	const [size_val, set_size_val] = useState(size_initial_value);
	const onApplySize = () => {
		set_size_val_onApply(size_val);
	};
	const onCancelSize = () => {
		set_size_val(size_val_onApply);
	};

	// radio for tier values
	const tier_initial_value = optionTier ? optionTier[0] : null;
	const [tier_val_onApply, set_tier_val_onApply] = useState(tier_initial_value);
	const [tier_val, set_tier_val] = useState(tier_initial_value);
	const onApplyTier = () => {
		set_tier_val_onApply(tier_val);
	};
	const onCancelTier = () => {
		set_tier_val(tier_val_onApply);
	};

	// radio for vertical values
	const vertical_initial_value = optionVertical ? optionVertical[0] : null;
	const [vertical_val_onApply, set_vertical_val_onApply] = useState(vertical_initial_value);
	const [vertical_val, set_vertical_val] = useState(vertical_initial_value);
	const onApplyVertical = () => {
		set_vertical_val_onApply(vertical_val);
	};
	const onCancelVertical = () => {
		set_vertical_val(vertical_val_onApply);
	};

	// radio for show values
	const show_initial_value = optionShowBy4 ? optionShowBy4[0] : null;
	const [show_val_onApply, set_show_val_onApply] = useState(show_initial_value);
	const [show_val, set_show_val] = useState(show_initial_value);
	const onApplyShow = () => {
		set_show_val_onApply(show_val);
	};
	const onCancelShow = () => {
		set_show_val(show_val_onApply);
	};

	// Axios call
	const fetchData = async () => {
		let url = `${API_BASE_URL}/pricing/growth.json`;

		setIsLoading(true);
		axiosRetry(axios, { retries: 3 });

		axios({
			method: 'GET',
			url: url
		}).then(result => {
			const data = result.data;
			setData(data);
			setIsLoading(false);
			setSelectedData(null);
		}).catch(error => {
			console.log(error);
			setData([]);
			setIsLoading(false);
		});
	};

	useEffect(() => {
		fetchData();
	}, []);

	if (isLoading) return <Loader withContainer />;

	return (
		<>
			<div className={`page-content ${showComment ? 'open' : 'closed'}`}>
				{growthData ?
					growthData.segment_ranking.by_rank.length > 0 ?
						<>
							<FilterContainer
								showComment={showComment}
								onShowFilter={onShowFilter}
								onShowComment={onShowComment}
							>
								<div className="filters custom-scroll">
									<CheckboxDD
										title="Deploy Region"
										id="region"
										valueApplied={region_val_onApply}
										options={optionRegion}
										checkVal={region_val}
										setCheckVal={set_region_val}
										onApply={onApplyRegion}
										onCancel={onCancelRegion}
									/>
									<RadioDD
										title="Size"
										id="size"
										options={optionCompanySize2}
										radioVal={size_val}
										setRadioVal={set_size_val}
										onApply={onApplySize}
										onCancel={onCancelSize}
										initialValue={size_initial_value}
									/>
									<RadioDD
										title="Tier"
										id="tier"
										options={optionTier}
										radioVal={tier_val}
										setRadioVal={set_tier_val}
										onApply={onApplyTier}
										onCancel={onCancelTier}
										initialValue={tier_initial_value}
									/>
									<RadioDD
										title="Vertical"
										id="vertical"
										options={optionVertical}
										radioVal={vertical_val}
										setRadioVal={set_vertical_val}
										onApply={onApplyVertical}
										onCancel={onCancelVertical}
										initialValue={vertical_initial_value}
									/>
									<RadioDD
										title="Show"
										id="show"
										options={optionShowBy4}
										radioVal={show_val}
										setRadioVal={set_show_val}
										onApply={onApplyShow}
										onCancel={onCancelShow}
										initialValue={show_initial_value}
									/>
								</div>

								<FilterModal
									title="Overview"
									setShowFilter={setShowFilter}
									showFilter={showFilter}
									onCloseFilter={onCloseFilter}
									onApplyFilter={onApplyFilter}
								>
									<CheckboxDD
										title="Deploy Region"
										id="region-modal"
										options={optionRegion}
										visibleInModal
										checkVal={region_val}
										setCheckVal={set_region_val}
									/>
									<RadioDD
										title="Size"
										id="size-modal"
										options={optionCompanySize2}
										visibleInModal
										radioVal={size_val}
										setRadioVal={set_size_val}
									/>
									<RadioDD
										title="Tier"
										id="tier-modal"
										options={optionTier}
										visibleInModal
										radioVal={tier_val}
										setRadioVal={set_tier_val}
									/>
									<RadioDD
										title="Vertical"
										id="vertical-modal"
										options={optionVertical}
										visibleInModal
										radioVal={vertical_val}
										setRadioVal={set_vertical_val}
									/>
									<RadioDD
										title="Show"
										id="show-modal"
										options={optionShowBy4}
										visibleInModal
										radioVal={show_val}
										setRadioVal={set_show_val}
									/>
								</FilterModal>
							</FilterContainer>
							{/* ============= Your Component here! ============= */}
							< GrowthContent
								selectedData={selectedData}
								setSelectedData={setSelectedData}
								growthData={growthData}
							/>
						</>
						: <ResourceNotFoundError redirectURL={"/executive-cockpit/pricing/growth"} />
					: <InternalServerError redirectURL={"/executive-cockpit/pricing/growth"} />}
			</div>
			<Comment
				data={comments}
				onCloseComment={onCloseComment}
				showComment={showComment}
			/>
		</>
	);
};

export default Growth;