import React, { useState, useEffect } from 'react';
import axiosRetry from 'axios-retry';
import axios from 'axios';
import { FilterContainer, FilterModal } from '../../components/filter';
import { optionRange, optionVertical, optionOwnerTerritory, optionForecastStatus, optionRegion, optionMetro, optionShowPipeline } from '../../constants/filterOptions';
import { RadioDD, CheckboxDD, SearchCheckboxDD } from '../../components/customDropdown'
import { comments } from '../../constants/comments';
import { InternalServerError, ResourceNotFoundError } from '../../components/error/Error';
import { API_BASE_URL } from '../../constants/config';
import Comment from '../../components/comment/Comment';
import Loader from '../../components/loader/Loader';
import GrowthContent from './GrowthContent';


const Growth = () => {
	const [isLoading, setIsLoading] = useState(true);
	const [growthData, setData] = useState([]);
	const [selectedData, setSelectedData] = useState(null);

	// show all filters
	const [showFilter, setShowFilter] = useState(false);
	const onShowFilter = () => {
		setShowFilter(true);
	};
	const onCloseFilter = async () => {
		await onCancelRange();
		await onCancelShow();
		setShowFilter(false);
	};
	const onApplyFilter = async () => {
		await onApplyRange();
		await onApplyShow();
		setShowFilter(false);
	};

	// comment area
	const [showComment, setShowComment] = useState(false);
	const onCloseComment = () => {
		setShowComment(false);
	};
	const onShowComment = () => {
		setShowComment(true);
	};


	// checkbox for region values
	const [region_val_onApply, set_region_val_onApply] = useState([]);
	const [region_val, set_region_val] = useState([]);
	const onApplyRegion = () => {
		set_region_val_onApply(region_val);
	};
	const onCancelRegion = () => {
		set_region_val(region_val_onApply);
	};

	// searchCheckbox for metro values
	const [metro_val_onApply, set_metro_val_onApply] = useState([]);
	const [metro_val, set_metro_val] = useState([]);
	const onApplyMetro = () => {
		set_metro_val_onApply(metro_val);
	};
	const onCancelMetro = () => {
		set_metro_val(metro_val_onApply);
	};

	// checkbox for forecast values
	const [forecast_val_onApply, set_forecast_val_onApply] = useState([]);
	const [forecast_val, set_forecast_val] = useState([]);
	const onApplyForecast = () => {
		set_forecast_val_onApply(forecast_val);
	};
	const onCancelForecast = () => {
		set_forecast_val(forecast_val_onApply);
	};

	// checkbox for owner territory values
	const [territory_val_onApply, set_territory_val_onApply] = useState([]);
	const [territory_val, set_territory_val] = useState([]);
	const onApplyTerritory = () => {
		set_territory_val_onApply(territory_val);
	};
	const onCancelTerritory = () => {
		set_territory_val(territory_val_onApply);
	};

	// radio for range values
	const range_initial_value = optionRange ? optionRange[0] : null;
	const [range_val_onApply, set_range_val_onApply] = useState(range_initial_value);
	const [range_val, set_range_val] = useState(range_initial_value);
	const onApplyRange = () => {
		set_range_val_onApply(range_val);
	};
	const onCancelRange = () => {
		set_range_val(range_val_onApply);
	};

	// radio for segmentation values
	const vertical_initial_value = optionVertical ? optionVertical[0] : null;
	const [vertical_val_onApply, set_vertical_val_onApply] = useState(vertical_initial_value);
	const [vertical_val, set_vertical_val] = useState(vertical_initial_value);
	const onApplyVertical = () => {
		set_vertical_val_onApply(vertical_val);
	};
	const onCancelVertical = () => {
		set_vertical_val(vertical_val_onApply);
	};

	// radio for show by values
	const show_initial_value = optionShowPipeline ? optionShowPipeline[0] : null;
	const [show_val_onApply, set_show_val_onApply] = useState(show_initial_value);
	const [show_val, set_show_val] = useState(show_initial_value);
	const onApplyShow = () => {
		set_show_val_onApply(show_val);
	};
	const onCancelShow = () => {
		set_show_val(show_val_onApply);
	};

	// Axios call
	const fetchData = async () => {
		let url = `${API_BASE_URL}/pipeline/growth.json`;

		setIsLoading(true);
		axiosRetry(axios, { retries: 3 });

		axios({
			method: 'GET',
			url: url
		}).then(result => {
			const data = result.data;
			setData(data);
			setIsLoading(false);
			setSelectedData(null);
		}).catch(error => {
			console.log(error);
			setData([]);
			setIsLoading(false);
		});
	};

	useEffect(() => {
		fetchData();
	}, []);

	if (isLoading) return <Loader withContainer />;

	return (
		<>
			<div className={`page-content ${showComment ? 'open' : 'closed'}`}>
				{growthData ?
					growthData.segment_ranking.by_rank.length > 0 ?
						<>
							<FilterContainer
								showComment={showComment}
								onShowFilter={onShowFilter}
								onShowComment={onShowComment}
							>
								<div className="filters custom-scroll">
									<CheckboxDD
										title="Deploy Region"
										id="region"
										options={optionRegion}
										valueApplied={region_val_onApply}
										checkVal={region_val}
										setCheckVal={set_region_val}
										onApply={onApplyRegion}
										onCancel={onCancelRegion}
									/>
									<SearchCheckboxDD
										title="Deploy Metro"
										id="metro"
										options={optionMetro}
										valueApplied={metro_val_onApply}
										checkVal={metro_val}
										setCheckVal={set_metro_val}
										onApply={onApplyMetro}
										onCancel={onCancelMetro}
									/>
									<CheckboxDD
										id="forecast"
										title="Forecast Status"
										options={optionForecastStatus}
										checkVal={forecast_val}
										setCheckVal={set_forecast_val}
										onApply={onApplyForecast}
										onCancel={onCancelForecast}
									/>
									<CheckboxDD
										id="territory"
										title="Owner Territory"
										options={optionOwnerTerritory}
										checkVal={territory_val}
										setCheckVal={set_territory_val}
										onApply={onApplyTerritory}
										onCancel={onCancelTerritory}
									/>
									<RadioDD
										title="Segmentation"
										id="segmentation"
										options={optionVertical}
										radioVal={vertical_val}
										setRadioVal={set_vertical_val}
										onApply={onApplyVertical}
										onCancel={onCancelVertical}
										initialValue={vertical_initial_value}
									/>
									<RadioDD
										title="Show By"
										id="show_by"
										options={optionShowPipeline}
										radioVal={show_val}
										setRadioVal={set_show_val}
										onApply={onApplyShow}
										onCancel={onCancelShow}
										initialValue={show_initial_value}
									/>
								</div>
								<FilterModal
									title="Growth"
									setShowFilter={setShowFilter}
									showFilter={showFilter}
									onCloseFilter={onCloseFilter}
									onApplyFilter={onApplyFilter}
								>
									<CheckboxDD
										title="Deploy Region"
										id="region-modal"
										visibleInModal
										options={optionRegion}
										checkVal={region_val}
										setCheckVal={set_region_val}
									/>
									<SearchCheckboxDD
										title="Deploy Metro"
										id="metro-modal"
										visibleInModal
										options={optionMetro}
										checkVal={metro_val}
										setCheckVal={set_metro_val}
									/>
									<CheckboxDD
										id="forecast-modal"
										title="Forecast Status"
										visibleInModal
										options={optionForecastStatus}
										checkVal={forecast_val}
										setCheckVal={set_forecast_val}
										onApply={onApplyForecast}
										onCancel={onCancelForecast}
									/>
									<CheckboxDD
										id="territory-modal"
										title="Owner Territory"
										visibleInModal
										options={optionOwnerTerritory}
										checkVal={territory_val}
										setCheckVal={set_territory_val}
										onApply={onApplyTerritory}
										onCancel={onCancelTerritory}
									/>
									<RadioDD
										title="Segmentation"
										id="segmentation"
										visibleInModal
										options={optionVertical}
										radioVal={vertical_val}
										setRadioVal={set_vertical_val}
										onApply={onApplyVertical}
										onCancel={onCancelVertical}
										initialValue={vertical_initial_value}
									/>
									<RadioDD
										title="Show By"
										id="show_by-modal"
										options={optionShowPipeline}
										visibleInModal
										radioVal={show_val}
										setRadioVal={set_show_val}
									/>
								</FilterModal>
							</FilterContainer>
							{/* ============= Your Component here! ============= */}
							< GrowthContent
								selectedData={selectedData}
								setSelectedData={setSelectedData}
								growthData={growthData}
							/>
						</>
						: <ResourceNotFoundError redirectURL={"/executive-cockpit/channel-bookings-revenue/growth"} />
					: <InternalServerError redirectURL={"/executive-cockpit/channel-bookings-revenue/growth"} />}
			</div>
			<Comment
				data={comments}
				onCloseComment={onCloseComment}
				showComment={showComment}
			/>
		</>
	);
};

export default Growth;
