import React, { useState } from 'react';
import { Dropdown, Form, Button } from 'react-bootstrap';
import { BiChevronDown } from 'react-icons/bi'
import './customDropdown.scss';


const CheckboxDD = ({ 
  options, 
  className, 
  id, 
  title, 
  visibleInModal, 
  setCheckVal,
  checkVal,
  valueApplied,
  onApply,
  onCancel
}) => {
  const [show, setShow] = useState(false);

  const handleSelect = (e) => {
    const checkValue = checkVal.some(item => item === e);
    const remove = checkVal.filter(item => item !== e);
    if (!checkValue) {
      setCheckVal([...checkVal, e])
    } else {
      setCheckVal(remove)
    }
  }

  const handleToggle = () => {
    setShow(!show)
    onCancel();
  }

  return (
    <>
      {visibleInModal
        ? <div className={`checkbox-in-modal narrow-list filter-modal ${className ? className : ''}`}>
          <div className="title">{title ? title : checkVal}</div>
          <Form>
            {options && options.map(option => (
              <Form.Check
                key={option.key}
                custom
                type='checkbox'
                id={`check_modal-${option.key}`}
                label={option.text}
                checked={checkVal.some(item => item === option.key)}
                onChange={() => handleSelect(option.key)}
              />
            ))}
          </Form>
        </div>

        : <Dropdown
          show={show}
          onToggle={handleToggle}
          className={`checkbox-dd narrow-list ${className ? className : ''}`}
        >
          <Dropdown.Toggle id={`checkbox_dd-${id}`} variant='light' className={checkVal.length !== 0 ? 'hasPick' : ''}>
            {valueApplied !== undefined 
              ? valueApplied.length === 1 
                ? valueApplied[0] 
                : title + (valueApplied.length >= 1 ? ` ( ${valueApplied.length} )` : '')
              : title
            } 
            <BiChevronDown viewBox="6 8 12 12" />
          </Dropdown.Toggle>


          <Dropdown.Menu show={show}>
            {options && options.map(option => (
              <Form.Check
                key={option.key}
                custom
                type='checkbox'
                id={`check_dd-${option.key}`}
                label={option.text}
                checked={checkVal.some(item => item === option.key)}
                onChange={() => handleSelect(option.key)}
              />
            ))}
            <div className="btn-block">
              <Button variant="link" onClick={() => handleToggle()}>Cancel</Button>
              <Button variant="primary" onClick={() => {onApply(); setShow(!show);}}>Apply</Button>
            </div>
          </Dropdown.Menu>
        </Dropdown>
      }
    </>
  );
}

export default CheckboxDD;
