import React from 'react';

import { ImArrowUpRight2, ImArrowDownRight2 } from "react-icons/im";
import { convertToUSD, roundUpToTenth, convertToPPvalue, convertToWholePercent } from '../../constants/formatter';
import './kpi.scss';

const Kpi = (props) => {
  const { data, total, isCount, isPercent } = props;

  let revenue = isCount ? total.value : isPercent ? total.value : convertToUSD(total.value);

  return (
    <>
      {isCount ? <div className="total-revenue"> <span>{revenue}</span> </div> :

        isPercent ? <div className="total-revenue"> <span>{revenue}</span>%</div>
          :
          <>
            {/M/g.test(revenue) === false ?
              <div className="total-revenue">{revenue.slice(0, 1)} <span>{revenue.slice(1)}</span> </div> :
              <div className="total-revenue">{revenue.slice(0, 1)} <span>{revenue.slice(1, -1)}</span> {revenue.slice(-1)}</div>
            }
          </>
      }

      {Object.keys(data).map((key, i) => {

        let pp_value = parseFloat(data[key].pp_value);
        let quarter_period = data[key].quarter_period;
        let counts = data.counts ? data.counts : [];
        let bgColor = pp_value >= 0.5 ? 'indicator-up' : pp_value <= -0.5 ? 'indicator-down' : 'indicator-middle';
        let icon = pp_value >= 0.5 ? <ImArrowUpRight2 /> : pp_value <= -0.5 ? <ImArrowDownRight2 /> : <span>&nbsp;&nbsp;&nbsp;</span>;
        return (key !== "total" && key !== "revenue" ?
          <div key={i}>
            {key === 'count' || key === 'counts' ?
              <>
                {counts ?
                  <>
                    {counts.map((item, index) => {
                      return (
                        <div className='kpi-item border-bottom'>
                          <div className="title">{item.label}</div>
                          <div className="div-table">
                            <div className="tr">
                              <div className="td val">
                                <span>{item.value}</span>
                              </div>
                            </div>
                          </div>
                        </div>
                      )
                    })}
                  </>
                  :
                  <div className={data[Object.keys(data)[Object.keys(data).length - 1]].label === data[key].label ? 'kpi-item' : 'kpi-item border-bottom'}>
                    <div className="title">{data[key].label}</div>
                    <div className="div-table">
                      <div className="tr">
                        <div className="td val">
                          <span>{data[key].value}</span>
                        </div>
                      </div>
                    </div>
                  </div>

                }

              </>
              :

              <div key={i}>
                {data[key].value.slice(0, 1) === '$' ? null :
                  <div className={data[Object.keys(data)[Object.keys(data).length - 1]].label === data[key].label ? 'kpi-item' : 'kpi-item border-bottom'}>
                    <div className="title">{data[key].label}</div>
                    <div className="div-table">
                      <div className="tr">
                        {/\$/g.test(data[key].label) === true ?
                          <div className="td val"><span>{data[key].value !== "" ? convertToUSD(data[key].value) : '-'}</span></div>
                          : /\%/g.test(data[key].label) === true ?
                        <div className="td val">{data[key].value !== "" ? <><span>{`${convertToWholePercent(data[key].value)}`}</span>{'%'}</> : <span>{'-'}</span>}</div>
                            : <div className="td val"><span>{data[key].value !== "" ? roundUpToTenth(data[key].value) : '-'}</span></div>
                        }
                        {data[key].pp_value ?
                          <>
                            <div className="td middle">
                              <span className={`indicator ${bgColor}`}>  {icon}
                                {pp_value !== "" ? convertToPPvalue(pp_value) : "0.0pp"}
                              </span>
                            </div>
                            <div className="td middle"><span className="period" > vs {quarter_period}</span></div>
                          </>
                          : null}
                      </div>
                    </div>
                  </div>
                }
              </div>
            }
          </div>

          : null)
      })}
    </>
  );
}
export default Kpi;