import React from 'react';
// Components
import { Dropdown, Tooltip, OverlayTrigger } from 'react-bootstrap';
import BootstrapTable from 'react-bootstrap-table-next';
import ToolkitProvider, { Search } from 'react-bootstrap-table2-toolkit';
import filterFactory from 'react-bootstrap-table2-filter';
import paginationFactory, { PaginationProvider, PaginationListStandalone, SizePerPageDropdownStandalone } from 'react-bootstrap-table2-paginator';
import { CgSortAz, CgSortZa } from "react-icons/cg";
// Stylesheets
import 'react-bootstrap-table-next/dist/react-bootstrap-table2.min.css';
import './customTable.scss';

const CustomTable = props => {
	const { data, columns, tableTitle, addSearch, addSort, addPaginate, tableName, rowClasses, selectRow, defaultSorted, rowsPerPage, expandRow, addFilters, addHover, rowEvents, addLegends } = props;
	const { SearchBar } = Search;
	const customCaret = (order, column) => {
		if (!order) return null;
		else if (order === 'asc') return (<CgSortAz className="sort-icon" />);
		else if (order === 'desc') return (<CgSortZa className="sort-icon" />);
		return null;
	}

	const customHeader = (column, colIndex, { sortElement, filterElement }) => {
		let str = column.text.toString()
		let new_id = str.replace(/[^a-zA-Z0-9]/g, "")
		const renderTooltip = (props) => (
			<Tooltip id={`tooltip-header-${new_id}`} {...props}>
				{column.text}
			</Tooltip>
		);

		return (
			<OverlayTrigger overlay={renderTooltip}>
				<div className="with-ellipsis">
					{column.text}
					{sortElement}
				</div>
			</OverlayTrigger>
		)
	}

	const emptyState = () => {
		return <p className="mb-0">Table is Empty</p>;
	}

	const sizePerPageRenderer = ({ options, currSizePerPage, onSizePerPageChange }) => {
		return (
			<Dropdown className="sort-dropdown">
				<span className="mr-2">Sort</span>
				<Dropdown.Toggle id="size-per-page" variant="light">
					{data.length === Number(currSizePerPage) ? 'All' : currSizePerPage}
				</Dropdown.Toggle>
				<Dropdown.Menu>
					{options.map(option => (
						<Dropdown.Item key={option.text} type="button" onClick={() => onSizePerPageChange(option.page)}>
							{option.text}
						</Dropdown.Item>
					))}
				</Dropdown.Menu>
			</Dropdown>
		);
	}

	const formatColumns = columns.map((item) => {
		if (item.sort === true) {
			return {
				...item,
				sortCaret: customCaret,
				headerFormatter: customHeader
			};
		}
		return {
			...item,
			headerFormatter: customHeader
		};
	});

	let rowsPerPageList = rowsPerPage ? rowsPerPage : [{
		text: '20',
		value: 20
	}, {
		text: '30',
		value: 30
	}]

	const options = {
		sizePerPageRenderer,
		custom: true,
		paginationSize: 1,
		pageStartIndex: 1,
		alwaysShowAllBtns: true,
		withFirstAndLast: true,
		firstPageText: '<<',
		prePageText: '<',
		nextPageText: '>',
		lastPageText: '>>',
		nextPageTitle: 'Next page',
		prePageTitle: 'Previous page',
		firstPageTitle: 'First page',
		lastPageTitle: 'Last page',
		showTotal: true,
		totalSize: data.length,
		sizePerPageList: rowsPerPageList
	}

	const contentTable = ({ paginationProps, paginationTableProps }) => {
		return (
			<ToolkitProvider
				keyField="_id"
				data={data}
				columns={formatColumns}
				// search={{ searchFormatted: true }}
				search
			>
				{toolkitprops => (
					<div className="table-container">
						<div className={`table-header ${tableTitle || addSearch || addSort ? 'mb-3' : ''}`}>
							<div className="left">
								{tableTitle && <h2 className="table-title">{tableTitle}</h2>}
								{addLegends}
							</div>
							<div className="right">
								{addFilters}
								{addSearch &&
									<div className={addFilters ? 'ml-3' : ''}>
										<SearchBar {...toolkitprops.searchProps} />
									</div>
								}
							</div>
						</div>
						<div className="table-body custom-scroll">
							<BootstrapTable
								classes={tableName ? tableName : ''}
								bordered={false}
								hover={addHover}
								defaultSorted={defaultSorted}
								rowClasses={rowClasses}
								expandRow={expandRow}
								noDataIndication={emptyState}
								filter={filterFactory()}
								selectRow={selectRow}
								rowEvents={rowEvents}
								{...toolkitprops.baseProps}
								{...paginationTableProps}
							/>
						</div>
						{addPaginate || addSort 
							? <div className="table-footer">
									{addPaginate && <PaginationListStandalone {...paginationProps} />}
									{addSort && <SizePerPageDropdownStandalone {...paginationProps} />}
								</div>
							: null	
						}
					</div>
				)}
			</ToolkitProvider>
		);
	}

	return (
		<div className={`custom-table ${props.className ? props.className : ''}`}>
			<PaginationProvider pagination={paginationFactory(options)}>
				{contentTable}
			</PaginationProvider>
		</div>
	);
}

export const formatLongText = (cell, row) => {
	let str = cell.toString()
	let new_id = str.replace(/[^a-zA-Z0-9]/g, "")
	const renderTooltip = (props) => (
		<Tooltip id={`tooltip-${new_id}`} {...props}>
			{cell}
		</Tooltip>
	);
	return (
		<>
			<OverlayTrigger overlay={renderTooltip} >
				<div className="with-ellipsis">{cell}</div>
			</OverlayTrigger>
		</>
	)
};

export default CustomTable;