import React, { useState } from 'react';
import { Container, Row, Col, Card } from 'react-bootstrap';
import { ImArrowUpRight2, ImArrowDownRight2 } from "react-icons/im";

import Legend from '../../components/legend/Legend';
import { BasicDD } from '../../components/customDropdown';
import CustomTable from '../../components/customTable/CustomTable';
import StackedColumnChart from '../../components/chart/StackedColumnChart';
import NestedDonutChart from '../../components/chart/NestedDonutChart';
import LineChart from '../../components/chart/LineChart';
import KPI_NEW from '../../components/kpi/KPI_NEW';

import { optionOverviewFilter, optionOverviewFilter1 } from '../../constants/filterOptions';
import { convertToUSDGrowth, convertToPercent, convertToWholePercent } from '../../constants/formatter';


const OverviewContent = (props) => {
  const { overviewData, selectedRangeValue } = props;
  const [kpiClicked, setkpiClicked] = useState(null);

  const stacked_column_initial_value = optionOverviewFilter ? optionOverviewFilter[0] : null;
  const [stackedColumnSelection, setStackedColumnSelection] = useState(stacked_column_initial_value);

  //by segment
  const overallSegment = overviewData ? overviewData.overall : [];
  const percentofGrossBySegment = overviewData ? overviewData.percent_of_gross_by_segment ? overviewData.percent_of_gross_by_segment : overviewData.percent_of_gross_by_segment : [];
  const segmentRevenueMixSegment = overviewData ? overviewData.cross_border_bkgs_mix_percent_by_segment : [];
  const crossBorderBKGSBySegment = overviewData ? overviewData.cross_border_bkgs_by_segment : [];
  const segmentDetailsSegment = overviewData ? overviewData.segment_details : [];

  let cross_border_bkgs_by_segment_in_gross_percentage = overviewData ? overviewData.cross_border_bkgs_by_segment_in_gross_percentage : [];
  let cross_border_bkgs_by_segment_in_p4q_percentage = overviewData ? overviewData.cross_border_bkgs_by_segment_in_p4q_percentage : [];
  let cross_border_bkgs_by_segment_in_quarter_percentage = overviewData ? overviewData.cross_border_bkgs_by_segment_in_quarter_percentage : [];
  let cross_border_bkgs_by_segment_in_year_percentage = overviewData ? overviewData.cross_border_bkgs_by_segment_in_year_percentage : [];
  const nested_segment_revenue_mix = selectedRangeValue === "last_completed_quarter" ? overviewData.nested_segment_revenue_mix_lcq : selectedRangeValue === "last_4_quarters" ? overviewData.nested_segment_revenue_mix_l4q : overviewData.nested_segment_revenue_mix_ytd || [];


  const indicatorPercent = (cell, row) => {
    let bgColor = cell >= 0.5 ? 'indicator-up' : cell <= -0.5 ? 'indicator-down' : 'indicator-middle';
    let icon = cell >= 0.5 ? <ImArrowUpRight2 /> : cell <= -0.5 ? <ImArrowDownRight2 /> : <span className="mr-3"></span>;

    return (
      <>
        <span className={`indicator ${bgColor}`}>
          {icon}
          {convertToPercent(cell)}
        </span>
      </>
    );
  };

  const formatUSDGrowth = (cell, row) => {
    return (
      <span>
        {convertToUSDGrowth(cell)}
      </span>
    );
  };

  const formatToPercent = (cell, row) => {
    return (
      <span>
        {convertToPercent(cell)}
      </span>
    );
  };

  const defaultSorted = [
    {
      dataField: '_id',
      order: 'asc',
    }
  ];

  const columns = [
    {
      dataField: '_id',
      text: 'id',
      hidden: true
    },
    {
      dataField: 'segment',
      text: 'SEGMENT',
      classes: 'segment-th',
      headerClasses: 'segment-td'
    },
    {
      dataField: 'mrc_usd',
      text: 'MRC $',
      sort: true,
      formatter: formatUSDGrowth,
      classes: 'right',
      headerClasses: 'right'
    },
    {
      dataField: 'mix_percent',
      text: 'MIX %',
      sort: true,
      formatter: formatToPercent,
      classes: 'center',
      headerClasses: 'center'
    },
    {
      dataField: 'q_by_q_growth_percent',
      text: 'Q/Q % GROWTH',
      sort: true,
      formatter: indicatorPercent,
      classes: 'with-ellipsis center',
      headerClasses: 'center',
    },
    {
      dataField: 'q_to_q_growth_usd',
      text: 'Q-Q $ GROWTH',
      sort: true,
      formatter: formatUSDGrowth,
      classes: 'right',
      headerClasses: 'right',
    },
    {
      dataField: 'y_by_y_growth_percent',
      text: 'Y/Y % GROWTH',
      sort: true,
      formatter: indicatorPercent,
      classes: 'with-ellipsis center',
      headerClasses: 'center',
    },
    {
      dataField: 'y_to_y_growth_usd',
      text: 'Y-Y $ GROWTH',
      sort: true,
      formatter: formatUSDGrowth,
      classes: 'right',
      headerClasses: 'right',
    },
    {
      dataField: 'percent_of_gross',
      text: '% of GROSS',
      sort: true,
      formatter: formatToPercent,
      classes: 'right',
      headerClasses: 'right'
    },
  ];

  return (
    <Container fluid>
      <Row className="contentDivider pb-30">
        <div className="fix-width">
          <Card className="TotalRevenue revenue " >
            <Card.Body>
              <Card.Title>cross border bkgs</Card.Title>
              <KPI_NEW
                totalValue={overallSegment.total.value}
                totalUnit={overallSegment.total.unit}
                details={overallSegment.details}
                setkpiClicked={setkpiClicked}
                kpiClicked={kpiClicked}
              />
              {/* <Kpi
                data={overall_segment}
                total={overall_segment.total}
                withPercent={true}
              /> */}
            </Card.Body>
          </Card>
        </div>
        <Col>
          <Card className="RevenueByType revenue">
            <Card.Body>
              <Card.Title>cross border bkgs by segment</Card.Title>

              {kpiClicked === '% of Gross' ?
                <>
                  <Legend legend={cross_border_bkgs_by_segment_in_gross_percentage.legend} />
                  <div style={{ height: "400px" }}>
                    <LineChart
                      id={`cross_border_bkgs_by_segment_in_gross_percentage-line_chart-segment`}
                      data={cross_border_bkgs_by_segment_in_gross_percentage.data}
                      legend={cross_border_bkgs_by_segment_in_gross_percentage.legend}
                      isAnimationEnabled={false}
                    />
                  </div>
                </>
                :

                kpiClicked === 'P4Q % Growth' ?
                  <>
                    <Legend legend={cross_border_bkgs_by_segment_in_p4q_percentage.legend} />
                    <div style={{ height: "400px" }}>
                      <LineChart
                        id={`cross_border_bkgs_by_segment_in_p4q_percentage-line_chart-segment`}
                        data={cross_border_bkgs_by_segment_in_p4q_percentage.data}
                        legend={cross_border_bkgs_by_segment_in_p4q_percentage.legend}
                        isAnimationEnabled={false}
                      />
                    </div>
                  </>
                  :
                  kpiClicked === 'Y/Y Growth %' ?
                    <>
                      <Legend legend={cross_border_bkgs_by_segment_in_year_percentage.legend} />
                      <div style={{ height: "400px" }}>
                        <LineChart
                          id={`cross_border_bkgs_by_segment_in_year_percentage-line_chart-segment`}
                          data={cross_border_bkgs_by_segment_in_year_percentage.data}
                          legend={cross_border_bkgs_by_segment_in_year_percentage.legend}
                          isAnimationEnabled={false}
                        />
                      </div>
                    </>
                    :
                    kpiClicked === 'Q/Q Growth %' ?
                      <>
                        <Legend legend={cross_border_bkgs_by_segment_in_quarter_percentage.legend} />
                        <div style={{ height: "400px" }}>
                          <LineChart
                            id={`cross_border_bkgs_by_segment_in_quarter_percentage-line_chart-segment`}
                            data={cross_border_bkgs_by_segment_in_quarter_percentage.data}
                            legend={cross_border_bkgs_by_segment_in_quarter_percentage.legend}
                            isAnimationEnabled={false}
                          />
                        </div>
                      </>

                      :
                      <>
                        <div className="d-flex">
                          <Legend legend={crossBorderBKGSBySegment.legend} />
                          {/* {selectedRangeValue === "last_completed_quarter" ? */}
                          <BasicDD
                            className="ml-auto"
                            id={`stacked_column_dd`}
                            options={optionOverviewFilter}
                            value={stackedColumnSelection}
                            setValue={setStackedColumnSelection}
                          />
                          {/* : null
													} */}
                        </div>
                        <div style={{ height: "400px", marginTop: "1rem" }}>
                          <StackedColumnChart
                            id={`stacked_column_chart`}
                            hiddenValueAxis={false}
                            isOneHundredPercent={false}
                            isPercentage={false}
                            largerWidth={true}
                            hideLabelBullet={false}
                            isOverviewChart={true}
                            isGrowthPercentToggable={true}
                            stackedColumnSelection={stackedColumnSelection.key}
                            selectedRangeValue={'last_completed_quarter'}
                            data={crossBorderBKGSBySegment.data}
                            legend={crossBorderBKGSBySegment.legend}
                            isAnimationEnabled={false}
                          />
                        </div>
                      </>
              }

              {/* <div className="d-flex">
                <Legend legend={crossBorderBKGSBySegment.legend} />
                {
                  selectedRangeValue === "last_completed_quarter" ?
                    <BasicDD
                      className=" ml-auto"
                      id="overviewStackedColumn"
                      options={optionOverviewFilter}
                      value={stackedColumnSelection}
                      setValue={setStackedColumnSelection}
                    /> : null
                }
              </div>
              <div style={{ height: "400px" }}>
                <StackedColumnChart
                  id="crossBorderBKGSBySegment_stacked_column_chart"
                  hiddenValueAxis={false}
                  isOneHundredPercent={false}
                  isPercentage={false}
                  largerWidth={true}
                  hideLabelBullet={false}
                  isOverviewChart={true}
                  isGrowthPercentToggable={true}
                  stackedColumnSelection={stackedColumnSelection.key}
                  selectedRangeValue={selectedRangeValue}
                  data={crossBorderBKGSBySegment.data}
                  legend={crossBorderBKGSBySegment.legend}
                />
              </div> */}
            </Card.Body>
          </Card>
        </Col>
        <Col sm={12} md={3} lg={3}>
          <Card className="Segment revenue">
            <Card.Body>
              <Card.Title>cross border bkgs mix % by segment</Card.Title>
              <Legend legend={nested_segment_revenue_mix.legend} />
              <div style={{ height: "92%" }}>
                <NestedDonutChart
                  id={`nested-donut-chart`}
                  insideValueName={"inner_quarter"}
                  insideValue={"inner_quarter_percentage"}
                  insideCategory={"hq_region"}
                  outsideValueName={"outer_quarter"}
                  outsideValue={"outer_quarter_percentage"}
                  outsideCategory={"hq_region"}
                  loadedData={nested_segment_revenue_mix.data}
                  isAnimationEnabled={true}
                />
              </div>
            </Card.Body>
          </Card>
        </Col>
      </Row>
      <Row className="contentDivider">
        <Col>
          <Card className="SegmentDetails revenue">
            <Card.Body>
              <Card.Title>segment details</Card.Title>
              <CustomTable
                tableName='segment-details'
                data={segmentDetailsSegment === undefined ? [] : segmentDetailsSegment}
                defaultSorted={defaultSorted}
                columns={columns}
              />
            </Card.Body>
          </Card>
        </Col>
        <Col sm={12} md={3} lg={3}>
          <Card className="SegmentTrends revenue">
            <Card.Body>
              <Card.Title>% of gross by segment</Card.Title>
              <Legend legend={percentofGrossBySegment.legend} />
              <div style={{ height: "300px" }}>
                <LineChart
                  id={"cross_border_trade_overview_line_chart"}
                  data={percentofGrossBySegment.data}
                  legend={percentofGrossBySegment.legend}
                />
              </div>
            </Card.Body>
          </Card>
        </Col>
      </Row>
    </Container>
  );
};

export default OverviewContent;