import React, { useState } from 'react';
import { Container, Row, Col, Card } from 'react-bootstrap';
import { ImArrowUpRight2, ImArrowDownRight2 } from "react-icons/im";
import Legend from '../../components/legend/Legend';
import CustomTable from '../../components/customTable/CustomTable';
import BubbleChart from '../../components/chart/BubbleChart';
import LineChart from '../../components/chart/LineChart';
import StackedColumnChart from '../../components/chart/StackedColumnChart';
import {BasicDD} from '../../components/customDropdown';
import { optionGrowthFilter2 } from '../../constants/filterOptions';
import { convertToUSDGrowth, convertToPercent, convertToWholePercent } from '../../constants/formatter';


const GrowthContent = (props) => {
  const { bubbleName, selectedData, idState, resultData, selectedBubbleData, setSelectedData, setSelectedBubbleData, setBubbleName, setReferenceID, growthData } = props;

  const bubble_initial_value = optionGrowthFilter2 ? optionGrowthFilter2[0] : null;
  const [bubbleSelection, setBubbleSelection] = useState(bubble_initial_value);

  if (idState) {
    selectedBubbleData.map(item => {

      if (item._id !== idState) {
        item.color = 'rgba(234, 240, 244, 0.2)';
      } else {
        if (item.region === 'APAC') {
          item.color = '#CD503C'
        } else if (item.region === 'AMER') {
          item.color = '#DC7431'
        } else if (item.region === 'EMEA') {
          item.color = '#F0CC71'
        }
      }

      return item;
    })
  } else if (growthData.total_revenue_size_and_growth.data) {
    growthData.total_revenue_size_and_growth.data.map(item => {
      item.borderColor = '#fff';
      item.less_y_to_y_usd_growth = '#fff';
      item.less_rev_dollar = '#fff';
      if (bubbleSelection.key === 'y-y_growth') {
        if (item.y_to_y_usd_growth < 0) {
          item.less_y_to_y_usd_growth = '#ff3333';
          if (item.region === 'APAC') {
            item.color = 'rgba(205, 80, 60, 0.5)';
          } else if (item.region === 'AMER') {
            item.color = 'rgba(220, 116, 49, 0.5)';
          } else if (item.region === 'EMEA') {
            item.color = 'rgba(240, 204, 113, 0.5)'
          }
        }
      } else if (bubbleSelection.key === 'rev_dollar') {
        if (item.total_revenue < 0) {
          item.less_rev_dollar = '#ff3333';
          if (item.region === 'APAC') {
            item.color = 'rgba(205, 80, 60, 0.5)';
          } else if (item.region === 'AMER') {
            item.color = 'rgba(220, 116, 49, 0.5)';
          } else if (item.region === 'EMEA') {
            item.color = 'rgba(240, 204, 113, 0.5)'
          }
        }
      }

    })

  }

  const indicatorPercent = (cell, row) => {
    let bgColor = cell >= 0.5 ? 'indicator-up' : cell <= -0.5 ? 'indicator-down' : 'indicator-middle';
    let icon = cell >= 0.5 ? <ImArrowUpRight2 /> : cell <= -0.5 ? <ImArrowDownRight2 /> : <span className="mr-3"></span>;

    return (
      <>
        <span className={`indicator ${bgColor}`}>
          {icon}
          {convertToPercent(cell)}
        </span>
      </>
    );
  };

  const formatUSDGrowth = (cell, row) => {
    return (
      <span>
        {convertToUSDGrowth(cell)}
      </span>
    );
  };

  const formatWholePercent = (cell, row) => {
    return (
      <span>
        {convertToWholePercent(cell)}
      </span>
    );
  };

  const rankFormat = (cell, row, rowIndex, formatExtraData) => {
    return (
      <span>{rowIndex + 1}</span>
    );
  };

  const handleOnSelect = (row, isSelect, rowIndex) => {
    let selectedData;

    for (const key in byRankId) {
      const element = byRankId[key];

      if (element._id === row._id) {
        selectedData = element;
      }
    }

    if (isSelect) {
      setReferenceID(row._id);
      setSelectedData(selectedData);
      setSelectedBubbleData(growthData.total_revenue_size_and_growth.data);
      setBubbleName(row.segment);
    }
  };

  const selectRow = {
    mode: 'radio',
    clickToSelect: true,
    hideSelectColumn: true,
    onSelect: handleOnSelect,
    classes: 'selected-row',
    bgColor: '#ecf2ff'
  };

  const rowClasses = () => {
    let classes = 'selectable';
    return classes
  }

  const defaultSorted = [
    {
      dataField: 'segment',
      order: 'desc',
    }
  ];

  const columns = [
    {
      dataField: '_id',
      text: 'id',
      hidden: true
    },
    {
      dataField: '',
      text: 'Rank',
      classes: 'rank center',
      headerClasses: 'rank center',
      formatter: rankFormat,
    },
    {
      dataField: 'segment',
      text: 'SEGMENT',
      
      
      classes: 'segment',
      headerClasses: 'segment'
    },
    {
      dataField: 'total_revenue',
      text: 'TOTAL REVENUE',
      sort: true,
      classes: 'right',
      headerClasses: 'right',
      formatter: formatUSDGrowth
    },

    {
      dataField: 'y_by_y_growth_percent',
      text: 'Y/Y % GROWTH',
      sort: true,
      formatter: indicatorPercent,
      classes: 'with-ellipsis right',
      headerClasses: 'right'
    },
    {
      dataField: 'y_to_y_usd_growth',
      text: 'Y-Y $ GROWTH',
      sort: true,
      classes: 'right',
      headerClasses: 'right',
      formatter: formatUSDGrowth
    },
    {
      dataField: 'y_mix_percent',
      text: 'MIX%',
      sort: true,
      classes: 'right',
      headerClasses: 'right',
      formatter: formatWholePercent
    },
    {
      dataField: 'q_by_q_growth_percent',
      text: 'Q/Q % GROWTH',
      sort: true,
      formatter: indicatorPercent,
      classes: 'with-ellipsis right',
      headerClasses: 'right'
    },
    {
      dataField: 'q_to_q_usd_growth',
      text: 'Q-Q $ GROWTH',
      sort: true,
      classes: 'right',
      headerClasses: 'right',
      formatter: formatUSDGrowth
    },
    {
      dataField: 'q_mix_percent',
      text: 'MIX%',
      sort: true,
      classes: 'right',
      headerClasses: 'right',
      formatter: formatWholePercent
    }
  ];

  let segmentRanking = growthData ? growthData.segment_ranking : [];
  let byRankId = resultData ? resultData.by_rank_id : [];
  let byRank = segmentRanking.by_rank === undefined ? [] : segmentRanking.by_rank;
  let segmentTrends = selectedData ? selectedData.segment_y_by_y_growth_trends : growthData.segment_trends;
  let segmentByRegion = selectedData ? selectedData.segment_by_region : growthData.segment_by_region;

  return (
    <Container fluid>
      <Row className="contentDivider">
        <Col md="5" xs="12">
          <Card className="revenue">
            <Card.Body>
              <Card.Title>total revenue size and growth</Card.Title>
              <div className="d-flex">
                <Legend legend={growthData.total_revenue_size_and_growth.legend} />
                <BasicDD
                  className=" ml-auto"
                  id="group"
                  addTitleOnWide
                  options={optionGrowthFilter2}
                  value={bubbleSelection}
                  setValue={setBubbleSelection}
                />
              </div>
              <div style={{ height: "92%" }}>
                <BubbleChart
                  id={idState ? bubbleName : bubbleSelection.key ? bubbleSelection.key : "customer_revenue_bubble_chart"}
                  data={idState ? selectedBubbleData : growthData.total_revenue_size_and_growth.data}
                  selected={idState}
                  bubbleSelection={bubbleSelection.key}
                  isAnimationEnabled={true}
                />
              </div>
            </Card.Body>
          </Card>
        </Col>
        <Col md="7" xs="12">
          <Row>
            <Col md="12" xs="12" className="pb-0">
              <Card className="revenue ">
                <Card.Body>
                  <Card.Title>segment ranking</Card.Title>
                  <CustomTable
                    tableName='segment-ranking scrollable'
                    data={byRank}
                    selectRow={selectRow} // For Selecting Row ===
                    defaultSorted={defaultSorted} 
                    rowClasses={rowClasses}
                    columns={columns}
                  />
                </Card.Body>
              </Card>
            </Col>
            <Col className="pb-0 pt-30" md="12" xs="12">
              <Row >
                <Col md="6" xs="12" className="pb-0">
                  <Card className="revenue">
                    <Card.Body>
                      <Card.Title>segment y/y growth trends</Card.Title>
                      <Legend legend={segmentTrends.legend} />
                      <div style={{ height: "350px" }}>
                        <LineChart
                          id={"total_revenue_growth_line_chart"}
                          data={segmentTrends.data}
                          legend={segmentTrends.legend}
                          isAnimationEnabled={true}
                        />
                      </div>
                    </Card.Body>
                  </Card >
                </Col>
                <Col md="6" xs="12" className="pb-0">
                  <Card className="revenue">
                    <Card.Body>
                      <Card.Title>segment contribution</Card.Title>
                      <Legend legend={segmentByRegion.legend} />
                      <div style={{ height: "350px" }}>
                        <StackedColumnChart
                          id="total_revenue_segment_by_region_stacked_column_chart"
                          hiddenValueAxis={true}
                          isOneHundredPercent={false}
                          isPercentage={false}
                          largerWidth={true}
                          hideLabelBullet={false}
                          data={segmentByRegion.data}
                          legend={segmentByRegion.legend}
                          isAnimationEnabled={true}
                        />
                      </div>
                    </Card.Body>
                  </Card>
                </Col>
              </Row>
            </Col>
          </Row>
        </Col>
      </Row>
    </Container>
  );
};

export default GrowthContent;