import React, { useState, useEffect } from 'react';
import { Row, Col, Card, Table, Button } from 'react-bootstrap';
import Owner from '../../assets/images/user-quincy.jpeg';
import CustomTable from '../../components/customTable/CustomTable';
import { API_BASE_URL } from '../../constants/config';
import axiosRetry from 'axios-retry';
import axios from 'axios';


const AdditionalResourcesContent = (props) => {
  const { customerData, rankingVal_onApply, tab_id } = props;
  const [idState, setReferenceID] = useState(null);
  const [modalShow, setModalShow] = useState(false);
  const [modalData, setModalData] = useState(null);
  const [toggleSelectedRow, setToggleSelectedRow] = useState();
  const [selectedCustomer, setCustomerSelected] = useState(null);
  const [referencesData, setReferencesData] = useState();
  const [standardReportsData, setStandardReportsData] = useState();
  const [otherResourcesData, setOtherResourcesData] = useState();
  const [isLoading, setIsLoading] = useState(true)

  // Data Distribution
  let topLabelTitle = rankingVal_onApply ? `${rankingVal_onApply.topBottom.split('_')[1]} ${rankingVal_onApply.numberInput} ` : '';
  const contribution_to_total_usd_by_customer_tier = customerData ? customerData.contribution_to_total_usd_by_customer_tier : [];
  const y_by_y_percent_growth_by_customer_tier = customerData ? customerData.y_by_y_percent_growth_by_customer_tier : [];
  const deploymentMap = customerData ? customerData.deployment_map : [];
  const byRank = customerData ? customerData.customer_ranking.by_rank : [];
  const byRankId = customerData ? customerData.customer_ranking.by_rank_id : [];

  // Axios call
  const fetchData = async () => {
    let url = `${API_BASE_URL}/references.json?print=pretty`;

    setIsLoading(true);
    axiosRetry(axios, { retries: 3 });

    await axios({
      method: 'GET',
      url: url
    }).then(result => {
      let data = result.data;
      setReferencesData(data['metrics_definition'])
      setStandardReportsData(data['standardized-reports'])
      setOtherResourcesData(data['other-resources'])
      setIsLoading(false);
    }).catch(error => {
      console.log(error);
      setReferencesData([]);
      setIsLoading(false);
    });
  };

  useEffect(() => {
    fetchData();
  }, []);



  const handleOnSelect = (row, isSelect, rowIndex) => {
    let modalDataByRank;

    setToggleSelectedRow(row._id)
    setCustomerSelected(row.customer);

    for (const key in byRankId) {
      const element = byRankId[key];
      if (element._id === row._id) {
        modalDataByRank = element
      }
    }

    if (isSelect) {
      setModalShow(true);
      setReferenceID(row._id);
      setModalData(modalDataByRank);
    }
  };

  const selectRow = {
    mode: 'radio',
    clickToSelect: true,
    hideSelectColumn: true,
    onSelect: handleOnSelect,
    classes: 'selected',
    bgColor: '#ecf2ff'
  };

  const onCloseModal = async () => {
    await setModalShow(false)
    await setCustomerSelected(null);
    await setReferenceID(null);
    await setToggleSelectedRow()

    setTimeout(() => {
      setModalData(null);
    }, 1000);
  };

  const rowClasses = (row) => {
    let classes = row._id === toggleSelectedRow ? 'selected' : 'selectable';
    return classes
  };

  const downloadBtn = (row) => {
    return (
      <Button variant="link" className="btn-clear download-btn" onClick={row.download_url}>
        Download
      </Button>
    )
  }

  const defaultSorted = [{
    dataField: 'metric',
    order: 'desc',
  }];

  const columns = [
    {
      dataField: 'metric',
      text: 'Metric',
      classes: 'metric',
      headerClasses: 'metric',
    },
    {
      dataField: 'definition',
      text: 'definition',
      classes: 'definition'
    },
  ];
  const standardSorted = [{
    dataField: 'metric',
    order: 'desc',
  }];

  const standardColumns = [
    {
      dataField: '_id',
      text: 'id',
      hidden: true
    },
    {
      dataField: 'resource',
      text: 'Report',
      classes: 'resource with-ellipsis',
      headerClasses: 'resource',
    },
    {
      dataField: 'date_updated',
      text: 'Date Updated',
      classes: 'right',
      headerClasses: 'right',
    },
    {
      dataField: 'download_url',
      text: '',
      formatter: downloadBtn,
      classes: 'center',
      headerClasses: 'center',
    },
  ];
  const otherSorted = [
    {
      dataField: '_id',
      text: 'id',
      hidden: true
    },
    {
      dataField: 'metric',
      order: 'desc',
    }
  ];

  const otherColumns = [
    {
      dataField: 'resource',
      text: 'Resource',
      classes: 'resource with-ellipsis',
      headerClasses: 'resource',
    },
    {
      dataField: 'date_updated',
      text: 'Date Updated',
      classes: 'right',
      headerClasses: 'right',
    },
    {
      dataField: 'download_url',
      text: '',
      formatter: downloadBtn,
      classes: 'center',
      headerClasses: 'center',
    },
  ];

  return (
    <>
      <Row className="contentDivider pt-30">
        <Col md={4}>
          <Col md={12} className="pb-30 pl-0 pr-0">
            <Card>
              <Card.Body>
                <Card.Title>DNA Analyst</Card.Title>
                <div className="key-people-container">
                  <Table>
                    <tbody>
                      <tr>
                        <td className="img">
                          <img clasName="key-perople-img" src={Owner} alt="" />
                        </td>
                        <td>
                          <div className="name">
                            Quincy Liao
                  </div>
                          <div className="position">
                            Market Strategy, Senior Manager
                  </div>
                          <Row className={{ paddingTop: '1rem' }}>
                            <Col>
                              <div className="header">
                                Email
                                  </div>
                              <div className="data">
                                xliao@equinix.com
                                  </div>
                            </Col>
                            <Col>
                              <div className="header">
                                IM
                                  </div>
                              <div className="data">
                                xliao@equinix.com
                                  </div>
                            </Col>
                          </Row>
                          <Row>
                            <Col>
                              <div className="header">
                                Mobile
                                  </div>
                              <div className="data">
                                +1 234 5678901

                                  </div>
                            </Col>
                            <Col>
                              <div className="header">
                                Location
                                  </div>
                              <div className="data">
                                Redwood City HQ
                                  </div>
                            </Col>
                            <Col xl={12} md={12} sm={12}>
                              <div className="header">
                                Department
                                  </div>
                              <div className="data">
                                Data and Analytics (DNA)
                                  </div>
                            </Col>
                          </Row>
                        </td>
                      </tr>
                    </tbody>
                  </Table>
                </div >
              </Card.Body>
            </Card>
          </Col>
          <Col md={12} className="pb-30 pl-0 pr-0">
            <Card>
              <Card.Body>
                <Card.Title>Standardized Reports</Card.Title>
                <CustomTable
                  tableName='standardized-resources-details'
                  data={standardReportsData === undefined ? [] : standardReportsData}
                  defaultSorted={standardSorted}
                  columns={standardColumns}
                />
              </Card.Body>
            </Card>

          </Col>
          <Col md={12} className="pl-0 pr-0">
            <Card>
              <Card.Body>
                <Card.Title>Other Resources</Card.Title>
                <CustomTable
                  tableName='other-resources-details'
                  data={otherResourcesData === undefined ? [] : otherResourcesData}
                  defaultSorted={otherSorted}
                  columns={otherColumns}
                />
              </Card.Body>
            </Card>
          </Col>
        </Col>
        <Col>
          <Card>
            <Card.Body>
              {/* <Card.Title>Metrics Definition</Card.Title> */}
              <CustomTable
                tableTitle="Metrics definition"
                tableName='metric-details'
                data={referencesData === undefined ? [] : referencesData}
                defaultSorted={defaultSorted}
                columns={columns}
                addSearch
              />
            </Card.Body>
          </Card>
        </Col>
      </Row>
    </>
  );
};

export default AdditionalResourcesContent;