import React, { useLayoutEffect } from 'react';
import * as am4core from "@amcharts/amcharts4/core";
import * as am4charts from "@amcharts/amcharts4/charts";
import am4themes_animated from "@amcharts/amcharts4/themes/animated";

am4core.useTheme(am4themes_animated);


const SankeyDiagaramChart = (props) => {
  const { id, data } = props;

  am4core.options.autoSetClassName = true;

  useLayoutEffect(() => {
    let chart = am4core.create(id, am4charts.SankeyDiagram);
    chart.hiddenState.properties.opacity = 0; // this creates initial fade-in
    chart.width = am4core.percent(100);
    chart.height = am4core.percent(100);
    chart.paddingRight = 0;
    chart.paddingLeft = 0;
    chart.paddingBottom = 0;
    chart.paddingTop = 40;
    chart.data = data;
  
    chart.dataFields.fromName = "from";
    chart.dataFields.toName = "to";
    chart.dataFields.value = "value";
    chart.dataFields.color = "color";

    chart.nodes.template.nameLabel.label.text = "";
    chart.nodes.template.clickable = false;
    chart.nodes.template.draggable = false
    
    chart.links.template.states.create("hover").properties.fillOpacity = 1;
    chart.links.template.tooltipText = "[#fff font-size: 12px;]from {from} to {to}:[/] [#fff bold font-size: 12px;] {value}[/]";
    chart.links.template.fillOpacity  = 0.5;
    chart.links.template.fill = "color";

    return () => {
      chart.dispose();
    };
  }, [id, data]
  );

  return (
    <div className="sankey-diagram-chart" id={id}></div>
  );
}

export default SankeyDiagaramChart;