import React from 'react';
import { Container, Row, Col, Card } from 'react-bootstrap';
import { ImArrowUpRight2, ImArrowDownRight2 } from "react-icons/im";

import HorizontalFunnelChart from '../../components/chart/HorizontalFunnelChart';
import ClusteredColumnChart from '../../components/chart/ClusteredColumnChart';
import StackedColumnChart from '../../components/chart/StackedColumnChart';
import DonutChart from '../../components/chart/DonutChart';
import KPI_NEW from '../../components/kpi/KPI_NEW';
import Legend from '../../components/legend/Legend';


const OpportunitiesContent = (props) => {
  const { opportunitiesData } = props;

  return (
    <Container className="opportunities" fluid>
      <Row className="contentDivider pb-30">
        <div className="fix-width">
          <Card className="TotalRevenue revenue" >
            <Card.Body>
                <Card.Title>{opportunitiesData.overall.total.label}</Card.Title>
                <KPI_NEW
                    totalValue={opportunitiesData.overall.total.value}
                    totalUnit={opportunitiesData.overall.total.unit}
                    details={opportunitiesData.overall.details}
                    count={opportunitiesData.overall.count}
                />
            </Card.Body>
          </Card>
        </div>
        <Col>
          <Card className="RevenueByType revenue">
            <Card.Body>
              <Card.Title>opportunity pipeline by stage</Card.Title>
              <Legend legend={opportunitiesData.opportunity_pipeline_by_stage.legend} />
              <div style={{ height: "400px" }}>
                <StackedColumnChart
                  id="total_revenue_by_revenue_type_stacked_column_chart"
                  hiddenValueAxis={false}
                  isOneHundredPercent={false}
                  isPercentage={false}
                  data={opportunitiesData.opportunity_pipeline_by_stage.data}
                  legend={opportunitiesData.opportunity_pipeline_by_stage.legend}
                  isAnimationEnabled={true}
                />
              </div>
            </Card.Body>
          </Card>
        </Col>
        <Col style={{ maxWidth: "400px" }}>
          <Card className="Segment revenue">
            <Card.Body>
              <Card.Title>opportunity pipeline mix</Card.Title>
              <Legend legend={opportunitiesData.opportunity_pipeline_mix.legend} />
              <div style={{ height: "350px" }}>
                <DonutChart
                  id={"pipeline_count_donut_chart"}
                  totalTitle={"PIPELINE COUNT"}
                  total={opportunitiesData.opportunity_pipeline_mix.pipeline_count}
                  data={opportunitiesData.opportunity_pipeline_mix.data}
                  legend={opportunitiesData.opportunity_pipeline_mix.legend}
                  isAnimationEnabled={true}
                />
              </div>
            </Card.Body>
          </Card>
        </Col>
      </Row>
      <Row className="contentDivider" style={{ paddingBottom: '30px' }}>
        <Col>
          <Card className="SegmentDetails revenue">
            <Card.Body>
              <Card.Title>open orders by stage</Card.Title>
              <Row>
                {opportunitiesData.open_orders_by_stage.data && opportunitiesData.open_orders_by_stage.data.map((item, i) => {
                  return (
                    <Col key={i}>
                      <Row>
                        <Col>
                          <div className="funnel-label-container text-right">
                            <div className="header">
                              {item.segment}
                            </div>
                            <div className="value">
                              <span>{item.order_count}</span>
                            </div>
                          </div>
                        </Col>
                        <Col>
                          <div className="funnel-label-container">
                            Avg Days
                            <div className="indicator indicator-up">
                              {item.avg_days} ({item.avg_days_delta})
                  </div>
                            <div className="quarter">
                              vs {item.quarter_period}
                            </div>
                          </div>
                        </Col>
                      </Row>

                    </Col>
                  )
                })}
              </Row>
              <div style={{ height: "300px" }}>
                <HorizontalFunnelChart id="open_orders_by_stage_chart"
                  data={opportunitiesData.open_orders_by_stage.data}
                  legend={opportunitiesData.open_orders_by_stage.legend} />
              </div>
            </Card.Body>
          </Card>
        </Col>
        <Col style={{ maxWidth: "700px" }}>
          <Card className="SegmentTrends revenue">
            <Card.Body>
              <Card.Title>opportunity pipeline aging</Card.Title>
              <Legend legend={opportunitiesData.opportunity_pipeline_aging.legend} />
              <div style={{ height: "350px" }}>
                <ClusteredColumnChart
                  id={"opportunity_pipeline_aging"}
                  data={opportunitiesData.opportunity_pipeline_aging.data}
                  legend={opportunitiesData.opportunity_pipeline_aging.legend}
                  isAnimationEnabled={true}
                />
              </div>
            </Card.Body>
          </Card>
        </Col>
      </Row>
      <Row className="contentDivider">
        {opportunitiesData.stages && opportunitiesData.stages.map((item, index) => {
          return (
            <Col md={3} lg={3} key={index}>
              <Card className="bg-gray">
                <Card.Body>
                  <Card.Title>{item.title}</Card.Title>
                  {item.cards && item.cards.map((cardItem, i) => {
                    return (
                      <Card className="account-card" key={i}>
                        <Card.Body>
                          <div className="number">
                            {cardItem.order_id}
                          </div>
                          <div className="title">
                            {cardItem.account_name}
                          </div>
                          <div className="tag-container">
                            <span className="tag region">{cardItem.region}</span>
                            <span className="tag metro">{cardItem.metro}</span>
                          </div>
                          <div className="total">
                            Total MRC: <span>{cardItem.total_mrc}</span>
                          </div>
                          <div className="total">
                            Total NRC: <span>{cardItem.total_nrc}</span>
                          </div>
                          <div className="days">
                            Days in Stage:
                            {cardItem.dis_indicator === 'red' || cardItem.dis_indicator === 'Red' ?
                              <span className="indicator indicator-down"><ImArrowDownRight2 /> {cardItem.days_in_stage}</span>
                              :
                              <span className="indicator indicator-up"><ImArrowUpRight2 /> {cardItem.days_in_stage}</span>
                            }
                          </div>
                          <div className="days">
                            Days since creation:
                            {cardItem.dsc_indicator === 'red' || cardItem.dsc_indicator === 'Red' ?
                              <span className="indicator indicator-down"><ImArrowDownRight2 /> {cardItem.days_since_creation}</span>
                              :
                              <span className="indicator indicator-up"><ImArrowUpRight2 /> {cardItem.days_since_creation}</span>
                            }
                          </div>
                        </Card.Body>
                      </Card>
                    )
                  })}
                </Card.Body>
              </Card>
            </Col>
          )
        })}
      </Row>
    </Container>
  );
};

export default OpportunitiesContent;