import React from 'react';
import { ReactComponent as LoadingIcon } from '../../assets/icons/loader.svg';
import './loader.scss'

const Loader = ({ withContainer, loaderRef, width }) => {
  return (
    <>
      {withContainer
        ? <div className="loader" ref={loaderRef ? loaderRef : null}>
          <LoadingIcon className="loadingSvg" style={{ width: width ? width : '200px' }}/>
        </div>
        : <LoadingIcon className="loadingSvg" style={{ width: width ? width : '200px' }}/>
      }
    </>
  );
}

export default Loader;