import React, { useState } from 'react';
import { Container, Row, Col, Card } from 'react-bootstrap';
import { ImArrowUpRight2, ImArrowDownRight2 } from "react-icons/im";

import Legend from '../../components/legend/Legend';
import { BasicDD } from '../../components/customDropdown';
import CustomTable from '../../components/customTable/CustomTable';
import StackedColumnChart from '../../components/chart/StackedColumnChart';
import SankeyDiagramChart from '../../components/chart/SankeyDiagramChart';
import LineChart from '../../components/chart/LineChart';
import KPI_NEW from '../../components/kpi/KPI_NEW';

import { optionOverviewFilter, optionOverviewFilter1 } from '../../constants/filterOptions';
import { convertToUSDGrowth, convertToPercent, convertToCount } from '../../constants/formatter';


const OverviewContent = (props) => {
  const { overviewData, selectedRangeValue } = props;
  const [kpiClicked, setkpiClicked] = useState(null);

  const stacked_column_initial_value = optionOverviewFilter ? optionOverviewFilter[0] : null;
  const [stackedColumnSelection, setStackedColumnSelection] = useState(stacked_column_initial_value);

  //by segment
  const overallSegment = overviewData ? overviewData.overall : [];
  const a_to_z_combination = overviewData ? overviewData.a_to_z_combination : [];
  const interconnections_by_a_side_segment = overviewData ? overviewData.interconnections_by_a_side_segment : [];
  const segmentDetailsSegment = overviewData ? overviewData.segment_details : [];

  let interconnections_by_a_side_segment_in_vc_percentage = overviewData ? overviewData.interconnections_by_a_side_segment_in_vc_percentage : [];
  let interconnections_by_a_side_segment_in_p4q_percentage = overviewData ? overviewData.interconnections_by_a_side_segment_in_p4q_percentage : [];
  let interconnections_by_a_side_segment_in_quarter_percentage = overviewData ? overviewData.interconnections_by_a_side_segment_in_quarter_percentage : [];
  let interconnections_by_a_side_segment_in_year_percentage = overviewData ? overviewData.interconnections_by_a_side_segment_in_year_percentage : [];

  const formatToCount = (cell, row) => {
    return <span>{convertToCount(cell)}</span>
  };

  const indicatorPercent = (cell, row) => {
    let bgColor = cell >= 0.5 ? 'indicator-up' : cell <= -0.5 ? 'indicator-down' : 'indicator-middle';
    let icon = cell >= 0.5 ? <ImArrowUpRight2 /> : cell <= -0.5 ? <ImArrowDownRight2 /> : <span className="mr-3"></span>;

    return (
      <>
        <span className={`indicator ${bgColor}`}>
          {icon}
          {convertToPercent(cell)}
        </span>
      </>
    );
  };

  const formatUSDGrowth = (cell, row) => {
    return (
      <span>
        {convertToUSDGrowth(cell)}
      </span>
    );
  };

  const formatToPercent = (cell, row) => {
    return (
      <span>
        {convertToPercent(cell)}
      </span>
    );
  };

  const defaultSorted = [
    {
      dataField: '_id',
      order: 'asc',
    }
  ];

  const columns = [
    {
      dataField: '_id',
      text: 'id',
      hidden: true
    },
    {
      dataField: 'segment',
      text: 'SEGMENT',
      classes: 'segment-th',
      headerClasses: 'segment-td'
    },
    {
      dataField: 'ic_count',
      text: 'IC #',
      sort: true,
      classes: 'right',
      headerClasses: 'right',
      formatter: formatToCount
    },
    {
      dataField: 'mix_percent',
      text: 'MIX%',
      sort: true,
      classes: 'right',
      headerClasses: 'right'
    },
    {
      dataField: 'q_by_q_growth_percent',
      text: 'Q/Q % GROWTH',
      sort: true,
      formatter: indicatorPercent,
      classes: 'with-ellipsis center',
      headerClasses: 'center',
    },
    {
      dataField: 'q_to_q_growth_count',
      text: 'Q-Q $ GROWTH',
      sort: true,
      formatter: formatToCount,
      classes: 'right',
      headerClasses: 'right',
    },
    {
      dataField: 'p4q_growth_percent',
      text: 'P4Q % GROWTH',
      sort: true,
      formatter: indicatorPercent,
      classes: 'with-ellipsis center',
      headerClasses: 'center',
    },
    {
      dataField: 'p4q_growth_count',
      text: 'P4Q # GROWTH',
      sort: true,
      formatter: formatToCount,
      classes: 'right',
      headerClasses: 'right',
    },
    {
      dataField: 'y_by_y_growth_percent',
      text: 'Y/Y % GROWTH',
      sort: true,
      formatter: indicatorPercent,
      classes: 'with-ellipsis center',
      headerClasses: 'center',
    },
    {
      dataField: 'y_to_y_growth_count',
      text: 'Y-Y $ GROWTH',
      sort: true,
      formatter: formatToCount,
      classes: 'right',
      headerClasses: 'right',
    }
  ];

  return (
    <Container fluid>
      <Row>
        <Col>
          <Row className="contentDivider pb-30">
            <div className="fix-width">
              <Card className="TotalRevenue revenue " >
                <Card.Body>
                  <Card.Title>interconnection #</Card.Title>
                  <KPI_NEW
                    totalValue={overallSegment.total.value}
                    totalUnit={overallSegment.total.unit}
                    details={overallSegment.details}
                    setkpiClicked={setkpiClicked}
                    kpiClicked={kpiClicked}
                  />
                </Card.Body>
              </Card>
            </div>
            <Col>
              <Card className="RevenueByType revenue">
                <Card.Body>
                  <Card.Title>interconnections by a-side segment</Card.Title>

                  {kpiClicked === '% VC' ?
                    <>
                      <Legend legend={interconnections_by_a_side_segment_in_vc_percentage.legend} />
                      <div style={{ height: "92%" }}>
                        <LineChart
                          id={`interconnections_by_a_side_segment_in_vc_percentage-line_chart-segment`}
                          data={interconnections_by_a_side_segment_in_vc_percentage.data}
                          legend={interconnections_by_a_side_segment_in_vc_percentage.legend}
                          isAnimationEnabled={false}
                        />
                      </div>
                    </>
                    :

                    kpiClicked === 'P4Q % Growth' ?
                      <>
                        <Legend legend={interconnections_by_a_side_segment_in_p4q_percentage.legend} />
                        <div style={{ height: "92%" }}>
                          <LineChart
                            id={`interconnections_by_a_side_segment_in_p4q_percentage-line_chart-segment`}
                            data={interconnections_by_a_side_segment_in_p4q_percentage.data}
                            legend={interconnections_by_a_side_segment_in_p4q_percentage.legend}
                            isAnimationEnabled={false}
                          />
                        </div>
                      </>
                      :
                      kpiClicked === 'Y/Y % Growth' ?
                        <>
                          <Legend legend={interconnections_by_a_side_segment_in_year_percentage.legend} />
                          <div style={{ height: "92%" }}>
                            <LineChart
                              id={`interconnections_by_a_side_segment_in_year_percentage-line_chart-segment`}
                              data={interconnections_by_a_side_segment_in_year_percentage.data}
                              legend={interconnections_by_a_side_segment_in_year_percentage.legend}
                              isAnimationEnabled={false}
                            />
                          </div>
                        </>
                        :
                        kpiClicked === 'Q/Q % Growth' ?
                          <>
                            <Legend legend={interconnections_by_a_side_segment_in_quarter_percentage.legend} />
                            <div style={{ height: "400px" }}>
                              <LineChart
                                id={`interconnections_by_a_side_segment_in_quarter_percentage-line_chart-segment`}
                                data={interconnections_by_a_side_segment_in_quarter_percentage.data}
                                legend={interconnections_by_a_side_segment_in_quarter_percentage.legend}
                                isAnimationEnabled={false}
                              />
                            </div>
                          </>

                          :
                          <>
                            <div className="d-flex">
                              <Legend legend={interconnections_by_a_side_segment.legend} />
                              {/* {selectedRangeValue === "last_completed_quarter" ? */}
                              <BasicDD
                                className="ml-auto"
                                id={`stacked_column_dd`}
                                options={optionOverviewFilter1}
                                value={stackedColumnSelection}
                                setValue={setStackedColumnSelection}
                              />
                              {/* : null
                         } */}
                            </div>
                            <div style={{ height: "85%", marginTop: "1rem" }}>
                              <StackedColumnChart
                                id={`stacked_column_chart`}
                                hiddenValueAxis={false}
                                isOneHundredPercent={false}
                                isPercentage={false}
                                largerWidth={true}
                                hideLabelBullet={false}
                                isOverviewChart={true}
                                isGrowthPercentToggable={true}
                                stackedColumnSelection={stackedColumnSelection.key}
                                selectedRangeValue={'last_completed_quarter'}
                                data={interconnections_by_a_side_segment.data}
                                legend={interconnections_by_a_side_segment.legend}
                                isAnimationEnabled={false}
                              />
                            </div>
                          </>
                  }

                </Card.Body>
              </Card>
            </Col>
          </Row>
          <Row className="contentDivider">
            <Col>
              <Card className="SegmentDetails revenue">
                <Card.Body>
                  <Card.Title>segment details</Card.Title>
                  <CustomTable
                    tableName='segment-details'
                    data={segmentDetailsSegment === undefined ? [] : segmentDetailsSegment}
                    defaultSorted={defaultSorted}
                    columns={columns}
                  />
                </Card.Body>
              </Card>
            </Col>
          </Row>

        </Col>
        <Col md={3}>
          <Card className="TotalRevenue revenue " >
            <Card.Body>
              <Card.Title>a-z combination</Card.Title>
              <Legend legend={a_to_z_combination.legend} />
              <SankeyDiagramChart
                id={"ecosystem_sankey_chart"}
                data={a_to_z_combination.data}
                legend={a_to_z_combination.legend}
              />

            </Card.Body>
          </Card>
        </Col>
      </Row>

    </Container>
  );
};

export default OverviewContent;