const BlankReportContent = () => {
  const [dragState, setDragState] = useState(draggableState);
  const [updateTable, setUpdateTable] = useState()
  const [isLoading, setLoading] = useState(false)
  const [dataTable, setDataTable] = useState();

  const onRun = () => {
    showTableResult();
    setUpdateTable(columns);
  }

  const showTableResult = () => {
    setLoading(true);
  
    axios({
      method: 'GET',
      url: `${API_BASE_URL}/ssrg/data.json?print=pretty`,
    }).then(result => {
      setDataTable(result.data)
      setLoading(false);
    }).catch(error => {
      console.log(error)
      // setTableError(true)
      setLoading(false);
    })
  }

  return (
    <>
      <div className="blank-report-page">
        <DraggableComponent
          buttonList={buttonList}
          grabableList={grabableList}
          dragState={dragState}
          setDragState={setDragState}
          withPreview
          narrow_view
        >
          <div className="preview-header">
            <Form.Group controlId="reportTitle">
              <Form.Label><GoPencil /></Form.Label>
              <Form.Control placeholder="Unnamed Report" />
            </Form.Group>
            <div className="btn-block">
              <Button variant="info" onClick={() => onRun()}>Run</Button>
              <Button variant="info">Save</Button>
              <Button variant="info">Download</Button>
            </div>
          </div>
          <div className="preview-body custom-scroll">
            {isLoading ? <Loader />
              : <>
                {updateTable ?
                    <div className="preview-wrap">
                    <table className="fake-table-head">
                      <tr>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th className="width-x-9">#-New Customers</th>
                        <th className="width-x-9">Customer MRR</th>
                      </tr>
                    </table>
                    <CustomTable
                      tableName='preview-table scrollable'
                      data={dataTable ? dataTable : []}
                      defaultSorted={defaultSorted}
                      columns={columns}
                      rowsPerPage={rowsPerPage}
                      addPaginate
                    />
                  </div>
                  : <p className="empty-fields">To generate a report, choose fields from the Field list on the left and click Run</p>
                }
              </>
            }
          </div>
        </DraggableComponent>
      </div>
    </>
  );
}

export default BlankReportContent;
