import React, { useState } from 'react';
import { Form, FormControl, FormGroup } from 'react-bootstrap';
import './searchCheckbox.scss';


const SearchCheckbox = ({ title, options, className, searchPlaceholder, checkBoxValue }) => {
  const [checkVal, setCheck] = useState([]);
  const [searchVal, setSearchVal] = useState('');

  const onChangeCheck = (e) => {
    const checkValue = checkVal.some(item => item === e);
    const remove = checkVal.filter(item => item !== e);
    if (!checkValue) {
      setCheck([...checkVal, e])
    } else {
      setCheck(remove)
    }
    checkBoxValue = checkVal;

  }

  return (
    <div className={`search-checkbox ${className ? className : ''}`}>
      {title && <div className="title">{title}</div>}
      <div>
        <FormControl
          placeholder={searchPlaceholder}
          onChange={(e) => setSearchVal(e.target.value)}
          value={searchVal}
        />
        <ul className="list-of-checkbox custom-scroll">
          <Form>
            {options.map((option, i) => (
              <FormGroup key={i} className={option.key}>
                {
                  option.icon &&
                  < span >
                    {
                      option.icon
                    }
                  </span>
                }
                <Form.Check
                  key={option.key}
                  custom
                  type='checkbox'
                  id={`check_search-${option.key}`}
                  label={option.text}
                  checked={checkVal.some(item => item === option.key)}
                  onChange={() => onChangeCheck(option.key)}
                />

              </FormGroup>
            )).filter(
              (child) => !searchVal || child.props.children[1].props.label.toLowerCase().includes(searchVal)
            )}
          </Form>
        </ul>
      </div>
    </div >
  );
}

export default SearchCheckbox;