import React, { useLayoutEffect } from 'react';
import * as am4core from "@amcharts/amcharts4/core";
import * as am4charts from "@amcharts/amcharts4/charts";

import am4themes_animated from "@amcharts/amcharts4/themes/animated";

am4core.useTheme(am4themes_animated);


const SparklineChart = (props) => {
  const { id, loadedData } = props;

  useLayoutEffect(() => {
    let chart = am4core.create(id, am4charts.XYChart);
    // chart.width = am4core.percent(50);
    chart.data = loadedData;
    chart.padding(0);

    let categoryAxis = chart.xAxes.push(new am4charts.CategoryAxis());
    categoryAxis.dataFields.category = "quarter_period";
    categoryAxis.renderer.grid.template.disabled = true;
    categoryAxis.renderer.labels.template.disabled = true;
    categoryAxis.renderer.cellStartLocation = 0;
    categoryAxis.renderer.cellEndLocation = 0.8;

    let valueAxis = chart.yAxes.push(new am4charts.ValueAxis());
    valueAxis.min = 0;
    valueAxis.renderer.grid.template.disabled = true;
    valueAxis.renderer.baseGrid.disabled = true;
    valueAxis.renderer.labels.template.disabled = true;
    valueAxis.cursorTooltipEnabled = false;

    let series = chart.series.push(new am4charts.LineSeries());
    series.dataFields.categoryX = "quarter_period";
    series.dataFields.valueY = "value";
    series.tensionX = 0.8;
    series.strokeWidth = 2;
    series.propertyFields.stroke = "color";
    series.propertyFields.fill = "color";
    series.hiddenState.transitionDuration = 0;
    series.defaultState.transitionDuration = 0;

    // render data points as bullets
    let bullet = series.bullets.push(new am4charts.CircleBullet());
    bullet.propertyFields.fill = "color"
    bullet.tooltipText = "[font-size: 12px #fff]{quarter_period}: [font-size: 12px; bold #fff]{value.formatNumber('#.0')}%[/][/]";
    bullet.circle.opacity = 1;
    bullet.circle.propertyFields.fill = "color";
    bullet.circle.propertyFields.stroke = "color";
    bullet.circle.radius = 3;

    return () => {
      chart.dispose();
    };
  }, [id, loadedData]);

  return (
    <div className="sparkline-chart" id={id}></div>
  );
}

export default SparklineChart;