import React, { useState, useEffect } from 'react';
import axiosRetry from 'axios-retry';
import axios from 'axios';
import { FilterContainer, FilterModal } from '../../components/filter';
import { optionRegion, optionCompanySize2, optionHyperscalerSize, optionGroup8 } from '../../constants/filterOptions';
import { CheckboxDD, RadioDD } from '../../components/customDropdown'
import { comments } from '../../constants/comments';
import { InternalServerError, ResourceNotFoundError } from '../../components/error/Error';
import { API_BASE_URL } from '../../constants/config';
import Comment from '../../components/comment/Comment';
import Loader from '../../components/loader/Loader';
import LFPContent from './LFPContent';


const LFP = () => {
	const [isLoading, setIsLoading] = useState(true);
	const [growthData, setData] = useState([]);
	const [selectedData, setSelectedData] = useState(null);

	// show all filters
	const [showFilter, setShowFilter] = useState(false);
	const onShowFilter = () => {
		setShowFilter(true)
	}
	const onCloseFilter = async () => {
		await onCancelHyperscalerSize();
		await onCancelRegion();
		await onCancelSize();
		await onCancelGroup();
		setShowFilter(false);
	}
	const onApplyFilter = async () => {
		await onApplyHyperscalerSize();
		await onApplyRegion();
		await onApplySize();
		await onApplyGroup();
		setShowFilter(false);
	}

	// comment area
	const [showComment, setShowComment] = useState(false);
	const onCloseComment = () => {
		setShowComment(false);
	};
	const onShowComment = () => {
		setShowComment(true);
	};

	// checkbox for hyperscaler size values
	const [hyperscaler_size_val_onApply, set_hyperscaler_size_val_onApply] = useState([]);
	const [hyperscaler_size_val, set_hyperscaler_size_val] = useState([]);
	const onApplyHyperscalerSize = () => {
		set_hyperscaler_size_val_onApply(hyperscaler_size_val);
	};
	const onCancelHyperscalerSize = () => {
		set_hyperscaler_size_val(hyperscaler_size_val_onApply);
	};

	// checkbox for region values
	const [region_val, set_region_val] = useState([]);
	const [region_val_onApply, set_region_val_onApply] = useState([]);
	const onApplyRegion = () => {
		set_region_val_onApply(region_val);
	};
	const onCancelRegion = () => {
		set_region_val(region_val_onApply);
	};

	// checkbox for size values
	const [size_val_onApply, set_size_val_onApply] = useState([]);
	const [size_val, set_size_val] = useState([]);
	const onApplySize = () => {
		set_size_val_onApply(size_val);
	};
	const onCancelSize = () => {
		set_size_val(size_val_onApply);
	};

	// radio for group values
	const group_initial_value = optionGroup8 ? optionGroup8[0] : null;
	const [group_val_onApply, set_group_val_onApply] = useState(group_initial_value);
	const [group_val, set_group_val] = useState(group_initial_value);
	const onApplyGroup = () => {
		set_group_val_onApply(group_val);
	};
	const onCancelGroup = () => {
		set_group_val(group_val_onApply);
	};

	// Axios call
	const fetchData = async () => {
		let url = `${API_BASE_URL}/pricing/LFP.json`;

		setIsLoading(true);
		axiosRetry(axios, { retries: 3 });

		axios({
			method: 'GET',
			url: url
		}).then(result => {
			const data = result.data;
			setData(data);
			setIsLoading(false);
			setSelectedData(null);
		}).catch(error => {
			console.log(error);
			setData([]);
			setIsLoading(false);
		});
	};

	useEffect(() => {
		fetchData();
	}, []);

	if (isLoading) return <Loader withContainer />

	return (
		<>
			<div className={`page-content ${showComment ? 'open' : 'closed'}`}>
				{growthData ?
					growthData.lfp_deal_details.by_rank.length > 0 ?
						<>
							<FilterContainer

								showComment={showComment}
								onShowFilter={onShowFilter}
								onShowComment={onShowComment}
							>
								<div className="filters custom-scroll">
									<CheckboxDD
										title="Hyperscaler Size"
										id="hyperscaler-size"
										options={optionHyperscalerSize}
										checkVal={hyperscaler_size_val}
										setCheckVal={set_hyperscaler_size_val}
										onApply={onApplyHyperscalerSize}
										onCancel={onCancelHyperscalerSize}
									/>
									<CheckboxDD
										title="Deploy Region"
										id="region"
										valueApplied={region_val_onApply}
										options={optionRegion}
										checkVal={region_val}
										setCheckVal={set_region_val}
										onApply={onApplyRegion}
										onCancel={onCancelRegion}
									/>
									<CheckboxDD
										title="Size"
										id="size"
										options={optionCompanySize2}
										checkVal={size_val}
										setCheckVal={set_size_val}
										onApply={onApplySize}
										onCancel={onCancelSize}
									/>
									<RadioDD
										title="Group"
										id="group"
										options={optionGroup8}
										radioVal={group_val}
										setRadioVal={set_group_val}
										onApply={onApplyGroup}
										onCancel={onCancelGroup}
										initialValue={group_initial_value}
									/>
								</div>
								<FilterModal
									title="LFP"
									setShowFilter={setShowFilter}
									showFilter={showFilter}
									onCloseFilter={onCloseFilter}
									onApplyFilter={onApplyFilter}
								>
									<CheckboxDD
										title="Hyperscaler Size"
										id="hyperscaler-size"
										visibleInModal
										options={optionHyperscalerSize}
										checkVal={hyperscaler_size_val}
										setCheckVal={set_hyperscaler_size_val}
										onApply={onApplyHyperscalerSize}
										onCancel={onCancelHyperscalerSize}
									/>
									<CheckboxDD
										title="Deploy Region"
										id="region"
										visibleInModal
										valueApplied={region_val_onApply}
										options={optionRegion}
										checkVal={region_val}
										setCheckVal={set_region_val}
										onApply={onApplyRegion}
										onCancel={onCancelRegion}
									/>
									<CheckboxDD
										title="Size"
										id="size"
										visibleInModal
										options={optionCompanySize2}
										checkVal={size_val}
										setCheckVal={set_size_val}
										onApply={onApplySize}
										onCancel={onCancelSize}
									/>
									<RadioDD
										title="Group"
										id="group-modal"
										options={optionGroup8}
										visibleInModal
										radioVal={group_val}
										setRadioVal={set_group_val}
									/>
								</FilterModal>
							</FilterContainer>
							{/* ============= Your Component here! ============= */}
							<LFPContent
								selectedData={selectedData}
								setSelectedData={setSelectedData}
								growthData={growthData}
							/>
						</>
						: <ResourceNotFoundError redirectURL={"/executive-cockpit/pricing/growth"} />
					: <InternalServerError redirectURL={"/executive-cockpit/pricing/growth"} />}
			</div>
			<Comment
				data={comments}
				onCloseComment={onCloseComment}
				showComment={showComment}
			/>
		</>
	);
};

export default LFP;