import React, { useState } from 'react';
import { Row, Col, Card } from 'react-bootstrap';
import { ImArrowUpRight2, ImArrowDownRight2 } from "react-icons/im";
import Legend from '../../components/legend/Legend';
import CustomTable, { formatLongText } from '../../components/customTable/CustomTable';
import BubbleChart from '../../components/chart/BubbleChart';
import LineChart from '../../components/chart/LineChart';
import StackedColumnChart from '../../components/chart/StackedColumnChart';
import { BasicDD } from '../../components/customDropdown';
import { optionGrowthFilter3 } from '../../constants/filterOptions';
import { convertToUSDGrowth, convertToPercent, convertToWholePercent } from '../../constants/formatter';


const GrowthContent = (props) => {
	const { selectedData, setSelectedData, growthData, isChanged, setIsChanged, tab_id } = props;
	const [selectedBubbleData, setSelectedBubbleData] = useState(null);
	const [idState, setReferenceID] = useState(null);
	const [bubbleName, setBubbleName] = useState(null);
	const [toggleSelectedRow, setToggleSelectedRow] = useState();

	// basic dd for bubble chart values
	const bubble_initial_value = optionGrowthFilter3 ? optionGrowthFilter3[0] : null;
	const [bubbleSelection, setBubbleSelection] = useState(bubble_initial_value);

	// Data Distribution
	const toggleData = growthData.gross_bookings_revenue_size_and_growth
		? growthData.gross_bookings_revenue_size_and_growth
		: growthData.net_bookings_revenue_size_and_growth
			? growthData.net_bookings_revenue_size_and_growth
			: growthData.pag_bookings_revenue_size_and_growth;
	let segmentRanking = growthData ? growthData.segment_ranking : [];
	let byRankId = segmentRanking ? segmentRanking.by_rank_id : [];
	let byRank = segmentRanking.by_rank ? segmentRanking.by_rank : [];
	let segmentTrends = selectedData ? selectedData.segment_y_by_y_growth_trends : growthData.segment_trends;
	let segmentByRegion = selectedData ? selectedData.segment_by_region : growthData.segment_by_region;
	let bubbleData = growthData.gross_bookings_revenue_size_and_growth
		? growthData.gross_bookings_revenue_size_and_growth
		: growthData.net_bookings_revenue_size_and_growth
			? growthData.net_bookings_revenue_size_and_growth
			: growthData.pag_bookings_revenue_size_and_growth;
	let bubbleDataLegend = bubbleData.legend;

	if (idState) {
		selectedBubbleData.map(item => {
			if (item._id !== idState) {
				item.color = 'rgba(234, 240, 244, 0.2)';
			} else {
				bubbleDataLegend.forEach(element => {
					if (item.region === element.name) {
						item.color = element.color
					}
				});
			}
			return item;
		})
		setIsChanged(false);
	} else if (toggleData.data) {
		toggleData.data.map(item => {
			item.borderColor = '#fff';
			item.less_y_to_y_usd_growth = '#fff';
			item.less_rev_dollar = '#fff';
			if (bubbleSelection.key === 'y-y_growth') {
				if (item.y_to_y_usd_growth < 0) {
					item.less_y_to_y_usd_growth = '#ff3333';
					if (item.region === 'APAC') {
						item.color = 'rgba(240, 204, 113, 0.5)'
					} else if (item.region === 'AMER') {
						item.color = 'rgba(220, 116, 49, 0.5)';
					} else if (item.region === 'EMEA') {
						item.color = 'rgba(205, 80, 60, 0.5)';
					}
				}
			} else if (bubbleSelection.key === 'rev_dollar') {
				if (item.total_revenue < 0) {
					item.less_rev_dollar = '#ff3333';
					if (item.region === 'APAC') {
						item.color = 'rgba(240, 204, 113, 0.5)'
					} else if (item.region === 'AMER') {
						item.color = 'rgba(220, 116, 49, 0.5)';
					} else if (item.region === 'EMEA') {
						item.color = 'rgba(205, 80, 60, 0.5)';
					}
				}
			}
		})
	};

	// Table Config
	const indicatorPercent = (cell, row) => {
		let bgColor = cell >= 0.5 ? 'indicator-up' : cell <= -0.5 ? 'indicator-down' : 'indicator-middle';
		let icon = cell >= 0.5 ? <ImArrowUpRight2 /> : cell <= -0.5 ? <ImArrowDownRight2 /> : <span className="mr-3"></span>;
		return (
			<span className={`indicator ${bgColor}`}>
				{icon}
				{convertToPercent(cell)}
			</span>
		);
	};

	const segmentFormat = (cell, row) => {
		return formatLongText(cell, row)
	};

	const formatUSDGrowth = (cell, row) => {
		let new_cell = convertToUSDGrowth(cell);
		return formatLongText(new_cell, row)
	};

	const formatWholePercent = (cell, row) => {
		return <span>{convertToWholePercent(cell)}</span>;
	};

	const formatToPercent = (cell, row) => {
		return <span>{convertToPercent(cell)}</span>
	};


	const rankFormat = (cell, row, rowIndex, formatExtraData) => {
		return <span>{rowIndex + 1}</span>;
	};

	const handleSelected = (row) => {
		let selectedData;
		for (const key in byRankId) {
			const element = byRankId[key];
			if (element._id === row._id) {
				selectedData = element;
			}
		}
		setReferenceID(row._id);
		setSelectedData(selectedData);
		setSelectedBubbleData(bubbleData.data);
		setBubbleName(row.segment);
	};

	const onSecondClick = () => {
		setReferenceID(null);
		setSelectedData(null);
		setSelectedBubbleData(null);
		setBubbleName(null);
		setToggleSelectedRow()

		selectedBubbleData.map(item => {
			bubbleDataLegend.forEach(element => {
				if (item.region === element.name) {
					item.color = element.color
				}
			})
			return item;
		})
	};

	const rowEvents = {
		onClick: (e, row, rowIndex) => {
			setToggleSelectedRow(row._id)
			if (toggleSelectedRow === row._id) {
				onSecondClick()
			} else {
				handleSelected(row)
			}
		}
	};

	const rowClasses = (row) => {
		let classes = row._id === toggleSelectedRow ? 'selectable selected' : 'selectable';
		return classes
	};

	const defaultSorted = [{
		dataField: 'segment',
		order: 'desc',
	}];

	const columns = [
		{
			dataField: '_id',
			text: 'id',
			hidden: true
		},
		{
			dataField: '',
			text: 'Rank',
			classes: 'rank center',
			headerClasses: 'rank center',
			formatter: rankFormat,
		},
		{
			dataField: 'segment',
			text: 'SEGMENT',
			sort: true,
			formatter: segmentFormat,
			classes: 'segment',
			headerClasses: 'segment'
		},
		{
			dataField: 'mrc_usd',
			text: 'MRC $',
			sort: true,
			formatter: formatUSDGrowth,
			classes: 'right',
			headerClasses: 'right'
		},
		{
			dataField: 'mix_percent',
			text: 'MIX %',
			sort: true,
			formatter: formatToPercent,
			classes: 'center',
			headerClasses: 'center'
		},
		{
			dataField: 'q_by_q_growth_percent',
			text: 'Q/Q % GROWTH',
			sort: true,
			formatter: indicatorPercent,
			classes: 'with-ellipsis center',
			headerClasses: 'center'
		},
		{
			dataField: 'q_to_q_usd_growth',
			text: 'Q-Q $ GROWTH',
			sort: true,
			formatter: formatUSDGrowth,
			classes: 'right',
			headerClasses: 'right'
		},
		{
			dataField: 'y_by_y_growth_percent',
			text: 'Y/Y % GROWTH',
			sort: true,
			formatter: indicatorPercent,
			classes: 'with-ellipsis center',
			headerClasses: 'center'
		},
		{
			dataField: 'y_to_y_usd_growth',
			text: 'Y-Y $ GROWTH',
			sort: true,
			formatter: formatUSDGrowth,
			classes: 'right',
			headerClasses: 'right'
		}
	];

	return (
		<>
			<Row className="contentDivider">
				<Col md="5" xs="12">
					<Card className="revenue">
						<Card.Body>
							<Card.Title>mrc size and growth</Card.Title>
							<div className="d-flex">
								<Legend legend={bubbleData.legend} />
								<BasicDD
									className="ml-auto"
									id={`${tab_id}-bubble_chart_dd`}
									addTitleOnWide
									options={optionGrowthFilter3}
									value={bubbleSelection}
									setValue={setBubbleSelection}
								/>
							</div>
							<div style={{ height: "92%" }}>
								<BubbleChart
									id={tab_id +
										(idState
											? bubbleName
											: bubbleSelection.key
												? bubbleSelection.key
												: "bubble_chart"
										)}
									data={idState && isChanged ? selectedBubbleData : bubbleData.data}
									selected={idState}
									bubbleSelection={bubbleSelection.key}
									isAnimationEnabled={false}
									yAxisLabel={'Y/Y % Growth'}
									xAxisLabel={bubbleSelection.key === 'mrc_dollar' ? 'Y-Y $ Growth' : 'MRC $'}
								/>
							</div>
						</Card.Body>
					</Card>
				</Col>
				<Col md="7" xs="12">
					<Row>
						<Col md="12" xs="12" className="pb-0">
							<Card className="revenue ">
								<Card.Body>
									<Card.Title>segment ranking</Card.Title>
									<CustomTable
										tableName='segment-ranking scrollable'
										data={byRank}
										rowEvents={rowEvents}
										defaultSorted={defaultSorted}
										rowClasses={rowClasses}
										columns={columns}
										addHover
									/>
								</Card.Body>
							</Card>
						</Col>
						<Col className="pt-30 pb-0" md="12" xs="12">
							<Row >
								<Col md="6" xs="12" className="pb-0">
									<Card className="revenue">
										<Card.Body>
											<Card.Title>y/y % growth by segment</Card.Title>
											<Legend legend={segmentTrends.legend} />
											<div style={{ height: "350px" }}>
												<LineChart
													id={`${tab_id}-line_chart`}
													data={segmentTrends.data}
													legend={segmentTrends.legend}
													isAnimationEnabled={false}
												/>
											</div>
										</Card.Body>
									</Card >
								</Col>
								<Col md="6" xs="12" className="pb-0">
									<Card className="revenue">
										<Card.Body>
											<Card.Title>contribution to total $ by segment</Card.Title>
											<Legend legend={segmentByRegion.legend} />
											<div style={{ height: "350px" }}>
												<StackedColumnChart
													id={`${tab_id}-stacked_column_chart`}
													hiddenValueAxis={true}
													isOneHundredPercent={false}
													isPercentage={false}
													largerWidth={true}
													hideLabelBullet={false}
													data={segmentByRegion.data}
													legend={segmentByRegion.legend}
													isAnimationEnabled={false}
												/>
											</div>
										</Card.Body>
									</Card>
								</Col>
							</Row>
						</Col>
					</Row>
				</Col>
			</Row>
		</>
	);
};

export default GrowthContent;