import React, { useRef } from 'react';
import { useHistory } from "react-router-dom";
import { DashboardLayoutComponent, PanelsDirective, PanelDirective } from '@syncfusion/ej2-react-layouts';
import { Card, DropdownButton, Dropdown, Row, Col } from 'react-bootstrap';
import KPI_NEW from '../../../components/kpi/KPI_NEW';
import Legend from '../../../components/legend/Legend';
import StackedColumnChart from '../../../components/chart/StackedColumnChart';
import { FiMoreVertical } from "react-icons/fi";
import LineChart from '../../../components/chart/LineChart';
import StackedColumnChartTwo from '../../../components/chart/StackedColumnChartTwo';
import { convertToUSD, convertToCount, convertToWholePercent } from '../../../constants/formatter';
import { v4 as uuidv4 } from 'uuid';

import "./dashboardLayout.scss";



const DashboardLayout = (props) => {
  let history = useHistory();
  let dashboardObj = useRef(null);
  const { data, setData, selectedItemMetro, setSelectedItemMetro, stepList, setStepList } = props;

  const goTo = (url) => {
    history.push('/analytics-dashboard' + url);
  }

  const doubleClick = (event, url) => {
    goTo(url)
  }

  const onRemove = (panelId, key) => {
    dashboardObj.removePanel(panelId);
    const remove_selected_item = selectedItemMetro.filter(item => item.key !== key);
    setSelectedItemMetro(remove_selected_item)

    const new_steplist = stepList.map((val) => {
      if (val.key === key) {
        val.isChecked = false;
      }
      return val
    })

    setStepList(new_steplist)
    console.log(stepList)
  }

  const smallCard = (item, panelId, size) => {
    console.log('smallCard', item)
    let availableSizes = item.layout.availableSizes;

    let totalUnit = item.overall.total.unit;
    let totalValue = item.overall.total.value;
    let total;

    let quarterOverQuarter = item.details;

    if (totalUnit && totalUnit === "$") {
      let convertedTotal = convertToUSD(totalValue);

      if (/M/g.test(convertedTotal) === false) {
        total = <div className={`total-revenue-sm no-hover`} onClick={() => {
        }}>
          {convertedTotal.slice(0, 1)} <span>{convertedTotal.slice(1)}</span>
        </div>;
      } else {
        total = <div className={`total-revenue-sm no-hover`} onClick={() => {

        }}>
          {convertedTotal.slice(0, 1)} <span>{convertedTotal.slice(1, -1)}</span> {convertedTotal.slice(-1)}
        </div>;
      }
    }

    // } else if (totalUnit && totalUnit === "%") {
    //   let convertedTotal = convertToPercent(totalValue);
    //   total = <div className={`total-revenue ${noHover ? 'no-hover' : 'with-hover'}`} onClick={() => {

    //   }}>
    //     <span>{convertedTotal.slice(0, -1)}</span> {convertedTotal.slice(-1)}
    //     {isAddText ? isAddText : null}
    //   </div>;
    // } else {
    //   total = <div className={`total-revenue ${noHover ? 'no-hover' : 'with-hover'}`} onClick={() => {
    //   }}>
    //     <span>{totalValue ? convertToCount(totalValue) : '-'}</span>{totalUnit ? totalUnit : null}
    //   </div>;
    // }

    return (
      <Card.Body onDoubleClick={(e) => { doubleClick(e, item.url) }} id={`card-` + panelId} className={`small-card ${item.chart.isLine ? 'line-height' : null}`}>
        <Card.Title> {item.title}</Card.Title>
        <div className="button-container">
          <DropdownButton id={`dropdown-basic-button`} drop={'left'} className="btn-gray" title={<FiMoreVertical />}>
            <Dropdown.Item onClick={() => { goTo(item.url) }}>View Metric</Dropdown.Item>
            <Dropdown.Item onClick={() => { onRemove(panelId, item.key) }}>Delete Card</Dropdown.Item>
            {availableSizes.map((element, i) => {
              let unique = uuidv4()
              return (
                <div key={i + unique}>
                  {size
                    ? <>
                      {size !== element.size ?
                        <Dropdown.Item
                          onClick={() => {
                            onResize(panelId, element.sizeX, element.sizeY, element.size, item)
                          }}>
                          Resize to {element.size}
                        </Dropdown.Item>
                        : null}
                    </>
                    :
                    <>
                      {item.layout.defaultSize !== element.size ?
                        <Dropdown.Item onClick={() => {
                          onResize(panelId, element.sizeX, element.sizeY, element.size, item)
                        }}>Resize to {element.size}</Dropdown.Item>
                        : null}
                    </>
                  }
                </div>
              )
            })}
            <Dropdown.Item onClick={() => { alert('You clicked details.') }}>Details</Dropdown.Item>
          </DropdownButton>
        </div>
        <Row>
          <Col>
            {total}
          </Col>
          <Col>
            Q/Q
          </Col>
        </Row>


        {item.chart.isLine ?
          <div style={{ height: '80%', paddingTop: '1rem' }}>
            <LineChart
              id={'executive-stacked-' + item._id}
              data={item.chart.data}
              legend={item.chart.legend}
              hideValueLabel={true}
            />
          </div>

          : null
        }

        {item.chart.isStackedColumn ?
          <div style={{ height: '80%', paddingTop: '1rem' }}>
            <StackedColumnChartTwo
              id={'executive-cockpit-' + item._id}
              data={item.chart.data}
              legend={item.chart.legend}
              style={{ marginTop: '100px' }}
            />
          </div>

          // <StackedColumnChart
          //   className="small-stacked-column"
          //   id={'executive-stacked-' + item._id}
          //   hiddenValueAxis={true}
          //   isOneHundredPercent={false}
          //   isPercentage={false}
          //   largerWidth={true}
          //   data={item.chart.data}
          //   legend={item.chart.legend}
          //   hideLabelBullet={true}
          //   disablePaddingTop={true}
          //   categoryAxisLabelDisabled={true}
          // />
          : null}



      </Card.Body>
    )

  }
  const mediumCard = (item, panelId, size) => {
    let availableSizes = item.layout.availableSizes;
    let details = item.overall.details;
    let totalUnit = item.overall.total.unit;
    let totalValue = item.overall.total.value;
    let total;

    if (totalUnit && totalUnit === "$") {
      let convertedTotal = convertToUSD(totalValue);

      if (/M/g.test(convertedTotal) === false) {
        total = <div className={`total-revenue-md no-hover`} onClick={() => {
        }}>
          {convertedTotal.slice(0, 1)} <span>{convertedTotal.slice(1)}</span>
        </div>;
      } else {
        total = <div className={`total-revenue-md no-hover`} onClick={() => {

        }}>
          {convertedTotal.slice(0, 1)} <span>{convertedTotal.slice(1, -1)}</span> {convertedTotal.slice(-1)}
        </div>;
      }
    }

    return (
      <Card.Body id={`card-` + panelId} onDoubleClick={(e) => { doubleClick(e, item.url) }}>
        <Card.Title> {item.title}</Card.Title>
        <div className="button-container">
          <DropdownButton id="dropdown-basic-button" drop={'left'} className="btn-gray" title={<FiMoreVertical />}>
            <Dropdown.Item onClick={() => { goTo(item.url) }}>View Metric</Dropdown.Item>
            <Dropdown.Item onClick={() => { onRemove(panelId, item.key) }}>Delete Card</Dropdown.Item>
            {availableSizes.map((element, i) => {
              let unique = uuidv4()
              return (
                <div key={i + unique}>
                  {size ?
                    <>
                      {size !== element.size ?
                        <Dropdown.Item onClick={() => {
                          onResize(panelId, element.sizeX, element.sizeY, element.size, item)
                        }}>Resize to {element.size}</Dropdown.Item>
                        : null}
                    </>
                    :
                    <>
                      {item.layout.defaultSize !== element.size ?
                        <Dropdown.Item onClick={() => {
                          onResize(panelId, element.sizeX, element.sizeY, element.size, item)
                        }}>Resize to {element.size}</Dropdown.Item>
                        : null}
                    </>
                  }
                </div>
              )
            })}
            <Dropdown.Item onClick={() => { alert('You clicked details.') }}>Details</Dropdown.Item>
          </DropdownButton>
        </div>
        {total}
        <Row>
          {details.map((item, index) => {
            let subValue, bgColor, icon;
            let bpsValue = item.bps_value ? item.bps_value : null;
            let percentValue = item.percent_value ? item.percent_value : null;
            let countValue = item.count_value ? item.count_value : null;
            let unique = uuidv4()
            return (
              <Col md={6} xs={6} key={index + unique}>
                <div className={` kpi-item no-hover`} key={index} onClick={() => {
                }}>
                  <div className="title">{item.label}</div>
                  <div className="div-table">
                    <div className="tr">
                      <div className="td val">
                        <div className="center-val">
                          {/%/g.test(item.label) === true ? (item.value ? <><span>{convertToWholePercent(item.value)}</span><span className="val-unit">%</span></> : <span>-</span>) :
                            /\$/g.test(item.label) === true ? (item.value ? <span>{convertToUSD(item.value)}</span> : <span>-</span>) :
                              (item.value ? <><span>{convertToCount(item.value)}</span>{item.unit ? item.unit : null}</> : <span>-</span>)}
                        </div>

                      </div>

                      {/* <div className="td middle">

                </div> : null} */}
                      <div className="td middle">
                        {(bpsValue || percentValue || countValue) ?
                          <div className={`indicator ${bgColor}`}>
                            {icon}
                            {subValue}
                          </div>
                          : null}
                        {/* <br /> */}
                      </div>
                    </div>
                  </div>
                </div>
              </Col>
            )
          })
          }


        </Row>

        {item.chart.isLine ?
          <div style={{ height: '58%', paddingTop: '1rem' }}>
            <Legend legend={item.chart.legend} />
            <LineChart
              id={'executive-stacked-' + item._id}
              data={item.chart.data}
              legend={item.chart.legend}
              hideValueLabel={true}
              isAnimationEnabled={true}
            />
          </div>
          : null
        }


        {item.chart.isStackedColumn ?
          <div style={{ height: '60%', paddingTop: '1rem' }}>
            <Legend legend={item.chart.legend} />
            <StackedColumnChart
              className="medium-stacked-column"
              id={'executive-stacked-' + item._id}
              hiddenValueAxis={true}
              isOneHundredPercent={false}
              isPercentage={false}
              largerWidth={true}
              data={item.chart.data}
              legend={item.chart.legend}
              hideLabelBullet={true}
              isAnimationEnabled={true}
            />
          </div>
          : null}


      </Card.Body>
    )
  }
  const largeCard = (item, panelId, size) => {
    let availableSizes = item.layout.availableSizes;
    return (
      <Card.Body id={`card-` + panelId} onDoubleClick={(e) => { doubleClick(e, item.url) }}>
        <Card.Title> {item.title}</Card.Title>
        <div className="button-container">
          <DropdownButton id="dropdown-basic-button" drop={'left'} className="btn-gray" title={<FiMoreVertical />}>
            <Dropdown.Item onClick={() => { goTo(item.url) }}>View Metric</Dropdown.Item>

            <Dropdown.Item onClick={() => { onRemove(panelId, item.key) }}>Delete Card</Dropdown.Item>
            {availableSizes.map((element, i) => {
              let unique = uuidv4()
              return (
                <div key={i + unique}>
                  {size ?
                    <>
                      {size !== element.size ?
                        <Dropdown.Item onClick={() => {
                          onResize(panelId, element.sizeX, element.sizeY, element.size, item)
                        }}>Resize to {element.size}</Dropdown.Item>
                        : null}
                    </>
                    :
                    <>
                      {item.layout.defaultSize !== element.size ?
                        <Dropdown.Item onClick={() => {
                          onResize(panelId, element.sizeX, element.sizeY, element.size, item)
                        }}>Resize to {element.size}</Dropdown.Item>
                        : null}
                    </>
                  }

                </div>
              )
            })}
            <Dropdown.Item onClick={() => { alert('You clicked details.') }}>Details</Dropdown.Item>
          </DropdownButton>
        </div>

        <div className="d-flex flex-row large-card">
          <div className="d-flex flex-column">
            <KPI_NEW
              totalValue={item.overall.total.value}
              totalUnit={item.overall.total.unit}
              details={item.overall.details}
              noHover={true}
            />
          </div>
          <div className="d-flex flex-column">
            <Legend legend={item.chart.legend} />
            {item.chart.isLine ?
              <div style={{ height: '100%', paddingTop: '1.5rem' }}>
                <LineChart
                  id={'executive-stacked-' + item._id}
                  data={item.chart.data}
                  legend={item.chart.legend}
                  hideValueLabel={true}
                />
              </div>

              : null
            }

            {item.chart.isStackedColumn ?
              <StackedColumnChart
                className="large-stacked-column"
                id={'executive-stacked-' + item._id}
                hiddenValueAxis={true}
                isOneHundredPercent={false}
                isPercentage={false}
                largerWidth={true}
                data={item.chart.data}
                legend={item.chart.legend}
                hideLabelBullet={true}
              />
              : null}

          </div>
        </div>
      </Card.Body>
    )
  }

  const onResize = (id, sizeX, sizeY, size, item) => {
    dashboardObj.resizePanel(id, sizeX, sizeY)

    let panelContent;
    if (size === 'S') {
      panelContent = smallCard(item, id, size)
    } else if (size === 'M') {
      panelContent = mediumCard(item, id, size)
    } else if (size === 'L') {
      panelContent = largeCard(item, id, size)
    }
    dashboardObj.updatePanel({
      id: id,
      sizeX: sizeX,
      sizeY: sizeY,
      content: () => {
        return (
          panelContent
        )

      }
    })
  }

  return (
    <DashboardLayoutComponent
      id="executive-cockpit"
      key={'executive-cockpit-1'}
      columns={6}
      allowResizing={false}
      allowDragging={true}
      cellAspectRatio={100 / 80}
      cellSpacing={[30, 30]}
      mediaQuery={"max-width: 700px"}
      ref={(scope) => {
        dashboardObj = scope;
      }}
    >
      <PanelsDirective
        id="executive-cockpit"
        key={'executive-cockpit-card-1'}
      >
        {data.map((item, i) => {
          let unique = uuidv4();
          let size, sizeX, sizeY, minSizeX, minSizeY, maxSizeX, maxSizeY;
          size = item.layout.defaultSize;
          sizeX = item.layout.defaultSizeX;
          sizeY = item.layout.defaultSizeY;
          minSizeX = item.layout.minSizeX;
          minSizeY = item.layout.minSizeY;
          maxSizeX = item.layout.maxSizeX;
          maxSizeY = item.layout.maxSizeY;
          return (
            <PanelDirective
              className="panel-class"
              key={'panel-' + i + unique}
              id={'panel-' + i + unique}
              sizeX={size === 'S' ? 1 : size === 'M' ? 1 : size === 'L' ? 2 : null}
              sizeY={size === 'S' ? 1 : size === 'M' ? 2 : size === 'L' ? 2 : null}
              minSizeX={1}
              minSizeY={1}
              maxSizeX={2}
              maxSizeY={2}
              row={item.layout.row}
              col={item.layout.col}
              content={() => {
                let panelContent;
                // if (i) {
                if (size === 'S') {
                  panelContent = smallCard(item, 'panel-' + i + unique)
                } else if (size === 'M') {
                  panelContent = mediumCard(item, 'panel-' + i + unique)
                } else if (size === 'L') {
                  panelContent = largeCard(item, 'panel-' + i + unique)
                } else if (sizeX === 1 && sizeY === 1) {
                  panelContent = smallCard(item, 'panel-' + i + unique)
                }
                // } else {
                //   panelContent = <div>hello</div>
                //   console.log('i is null')
                // }
                return (
                  panelContent
                )
              }
              }
            />
          )
        })}

      </PanelsDirective>
    </DashboardLayoutComponent>

  )

};

export default DashboardLayout;