import React, { useState } from 'react';
import { Container, Row, Col, Card } from 'react-bootstrap';

import StackedColumnChart from '../../components/chart/StackedColumnChart';
import MapwithPieChart from '../../components/chart/MapwithPieChart';
import KPI_NEW from '../../components/kpi/KPI_NEW';
import Legend from '../../components/legend/Legend';
import { convertToUSDGrowth } from '../../constants/formatter';
import CustomTable from '../../components/customTable/CustomTable';

import MatchmakerModal from './MatchmakerModal';

const MatchmakerContent = (props) => {
  const { matchmakerData } = props;
  const [modalShow, setModalShow] = useState(false);
  const [modalData, setModalData] = useState(null);
  const [modalTitle, setModalTitle] = useState(null);
  const by_rank_id = matchmakerData ? matchmakerData.partner_ranking.by_rank_id : [null];

  const formatUSDGrowth = (cell, row) => {
    return (
      <span>
        {convertToUSDGrowth(cell)}
      </span>
    );
  };

  const handleOnSelect = (row, isSelect, rowIndex) => {
    let modalDataByRank;
    for (const key in by_rank_id) {
      const element = by_rank_id[key];
      if (element._id === 1) {
        modalDataByRank = element
      }
    }
    if (isSelect) {
      setModalTitle(row.partner)
      setModalShow(true);
      setModalData(modalDataByRank);
    }
  }

  const selectRow = {
    mode: 'radio',
    clickToSelect: true,
    hideSelectColumn: true,
    onSelect: handleOnSelect,
    classes: 'selected-row',
    bgColor: '#ecf2ff'
  };

  const rowClasses = () => {
    let classes = 'selectable';
    return classes
  }

  const defaultSorted = [
    {
      dataField: 'segment',
      order: 'desc',
    }
  ];

  const columns = [
    {
      dataField: '_id',
      text: 'id',
      hidden: true
    },
    {
      dataField: 'rank',
      text: 'RANK',
      classes: 'rank center',
      headerClasses: 'rank center'
    },
    {
      dataField: 'partner',
      text: 'PARTNER',
      sort: true,
      
      
      classes: 'partner',
      headerClasses: 'partner'
    },
    {
      dataField: 'vertical',
      text: 'VERTICAL',
      sort: true,
      
      headerClasses: 'left'
    },
    {
      dataField: 'countries',
      text: 'COUNTRIES',
      sort: true,
      classes: 'right',
      headerClasses: 'right'
    },
    {
      dataField: 'metros',
      text: 'METROS',
      sort: true,
      classes: 'right',
      headerClasses: 'right'
    },
    {
      dataField: 'total_ic_oppty_number',
      text: 'TOTAL IC OPPTY #',
      sort: true,
      classes: 'right',
      headerClasses: 'right'
    },
    {
      dataField: 'total_ic_oppty_usd',
      text: 'TOTAL IC OPPTY $',
      sort: true,
      classes: 'right',
      headerClasses: 'right',
      formatter: formatUSDGrowth
    },
    {
      dataField: 'percent_capture',
      text: '% CAPTURE',
      sort: true,
      classes: 'right',
      headerClasses: 'right'
    },
    {
      dataField: 'est_upside_oppty_number',
      text: 'EST UPSIDE OPPTY #',
      sort: true,
      classes: 'right',
      headerClasses: 'right'
    },
    {
      dataField: 'est_upside_oppty_usd',
      text: 'EST UPSIDE OPPTY $',
      sort: true,
      classes: 'right',
      headerClasses: 'right',
      formatter: formatUSDGrowth
    }
  ]
  // End Table

  return (
    <Container className="matchmaker" fluid>
      <MatchmakerModal
        show={modalShow}
        onHide={() => setModalShow(false)}
        data={modalData}
        title={modalTitle}
      />
      <Row className="contentDivider pb-30">
        <div className="fix-width">
          <Card className="TotalRevenue revenue" >
            <Card.Body>
                 <Card.Title>{matchmakerData.overall.total.label}</Card.Title>
                <KPI_NEW
                    totalValue={matchmakerData.overall.total.value}
                    totalUnit={matchmakerData.overall.total.unit}
                    details={matchmakerData.overall.details}
                />
            </Card.Body>
          </Card>
        </div>
        <Col>
          <Card>
            <Card.Body>
              <Card.Title>capture vs estimated upside</Card.Title>
              <Legend legend={matchmakerData.ecosystem_partners_mix.legend} />
              <div style={{ height: '400px' }}>
                <MapwithPieChart id="ecosystem_partners_mix"
                  data={matchmakerData.ecosystem_partners_mix.data}
                  legend={matchmakerData.ecosystem_partners_mix.legend}
                />
              </div>
            </Card.Body>
          </Card>

        </Col>
        <Col>
          <Card className="RevenueByType revenue">
            <Card.Body>
              <Card.Title>% capture per vertical</Card.Title>
              <Legend legend={matchmakerData.percent_capture_per_vertical.legend} />
              <div style={{ height: "400px" }}>
                <StackedColumnChart
                  id="percent_capture_per_vertical_stacked_column_chart"
                  hiddenValueAxis={false}
                  isOneHundredPercent={false}
                  isPercentage={false}
                  largerWidth={true}
                  hideLabelBullet={false}
                  data={matchmakerData.percent_capture_per_vertical.data}
                  legend={matchmakerData.percent_capture_per_vertical.legend}
                  isAnimationEnabled={true}
                />
              </div>
            </Card.Body>
          </Card>
        </Col>
      </Row>
      <Row className="contentDivider">
        <Col>
          <Card className="PartnerRanking revenue">
            <Card.Body>
              <Card.Title>partner ranking</Card.Title>
              <CustomTable
                tableName='partner-ranking scrollable'
                data={matchmakerData.partner_ranking.by_rank === undefined ? [] : matchmakerData.partner_ranking.by_rank}
                defaultSorted={defaultSorted}
                columns={columns}
                selectRow={selectRow}
                rowClasses={rowClasses}
              />
            </Card.Body>
          </Card>
        </Col>
      </Row>
    </Container>
  );
};

export default MatchmakerContent;