import React from 'react';
import { ImArrowDownRight2, ImArrowUpRight2 } from "react-icons/im";
import { Modal, Row, Col } from 'react-bootstrap';

import Legend from '../../components/legend/Legend';
import CustomTable from '../../components/customTable/CustomTable';
import StackedColumnChart from '../../components/chart/StackedColumnChart';
import MicroBarChart from '../../components/chart/MicroBarChart';
import SparklineChart from '../../components/chart/SparklineChart';
import KPI_NEW from '../../components/kpi/KPI_NEW';

import { convertToUSDGrowth, convertToPercent, convertToWholePercent } from '../../constants/formatter';

// import './totalRevenue.scss';


const MetroModal = (props) => {
  const { data, selectedMetro } = props;

  let overall = data ? data.overall : [];
  let bookings_revenue = data ? data.bookings_revenue : [];
  let revenue_by_revenue_type = data ? data.revenue_by_revenue_type : [];
  let bookings_details = data ? data.bookings_details : [];

  const indicatorPercent = (cell, row) => {
    let bgColor = cell >= 0.5 ? 'indicator-up' : cell <= -0.5 ? 'indicator-down' : 'indicator-middle';
    let icon = cell >= 0.5 ? <ImArrowUpRight2 /> : cell <= -0.5 ? <ImArrowDownRight2 /> : <span>&nbsp;</span>;

    return (
      <>
        <span className={`indicator ${bgColor}`}>
          {icon}
          {convertToPercent(cell)}
        </span>
      </>
    );
  };

  const formatUSDGrowth = (cell, row) => {
    return (
      <span>
        {convertToUSDGrowth(cell)}
      </span>
    );
  };

  const formatWholePercent = (cell, row) => {
    return (
      <span>
        {convertToWholePercent(cell)}
      </span>
    );
  };

  const sparkline = (cell, row) => {
    return (
      <SparklineChart loadedData={cell} id={"micro-sparkline_" + row._id} />
    );
  }
  const microBarChart = (cell, row) => {
    return (
      <MicroBarChart loadedData={cell} id={"micro_bar_" + row._id} />
    );
  }

  const defaultSorted = [
    {
      dataField: 'segment',
      order: 'desc',
    }
  ];

  const columns = [
    {
      dataField: '_id',
      text: 'id',
      hidden: true
    },
    {
      dataField: 'segment',
      text: 'SEGMENT',
      
      
      classes: 'segment',
      headerClasses: 'segment'
    },
    {
      dataField: 'micro_column_bar_data',
      text: '',
      sort: true,
      classes: 'sparkline-cell right',
      headerClasses: 'sparkline-cell right',
      formatter: microBarChart
    },
    {
      dataField: 'mrc_usd',
      text: 'MRC $',
      sort: true,
      classes: 'right',
      headerClasses: 'right',
      formatter: formatUSDGrowth
    },
    {
      dataField: 'mix_percent',
      text: 'MIX%',
      sort: true,
      classes: 'right',
      headerClasses: 'right',
      formatter: formatWholePercent
    },
    {
      dataField: 'q_by_q_growth_percent',
      text: 'Q/Q % GROWTH',
      sort: true,
      formatter: indicatorPercent,
      classes: 'with-ellipsis center',
      headerClasses: 'center',
    },
    {
      dataField: 'q_to_q_usd_growth',
      text: 'Q-Q $ GROWTH',
      sort: true,
      classes: 'center',
      headerClasses: 'center',
      formatter: formatUSDGrowth
    },
    {
      dataField: 'y_by_y_growth_percent',
      text: 'Y/Y % GROWTH',
      sort: true,
      formatter: indicatorPercent,
      classes: 'with-ellipsis center',
      headerClasses: 'center',
    },
    {
      dataField: 'y_to_y_usd_growth',
      text: 'Y-Y $ GROWTH',
      sort: true,
      classes: 'center',
      headerClasses: 'center',
      formatter: formatUSDGrowth
    },
    {
      dataField: 'sparkline_data',
      text: '',
      sort: true,
      classes: 'sparkline-cell right',
      headerClasses: 'sparkline-cell right',
      formatter: sparkline
    },
    {
      dataField: 'percent_of_gross',
      text: '% of gross',
      sort: true,
      classes: 'center',
      headerClasses: 'center',
    }
  ]

  return (
    <Modal id="metro-modal" className="metro-modal"
      {...props}
      aria-labelledby="contained-modal-title-vcenter"
      centered
    >
      <Modal.Header closeButton>
        <Modal.Title id="contained-modal-title-vcenter">
          {selectedMetro} total revenue
        </Modal.Title>
      </Modal.Header>
      <Modal.Body>
        {data ?
          <>
            <Row>
              <Col sm={12} md={3}>
                <KPI_NEW
                  totalValue={overall.total.value}
                  totalUnit={overall.total.unit}
                  details={overall.details}
                />
              </Col>
              <Col >
                <Legend legend={bookings_revenue.legend} />
                <div style={{ height: "400px" }}>
                  <StackedColumnChart
                    id="bookings_revenue_stacked_column_chart"
                    largerWidth={true}
                    hiddenValueAxis={false}
                    isOneHundredPercent={false}
                    isPercentage={false}
                    hideLabelBullet={false}
                    data={bookings_revenue.data}
                    legend={bookings_revenue.legend}
                    isAnimationEnabled={true}
                  />
                </div>
              </Col>
              <Col sm={12} md={3} lg={3}>
                <Legend legend={revenue_by_revenue_type.legend} />
                <div style={{ height: "400px" }}>
                  <StackedColumnChart
                    id="revenue_by_revenue_type_stacked_column_chart"
                    largerWidth={true}
                    hiddenValueAxis={false}
                    isOneHundredPercent={false}
                    isPercentage={false}
                    hideLabelBullet={false}
                    data={revenue_by_revenue_type.data}
                    legend={revenue_by_revenue_type.legend}
                    isAnimationEnabled={true}
                  />
                </div>
              </Col>
            </Row>
            <Row>
              <Col md={12} style={{ paddingTop: '1.5rem' }}>
                <CustomTable
                  tableName='metro-bookings-details scrollable'
                  data={bookings_details === undefined ? [] : bookings_details}
                  defaultSorted={defaultSorted} 
                  columns={columns}
                />
              </Col>
            </Row>
          </>
          : <div>No data available.</div>
        }
      </Modal.Body>
    </Modal>
  );
}

export default MetroModal;