
import * as React from "react";
import {
  PivotViewComponent,
  PivotFieldListComponent,
  Inject,
  CalculatedField,
  PivotFieldList
} from "@syncfusion/ej2-react-pivotview";
import { Browser, setStyleAttribute } from "@syncfusion/ej2-base";
import { SampleBase } from "./sample-base";

import "./index.scss";


let dataSourceSettings = {
  catalog: 'Adventure Works DW 2008 SE',
  cube: 'Adventure Works',
  providerType: 'SSAS',
  url: 'https://bi.syncfusion.com/olap/msmdpump.dll',

  expandAll: false,
  enableSorting: true,
  columns: [
    { name: '[Product].[Product Categories]', caption: 'Product Categories' },
    { name: '[Measures]', caption: 'Measures' }
  ],
  valueSortSettings: { headerDelimiter: ' - ' },
  values: [
    { name: '[Measures].[Customer Count]', caption: 'Customer Count' },
    { name: '[Measures].[Internet Sales Amount]', caption: 'Internet Sales Amount' }
  ],
  rows: [
    { name: '[Customer].[Customer Geography]', caption: 'Customer Geography' }
  ],
  formatSettings: [],
  filters: [{ name: '[Date].[Fiscal]', caption: 'Date Fiscal' }],
  filterSettings: [
    {
      name: '[Date].[Fiscal]',
      items: ['[Date].[Fiscal].[Fiscal Quarter].&[2002]&[4]', '[Date].[Fiscal].[Fiscal Year].&[2005]'],
      levelCount: 3
    }
  ]
};
export default class FieldListWithView extends SampleBase {
  afterPopulate() {
    if (this.fieldlistObj && this.pivotObj) {
      this.fieldlistObj.updateView(this.pivotObj);
    }
  }
  afterPivotPopulate() {
    if (!Browser.isDevice && this.fieldlistObj && this.pivotObj) {
      this.fieldlistObj.update(this.pivotObj);
    }
  }
  rendereComplete() {
    this.fieldlistObj.updateView(this.pivotObj);
    this.fieldlistObj.update(this.pivotObj);
  }
  ondataBound() {
    this.pivotObj.tooltip.destroy();
    if (Browser.isDevice) {
      this.pivotObj.element.style.width = "100%";
      this.pivotObj.allowCalculatedField = true;
      this.pivotObj.showFieldList = true;
    }
    this.pivotObj.refresh();
  }
  onLoad() {
    if (Browser.isDevice) {
      this.renderMode = "Popup";
      this.target = ".control-section";
      setStyleAttribute(document.getElementById("PivotFieldList"), {
        width: 0,
        height: 0,
        float: "left",
        display: "none"
      });
    }
  }
  render() {
    return (
      <div className="field-list-with-view">
        <div className="control-pane">
          <div className="control-section" style={{ overflow: "auto" }}>
            <PivotViewComponent
              id="PivotView"
              ref={d => (this.pivotObj = d)}
              enginePopulated={this.afterPivotPopulate.bind(this)}
              width={"99%"}
              height={"580"}
              gridSettings={{ columnWidth: 140 }}
            >
              <Inject services={[CalculatedField, PivotFieldList]} />
            </PivotViewComponent>
            <PivotFieldListComponent
              id="PivotFieldList"
              ref={d => (this.fieldlistObj = d)}
              enginePopulated={this.afterPopulate.bind(this)}
              dataSourceSettings={dataSourceSettings}
              renderMode={"Fixed"}
              allowCalculatedField={true}
              load={this.onLoad}
              dataBound={this.ondataBound.bind(this)}
            >
              <Inject services={[CalculatedField]} />
            </PivotFieldListComponent>
          </div>
        </div>
      </div>
    );
  }
}