import React from 'react';
import { Container, Row, Col, Card } from 'react-bootstrap';
import { ImArrowUpRight2 } from "react-icons/im";

import MapChart from '../../components/chart/MapChart';
import StackedColumnBar from '../../components/chart/StackedColumnBar';
import StackedColumnChart from '../../components/chart/StackedColumnChart';
import DonutChart from '../../components/chart/DonutChart';
import LineChart from '../../components/chart/LineChart';
import KPI_NEW from '../../components/kpi/KPI_NEW';
import Legend from '../../components/legend/Legend';
import CustomTable from '../../components/customTable/CustomTable';

import { convertToUSDGrowth, convertToPercent } from '../../constants/formatter';


const PartnersContent = (props) => {
  const { partnersData } = props;

  const formatToPercent = (cell, row) => {
    return (
      <span>
        {convertToPercent(cell)}
      </span>
    );
  }

  const formatUSDGrowth = (cell, row) => {
    return (
      <span>
        {convertToUSDGrowth(cell)}
      </span>
    );
  };
  // Start Table
  const indicatorUp = (cell, row) => {
    return (
      <span className="indicator indicator-up">
        <ImArrowUpRight2 />
        {cell}
      </span>
    );
  }

  const defaultSorted = [
    {
      dataField: 'segment',
      order: 'desc',
    }
  ];

  const columns = [
    {
      dataField: '_id',
      text: 'id',
      hidden: true
    },
    {
      dataField: 'segment',
      text: 'SEGMENT',
      
      
      classes: 'segment-th',
      headerClasses: 'segment-td'
    },
    {
      dataField: 'eco_partners',
      text: 'TOTAL ECO PARTNERS',
      sort: true,
      classes: 'right',
      headerClasses: 'right'
    },
    {
      dataField: 'mix_percent',
      text: ' MIX%',
      sort: true,
      classes: 'right',
      headerClasses: 'right',
      formatter: formatToPercent
    },
    {
      dataField: 'y_to_y_growth',
      text: 'Y-Y # GROWTH',
      sort: true,
      classes: 'right',
      headerClasses: 'right'
    },
    {
      dataField: 'q_to_q_growth',
      text: 'Q-Q # GROWTH',
      sort: true,
      classes: 'right',
      headerClasses: 'right'
    },
    {
      dataField: 'ic_per_partner',
      text: 'IC PER PARTNER',
      sort: true,
      classes: 'right',
      headerClasses: 'right'
    },
  ]
  // End Table
  // Start Table

  const barchart = (cell, row) => {
    return (
      <StackedColumnBar data={cell} />
    )
  }


  
  const rankFormat = (cell, row, rowIndex, formatExtraData) => {
    return (
      <span>{rowIndex + 1}</span>
    );
  }
  
  // For Selecting Row ===
  // const handleOnSelect = (row, isSelect, rowIndex) => {
  //   let modalDataByRank;
  //   for (const key in by_rank_id) {
  //     const element = by_rank_id[key];
  //     if (element._id === row._id) {
  //       modalDataByRank = element
  //     }
  //   }
  //   if (isSelect) {
  //     setModalShow(true);
  //     setReferenceID(row._id);
  //     setModalData(modalDataByRank);
  //   }
  // }

  const metro = (cell, row) => {
    return (
      <>
        <img className="flag-icon" src={cell.image_url} alt="sample flag" />
        <span className="body-name">{cell.name}</span>
      </>

    )
  }

  const selectRowMetro = {
    mode: 'radio',
    clickToSelect: true,
    hideSelectColumn: true,
    // onSelect: handleOnSelect,
    classes: 'selected-row',
    bgColor: '#ecf2ff'
  };

  const rowClasses = () => {
    let classes = 'selectable';
    return classes
  }
  // For Selecting Row ===

  
  const defaultSortedMetro = [
    {
      dataField: 'segment',
      order: 'desc',
    }
  ];
  

  const metroColumns = [
    {
      dataField: '_id',
      text: 'id',
      hidden: true
    },
    {
      dataField: '',
      text: 'Rank',
      classes: 'rank center',
      headerClasses: 'rank center',
      formatter: rankFormat,
    },
    {
      dataField: 'metro',
      text: 'Metro',
      
      
      classes: 'metro',
      headerClasses: 'metro',
      formatter: metro
    },
    {
      dataField: 'total_eco_partners',
      text: 'Total Eco Partners',
      classes: 'right',
      headerClasses: 'right',
    },
    {
      dataField: 'mix_percent',
      text: 'MIX%',
      sort: true,
      classes: 'right',
      headerClasses: 'right',
      formatter: formatToPercent
    },
    {
      dataField: 'stacked_column_bar_data',
      text: '',
      classes: 'sparkline-cell center',
      headerClasses: 'sparkline-cell center',
      formatter: barchart
    },
    {
      dataField: 'y_by_y_growth_percent',
      text: 'Y/Y % GROWTH',
      sort: true,
      formatter: indicatorUp,
      classes: 'right',
      headerClasses: 'right'
    },
    {
      dataField: 'y_to_y_usd_growth',
      text: 'Y-Y $GROWTH',
      sort: true,

      classes: 'right',
      headerClasses: 'right',
      formatter: formatUSDGrowth
    },
    {
      dataField: 'ic_per_partner',
      text: 'IC per Partner',
      sort: true,
      classes: 'right',
      headerClasses: 'right'
    }
  ]
  // End Table

  return (
    <Container className="partners" fluid>
      <Row className="contentDivider pb-30">
        <div className="fix-width">
          <Card className="TotalRevenue revenue" >
            <Card.Body>
                <Card.Title>{partnersData.overall.total.label}</Card.Title>
                <KPI_NEW
                    totalValue={partnersData.overall.total.value}
                    details={partnersData.overall.details}
                />
            </Card.Body>
          </Card>
        </div>
        <Col>
          <Card className="RevenueByType revenue">
            <Card.Body>
              <Card.Title>ecosystem partners by segment</Card.Title>
              {partnersData.ecosystem_partners_by_region ?
                <>
                  <Legend legend={partnersData.ecosystem_partners_by_region.legend} />
                  <div style={{ height: "400px" }}>
                    <StackedColumnChart
                      id="ecosystem_partners_by_region_stacked_column_chart"
                      hiddenValueAxis={false}
                      isOneHundredPercent={false}
                      isPercentage={false}
                      largerWidth={true}
                      hideLabelBullet={false}
                      data={partnersData.ecosystem_partners_by_region.data}
                      legend={partnersData.ecosystem_partners_by_region.legend}
                      isAnimationEnabled={true}
                    />
                  </div>
                </>
                : null}
            </Card.Body>
          </Card>
        </Col>
        <Col style={{ maxWidth: "400px" }}>
          <Card className="Segment revenue">
            <Card.Body>
              <Card.Title>ecosystem partners mix</Card.Title>
              {partnersData.ecosystem_partners_mix ? <>
                <Legend legend={partnersData.ecosystem_partners_mix.legend} />
                <div style={{ height: "350px" }}>
                  <DonutChart
                    id={"ecosystem_partners_mix_donut_chart"}
                    totalTitle={"ECO PARTNERS"}
                    total={partnersData.ecosystem_partners_mix.total_eco_partners}
                    data={partnersData.ecosystem_partners_mix.data}
                    legend={partnersData.ecosystem_partners_mix.legend}
                    isAnimationEnabled={true}
                  />
                </div>
              </> : null}
            </Card.Body>
          </Card>
        </Col>
      </Row>
      <Row className="contentDivider" style={{ paddingBottom: '30px' }}>
        <Col>
          <Card className="SegmentDetails revenue">
            <Card.Body>
              <Card.Title>segment details</Card.Title>
              {partnersData.segment_details ?
                <CustomTable
                  tableName='segment-details'
                  data={partnersData.segment_details === undefined ? [] : partnersData.segment_details}
                  defaultSorted={defaultSorted}
                  columns={columns}
                />
                : null}
            </Card.Body>
          </Card>
        </Col>
        <Col style={{ maxWidth: "400px" }}>
          <Card className="SegmentTrends revenue">
            <Card.Body>
              <Card.Title>segment y/y growth trends</Card.Title>
              {partnersData.segment_y_by_y_growth_trends ? <>
                <Legend legend={partnersData.segment_y_by_y_growth_trends.legend} />
                <div style={{ height: "300px" }}>
                  <LineChart
                    id={"segment_y_by_y_growth_trends_line_chart"}
                    data={partnersData.segment_y_by_y_growth_trends.data}
                    legend={partnersData.segment_y_by_y_growth_trends.legend}
                    isAnimationEnabled={true}
                  />
                </div>
              </> : null}
            </Card.Body>
          </Card>
        </Col>
      </Row>
      <Row className="contentDivider" style={{ paddingBottom: '30px' }}>
        <Col>
          <Card className="RevenueByType revenue">
            <Card.Body>
              <Card.Title>average ic $ per partner</Card.Title>
              {partnersData.average_ic_usd_per_partner ? <>
                <Legend legend={partnersData.average_ic_usd_per_partner.legend} />
                <div style={{ height: "300px" }}>
                  <LineChart
                    id={"average_ic_usd_per_partner_line_chart"}
                    data={partnersData.average_ic_usd_per_partner.data}
                    legend={partnersData.average_ic_usd_per_partner.legend}
                    isAnimationEnabled={true}
                  />
                </div>
              </> : null}
            </Card.Body>
          </Card>
        </Col>
        <Col>
          <Card className="RevenueByType revenue">
            <Card.Body>
              <Card.Title>average ic # per partner</Card.Title>
              {partnersData.average_ic_number_per_partner ? <>
                <Legend legend={partnersData.average_ic_number_per_partner.legend} />
                <div style={{ height: "300px" }}>
                  <LineChart
                    id={"average_ic_number_per_partner_line_chart"}
                    data={partnersData.average_ic_number_per_partner.data}
                    legend={partnersData.average_ic_number_per_partner.legend}
                    isAnimationEnabled={true}
                  />
                </div>
              </> : null}
            </Card.Body>
          </Card>
        </Col>
        <Col>
          <Card className="RevenueByType revenue">
            <Card.Body>
              <Card.Title>a-side vs. z-side</Card.Title>
              {partnersData.a_side_vs_z_side ? <>
                <Legend legend={partnersData.a_side_vs_z_side.legend} />
                <div style={{ height: "300px" }}>
                  <StackedColumnChart
                    id="a_side_vs_z_side_stacked_column_chart"
                    hiddenValueAxis={true}
                    isOneHundredPercent={true}
                    isPercentage={true}
                    largerWidth={true}
                    hideLabelBullet={false}
                    data={partnersData.a_side_vs_z_side.data}
                    legend={partnersData.a_side_vs_z_side.legend}
                    isAnimationEnabled={true}
                  />
                </div>
              </> : null}
            </Card.Body>
          </Card>
        </Col>
      </Row>
      <Row className="contentDivider">
        <Col>
          <Card className="revenue">
            <Card.Body>
              <Card.Title>ecosystem partners map</Card.Title>
              {partnersData.ecosystem_partners_map ? <>
                <Legend legend={partnersData.ecosystem_partners_map.legend} />
                <div style={{ height: "400px ", paddingTop: "2rem" }}>
                  <MapChart
                    id={"ecosystem_partners_map_chart"}
                    largerWidth={true}
                    data={partnersData.ecosystem_partners_map.data}
                  />
                </div>
              </> : null}
            </Card.Body>
          </Card>
        </Col>
        <Col>
          <Card className="revenue">
            <Card.Body>
              <Card.Title>metro ranking</Card.Title>
              {partnersData.metro_ranking ?
                <CustomTable
                  tableName='metro-ranking-partners scrollable'
                  data={partnersData.metro_ranking.by_rank.data === undefined ? [] : partnersData.metro_ranking.by_rank.data}
                  selectRow={selectRowMetro} // For Selecting Row ===
                  defaultSorted={defaultSortedMetro} 
                  rowClasses={rowClasses}
                  columns={metroColumns}
                />
                : null}
              {/* <Table responsive className="separate-table">
                <tbody>
                  {
                    partnersData.metro_ranking.by_top_rank.map((data, i) => {
                      return (
                        <tr key={i} className={i === 0 ? 'border-head height1' : 'height1'}>
                          <td className="rank"></td>
                          <td className="metro">{data.metro}</td>
                          <td className="sparkline-cell">
                            <StackedColumnBar data={data.stacked_column_bar_data} />
                          </td>
                          <td className="text-center" style={{ paddingLeft: '2rem' }}>{data.total_revenue}</td>
                          <td style={{ paddingLeft: '2.7rem' }} >{data.mix_percent}</td>
                          <td style={{ paddingLeft: '1.2rem' }}>
                            <span className="indicator indicator-up " style={{ top: "70.7%" }}>
                              <ImArrowUpRight2 />
                              {data.y_by_y_growth_percent}
                            </span></td>
                          <td style={{ paddingLeft: '1.75rem' }}>{data.y_to_y_usd_growth}</td>
                          <td></td>
                          <td></td>
                        </tr>
                      )
                    })
                  }
                </tbody>
              </Table> */}

            </Card.Body>
          </Card>
        </Col>
      </Row>
    </Container>
  );
};

export default PartnersContent;