import React, { useState, useEffect } from 'react';
import axiosRetry from 'axios-retry';
import axios from 'axios';
import { Button, ButtonGroup, Container } from 'react-bootstrap';
import { FilterContainer, FilterModal } from '../../components/filter';
import {
	optionRegion, optionCountry, optionMeasure, optionRange, optionGroup6, optionVertical2,
	optionProduct, optionCompanySize, optionDeaveraging, optionHyperscaler, optionLighthouseFlag,
	optionMetro, optionEntity2, optionSalesProgram, optionSuper9Metro, optionRevenueType
} from '../../constants/filterOptions';
import { Separator, RadioDD, CheckboxDD, SearchCheckboxDD } from '../../components/customDropdown';
import { comments } from '../../constants/comments';
import { InternalServerError, ResourceNotFoundError } from '../../components/error/Error';
import { API_BASE_URL } from '../../constants/config';
import Comment from '../../components/comment/Comment';
import Loader from '../../components/loader/Loader';
import OverviewContentType1 from './OverviewContentType1';
import OverviewContentType2 from './OverviewContentType2';


const Overview = ({ activeTab, setActiveTab }) => {
	const [totalRevData, setTotalRevData] = useState([]);
	const [customerRevData, setCustomerRevData] = useState([]);
	const [isLoading, setIsLoading] = useState(true)

	// show all filters
	const [showFilter, setShowFilter] = useState(false);
	const onShowFilter = () => {
		setShowFilter(true)
	}
	const onCloseFilter = async () => {
		await onCancelVertical();
		await onCancelRegion();
		await onCancelCountry();
		await onCancelProduct();
		await onCancelMeasure();
		await onCancelRange();
		await onCancelGroup();
		await onCancelHQCountry();
		await onCancelHQRegion();
		await onCancelSize();
		setShowFilter(false);
	}
	const onApplyFilter = async () => {
		await onApplyVertical();
		await onApplyRegion();
		await onApplyCountry();
		await onApplyProduct();
		await onApplyMeasure();
		await onApplyRange();
		await onApplyGroup();
		await onApplyHQCountry();
		await onApplyHQRegion();
		await onApplySize();
		setShowFilter(false);
	}

	// comment area
	const [showComment, setShowComment] = useState(false);
	const onCloseComment = () => {
		setShowComment(false);
	}
	const onShowComment = () => {
		setShowComment(true)
	};

	// checkbox for vertical values
	const [vertical_val_onApply, set_vertical_val_onApply] = useState([]);
	const [vertical_val, set_vertical_val] = useState([]);
	const onApplyVertical = () => {
		set_vertical_val_onApply(vertical_val);
	};
	const onCancelVertical = () => {
		set_vertical_val(vertical_val_onApply);
	};

	// checkbox for region values
	const [region_val_onApply, set_region_val_onApply] = useState([]);
	const [region_val, set_region_val] = useState([]);
	const onApplyRegion = () => {
		set_region_val_onApply(region_val);
	};
	const onCancelRegion = () => {
		set_region_val(region_val_onApply);
	};

	// checkbox for entity values
	const [entity_val_onApply, set_entity_val_onApply] = useState([]);
	const [entity_val, set_entity_val] = useState([]);
	const onApplyEntity = () => {
		set_entity_val_onApply(entity_val);
	};
	const onCancelEntity = () => {
		set_entity_val(entity_val_onApply);
	};


	// checkbox for revenue type values
	const [revenuetype_val_onApply, set_revenuetype_val_onApply] = useState([]);
	const [revenuetype_val, set_revenuetype_val] = useState([]);
	const onApplyRevenueType = () => {
		set_revenuetype_val_onApply(revenuetype_val);
	};
	const onCancelRevenueType = () => {
		set_revenuetype_val(revenuetype_val_onApply);
	};

	// checkbox for comapany size values
	const [size_val_onApply, set_size_val_onApply] = useState([]);
	const [size_val, set_size_val] = useState([]);
	const onApplySize = () => {
		set_size_val_onApply(size_val);
	};
	const onCancelSize = () => {
		set_size_val(size_val_onApply);
	};


	// checkbox for sales program type size values
	const [sales_val_onApply, set_sales_val_onApply] = useState([]);
	const [sales_val, set_sales_val] = useState([]);
	const onApplySales = () => {
		set_sales_val_onApply(sales_val);
	};
	const onCancelSales = () => {
		set_sales_val(sales_val_onApply);
	};

	// checkbox for product values
	const [product_val_onApply, set_product_val_onApply] = useState([]);
	const [product_val, set_product_val] = useState([]);
	const onApplyProduct = () => {
		set_product_val_onApply(product_val);
	};
	const onCancelProduct = () => {
		set_product_val(product_val_onApply);
	};

	// searchCheckbox for country values
	const [country_val_onApply, set_country_val_onApply] = useState([]);
	const [country_val, set_country_val] = useState([]);
	const onApplyCountry = () => {
		set_country_val_onApply(country_val);
	};
	const onCancelCountry = () => {
		set_country_val(country_val_onApply);
	};

	// radio for range values
	const range_initial_value = optionRange ? optionRange[0] : null;
	const [range_val_onApply, set_range_val_onApply] = useState(range_initial_value);
	const [range_val, set_range_val] = useState(range_initial_value);
	const onApplyRange = () => {
		set_range_val_onApply(range_val);
	};
	const onCancelRange = () => {
		set_range_val(range_val_onApply);
	};

	// radio for measure values
	const measure_initial_value = optionMeasure ? optionMeasure[0] : null;
	const [measure_val_onApply, set_measure_val_onApply] = useState(measure_initial_value);
	const [measure_val, set_measure_val] = useState(measure_initial_value);
	const onApplyMeasure = () => {
		set_measure_val_onApply(measure_val);
	};
	const onCancelMeasure = () => {
		set_measure_val(measure_val_onApply);
	};


	// radio for super9metro values
	const super_initial_value = optionSuper9Metro ? optionSuper9Metro[0] : null;
	const [super_val_onApply, set_super_val_onApply] = useState(super_initial_value);
	const [super_val, set_super_val] = useState(super_initial_value);
	const onApplySuper = () => {
		set_super_val_onApply(super_val);
	};
	const onCancelSuper = () => {
		set_super_val(super_val_onApply);
	};


	// checkbox for region values
	const [hqregion_val_onApply, set_hqregion_val_onApply] = useState([]);
	const [hqregion_val, set_hqregion_val] = useState([]);
	const onApplyHQRegion = () => {
		set_hqregion_val_onApply(hqregion_val);
	};
	const onCancelHQRegion = () => {
		set_hqregion_val(hqregion_val_onApply);
	};


	// searchCheckbox for country values
	const [hqcountry_val_onApply, set_hqcountry_val_onApply] = useState([]);
	const [hqcountry_val, set_hqcountry_val] = useState([]);
	const onApplyHQCountry = () => {
		set_hqcountry_val_onApply(hqcountry_val);
	};
	const onCancelHQCountry = () => {
		set_hqcountry_val(hqcountry_val_onApply);
	};

	// checkbox for deaveraging values
	const [deaveraging_val_onApply, set_deaveraging_val_onApply] = useState([]);
	const [deaveraging_val, set_deaveraging_val] = useState([]);
	const onApplyDeaveraging = () => {
		set_deaveraging_val_onApply(deaveraging_val);
	};
	const onCancelDeaveraging = () => {
		set_deaveraging_val(deaveraging_val_onApply);
	};

	// radio for hyperscaler values
	const hyperscaler_initial_value = optionHyperscaler ? optionHyperscaler[0] : null;
	const [hyperscaler_val_onApply, set_hyperscaler_val_onApply] = useState(hyperscaler_initial_value);
	const [hyperscaler_val, set_hyperscaler_val] = useState(hyperscaler_initial_value);
	const onApplyHyperscaler = () => {
		set_hyperscaler_val_onApply(hyperscaler_val);
	};
	const onCancelHyperscaler = () => {
		set_hyperscaler_val(hyperscaler_val_onApply);
	};

	// checkbox for lighthouse values
	const [lighthouse_val_onApply, set_lighthouse_val_onApply] = useState([]);
	const [lighthouse_val, set_lighthouse_val] = useState([]);
	const onApplyLighthouse = () => {
		set_lighthouse_val_onApply(lighthouse_val);
	};
	const onCancelLighthouse = () => {
		set_lighthouse_val(lighthouse_val_onApply);
	};

	// searchCheckbox for metro values
	const [metro_val_onApply, set_metro_val_onApply] = useState([]);
	const [metro_val, set_metro_val] = useState([]);
	const onApplyMetro = () => {
		set_metro_val_onApply(metro_val);
	};
	const onCancelMetro = () => {
		set_metro_val(metro_val_onApply);
	};

	let group_by = [];

	optionGroup6.forEach(element => {
		let item = element.key;
		let group_by_values = {};

		if (activeTab === 'total') {
			if (item.includes('total') || item.includes('both')) {
				group_by_values = element;
			}
		} else if (activeTab === "customer") {
			if (item.includes('mrr') || item.includes('both')) {
				group_by_values = element;
			}
		}
		if (Object.keys(group_by_values).length != 0) {
			group_by.push(group_by_values);
		}

	});

	// radio for group-by values
	const group_initial_value = group_by ? group_by[0] : null;
	const [group_val_onApply, set_group_val_onApply] = useState(group_initial_value);
	const [group_val, set_group_val] = useState(group_initial_value);
	const onApplyGroup = () => {
		set_group_val_onApply(group_val);
	};
	const onCancelGroup = () => {
		set_group_val(group_val_onApply);
	};

	// Axios call
	const fetchData = async () => {
		let url = `${API_BASE_URL}/revenue/overview.json?print=pretty`;

		setIsLoading(true);
		axiosRetry(axios, { retries: 3 });

		await axios({
			method: 'GET',
			url: url
		}).then(result => {
			let data = result.data;
			setTotalRevData(data['total-revenue']);
			setCustomerRevData(data['customer-revenue']);
			setIsLoading(false);
		}).catch(error => {
			console.log(error);
			setTotalRevData([]);
			setCustomerRevData([]);
			setIsLoading(false);
		});
	};

	useEffect(() => {
		fetchData();
	}, []);




	if (isLoading) return <Loader withContainer />;

	return (
		<>
			<div className={`page-content overview-page ${showComment ? 'open' : 'closed'}`}>
				{(totalRevData || customerRevData) ?
					(totalRevData.segment_details.length > 0 || customerRevData.net_percent_of_gross_by_segment.length > 0) ?
						<>
							<FilterContainer
								showComment={showComment}
								onShowFilter={onShowFilter}
								onShowComment={onShowComment}
							>
								<div className="filters custom-scroll">
									<ButtonGroup className="sub-tab">
										<Button
											variant='light'
											className={activeTab === 'total' ? 'active' : ''}
											onClick={() => { setActiveTab('total') }}>
											Total Revenue
                		</Button>
										<Button
											variant='light'
											className={activeTab === 'customer' ? 'active' : ''}
											onClick={() => { setActiveTab('customer') }}>
											Customer MRR
                		</Button>
									</ButtonGroup>
									<Separator height='39px' />
									{activeTab === 'customer' ?
										<CheckboxDD
											title="Vertical"
											id="vertical"
											options={optionVertical2}
											valueApplied={vertical_val_onApply}
											checkVal={vertical_val}
											setCheckVal={set_vertical_val}
											onApply={onApplyVertical}
											onCancel={onCancelVertical}
										/>
										: null}
									<CheckboxDD
										title="Deploy Region"
										id="region"
										options={optionRegion}
										valueApplied={region_val_onApply}
										checkVal={region_val}
										setCheckVal={set_region_val}
										onApply={onApplyRegion}
										onCancel={onCancelRegion}
									/>

									<CheckboxDD
										title="Product Type"
										id="product-type"
										options={optionProduct}
										valueApplied={product_val_onApply}
										checkVal={product_val}
										setCheckVal={set_product_val}
										onApply={onApplyProduct}
										onCancel={onCancelProduct}
									/>
									{activeTab === 'customer' ?
										<CheckboxDD
											title="Revenue Type"
											id="revenue-type"
											options={optionRevenueType}
											valueApplied={revenuetype_val_onApply}
											checkVal={revenuetype_val}
											setCheckVal={set_revenuetype_val}
											onApply={onApplyRevenueType}
											onCancel={onCancelRevenueType}
										/>
										: null}
									<RadioDD
										title="Measure"
										id="measure"
										options={optionMeasure}
										radioVal={measure_val}
										setRadioVal={set_measure_val}
										onApply={onApplyMeasure}
										onCancel={onCancelMeasure}
										initialValue={measure_initial_value}
									/>
									<RadioDD
										id="range"
										options={optionRange}
										radioVal={range_val}
										setRadioVal={set_range_val}
										onApply={onApplyRange}
										onCancel={onCancelRange}
										initialValue={range_initial_value}
									/>
									<RadioDD
										id="group-by"
										options={group_by}
										radioVal={group_val}
										setRadioVal={set_group_val}
										onApply={onApplyGroup}
										onCancel={onCancelGroup}
										initialValue={group_initial_value}
									/>
								</div>
								<FilterModal
									title="Overview"
									setShowFilter={setShowFilter}
									showFilter={showFilter}
									onCloseFilter={onCloseFilter}
									onApplyFilter={onApplyFilter}
								>
									{activeTab === 'customer' ?
										<CheckboxDD
											title="Vertical"
											id="vertical-modal"
											options={optionVertical2}
											visibleInModal
											valueApplied={vertical_val_onApply}
											checkVal={vertical_val}
											setCheckVal={set_vertical_val}
											onApply={onApplyVertical}
											onCancel={onCancelVertical}
										/>
										: null}

									< CheckboxDD
										title="Deploy Region"
										id="region-modal"
										visibleInModal
										options={optionRegion}
										checkVal={region_val}
										setCheckVal={set_region_val}
									/>
									<SearchCheckboxDD
										title="Deploy Country"
										id="country-modal"
										visibleInModal
										options={optionCountry}
										checkVal={country_val}
										setCheckVal={set_country_val}
									/>
									<CheckboxDD
										title="Product Type"
										id="product-type-modal"
										options={optionProduct}
										visibleInModal
										valueApplied={product_val_onApply}
										checkVal={product_val}
										setCheckVal={set_product_val}
										onApply={onApplyProduct}
										onCancel={onCancelProduct}
									/>
									<CheckboxDD
										title="HQ Region"
										id="hqregion"
										visibleInModal
										options={optionRegion}
										checkVal={hqregion_val}
										setCheckVal={set_hqregion_val}
										onApply={onApplyHQRegion}
										onCancel={onCancelHQRegion}
									/>
									<SearchCheckboxDD
										title="HQ Country"
										id="country-modal"
										visibleInModal
										options={optionCountry}
										checkVal={hqcountry_val}
										setCheckVal={set_hqcountry_val}
									/>
									<CheckboxDD
										title="Company Size"
										id="company-modal"
										visibleInModal
										options={optionCompanySize}
										checkVal={size_val}
										setCheckVal={set_size_val}
									/>
									<CheckboxDD
										title="Deaveraging Segment"
										id="deaveraging-modal"
										visibleInModal
										options={optionDeaveraging}
										checkVal={deaveraging_val}
										setCheckVal={set_deaveraging_val}
									/>
									<RadioDD
										title="Hyperscaler Flag"
										id="hyperscaler-modal"
										visibleInModal
										options={optionHyperscaler}
										radioVal={hyperscaler_val}
										setRadioVal={set_hyperscaler_val}
									/>
									<CheckboxDD
										title="Lighthouse Flag"
										id="lighthouse-modal"
										visibleInModal
										options={optionLighthouseFlag}
										checkVal={lighthouse_val}
										setCheckVal={set_lighthouse_val}
									/>
									<SearchCheckboxDD
										title="Deploy Metro"
										id="metro-modal"
										visibleInModal
										options={optionMetro}
										checkVal={metro_val}
										setCheckVal={set_metro_val}
									/>
									<CheckboxDD
										title="Entity Type"
										id="entity-modal"
										visibleInModal
										options={optionEntity2}
										checkVal={entity_val}
										setCheckVal={set_entity_val}
									/>
									<CheckboxDD
										title="Sales Program Type"
										id="sales-modal"
										visibleInModal
										options={optionSalesProgram}
										checkVal={sales_val}
										setCheckVal={set_sales_val}
									/>
									<RadioDD
										title="Super9 Metro"
										id="super-modal"
										visibleInModal
										options={optionSuper9Metro}
										radioVal={super_val}
										setRadioVal={set_super_val}
									/>


								</FilterModal>
							</FilterContainer>
							{/* ============= Your Component here! ============= */}
							<Container fluid className={`${activeTab === 'total' ? 'd-block' : 'd-none'}`}>
								<OverviewContentType1
									activeTab={activeTab}
									selectedRangeValue={range_val_onApply.key}
									overviewData={totalRevData}
									tab_name='TOTAL REVENUE'
									tab_id="total-overview" />
							</Container>
							<Container fluid className={`${activeTab === 'customer' ? 'd-block' : 'd-none'}`}>
								<OverviewContentType2
									activeTab={activeTab}
									overviewData={customerRevData}
									selectedRangeValue={range_val_onApply.key}
									tab_name='CUSTOMER MRR'
									tab_id="customer-overview" />
							</Container>
						</>
						: <ResourceNotFoundError redirectURL={"/executive-cockpit/bookings/overview"} />
					: <InternalServerError redirectURL={"/executive-cockpit/bookings/overview"} />
				}
			</div>
			<Comment
				data={comments}
				onCloseComment={onCloseComment}
				showComment={showComment}
			/>
		</>
	);
};

export default Overview;