import React, { useState, useEffect } from 'react';
import axiosRetry from 'axios-retry';
import axios from 'axios';
import {RadioDD} from '../../components/customDropdown';
import {FilterContainer, FilterModal} from '../../components/filter';
import Loader from '../../components/loader/Loader';
import Comment from '../../components/comment/Comment';
import { InternalServerError, ResourceNotFoundError } from '../../components/error/Error';
import { optionAccounts, optionProspect } from '../../constants/filterOptions';
import { API_BASE_URL } from '../../constants/config';
import { comments } from '../../constants/comments';
import NavigatorContent from './NavigatorContent';


const Navigator = () => {
  const [navigatorData, setNavigatorData] = useState([]);
  const [deploymentMapData, setDeploymentMapData] = useState([]);
  const [isLoading, setIsLoading] = useState(true);

  const all_accounts = navigatorData ? navigatorData.all_accounts : [];
  const newly_added_accounts = navigatorData ? navigatorData.newly_added_accounts : [];

  // show all filters
  const [showFilter, setShowFilter] = useState(false);
  const onShowFilter = () => {
    setShowFilter(true)
  }
  const onCloseFilter = async () => {
    await onCancelAccounts()
    await onCancelProspects()
    setShowFilter(false);
  }
  const onApplyFilter = async () => {
    await onApplyAccounts()
    await onApplyProspects()
    setShowFilter(false);
  }

  // comment area
  const [showComment, setShowComment] = useState(false);
  const onCloseComment = () => {
    setShowComment(false);
  }
  const onShowComment = () => {
    setShowComment(true)
  };

  // radio for accounts values
  const accounts_initial_value = optionAccounts ? optionAccounts[0] : null;
  const [accounts_val_onApply, set_accounts_val_onApply] = useState(accounts_initial_value);
  const [accounts_val, set_accounts_val] = useState(accounts_initial_value);
  const onApplyAccounts = () => {
    set_accounts_val_onApply(accounts_val)
  }
  const onCancelAccounts = () => {
    set_accounts_val(accounts_val_onApply)
  }

  // radio for prospects values
  const prospects_initial_value = optionProspect ? optionProspect[0] : null;
  const [prospects_val_onApply, set_prospects_val_onApply] = useState(prospects_initial_value);
  const [prospects_val, set_prospects_val] = useState(prospects_initial_value);
  const onApplyProspects = () => {
    set_prospects_val_onApply(prospects_val)
  }
  const onCancelProspects = () => {
    set_prospects_val(prospects_val_onApply)
  }

  // Axios call
  const fetchData = () => {
    let url = `${API_BASE_URL}/account-navigator/navigator.json`;
    // Uncomment only when API is now available and working
    // const token = localStorage.getItem('token');
    // TODO: Add url and defaultQueryParameters to call Filter API calls

    setIsLoading(true);

    axiosRetry(axios, { retries: 3 });

    Promise.all([
      axios({
        method: 'GET',
        url: url
        // Uncomment only when API is now available and working
        // url: url + defaultQueryParameters,
        // headers: {
        //   'Content-Type': 'application/json',
        //   "Authorization": `bearer ${token}`
        // }
      }),
      axios({
        // From Chart Template API
        method: 'GET',
        url: `${API_BASE_URL}/charts/bubble-map-chart/deployment-map/account-navigator.json?print=pretty`
      })
    ]).then(results => {
      // Uncomment only if for API testing purposes
      // console.log(result.config.url);
      setNavigatorData(results[0].data);
      setDeploymentMapData(results[1].data);
      setIsLoading(false);
    }).catch(error => {
      console.log(error);
      setNavigatorData(null);
      setIsLoading(false);
    });
  };

  useEffect(() => {
    fetchData();
  }, 
    []
    // Uncomment only when API is now available and working
    // [
    //   accounts_val_onApply,
    //   prospects_val_onApply,
    // ]
  );

  if (isLoading) return <Loader withContainer />;
  
  return (
    <>
      <div className={`page-content ${showComment ? 'open' : 'closed'}`}>
       { navigatorData ? 
          navigatorData.all_accounts.length > 0 ?
        <>
        <FilterContainer
          showComment={showComment}
          onShowFilter={onShowFilter}
          onShowComment={onShowComment}
        >
          <div className="filters custom-scroll">
            <RadioDD
              id="accounts"
              options={optionAccounts}
              radioVal={accounts_val}
              setRadioVal={set_accounts_val}
              onApply={onApplyAccounts}
              onCancel={onCancelAccounts}
              initialValue={accounts_initial_value}
            />
            
            <RadioDD
              id="include-only"
              options={optionProspect}
              radioVal={prospects_val}
              setRadioVal={set_prospects_val}
              onApply={onApplyProspects}
              onCancel={onCancelProspects}
              initialValue={prospects_initial_value}
            />
          </div>

          <FilterModal
            title="Navigator"
            setShowFilter={setShowFilter}
            showFilter={showFilter}
            onCloseFilter={onCloseFilter}
            onApplyFilter={onApplyFilter}
          >
            <RadioDD
              id="accounts-modal"
              options={optionAccounts}
              visibleInModal
              radioVal={accounts_val}
              setRadioVal={set_accounts_val}
            />
            <RadioDD
              id="include-only-modal"
              options={optionProspect}
              visibleInModal
              radioVal={prospects_val}
              setRadioVal={set_prospects_val}
            />
          </FilterModal>
        </FilterContainer>

        {/* ============= Your Component here! ============= */}

        <NavigatorContent
          all_accounts={all_accounts}
          newly_added_accounts={newly_added_accounts}
          world_wide_map={deploymentMapData}
        />
        </>
        : <ResourceNotFoundError redirectURL={"/account-navigator/navigator"} />
        : <InternalServerError redirectURL={"/account-navigator/navigator"}/> }
      </div>
      <Comment
        data={comments}
        onCloseComment={onCloseComment}
        showComment={showComment}
      />
    </>
  );
};

export default Navigator;
