import React from 'react';
import { Container, Row, Col, Card } from 'react-bootstrap';

import HorizontalBarChart from '../../components/chart/HorizontalBarChart';
import DonutChart from '../../components/chart/DonutChart';
import MapChart from '../../components/chart/MapChart';
import LineChart from '../../components/chart/LineChart';
import StackedColumnChart from '../../components/chart/StackedColumnChart';
import KPI_NEW from '../../components/kpi/KPI_NEW';
import Legend from '../../components/legend/Legend';
import PromoterComments from '../../assets/images/promoters_comments.png';
import DetractorComments from '../../assets/images/detractor_comments.png';
import CustomTable from '../../components/customTable/CustomTable';


const NPSContent = (props) => {
  const { npsData } = props;

  const nps = (cell, row) => {
    return (
      <span className="indicator indicator-up">
        {cell}
      </span>
    );
  }

  const defaultSorted = [
    {
      dataField: 'segment',
      order: 'desc',
    }
  ];

  const columns = [
    {
      dataField: '_id',
      text: 'id',
      hidden: true
    },
    {
      dataField: 'first_name',
      text: 'FIRST NAME',
      sort: true,
      
      
    },
    {
      dataField: 'last_name',
      text: 'LAST NAME',
      sort: true,
      
      headerClasses: 'left'
    },
    {
      dataField: 'recipient_email',
      text: 'RECIPIENT EMAIL',
      sort: true,
      
      headerClasses: 'left'
    },
    {
      dataField: 'date',
      text: 'DATE',
      sort: true,
      
      headerClasses: 'left'
    },
    {
      dataField: 'region',
      text: 'REGION',
      sort: true,
      
      headerClasses: 'left'
    },
    {
      dataField: 'country',
      text: 'Country',
      sort: true,
      
      headerClasses: 'left'
    },
    {
      dataField: 'gam_others',
      text: 'GAM/OTHERS',
      sort: true,
      
      headerClasses: 'left'
    },
    {
      dataField: 'comment',
      text: 'COMMENT',
      sort: true,
      
      
      classes: 'comment',
      headerClasses: 'comment'
    },
    {
      dataField: 'score',
      text: 'NPS',
      sort: true,
      classes: 'center',
      headerClasses: 'center',
      formatter: nps
    }
  ]

  return (
    <Container className="nps" fluid>
      <Row className="contentDivider pb-30">
        <div className="fix-width">
          <Card className="TotalRevenue revenue" >
            <Card.Body>
              <Card.Title>{npsData.overall.total.label}</Card.Title>
                <KPI_NEW
                    totalValue={npsData.overall.total.value}
                    totalUnit={npsData.overall.total.unit}
                    details={npsData.overall.details}
                />
            </Card.Body>
          </Card>
        </div>
        <Col>
          <Card className="Segment revenue">
            <Card.Body>
              <Card.Title> nps trends by region </Card.Title>
              <Legend legend={npsData.nps_trends_by_region.legend} />
              <div style={{ height: "350px" }}>
                <LineChart
                  id={"nps_trends_by_region_line_chart"}
                  data={npsData.nps_trends_by_region.data}
                  legend={npsData.nps_trends_by_region.legend}
                  isAnimationEnabled={true}
                />
              </div>
            </Card.Body>
          </Card>
        </Col>
        <Col style={{ maxWidth: "400px" }}>
          <Card className="Segment revenue">
            <Card.Body>
              <Card.Title>nps category mix</Card.Title>
              <Legend legend={npsData.nps_response_category.legend} />
              <div style={{ height: "350px" }}>
                <DonutChart
                  id={"nps_response_category_donut_chart"}
                  totalTitle={"TOTAL RESPONSES"}
                  total={npsData.nps_response_category.total_responses}
                  data={npsData.nps_response_category.data}
                  legend={npsData.nps_response_category.legend}
                  isAnimationEnabled={true}
                />
              </div>
            </Card.Body>
          </Card>
        </Col>
      </Row>
      <Row className="contentDivider" style={{ paddingBottom: " 30px" }}>
        <Col xl={4}>
          <Card className="Segment revenue">
            <Card.Body>
              <Card.Title> nps score distribution </Card.Title>
              <Legend legend={npsData.nps_score_distribution.legend} />
              <div style={{ height: "350px" }}>
                <StackedColumnChart
                  id={"nps_response_distribution_bar"}
                  data={npsData.nps_score_distribution.data}
                  legend={npsData.nps_score_distribution.legend}
                  isAnimationEnabled={true}
                />
              </div>
            </Card.Body>
          </Card>
        </Col>
        <Col xl={3}>
          <Card className="Segment revenue">
            <Card.Body>
              <Card.Title> nps response by country </Card.Title>
              <div style={{ height: "350px" }}>
                <HorizontalBarChart
                  id={"nps_response_by_country_bar"}
                  data={npsData.nps_response_by_country.data}
                  legend={npsData.nps_response_by_country.legend}
                  isAnimationEnabled={true}
                />
              </div>
            </Card.Body>
          </Card>
        </Col>
        <Col xl={5}>
          <Card className="Segment revenue">
            <Card.Body>
              <Card.Title> nps response locations </Card.Title>
              <Legend legend={npsData.nps_response_distribution.legend} />
              <div style={{ height: "350px" }}>
                <MapChart
                  id={"nps_response_distribution_map"}
                  data={npsData.nps_response_distribution.data}
                  legend={npsData.nps_response_distribution.legend}
                />
              </div>
            </Card.Body>
          </Card>
        </Col>
      </Row>
      <Row className="contentDivider" style={{ paddingBottom: '30px' }}>
        <Col>
          <Card>
            <Card.Body className="comments-container">
              <Card.Title>promoter comments</Card.Title>
              <img src={PromoterComments} alt="promoters comments" />
            </Card.Body>
          </Card>
        </Col>
        <Col>
          <Card>
            <Card.Body className="comments-container">
              <Card.Title>detractor comments</Card.Title>
              <img src={DetractorComments} alt="detractor comments" />
            </Card.Body>
          </Card>
        </Col>
      </Row>
      <Row className="contentDivider">
        <Col>
          <Card className="SegmentDetails revenue">
            <Card.Body>
              <Card.Title>segment details</Card.Title>
              <CustomTable
                tableName='response-details scrollable'
                data={npsData.response_details === undefined ? [] : npsData.response_details}
                defaultSorted={defaultSorted}
                columns={columns}
              />
            </Card.Body>
          </Card>
        </Col>
      </Row>
    </Container>
  );
};

export default NPSContent;