import React from 'react';
import { ImArrowUpRight2, ImArrowDownRight2 } from "react-icons/im";
import { Modal, Row, Col } from 'react-bootstrap';

import KPI_NEW from '../../components/kpi/KPI_NEW';
import Legend from '../../components/legend/Legend';
import SparklineCustom from '../../components/chart/SparklineCustom';

import CustomTable from '../../components/customTable/CustomTable';
import MapModalChart from '../../components/chart/MapModalChart';
import DonutChart from '../../components/chart/DonutChart';

import { convertToUSD, convertToPercent } from '../../constants/formatter';

import './pricing.scss';


const CustomerModal = (props) => {
	const { data, selectedCustomer, show, onClose } = props;
	console.log(data)
	let overall = data ? data.overall : [];
	let tableDetails = data ? data.customer_details : [];
	let revenueMix = data ? data.customer_revenue_mix : [];
	let deploymentMap = data ? data.deployment_map : [];

	const indicatorPercent = (cell, row) => {
		let bgColor = cell >= 0.5 ? 'indicator-up' : cell <= -0.5 ? 'indicator-down' : 'indicator-middle';
		let icon = cell >= 0.5 ? <ImArrowUpRight2 /> : cell <= -0.5 ? <ImArrowDownRight2 /> : <span>&nbsp;</span>;

		return (
			<>
				<span className={`indicator ${bgColor}`}>
					{icon}
					{convertToPercent(cell)}
				</span>
			</>
		);
	};

	const formatUSDGrowth = (cell, row) => {
		return (
			<span>
				{convertToUSD(cell)}
			</span>
		);
	};

	const sparkline = (cell, row) => {
		return (
			<SparklineCustom loadedData={cell} />

		);
	};

	const metroColumn = (cell, row) => {
		return (
			<>
				<img className="flag-icon" src={cell.image_url} alt={cell.name} />
				<span className="body-name">{cell.name}</span>
			</>

		)
	};


	const rankFormat = (cell, row, rowIndex, formatExtraData) => {
		return (
			<span>{rowIndex + 1}</span>
		);
	};



	const defaultSorted = [
		{
			dataField: 'metro',
			order: 'desc',
		}
	];


	const columns = [
		{
			dataField: '_id',
			text: 'id',
			hidden: true
		},
		{
			dataField: '',
			text: 'Rank',
			classes: 'rank center',
			headerClasses: 'rank center',
			formatter: rankFormat,
		},
		{
			dataField: 'metro',
			text: 'SEGMENT',
			classes: 'metro with-ellipsis',
			headerClasses: 'metro',
			formatter: metroColumn
		},
		{
			dataField: 'usd_per_kva_sparkline_data',
			text: '',
			classes: 'sparkline-cell center',
			headerClasses: 'sparkline-cell center',
			formatter: sparkline
		},
		{
			dataField: 'usd_per_kva',
			text: '$/KVA',
			sort: true,
			classes: 'right',
			headerClasses: 'right',
			formatter: formatUSDGrowth
		},
		{
			dataField: 'usd_per_kva_p4q_percent_growth',
			text: 'P4Q % Growth',
			sort: true,
			classes: 'with-ellipsis center border-right',
			headerClasses: 'center border-right',
			formatter: indicatorPercent
		},
		{
			dataField: 'mw_sparkline_data',
			text: '',
			classes: 'sparkline-cell center',
			headerClasses: 'sparkline-cell center',
			formatter: sparkline
		},
		{
			dataField: 'mw',
			text: 'MW',
			sort: true,
			classes: 'right',
			headerClasses: 'right',
		},
		{
			dataField: 'mw_p4q_percent_growth',
			text: 'P4Q % Growth',
			sort: true,
			classes: 'with-ellipsis center border-right',
			headerClasses: 'center border-right',
			formatter: indicatorPercent
		},
		{
			dataField: 'deploy_count_sparkline_data',
			text: '',
			classes: 'sparkline-cell center',
			headerClasses: 'sparkline-cell center',
			formatter: sparkline
		},
		{
			dataField: 'deploy_count',
			text: 'Deploy Count',
			sort: true,
			classes: 'right',
			headerClasses: 'right',
		},
		{
			dataField: 'deploy_count_p4q_percent_growth',
			text: 'P4Q % Growth',
			sort: true,
			classes: 'with-ellipsis right',
			headerClasses: 'right',
			formatter: indicatorPercent
		},
	];

	return (
		<Modal id="customer-modal" className="customer-modal pricing"
			{...props}
			// size="lg"
			show={show}
			onHide={onClose}
			aria-labelledby="contained-modal-title-vcenter"
			centered
		>
			<Modal.Header>
				<Modal.Title id="contained-modal-title-vcenter">
					{selectedCustomer} pricing
        </Modal.Title>
				<button type="button" className="close" onClick={onClose}>
					<span aria-hidden="true">×</span><span className="sr-only">
						Close
          </span>
				</button>
			</Modal.Header>
			<Modal.Body>
				{data ?
					<>
						<Row>
							<Col className="fix-width" style={{ paddingRight: "30px" }}>
								<KPI_NEW
									totalValue={overall.total.value}
									totalUnit={overall.total.unit}
									details={overall.details}
									isAddText = "/kVa"
									noHover={true}
								/>
							</Col>
							<Col md={6} xs={12}>
								<Legend legend={deploymentMap.legend} />
								<div style={{ height: "350px ", paddingTop: "2rem" }}>
									<MapModalChart
										id={"pricing_metro_modal_map_chart"}
										largerWidth={false}
										data={deploymentMap.data}
										bubbleSizeKey="mw_sold"
									/>
								</div>
							</Col>
							<Col style={{ paddingLeft: "30px" }}>
								<div style={{ height: "350px" }}>
									<DonutChart
										id={"pricing_metro_modal_donut_chart"}
										totalTitle={"MW SOLD"}
										total={revenueMix.mw_sold}
										data={revenueMix.data}
										legend={revenueMix.legend}
										isDollar={true}
										isAnimationEnabled={true}
									/>
								</div>
							</Col>
						</Row>
						<Row>
							<Col md={12} style={{ paddingTop: '1.5rem' }}>
								<CustomTable
									tableName='vertical-details'
									data={tableDetails === undefined ? [] : tableDetails}
									defaultSorted={defaultSorted}
									columns={columns}
								/>
							</Col>
						</Row>
					</>
					: <div>No data available.</div>
				}
			</Modal.Body>
		</Modal>
	);
}

export default CustomerModal;