const Metrics = ({ data }) => {
  const [modalShow, setModalShow] = useState(false);
  const [modalData, setModalData] = useState(null);
  const [stepList, setStepList] = useState(buttonList);

  const [selectedItem, setSelectedItem] = useState([]);
  const [isNextPage, setNextPage] = useState(false);
  const onCancelModal = () => setModalShow(false);

  console.log('isNextPage', isNextPage)
  // useEffect(() => {
  //   setStepList()
  // }, [])
  return (
    <>
      {isNextPage ?
        // <Metro  setStepList={setStepList} stepList={stepList} />
        <Metro selectedItem={selectedItem} setSelectedItem={setSelectedItem} setStepList={setStepList} stepList={stepList} />
        :
        <div className={`page-content`}>
          <Container className="container-metrics-page" fluid >
            <Row>
              <Col xl={7}>
                <div className="inner-wrap">
                  <div className="block content-block">
                    <motion.div
                      variants={animateContent}
                      initial='hidden'
                      animate='visible'
                    >
                      <h2 className="title">
                        Essential analytics for agile leadership</h2>
                      <p className="text">
                        Navigate complex decision-making with a single view of all essential and up-to-date metrics,
                  leading to more timely and relevant insights.</p>
                      <Button className="btn-metrics"
                        onClick={() => {
                          setModalShow(true);
                        }}>Add Metrics</Button>
                    </motion.div>
                  </div>
                </div>
              </Col>
              <Col xl={5}>
                <div className="inner-wrap">
                  <div className="block image-block">
                    <motion.img
                      variants={animateMonitor}
                      initial='hidden'
                      animate='visible'
                      className="img-fluid"
                      src={laptop}
                      alt="sample flag" />
                    <motion.div
                      variants={animatePerson}
                      initial='hidden'
                      animate='visible'
                      className="person person-1" >
                      <img src={Pilot1} />
                    </motion.div>
                    <motion.div variants={animatePerson}
                      initial='hidden'
                      animate='visible'
                      className="person person-2">
                      <img src={Pilot2} />
                    </motion.div>
                  </div>
                </div>
              </Col>
            </Row>
          </Container>
          <AddMetricsModal
            onCancelModal={onCancelModal}
            showBuilderModal={modalShow}
            data={modalData}
            setNextPage={setNextPage}
            selectedItem={selectedItem}
            setSelectedItem={setSelectedItem}
            setModalShow={setModalShow}
            setStepList={setStepList}
            stepList={stepList}
          />
        </div>


      }

    </>
  )
};

export default Metrics;


