import React, { useLayoutEffect } from 'react';
import * as am4core from "@amcharts/amcharts4/core";
import * as am4charts from "@amcharts/amcharts4/charts";
import am4themes_animated from "@amcharts/amcharts4/themes/animated";

import { roundUpToTenth } from '../../constants/formatter';

am4core.useTheme(am4themes_animated);


function OneLineBuubleChart(props) {
  const { data, id, legend, isAnimationEnabled, isHideYValueAxis, isHideAllLabels, xAxisLabel, yAxisLabel, axisValueLabel, xAxisValue, yAxisValue, axisValue } = props;

  useLayoutEffect(() => {
    let chart = am4core.create(id, am4charts.XYChart);
    chart.width = am4core.percent(100);
    chart.height = am4core.percent(100);

    chart.paddingRight = 0;
    chart.paddingLeft = 0;
    chart.paddingTop = 30;
    chart.paddingBottom = 0;

    chart.data = data;

    let xAxis = chart.xAxes.push(new am4charts.ValueAxis());
    xAxis.id = "myCategoryAxis";
    xAxis.renderer.minGridDistance = 50;
    xAxis.renderer.grid.template.location = 0;
    xAxis.renderer.grid.template.strokeWidth = 0.5;

    let yAxis = chart.yAxes.push(new am4charts.ValueAxis());
    yAxis.id = "myValueAxis";

    if (isHideYValueAxis === true) {
      yAxis.renderer.labels.template.disabled = true;
    } else {
      yAxis.renderer.labels.template.disabled = false;
    }

    if (isHideAllLabels === false) {
      yAxis.title.text = yAxisLabel;
      yAxis.title.rotation = 0;
      yAxis.title.align = "center";
      yAxis.title.valign = "top";
      yAxis.renderer.minGridDistance = 20;
      xAxis.title.text = xAxisLabel;
      xAxis.title.rotation = 0;
      xAxis.title.align = "center";
      xAxis.title.valign = "top";
      xAxis.renderer.minGridDistance = 50;
    }

    let series = chart.series.push(new am4charts.LineSeries());
    series.strokeOpacity = 0;
    series.sequencedInterpolation = true;
    series.tooltip.pointerOrientation = "vertical";
    series.hiddenState.transitionDuration = 0;
    series.defaultState.transitionDuration = isAnimationEnabled ? 1000 : 0;

    let bullet = series.bullets.push(new am4charts.CircleBullet())

    series.dataFields.valueX = xAxisValue;
    series.dataFields.valueY = yAxisValue;
    series.dataFields.value = axisValue;

    yAxis.renderer.labels.template.adapter.add("text", (text, label) => { return `${roundUpToTenth(label.dataItem.value)}%`; });

    function createSeries(opacity) {
      bullet.autoTextColor = false;
      bullet.propertyFields.fill = "color";
      bullet.strokeOpacity = 0;
      bullet.strokeWidth = 2;
      bullet.hiddenState.properties.opacity = 0;
      bullet.circle.propertyFields.fill = "color";
      bullet.circle.propertyFields.stroke = "color";
      bullet.circle.strokeWidth = 2;
      bullet.tooltipText = "[#fff]" + axisValueLabel + ":[/] [#fff font-weight:bold]{" + axisValue + "}[/]\n[#fff]" + xAxisLabel + ":[/] [font-weight:bold #fff]{" + xAxisValue + "}[/]";
      series.heatRules.push({
        target: bullet.circle,
        min: 15,
        max: 60,
        property: "radius"
      });
    }

    let seriesArr = [];
    data.map(item => seriesArr.push(createSeries(item.opacity)))

    chart.responsive.enabled = true;

    return () => {
      chart.dispose();
    };
  }, [data, id, legend, isAnimationEnabled, xAxisLabel, yAxisLabel, xAxisValue, yAxisValue, axisValue, isHideYValueAxis, isHideAllLabels, axisValueLabel]);

  return (
    <div id={id} className="mini-bubble-chart" ></div>
  );
}

export default OneLineBuubleChart;

