import React, { useState, useEffect } from 'react';
import axiosRetry from 'axios-retry';
import axios from 'axios';
import GrowthContent from './GrowthContent';
import {RadioDD} from '../../components/customDropdown';
import {FilterContainer, FilterModal} from '../../components/filter';
import Comment from '../../components/comment/Comment';
import Loader from '../../components/loader/Loader';
import { InternalServerError, ResourceNotFoundError } from '../../components/error/Error';
import { optionMeasure, optionRange, optionGroup1 } from '../../constants/filterOptions';
import { API_BASE_URL } from '../../constants/config';
import { comments } from '../../constants/comments';


const Growth = () => {
  const [isLoading, setIsLoading] = useState(true);
  const [growthData, setData] = useState(null);
  const [resultData, setResultData] = useState(null);
  const [selectedData, setSelectedData] = useState(null);
  const [selectedBubbleData, setSelectedBubbleData] = useState(null);
  const [idState, setReferenceID] = useState(null);
  const [bubbleName, setBubbleName] = useState(null);

  if (idState) {
    selectedBubbleData.map(item => {
      if (item._id !== idState) {
        item.color = 'rgba(234, 240, 244, 0.2)';
      }

      return item;
    });
  }

  // show all filters
  const [showFilter, setShowFilter] = useState(false);
  const onShowFilter = () => {
    setShowFilter(true)
  }
  const onCloseFilter = async () => {
    await onCancelMeasure();
    await onCancelRange();
    await onCancelGroup1();
    setShowFilter(false);
  }
  const onApplyFilter = async () => {
    await onApplyMeasure();
    await onApplyRange();
    await onApplyGroup1();
    setShowFilter(false);
  }

  // comment area
  const [showComment, setShowComment] = useState(false);
  const onCloseComment = () => {
    setShowComment(false);
  };
  const onShowComment = () => {
    setShowComment(true);
  };

  // radio for measure values
  const measure_initial_value = optionMeasure ? optionMeasure[0] : null;
  const [measure_val_onApply, set_measure_val_onApply] = useState(measure_initial_value);
  const [measure_val, set_measure_val] = useState(measure_initial_value);
  const onApplyMeasure = () => {
    set_measure_val_onApply(measure_val);
  };
  const onCancelMeasure = () => {
    set_measure_val(measure_val_onApply);
  };

  // radio for range values
  const range_initial_value = optionRange ? optionRange[0] : null;
  const [range_val_onApply, set_range_val_onApply] = useState(range_initial_value);
  const [range_val, set_range_val] = useState(range_initial_value);
  const onApplyRange = () => {
    set_range_val_onApply(range_val);
  };
  const onCancelRange = () => {
    set_range_val(range_val_onApply);
  };

  // radio for group1 values
  const group1_initial_value = optionGroup1 ? optionGroup1[0] : null;
  const [group1_val_onApply, set_group1_val_onApply] = useState(group1_initial_value);
  const [group1_val, set_group1_val] = useState(group1_initial_value);
  const onApplyGroup1 = () => {
    set_group1_val_onApply(group1_val);
  };
  const onCancelGroup1 = () => {
    set_group1_val(group1_val_onApply);
  };

 // Axios call
 const fetchData = () => {
    let url = `${API_BASE_URL}/gross-bookings-revenue/growth.json`;
    // Uncomment only when API is now available and working
    // const token = localStorage.getItem('token');
    // let url = `${API_BASE}/orbis/api/totalrevenuegrowth`;
    // let defaultQueryParameters = `?measure=${measure_val_onApply.key}&range=${range_val_onApply.key}&showby=${group1_val_onApply.key}`;

    setIsLoading(true);

    axiosRetry(axios, { retries: 3 });

    axios({
      method: 'GET',
      url: url
      // Uncomment only when API is now available and working
      // url: url + defaultQueryParameters,
      // headers: {
      //   'Content-Type': 'application/json',
      //   "Authorization": `bearer ${token}`
      // }
    }).then(result => {
      const data = result.data;
      const resultSelected = data.segment_ranking;
      // Uncomment only if for API testing purposes
      // console.log(result.config.url);
      setData(data);
      setResultData(resultSelected);
      setIsLoading(false);
    }).catch(error => {
      console.log(error);
      setData(null);
      setIsLoading(false);
    });;

  };

  useEffect(() => {
    fetchData();
  }, 
    []
  // Uncomment only when API is now available and working
  //   [   
  //     measure_val_onApply,
  //     range_val_onApply,
  //     group1_val_onApply
  //  ]
  );

  if (isLoading) return <Loader withContainer />;

  return (
    <>
      <div className={`page-content ${showComment ? 'open' : 'closed'}`}>
        { growthData ?  
          growthData ?
        <>
          <FilterContainer
              showComment={showComment}
              onShowFilter={onShowFilter}
              onShowComment={onShowComment}
            >
              <div className="filters custom-scroll">
                <RadioDD
                  title="Measure"
                  id="measure"
                  options={optionMeasure}
                  radioVal={measure_val}
                  setRadioVal={set_measure_val}
                  onApply={onApplyMeasure}
                  onCancel={onCancelMeasure}
                />
                
                <RadioDD
                  id="range"
                  options={optionRange}
                  radioVal={range_val}
                  setRadioVal={set_range_val}
                  onApply={onApplyRange}
                  onCancel={onCancelRange}
                  initialValue={range_initial_value}
                />
                
                <RadioDD
                  title="Show"
                  id="show"
                  options={optionGroup1}
                  radioVal={group1_val}
                  setRadioVal={set_group1_val}
                  onApply={onApplyGroup1}
                  onCancel={onCancelGroup1}
                  initialValue={group1_initial_value}
                />
              </div>
              <FilterModal
                title="Growth"
                setShowFilter={setShowFilter}
                showFilter={showFilter}
                onCloseFilter={onCloseFilter}
                onApplyFilter={onApplyFilter}
              >
                <RadioDD
                  title="Measure"
                  id="measure-modal"
                  options={optionMeasure}
                  visibleInModal
                  radioVal={measure_val}
                  setRadioVal={set_measure_val}
                />
                <RadioDD
                  title="Time Period"
                  id="range-modal"
                  options={optionRange}
                  visibleInModal
                  radioVal={range_val}
                  setRadioVal={set_range_val}
                />
                <RadioDD
                  title="Show by"
                  id="show-modal"
                  options={optionGroup1}
                  visibleInModal
                  radioVal={group1_val}
                  setRadioVal={set_group1_val}
                />
              </FilterModal>
            </FilterContainer>
          <GrowthContent
            bubbleName={bubbleName}
            selectedData={selectedData}
            idState={idState}
            resultData={resultData}
            selectedBubbleData={selectedBubbleData}
            setSelectedData={setSelectedData}
            setSelectedBubbleData={setSelectedBubbleData}
            setBubbleName={setBubbleName}
            setReferenceID={setReferenceID}
            growthData={growthData}
          />
        </>
        : <ResourceNotFoundError redirectURL={"/executive-cockpit/gross-bookings-revenue/growth"} />
        : <InternalServerError redirectURL={"/executive-cockpit/gross-bookings-revenue/growth"}/> }
      </div>
      <Comment
        data={comments}
        onCloseComment={onCloseComment}
        showComment={showComment}
      />
    </>
  );
};

export default Growth;