import React, { useState, useEffect } from 'react';
import axiosRetry from 'axios-retry';
import axios from 'axios';
import { FilterContainer, FilterModal } from '../../components/filter';
import { optionRegion, optionCompanySize2, optionTier, optionVertical, optionGroup7, optionAveraging } from '../../constants/filterOptions';
import { RadioDD, CheckboxDD } from '../../components/customDropdown';
import { comments } from '../../constants/comments';
import { InternalServerError, ResourceNotFoundError } from '../../components/error/Error';
import { API_BASE_URL } from '../../constants/config';
import Comment from '../../components/comment/Comment';
import Loader from '../../components/loader/Loader';
import OverviewContent from './OverviewContent';


const Overview = () => {
	const [isLoading, setIsLoading] = useState(true);
	const [overviewData, setData] = useState(null);

	// show all filters
	const [showFilter, setShowFilter] = useState(false);
	const onShowFilter = () => {
		setShowFilter(true);
	};
	const onCloseFilter = async () => {
		await onCancelRegion();
		await onCancelSize();
		await onCancelTier();
		await onCancelVertical();
		await onCancelGroup();
		await onCancelAverage();
		setShowFilter(false);
	};
	const onApplyFilter = async () => {
		await onApplyRegion();
		await onApplySize();
		await onApplyTier();
		await onApplyVertical();
		await onApplyGroup();
		await onApplyAverage();
		setShowFilter(false);
	};

	// comment area
	const [showComment, setShowComment] = useState(false);
	const onCloseComment = () => {
		setShowComment(false);
	};
	const onShowComment = () => {
		setShowComment(true);
	};

	// checkbox for region values
	const [region_val, set_region_val] = useState([]);
	const [region_val_onApply, set_region_val_onApply] = useState([]);
	const onApplyRegion = () => {
		set_region_val_onApply(region_val);
	};
	const onCancelRegion = () => {
		set_region_val(region_val_onApply);
	};


	// checkbox for size values
	const [size_val_onApply, set_size_val_onApply] = useState([]);
	const [size_val, set_size_val] = useState([]);
	const onApplySize = () => {
		set_size_val_onApply(size_val);
	};
	const onCancelSize = () => {
		set_size_val(size_val_onApply);
	};

	// checkbox for tier values
	const [tier_val_onApply, set_tier_val_onApply] = useState([]);
	const [tier_val, set_tier_val] = useState([]);
	const onApplyTier = () => {
		set_tier_val_onApply(tier_val);
	};
	const onCancelTier = () => {
		set_tier_val(tier_val_onApply);
	};

	// radio for vertical values
	const vertical_initial_value = optionVertical ? optionVertical[0] : null;
	const [vertical_val_onApply, set_vertical_val_onApply] = useState(vertical_initial_value);
	const [vertical_val, set_vertical_val] = useState(vertical_initial_value);
	const onApplyVertical = () => {
		set_vertical_val_onApply(vertical_val);
	};
	const onCancelVertical = () => {
		set_vertical_val(vertical_val_onApply);
	};

	// radio for group values
	const group_initial_value = optionGroup7 ? optionGroup7[0] : null;
	const [group_val_onApply, set_group_val_onApply] = useState(group_initial_value);
	const [group_val, set_group_val] = useState(group_initial_value);
	const onApplyGroup = () => {
		set_group_val_onApply(group_val);
	};
	const onCancelGroup = () => {
		set_group_val(group_val_onApply);
	};

	// radio for average values
	const average_initial_value = optionAveraging ? optionAveraging[0] : null;
	const [average_val_onApply, set_average_val_onApply] = useState(average_initial_value);
	const [average_val, set_average_val] = useState(average_initial_value);
	const onApplyAverage = () => {
		set_average_val_onApply(average_val);
	};
	const onCancelAverage = () => {
		set_average_val(average_val_onApply);
	};

	// Axios call
	const fetchData = async () => {
		let url = `${API_BASE_URL}/pricing/overview.json`;

		setIsLoading(true);
		axiosRetry(axios, { retries: 3 });

		await axios({
			method: 'GET',
			url: url
		}).then(result => {
			const data = result.data;
			setData(data);
			setIsLoading(false);
		}).catch(error => {
			console.log(error);
			setData([]);
			setIsLoading(false);
		});
	};

	useEffect(() => {
		fetchData();
	}, []);

	if (isLoading) return <Loader withContainer />

	return (
		<>
			<div className={`page-content ${showComment ? 'open' : 'closed'}`}>
				{overviewData ?
					overviewData ?
						<>
							<FilterContainer
								showComment={showComment}
								onShowFilter={onShowFilter}
								onShowComment={onShowComment}
							>
								<div className="filters custom-scroll">
									<CheckboxDD
										title="Deploy Region"
										id="region"
										valueApplied={region_val_onApply}
										options={optionRegion}
										checkVal={region_val}
										setCheckVal={set_region_val}
										onApply={onApplyRegion}
										onCancel={onCancelRegion}
									/>
									<CheckboxDD
										title="Size"
										id="size"
										options={optionCompanySize2}
										checkVal={size_val}
										setCheckVal={set_size_val}
										onApply={onApplySize}
										onCancel={onCancelSize}
									/>
									<CheckboxDD
										title="Tier"
										id="tier"
										options={optionTier}
										checkVal={tier_val}
										setCheckVal={set_tier_val}
										onApply={onApplyTier}
										onCancel={onCancelTier}
									/>
									<RadioDD
										title="Vertical"
										id="vertical"
										options={optionVertical}
										radioVal={vertical_val}
										setRadioVal={set_vertical_val}
										onApply={onApplyVertical}
										onCancel={onCancelVertical}
										initialValue={vertical_initial_value}
									/>
									<RadioDD
										title="Group"
										id="group"
										options={optionGroup7}
										radioVal={group_val}
										setRadioVal={set_group_val}
										onApply={onApplyGroup}
										onCancel={onCancelGroup}
										initialValue={group_initial_value}
									/>
									<RadioDD
										title="Averaging"
										id="averaging"
										options={optionAveraging}
										radioVal={average_val}
										setRadioVal={set_average_val}
										onApply={onApplyAverage}
										onCancel={onCancelAverage}
										initialValue={average_initial_value}
									/>
								</div>

								<FilterModal
									title="Overview"
									setShowFilter={setShowFilter}
									showFilter={showFilter}
									onCloseFilter={onCloseFilter}
									onApplyFilter={onApplyFilter}
								>
									<CheckboxDD
										title="Deploy Region"
										id="region-modal"
										options={optionRegion}
										visibleInModal
										checkVal={region_val}
										setCheckVal={set_region_val}
									/>
									<CheckboxDD
										title="Size"
										id="size"
										visibleInModal
										options={optionCompanySize2}
										checkVal={size_val}
										setCheckVal={set_size_val}
										onApply={onApplySize}
										onCancel={onCancelSize}
									/>
									<CheckboxDD
										title="Tier"
										id="tier"
										visibleInModal
										options={optionTier}
										checkVal={tier_val}
										setCheckVal={set_tier_val}
										onApply={onApplyTier}
										onCancel={onCancelTier}
									/>
									<RadioDD
										title="Vertical"
										id="vertical-modal"
										options={optionVertical}
										visibleInModal
										radioVal={vertical_val}
										setRadioVal={set_vertical_val}
									/>
									<RadioDD
										title="Group"
										id="group-modal"
										options={optionGroup7}
										visibleInModal
										radioVal={group_val}
										setRadioVal={set_group_val}
									/>
									<RadioDD
										title="Averaging"
										id="averaging-modal"
										options={optionAveraging}
										visibleInModal
										radioVal={average_val}
										setRadioVal={set_average_val}
									/>
								</FilterModal>
							</FilterContainer>
							{/* ============= Your Component here! ============= */}
							<OverviewContent overviewData={overviewData} />
						</>
						: <ResourceNotFoundError redirectURL={"/executive-cockpit/pricing/overview"} />
					: <InternalServerError redirectURL={"/executive-cockpit/pricing/overview"} />}
			</div>
			<Comment
				data={comments}
				onCloseComment={onCloseComment}
				showComment={showComment}
			/>
		</>
	);
};

export default Overview;