import React, { useState, useEffect } from 'react';
import { Button, ButtonGroup } from 'react-bootstrap';
import axiosRetry from 'axios-retry';
import axios from 'axios';
import {Separator, RadioDD, CheckboxDD, SearchCheckboxDD} from '../../components/customDropdown';
import {FilterContainer, FilterModal} from '../../components/filter';
import Loader from '../../components/loader/Loader';
import Comment from '../../components/comment/Comment';
import { InternalServerError, ResourceNotFoundError } from '../../components/error/Error';
import { optionMeasure, optionRange, optionRegion, optionMetro } from '../../constants/filterOptions';
import { API_BASE_URL } from '../../constants/config';
import { comments } from '../../constants/comments';
import RevenueContent from './RevenueContent';
import BookingsContent from './BookingsContent';
import OrdersContent from './OrdersContent';
import OpportunitiesContent from './OpportunitiesContent';


const Financials = () => {
  const [revenueData, setRevenueData] = useState([]);
  const [bookingsData, setBookingsData] = useState([]);
  const [ordersData, setOrdersData] = useState([]);
  const [opportunitiesData, setOpportunitiesData] = useState([]);
  const [isLoading, setIsLoading] = useState(true)
  const [activeTab, setActiveTab] = useState('revenue');

  // show all filters
  const [showFilter, setShowFilter] = useState(false);
  const onShowFilter = () => {
    setShowFilter(true)
  }
  const onCloseFilter = async () => {
    await onCancelRegion();
    await onCancelMetro();
    await onCancelMeasure();
    await onCancelRange();
    setShowFilter(false);
  }
  const onApplyFilter = async () => {
    await onApplyRegion();
    await onApplyMetro();
    await onApplyMeasure();
    await onApplyRange();
    setShowFilter(false);
  }

  // comment area
  const [showComment, setShowComment] = useState(false);
  const onCloseComment = () => {
    setShowComment(false);
  }
  const onShowComment = () => {
    setShowComment(true)
  };

  // checkbox for region values
  const [region_val_onApply, set_region_val_onApply] = useState([]);
  const [region_val, set_region_val] = useState([]);
  const onApplyRegion = () => {
    set_region_val_onApply(region_val);
  };
  const onCancelRegion = () => {
    set_region_val(region_val_onApply);
  };

  // searchCheckbox for metro values
  const [metro_val_onApply, set_metro_val_onApply] = useState([]);
  const [metro_val, set_metro_val] = useState([]);
  const onApplyMetro = () => {
    set_metro_val_onApply(metro_val);
  };
  const onCancelMetro = () => {
    set_metro_val(metro_val_onApply);
  };

  // radio for measure values
  const measure_initial_value = optionMeasure ? optionMeasure[0] : null;
  const [measure_val_onApply, set_measure_val_onApply] = useState(measure_initial_value);
  const [measure_val, set_measure_val] = useState(measure_initial_value);
  const onApplyMeasure = () => {
    set_measure_val_onApply(measure_val);
  };
  const onCancelMeasure = () => {
    set_measure_val(measure_val_onApply);
  };

  // radio for range values
  const range_initial_value = optionRange ? optionRange[0] : null;
  const [range_val_onApply, set_range_val_onApply] = useState(range_initial_value);
  const [range_val, set_range_val] = useState(range_initial_value);
  const onApplyRange = () => {
    set_range_val_onApply(range_val);
  };
  const onCancelRange = () => {
    set_range_val(range_val_onApply);
  };

  // Axios call
  const fetchData = async () => {
    let url = `${API_BASE_URL}/account-navigator/financials.json?print=pretty`;
    // Uncomment only when API is now available and working
    // const token = localStorage.getItem('token');
    // TODO: Add url and defaultQueryParameters to call Filter API calls

    setIsLoading(true);

    axiosRetry(axios, { retries: 3 });

    await axios({
      method: 'GET',
      url: url
      // Uncomment only when API is now available and working
      // url: url + defaultQueryParameters,
      // headers: {
      //   'Content-Type': 'application/json',
      //   "Authorization": `bearer ${token}`
      // }
    }).then(result => {
      let data = result.data;
      // Uncomment only if for API testing purposes
      // console.log(result.config.url);
      setRevenueData(data.revenue);
      setBookingsData(data.bookings);
      setOrdersData(data.orders);
      setOpportunitiesData(data.opportunities);
      setIsLoading(false);
    }).catch(error => {
      console.log(error);
      setRevenueData(null);
      setBookingsData(null);
      setOrdersData(null);
      setOpportunitiesData(null);
      setIsLoading(false);
    });
  };

  useEffect(() => {
    fetchData();
  }, 
    []
    // Uncomment only when API is now available and working
    // [
    //   region_val_onApply,
    //   metro_val_onApply,
    //   measure_val_onApply,
    //   range_val_onApply
    // ]
  );

  if (isLoading) return <Loader withContainer />;

  return (
    <>
      <div className={`page-content ${showComment ? 'open' : 'closed'}`}>
        {
          (revenueData  && bookingsData && ordersData && opportunitiesData) ? 
          (revenueData.length > 0 && bookingsData.length > 0 && ordersData.length > 0 && opportunitiesData.length > 0) ?
          <>
          <FilterContainer
            showComment={showComment}
            onShowFilter={onShowFilter}
            onShowComment={onShowComment}
          >
            <div className="filters custom-scroll">
              <ButtonGroup className="sub-tab">
                <Button 
                  variant='light' 
                  className={activeTab === 'revenue' ? 'active' : ''}
                  onClick={() => {setActiveTab('revenue')}}>
                  Revenue
                </Button>
                <Button 
                  variant='light' 
                  className={activeTab === 'bookings' ? 'active' : ''}
                  onClick={() => {setActiveTab('bookings')}}>
                  Bookings
                </Button>
                <Button 
                  variant='light' 
                  className={activeTab === 'orders' ? 'active' : ''}
                  onClick={() => {setActiveTab('orders')}}>
                  Orders
                </Button>
                <Button 
                  variant='light' 
                  className={activeTab === 'opportunities' ? 'active' : ''}
                  onClick={() => {setActiveTab('opportunities')}}>
                  Opportunities
                </Button>
              </ButtonGroup>
              <Separator height='39px' />
              <CheckboxDD
                title="Deploy Region"
                id="region"
                options={optionRegion}
                valueApplied={region_val_onApply}
                checkVal={region_val}
                setCheckVal={set_region_val}
                onApply={onApplyRegion}
                onCancel={onCancelRegion}
              />
              <SearchCheckboxDD
                title="Deploy Metro"
                id="metro"
                options={optionMetro}
                valueApplied={metro_val_onApply}
                checkVal={metro_val}
                setCheckVal={set_metro_val}
                onApply={onApplyMetro}
                onCancel={onCancelMetro}
              />
              <RadioDD
                title="Measure"
                id="measure"
                options={optionMeasure}
                radioVal={measure_val}
                setRadioVal={set_measure_val}
                onApply={onApplyMeasure}
                onCancel={onCancelMeasure}
              />
              <RadioDD
                title="Time Period"
                id="range2"
                options={optionRange}
                radioVal={range_val}
                setRadioVal={set_range_val}
                onApply={onApplyRange}
                onCancel={onCancelRange}
                initialValue={range_initial_value}
              />
            </div>
            <FilterModal
              title="Financials"
              setShowFilter={setShowFilter}
              showFilter={showFilter}
              onCloseFilter={onCloseFilter}
              onApplyFilter={onApplyFilter}
            >
              <CheckboxDD
                title="Deploy Region"
                id="region-modal"
                visibleInModal
                options={optionRegion}
                checkVal={region_val}
                setCheckVal={set_region_val}
              />
              <SearchCheckboxDD
                title="Deploy Metro"
                id="metro-modal"
                visibleInModal
                options={optionMetro}
                checkVal={metro_val}
                setCheckVal={set_metro_val}
              />
              <RadioDD
                title="Measure"
                id="measure-modal"
                visibleInModal
                options={optionMeasure}
                radioVal={measure_val}
                setRadioVal={set_measure_val}
              />
              <RadioDD
                title="Time Period"
                id="range-modal"
                visibleInModal
                options={optionRange}
                radioVal={range_val}
                setRadioVal={set_range_val}
                initialValue={range_initial_value}
              />
            </FilterModal>
          </FilterContainer>
          {/* ============= Your Component here! ============= */}
          {activeTab === 'revenue' && <RevenueContent revenueData={revenueData ? revenueData[0] : []} />}
          {activeTab === 'bookings' && <BookingsContent bookingsData={bookingsData ? bookingsData[0] : []} />}
          {activeTab === 'orders' && <OrdersContent ordersData={ordersData ? ordersData[0]: []} />}
          {activeTab === 'opportunities' && <OpportunitiesContent opportunitiesData={opportunitiesData ? opportunitiesData[0] : []} />}
        </>
          : <ResourceNotFoundError redirectURL={`/account-navigator/navigator/financials${window.location.search}`} />
          : <InternalServerError redirectURL={`/account-navigator/navigator/financials${window.location.search}`}/> 
        }
      </div>

      <Comment
        data={comments}
        onCloseComment={onCloseComment}
        showComment={showComment}
      />
    </>
  );
};

export default Financials;
