import React from 'react';
import { Form } from 'react-bootstrap';
import './checkboxList.scss';


const CheckboxList = ({
  options,
  className,
  id,
  title,
  setCheckVal,
  checkVal,
}) => {

  const handleSelect = (e) => {
    const checkValue = checkVal.some(item => item === e);
    const remove = checkVal.filter(item => item !== e);
    if (!checkValue) {
      setCheckVal([...checkVal, e])
    } else {
      setCheckVal(remove)
    }
  }

  return (
    <>
      <div className={`checkbox-list ${className ? className : ''}`}>
        <div className="title">{title ? title : checkVal}</div>
        <Form className="custom-scroll">
          {options && options.map(option => (
            <Form.Check
              key={option.key}
              custom
              type='checkbox'
              id={`checkbox_list-${id}-${option.key}`}
              label={option.text}
              checked={checkVal.some(item => item === option.key)}
              onChange={() => handleSelect(option.key)}
            />
          ))}
        </Form>
      </div>
    </>
  );
}

export default CheckboxList;
