import React, { useState, useEffect } from 'react';

import PageHeader from '../../components/pageHeader/PageHeader';

import Overview from './Overview';
import Growth from './Growth';
import Metro from './Metro';
import Customer from './Customer';
import AdditionalResources from './AdditionalResources';

import './crossBorderTrade.scss';


const CrossBorderTrade = (props) => {
  let path = props.location.pathname;
  const currentPath = path.substring(path.lastIndexOf('/') + 1);
  const [activePage, setActivePage] = useState(currentPath);

  const handleActivePage = (page) => {
    setActivePage(page)
  }

  const handleBreadcrubsPreviousLink = () => {
    setActivePage('Overview')
  }

  useEffect(() => {
    setActivePage(currentPath)
  }, [currentPath])

  const breadcrumbs = [
    {
      path: '/analytics-dashboard/page',
      label: 'Analytics Dashboard '
    },
    {
      path: path === '/analytics-dashboard/cross-border-trade/overview' ? null : '/analytics-dashboard/cross-border-trade/overview',
      label: 'Cross Border Bookings'
    },
    {
      label: activePage,
    }
  ]

  const innerPage = [
    {
      path: '/analytics-dashboard/cross-border-trade/overview',
      label: 'Overview'
    },
    {
      path: '/analytics-dashboard/cross-border-trade/growth',
      label: 'Growth'
    },
    {
      path: '/analytics-dashboard/cross-border-trade/metro',
      label: 'Metro'
    },
    {
      path: '/analytics-dashboard/cross-border-trade/customer',
      label: 'Customer'
    },
    {
      path: '/analytics-dashboard/cross-border-trade/additional-resources',
      label: 'Resources'
    }
  ]

  return (
    <div className="cross-border-bookings-page">
      <PageHeader
        title="CROSS BORDER TRADE"
        color="dark-orange"
        innerPage={innerPage}
        breadcrumbs={breadcrumbs}
        previousPage="/analytics-dashboard/page"
        handleActivePage={handleActivePage}
        handleBreadcrubsPreviousLink={handleBreadcrubsPreviousLink}
      />
      <div className="page-wrapper">
        {currentPath === 'overview' && <Overview />}
        {currentPath === 'growth' && <Growth />}
        {currentPath === 'metro' && <Metro />}
        {currentPath === 'customer' && <Customer />}
        {currentPath === 'additional-resources' && <AdditionalResources  />}
      </div>
    </div>
  );
}

export default CrossBorderTrade;
