
import React, { useState, useEffect } from 'react';
import axiosRetry from 'axios-retry';
import axios from 'axios';
import { Container, Row, Col, Card, Table } from 'react-bootstrap';
import { ImArrowUpRight2, ImArrowDownRight2 } from "react-icons/im";

import MetroModal from './MetroModal';

import CustomTable from '../../components/customTable/CustomTable';
import StackedColumnChart from '../../components/chart/StackedColumnChart';
import StackedColumnBar from '../../components/chart/StackedColumnBar';
import MapChart from '../../components/chart/MapChart';
import Legend from '../../components/legend/Legend';
import Loader from '../../components/loader/Loader';

import { API_BASE } from '../../constants/config';
import { convertToUSDGrowth, convertToPercent } from '../../constants/formatter';


const MetroContent = (props) => {
  const { metroData, modalShow, modalData, setModalShow, setModalData, setReferenceID, measure_val_onApply, range_val_onApply, hyperscaler_val_onApply, region_val_onApply } = props;
  
  const [map_region_val, set_map_region_val] = useState(null);
  const [isRankingTableFiltered, setIsRankingTableFiltered] = useState(false);
  const [selectedMetro, setSelectedMetro] = useState(null);

  const byRank = metroData ? metroData.metro_ranking.by_rank : [];
  const byTopRank = metroData ? metroData.metro_ranking.by_top_rank : [];
  const byRankId = metroData ? metroData.metro_ranking.by_rank_id : [];
  const deploymentMap = metroData ? metroData.deployment_map : [];
  const metro_number_by_customer_revenue_size = metroData ? metroData.metro_number_by_mrr_size : [];
  const metro_number_by_y_by_growth = metroData ? metroData.metro_number_by_y_to_y_growth : [];
  const metro_number_by_y_to_y_usd_growth = metroData ? metroData.metro_number_by_y_to_y_usd_growth : [];
  const top_ten_metros_by_total_revenue = metroData ? metroData.top_ten_metros_by_mrr : [];

  const indicatorPercent = (cell, row) => {
    let bgColor = cell >= 0.5 ? 'indicator-up' : cell <= -0.5 ? 'indicator-down' : 'indicator-middle';
    let icon = cell >= 0.5 ? <ImArrowUpRight2 /> : cell <= -0.5 ? <ImArrowDownRight2 /> : <span>&nbsp;</span>;

    return (
      <>
        <span className={`indicator ${bgColor}`}>
          {icon}
          {convertToPercent(cell)}
        </span>
      </>
    );
  };

  const formatUSDGrowth = (cell, row) => {
    return (
      <span>
        {convertToUSDGrowth(cell)}
      </span>
    );
  };

  const formatToPercent = (cell, row) => {
    return (
      <span>
        {convertToPercent(cell)}
      </span>
    );
  };

  const barchart = (cell, row) => {
    return (
      <StackedColumnBar data={cell} />
    )
  };

  const metro = (cell, row) => {
    return (
      <>
        <img className="flag-icon" src={cell.image_url} alt={cell.name} />
        <span className="body-name">{cell.name}</span>
      </>

    )
  };

  
  const rankFormat = (cell, row, rowIndex, formatExtraData) => {
    return (
      <span>{rowIndex + 1}</span>
    );
  };
  

  // For Selecting Row ===
  const handleOnSelect = (row, isSelect, rowIndex) => {
    let modalDataByRank;
    let metroName = row.metro.name;

    setSelectedMetro(metroName);

    for (const key in mdalRankingById) {
      const element = mdalRankingById[key];
      if (element._id === row._id) {
        modalDataByRank = element
      }
    }
    if (isSelect) {
      setModalShow(true);
      setReferenceID(row._id);
      setModalData(modalDataByRank);
    }
  };

  const selectRow = {
    mode: 'radio',
    clickToSelect: true,
    hideSelectColumn: true,
    onSelect: handleOnSelect,
    classes: 'selected-row',
    bgColor: '#ecf2ff'
  };

  const rowClasses = () => {
    let classes = 'selectable';
    return classes
  };
  // For Selecting Row ===

  
  const defaultSorted = [
    {
      dataField: 'mrr_mix_percent',
      order: 'desc',
    }
  ];
  

  const columns = [
    {
      dataField: '_id',
      text: 'id',
      hidden: true
    },
    {
      dataField: '',
      text: 'Rank',
      classes: 'rank center',
      headerClasses: 'rank center',
      formatter: rankFormat,
    },
    {
      dataField: 'metro',
      text: 'METRO',
      
      
      classes: 'metro',
      headerClasses: 'metro',
      formatter: metro
    }, {
      dataField: 'stacked_column_bar_data',
      text: '',
      classes: 'sparkline-cell center',
      headerClasses: 'sparkline-cell center',
      formatter: barchart
    },
    {
      dataField: 'mrr',
      text: 'MRR',
      sort: true,
      classes: 'right',
      headerClasses: 'right',
      formatter: formatUSDGrowth
    },

    {
      dataField: 'mrr_mix_percent',
      text: 'MIX %',
      sort: true,
      classes: 'right',
      headerClasses: 'right',
      formatter: formatToPercent
    },

    {
      dataField: 'y_by_y_growth_percent',
      text: 'Y/Y % GROWTH',
      sort: true,
      formatter: indicatorPercent,
      classes: 'with-ellipsis right',
      headerClasses: 'right'
    },
    {
      dataField: 'y_to_y_usd_growth',
      text: 'Y-Y $GROWTH',
      sort: true,
      classes: 'right',
      headerClasses: 'right',
      formatter: formatUSDGrowth
    },
    {
      dataField: 'percent_total_usd_growth',
      text: '% TOTAL $ GROWTH',
      sort: true,
      classes: 'right',
      headerClasses: 'right',
      formatter: formatToPercent
    }
  ];

  const [isLoading, setIsLoading] = useState(true);
  const [tableData, setTableData] = useState([]);
  const [topTableData, setTopTableData] = useState([]);
  const [mdalRankingById, setModalRankingById] = useState([]);

  const onRankingTableFiltered = () => {
    const token = localStorage.getItem('token');
    let url = `${API_BASE}/orbis/api/customerrevenuemetro`;
    let defaultQueryParameters = `?measure=${measure_val_onApply.key}&range=${range_val_onApply.key}&hyperscaler=${hyperscaler_val_onApply}`;
    let regionParameter = region_val_onApply.length > 0 ? `&region=${region_val_onApply.join(",").toString()}` : '';
    let mapRegionParameter = map_region_val != null ? `&mapregion=${map_region_val}` : '';
    
    setIsLoading(true);

    if (isRankingTableFiltered === true) {
      axiosRetry(axios, { retries: 3 });

      axios({
        method: 'GET',
        url: url + defaultQueryParameters + regionParameter + mapRegionParameter,
        headers: {
          'Content-Type': 'application/json',
          "Authorization": `bearer ${token}`
        }
      }).then(result => {
        let metro_ranking_table = result.data.metro_ranking;
        // Uncomment only if for API testing purposes
        // console.log(result.config.url);
        setTableData(metro_ranking_table.by_rank.data);
        setTopTableData(metro_ranking_table.by_top_rank);
        setModalRankingById(metro_ranking_table.by_rank_id);
        setIsLoading(false);
      }).catch(error => {
        console.log(error);
        setTableData([]);
        setIsLoading(false);
      });

    } else {
      setIsLoading(false);
      setTableData(byRank.data);
      setTopTableData(byTopRank);
      setModalRankingById(byRankId);
    }

  };

  useEffect(() => {
    onRankingTableFiltered();
  }, [ map_region_val ]);

  const onLoadTopRanks = () => {
      return isRankingTableFiltered === true ? topTableData : byTopRank;
  };

  return (
    <Container fluid>
      <MetroModal
        show={modalShow}
        onHide={() => setModalShow(false)}
        data={modalData}
        selectedMetro={selectedMetro}
      />
      <Row className="contentDivider">
        <Col md={3} xs={12}>
          <Card className="revenue-card">
            <Card.Body>
              <Card.Title>top 10 metros by customer rev.</Card.Title>
              <Legend legend={top_ten_metros_by_total_revenue.legend} />
              <div style={{ height: '250px' }}>
                <StackedColumnChart
                  id="top_ten_metros_by_customer_revenue"
                  largerWidth={true}
                  isOneHundredPercent={true}
                  isPercentage={true}
                  insideLabel={true}
                  percentLabel={true}
                  hiddenValueAxis={true}
                  data={top_ten_metros_by_total_revenue.data}
                  legend={top_ten_metros_by_total_revenue.legend}
                  isAnimationEnabled={true}
                />
              </div>
            </Card.Body>
          </Card>
        </Col>
        <Col md={3} xs={12}>
          <Card className="revenue-card">
            <Card.Body>
              <Card.Title>Metro # By Customer rev size</Card.Title>
              <Legend legend={metro_number_by_customer_revenue_size.legend} />
              <div style={{ height: "250px" }}>
                <StackedColumnChart
                  id="metro_number_by_customer_revenue_size"
                  largerWidth={true}
                  hiddenValueAxis={true}
                  isOneHundredPercent={false}
                  isPercentage={false}
                  insideLabel={true}
                  hideLabelBullet={false}
                  data={metro_number_by_customer_revenue_size.data}
                  legend={metro_number_by_customer_revenue_size.legend}
                  isAnimationEnabled={true}
                />
              </div>
            </Card.Body>
          </Card>
        </Col>
        <Col md={3} xs={12}>
          <Card className="revenue-card">
            <Card.Body>
              <Card.Title>Metro # By Y/Y growth</Card.Title>
              <Legend legend={metro_number_by_y_by_growth.legend} />
              <div style={{ height: "250px" }}>
                <StackedColumnChart
                  id="metro_number_by_y_by_y_growth_stacked"
                  largerWidth={true}
                  hiddenValueAxis={true}
                  isOneHundredPercent={false}
                  isPercentage={false}
                  insideLabel={true}
                  hideLabelBullet={false}
                  data={metro_number_by_y_by_growth.data}
                  legend={metro_number_by_y_by_growth.legend}
                  isAnimationEnabled={true}
                />
              </div>
            </Card.Body>
          </Card>
        </Col>
        <Col md={3} xs={12}>
          <Card className="revenue-card">
            <Card.Body>
              <Card.Title>Metro # By Y-Y $Growth</Card.Title>
              <Legend legend={metro_number_by_y_to_y_usd_growth.legend} />
              <div style={{ height: "250px" }}>
                <StackedColumnChart
                  id="metro_number_by_y_by_y_growth_line"
                  largerWidth={true}
                  hiddenValueAxis={true}
                  isOneHundredPercent={false}
                  isPercentage={false}
                  insideLabel={true}
                  hideLabelBullet={false}
                  data={metro_number_by_y_to_y_usd_growth.data}
                  legend={metro_number_by_y_to_y_usd_growth.legend}
                  isAnimationEnabled={true}
                />
              </div>
            </Card.Body>
          </Card>
        </Col>
      </Row>
      <Row className="contentDivider" style={{ paddingTop: "30px" }}>
        <Col md={6}>
          <Card className="revenue-card" style={{ minHeight: "420px" }} >
            <Card.Body>
              <Card.Title>deployment map</Card.Title>
              { isLoading ? <div className={"map-loader"}> <Loader /> </div> : null }
              <>             
                <Legend legend={deploymentMap.legend} />
                <div style={{ height: "520px ", paddingTop: "2rem" }}>
                  <MapChart
                    id={"customer_revenue_deployment_map"}
                    largerWidth={true}
                    data={deploymentMap.data}
                    setMapRegion={set_map_region_val}
                    setIsRankingTableFiltered={setIsRankingTableFiltered}
                  />
                </div>
              </> 
            </Card.Body>
          </Card>
        </Col>
        <Col md={6}>
          <Card className="revenue-card" style={{minHeight: "420px"}}>
          <Card.Body>
          <Card.Title>metro ranking</Card.Title>
          {isLoading ? <Loader /> : 
            <>
              <Legend legend={byRank.legend} />
              <div className="pb-5"></div>
                <CustomTable
                  tableName='metro-ranking scrollable'
                  data={ byRank.data === undefined ? [] : tableData }
                  selectRow={selectRow} // For Selecting Row ===
                  defaultSorted={defaultSorted} 
                  rowClasses={rowClasses}
                  columns={columns}
                />
              <Table responsive className="separate-table">
                <tbody>
                  {
                    onLoadTopRanks().map((data, i) => {
                      return (
                        <tr key={i} className={i === 0 ? 'border-head height1' : 'height1'}>
                          <td className="rank"></td>
                          <td className="metro">{data.metro}</td>
                          <td className="sparkline-cell">
                            <StackedColumnBar data={data.stacked_column_bar_data} />
                          </td>
                          <td className="text-center">{formatUSDGrowth(data.mrr)}</td>
                          <td className="text-right">{formatToPercent(data.mix_percent)}</td>
                          <td className="text-right">
                              {indicatorPercent(data.y_by_y_growth_percent)}
                          </td>
                          <td className="text-right">{formatUSDGrowth(data.y_to_y_usd_growth)}</td>
                          <td className="text-right">{formatToPercent(data.percent_total_usd_growth)}</td>
                        </tr>
                      )
                    })
                  }
                </tbody>
              </Table>
            </>
          }
          </Card.Body>
          </Card>
        </Col>
      </Row>
    </Container>
  );
};

export default MetroContent;