import React, { useState, useEffect } from 'react';

import PageHeader from '../../components/pageHeader/PageHeader';

import Overview from './Overview';
import Growth from './Growth';
import Metro from './Metro';
import Lighthouse from './Lighthouse';

import './customerCount.scss';


const CustomerCount = (props) => {
  let path = props.location.pathname;
  const currentPath = path.substring(path.lastIndexOf('/') + 1);
  const [activePage, setActivePage] = useState(currentPath);

  const handleActivePage = (page) => {
    setActivePage(page)
  }

  const handleBreadcrubsPreviousLink = () => {
    setActivePage('Overview')
  }

  useEffect(() => {
    setActivePage(currentPath)
  }, [currentPath])

  const breadcrumbs = [
    {
      path: '/analytics-dashboard/page',
      label: 'ANALYTICS DASHBOARD'
    },
    {
      path: path === '/analytics-dashboard/customer-count/overview' ? null : '/analytics-dashboard/customer-count/overview',
      label: 'Customer Count'
    },
    {
      label: activePage,
    }
  ]

  const innerPage = [
    {
      path: '/analytics-dashboard/customer-count/overview',
      label: 'Overview'
    },
    {
      path: '/analytics-dashboard/customer-count/growth',
      label: 'Growth'
    },
    {
      path: '/analytics-dashboard/customer-count/metro',
      label: 'Metro'
    },
    {
      path: '/analytics-dashboard/customer-count/lighthouse',
      label: 'Lighthouse'
    }
  ]

  const [activeTab, setActiveTab] = useState('g2000');

  return (
    <div className="customer-count-page">
      <PageHeader
        title="CUSTOMER COUNT"
        color="dark-orange"
        innerPage={innerPage}
        breadcrumbs={breadcrumbs}
        previousPage="/analytics-dashboard/page"
        handleActivePage={handleActivePage}
        handleBreadcrubsPreviousLink={handleBreadcrubsPreviousLink}
      />
      <div className="page-wrapper">
        {currentPath === 'overview' && <Overview />}
        {currentPath === 'growth' && <Growth />}
        {currentPath === 'metro' && <Metro />}
        {currentPath === 'lighthouse' && <Lighthouse activeTab={activeTab} setActiveTab={setActiveTab} />}
      </div>
    </div>
  );
}

export default CustomerCount;
