import React, { useContext, useEffect } from 'react';
import moment from 'moment';
import { Switch, Route, Redirect, useHistory } from 'react-router-dom';
import Login from '../domain/login/Login';
import Signup from '../domain/signup/Signup';
import Dashboard from '../components/dashboard/Dashboard';

import AuthProvider from '../context/AuthContext';
import { AuthContext } from '../context/AuthContext';

import './global.scss';
import FakeLogin from '../domain/fakeLogin/FakeLogin';


const PrivateRoute = ({ component: Component, ...rest }) => {
  const { authState, dispatch } = useContext(AuthContext);
  let history = useHistory();
  let expiration_date = moment(localStorage.getItem('token_expiration')).utc().format("YYYY-MM-DD HH:mm:ss");
  let date_now = moment(Date.now()).utc().format("YYYY-MM-DD HH:mm:ss");

  useEffect(() => {
    (async () => {
      if (moment.duration(moment(expiration_date).diff(moment(date_now))).asHours() <= 0) {
        dispatch({
          type: "LOGOUT"
        })
        history.push('/login');
      }
    })();
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [date_now, expiration_date]);

  return (
    <Route {...rest} render={(props) => (
      authState.isAuthenticated
        ? <Component {...props} />
        : <Redirect to={{ pathname: '/login', state: { from: props.location } }} />
    )} />
  )
}

const App = () => {
  return (
    <AuthProvider>
      <Switch>
        <Route path="/login" component={Login} />
        <Route path="/sso-equinix-com" component={FakeLogin} />
        <Route path="/signup" component={Signup} />
        <PrivateRoute path="/" component={Dashboard} />
        <Route exact path="/">
          <Redirect exact from="/" to="/" />
        </Route>
        <Route path="*">
          <Redirect from="/" to="/" />
        </Route>
      </Switch>
    </AuthProvider>
  );
}

export default App;