import React, { useLayoutEffect } from 'react';
import * as am4core from "@amcharts/amcharts4/core";
import * as am4charts from "@amcharts/amcharts4/charts";
import am4themes_animated from "@amcharts/amcharts4/themes/animated";

am4core.useTheme(am4themes_animated);


const HorizontalBarChart = (props) => {
  const { id, data, isAnimationEnabled } = props;

  useLayoutEffect(() => {
    // Create chart instance
    var chart = am4core.create(id, am4charts.XYChart);
    chart.width = am4core.percent(100);
    chart.height = am4core.percent(100);

    chart.paddingRight = 0;
    chart.paddingLeft = -10;
    chart.paddingTop = 30;
    chart.paddingBottom = 0;
    // Add data
    chart.data = data;

    //assign color
    let seriesColors = [];
    for (let i = 0; i < props.legend.length; i++) {
      seriesColors.push(am4core.color(props.legend[i].color));
    }
    chart.colors.list = seriesColors;


    var categoryAxis = chart.yAxes.push(new am4charts.CategoryAxis());
    categoryAxis.dataFields.category = "category";
    categoryAxis.renderer.grid.template.location = 0;
    categoryAxis.renderer.grid.template.strokeWidth = 0;
    categoryAxis.renderer.inversed = true;

    var valueAxis = chart.xAxes.push(new am4charts.ValueAxis());

    var series = chart.series.push(new am4charts.ColumnSeries());
    // series.columns.template.fill = color;
    series.dataFields.valueX = "value";
    series.dataFields.categoryY = "category";
    series.hiddenState.transitionDuration = 0;
    series.defaultState.transitionDuration = isAnimationEnabled ? 1000 : 0;

  });
  return (
    <div className="horizontal-bar-chart" id={id}></div>
  );
}

export default HorizontalBarChart;