import React, { useLayoutEffect } from 'react';
import * as am4core from "@amcharts/amcharts4/core";
import * as am4charts from "@amcharts/amcharts4/charts";
import am4themes_animated from "@amcharts/amcharts4/themes/animated";

am4core.useTheme(am4themes_animated);


function HorizontalfunnelChart(props) {
  useLayoutEffect(() => {
    var chart = am4core.create(props.id, am4charts.SlicedChart);
    chart.hiddenState.properties.opacity = 0; // this makes initial fade in effect

    chart.width = am4core.percent(100);
    chart.height = am4core.percent(100);

    chart.paddingRight = 0;
    chart.paddingLeft = -10;
    chart.paddingTop = 30;
    chart.paddingBottom = 0;

    chart.data = props.data;

    var series = chart.series.push(new am4charts.FunnelSeries());
    series.labels.template.fill = am4core.color("rgba(255, 255, 255, 0)");
    series.dataFields.value = "value";
    series.dataFields.category = "name";
    series.orientation = "horizontal";
    series.bottomRatio = 1;
    series.hiddenState.transitionDuration = 0;
    series.defaultState.transitionDuration = props.isAnimationEnabled ? 1000 : 0;

    var valueLabel = series.bullets.push(new am4charts.LabelBullet());
    valueLabel.label.text = "\${value}";
    valueLabel.label.fontSize = 14;
    valueLabel.label.fill = am4core.color('#ffffff');

    let seriesColors = [];
    for (let i = 0; i < props.legend.length; i++) {
      seriesColors.push(am4core.color(props.legend[i].color));
    }

    series.colors.list = seriesColors;

  });

  return (
    <div id={props.id} className="funnel-chart" ></div>
  );
}

export default HorizontalfunnelChart;

